<template>
  <div class="UpdateCustomerId">
    <TopBar></TopBar>
    <el-card class="box-card" style="max-width:80vw; margin:10px auto;">
      <h3 class="form-section-header">Update Customer Id</h3>
      <el-row class="vertical-center margin-top-20" :gutter="10">
        <el-col :span="12" class="text-left">
          Customer Id<span style="color: red">*</span>
          <input type="text" v-model="customerId" required>
        </el-col>
      </el-row>
      <el-row class="vertical-center margin-top-20" :gutter="10">
        <el-col :span="24" class="text-left">
          <el-button type="primary" @click.native="update()">Update</el-button>
        </el-col>
      </el-row>
    </el-card>
  </div>
</template>
<script>
import { serverAddr } from '../networkConfig.js';
import CripNotice from 'crip-vue-notice';
import TopBar from "./TopBar";

export default {
  name: "UpdateCustomerId",
  components: {TopBar},
  props: ["app_id", "p_id", "customer_id"],
  data(){
    return{
      customerId : null
    }
  },
  created(){
    console.log('app_id, p_id', this.app_id, this.p_id, this.customer_id);
    this.customerId = this.customer_id;
  },
  methods: {
    update(){
      if(this.customerId){
        serverAddr.post('/v1/user-update-customer-id',{customer_id: this.customerId, app_id: this.app_id, p_id: this.p_id})
        .then(response =>{

          if(response.data.status){
            new CripNotice({
              title: "Success",
              description: "Customer Id updated successfully.",
              type: "success",
              duration: 3,
            });
            this.$router.go(-1);
            this.customerId = '';
          }

        })
        .catch(function (error) {
          console.log(error);
        })
      }
    }
  },
}
</script>



