<template>
    <div class="StoreBrandList">
    <TopBar></TopBar>

    <el-card class="box-card" style="max-width:80vw; margin:10px auto;">
      <div class="clearfix">
          <h3 style="margin: 0px; display: flex; align-items: center;">
            <span @click="redirectToBack()" style="cursor: pointer">{{storeNameAttribute.length? storeNameAttribute[0].value : 'Store'}}
            <i class="el-icon-arrow-right"></i> &nbsp;</span>
            <span style="flex-grow: 1; color: #66b1ff;">Store Brands</span>
            <router-link :to="{path: `/store/assign-brand/${app_id}/${st_id}`}">
            <el-button type="success" size="mini">Add Brand</el-button>
          </router-link>
          </h3>
        </div>
    </el-card>

    <el-row>
      <el-card class="box-card" style="margin: 10px auto; width: 80vw;">

        <el-col :xs="24" :sm="24" :md="12" :lg="24" :xl="24" v-for="(profile, index) in [1,2,3]" :key="'loader' +index" style="padding: 10px;" v-if="isLoading">
          <div style="margin-bottom: 30px;">
            <content-placeholders>
              <content-placeholders-heading :img="true" />
              <content-placeholders-text :lines="2" />
            </content-placeholders>
          </div>
        </el-col>


        <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24" v-for="(brand, index) in brands" :key="index" style="margin-bottom: 15px">

              <el-row class="vertical-center text-left" style="background-color: #F1F1F1; padding: 10px 15px;">
                <el-col :span="4">
                  <h4 style="margin: 0px;"> Name </h4>
                </el-col>

                <el-col :span="8">
                  <h4 style="margin: 0px;"> Id </h4>
                </el-col>

                <el-col :span="4">
                  <h4 style="margin: 0px;"> Custom Brand Id </h4>
                </el-col>

                <el-col :span="4">
                  <h4 style="margin: 0px;"> Weight </h4>
                </el-col>

                <el-col :span="4">
                  <h4 style="margin: 0px;"> Actions </h4>
                </el-col>
              </el-row>

            <el-row class="vertical-center text-left" style="padding: 5px 10px; border-bottom: 1px solid #F1F1F1;">
              <el-col :span="4">
                {{brand.name}}&nbsp;
              </el-col>

              <el-col :span="8">
                {{brand.br_id}}&nbsp;
              </el-col>

              <el-col :span="4">
                {{brand.custom_brand_id}}&nbsp;
              </el-col>

              <el-col :span="4">
                {{brand.weight}}&nbsp;
              </el-col>

              <el-col :span="4">
                <el-button type="text"  @click.native="RemoveBrand(index)">Remove</el-button>
              </el-col>
            </el-row>


        </el-col>
      </el-card>
    </el-row>

  </div>
</template>

<script>
import { serverAddr } from '../networkConfig.js';
import CripNotice from 'crip-vue-notice';
import TopBar from "./TopBar";

export default {
  name: "StoreBrandList",
  components: {TopBar},
  props: ["app_id", "st_id", "e_id"],
  data(){
    return {
      storeNameAttribute: [],
      storeDetails: {
        store_attributes: []
      },
      storeDetailsIsLoading: false,
      brands: [],
    }
  },
  created(){
    this.isLoading = true;
    serverAddr.post('/v1/store/get/brand/', {
        'app_id':this.app_id,
        'st_id':this.st_id
    })
    .then(this.getBrandCallback)
    .catch(function (error) {
      console.log(error);
    })

    this.storeDetailsIsLoading = true;
    serverAddr.post('/v1/store/get/', {
        'app_id':this.app_id,
        'st_id':this.st_id,
        'e_id':this.e_id
    })
    .then((response)=>{
      this.storeDetailsIsLoading = false;
      this.storeDetails = response.data.data[0];
       this.storeNameAttribute = this.storeDetails.store_attributes.filter((item)=>{
        return item.key == 'name'
      })
    })
    .catch(function (error) {
      console.log(error);
    })
  },
  methods:{
    getBrandCallback: function(response){
      this.isLoading = false;
      this.brands = response.data.data;
    },
    RemoveBrand: function(index) {
      serverAddr.post('/v1/store/remove-brand/',{
        app_id: this.app_id,
        br_id: this.brands[index].br_id,
        st_id: this.st_id
      })
      .then(this.postRemoveCallback)
      .catch(function (error) {
        console.log(error);
      })
    },
    postRemoveCallback: function(response){
      if(response.data.status){
        new CripNotice({
          title: "Success",
          description: "Association Removed successfully.",
          className: "notices",
          type: "success",
          duration: 3,
        });
        serverAddr.post('/v1/store/get/brand/', {
            'app_id':this.app_id,
            'st_id':this.st_id
        })
        .then(this.getBrandCallback)
        .catch(function (error) {
          console.log(error);
        })
      }
    },
    redirectToBack(){
      this.$router.go(-1);
    }
  }
}
</script>
<style scoped>
.StoreBrandList{
  font-size: 14px;
}
</style>



