<template>
  <div class="EngagementZoneCreate">
    <TopBar></TopBar>

    <el-card class="box-card" style="max-width:50vw; margin:0 auto;">
      <h3> Engagement Zone - Create/ Edit</h3>

      <el-row>
        <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24" v-for="(loader,index) in [1,2,3,4,5,6,7]" :key="'loader' + index" style="margin-bottom: 25px;" v-if="isLoaderLoading">
          <content-placeholders>
            <!-- <content-placeholders-heading :img="true" /> -->
            <content-placeholders-text :lines="2" />
          </content-placeholders>
        </el-col>
      </el-row>

      <div class="text item" v-if="isFormLoading">
        <el-row class="vertical-center margin-10">
          <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8" class="form__label">
            Name <span style="color: red;">*</span>
          </el-col>
          <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16">
            <div class="form-group" :class="{ 'form-group--error': $v.name.$error }">
              <input style="width:80%;" type="text" placeholder="e.g. Rcity mal" v-model="name" class="form__input" @input="setName($event.target.value)">
            </div>
            <div class="error" v-if="!$v.name.required">Field is required</div>
          </el-col>
        </el-row>

        <el-row class="vertical-center margin-10">
          <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8">
            Radius <span style="color: red;">*</span> <span class="units"> in meters</span>
          </el-col>
          <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16">
            <div class="form-group" :class="{ 'form-group--error': $v.radius.$error }">
              <input style="width:80%;" type="text" v-model="radius" class="form__input" @input="setRadius($event.target.value)">
            </div>
            <div class="error" v-if="!$v.radius.required">Field is required</div>
            <div class="error" v-if="!$v.radius.minValue">Raidus must be atleast {{$v.radius.$params.minValue.min}} km.</div>
          </el-col>
        </el-row>

        <el-row class="vertical-center margin-10">
          <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8">
            Loitering Delay <span style="color: red;">*</span>
            <span class="units"> in milliseconds</span>
          </el-col>
          <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16">
            <div class="form-group" :class="{ 'form-group--error': $v.loitering_delay.$error }">
              <input style="width:80%;" type="text" v-model="loitering_delay" class="form__input" @input="setLoiteringDelay($event.target.value)">
            </div>
            <div class="error" v-if="!$v.loitering_delay.required">Field is required</div>
            <div class="error" v-if="!$v.loitering_delay.minValue">Loitering Delay must be atleast {{$v.loitering_delay.$params.minValue.min}} s.</div>
          </el-col>
        </el-row>

        <el-row class="vertical-center margin-10">
          <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8">
            Location <span style="color: red;">*</span>
            <span class="units">Lat/Lng</span>
          </el-col>
          <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16">
            <div class="form-group" :class="{ 'form-group--error': $v.location.$error }">
              <input style="width:80%;" type="text" placeholder="e.g. 19.1208854, 72.9096773" v-model="location" class="form__input" @input="setLocation($event.target.value)">
            </div>
            <div class="error" v-if="!$v.location.required">Field is required</div>
            <div class="error" v-if="!$v.location.isLocation">Please enter valid input (lat, lng)</div>
          </el-col>
        </el-row>

        <el-row class="vertical-center margin-10">
          <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8">
            Valid Until <span style="color: red;">*</span>
          </el-col>
          <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16">
            <div class="form-group" :class="{ 'form-group--error': $v.expiry_timestamp.$error }">
              <el-date-picker
                v-model="expiry_timestamp"
                type="datetime"
                :format="'dd-MM-yyyy HH:mm'"
                placeholder="Select date and time"
                @input="setExpiryTimestamp(expiry_timestamp)">
              </el-date-picker>
              <!-- <input style="width:80%;" type="datetime-local" v-model="expiry_timestamp" class="form__input" @input="setExpiryTimestamp($event.target.value)"> -->
            </div>
            <div class="error" v-if="!$v.expiry_timestamp.required">Field is required</div>
            <div class="error" v-if="!$v.expiry_timestamp.greaterThanCurrentTime">Valid Until must be greater than current time.</div>
          </el-col>
        </el-row>

        <el-row class="vertical-center margin-10">
          <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8">
            Image url
          </el-col>
          <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16">
            <div class="form-group">
              <input style="width:80%;" type="text" v-model="img_url" class="form__input">
            </div>
          </el-col>
        </el-row>

        <el-row class="vertical-center margin-10">
          <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8">
            Mute Duration <span style="color: red;">*</span>
          </el-col>
          <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16">
            <div class="form-group" :class="{ 'form-group--error': $v.mute_duration.$error }">
              <input style="width:80%;" type="number" placeholder="Mute notification for days" v-model="mute_duration" min="0" max="7" class="form__input" @input="setMuteDuration($event.target.value)">
            </div>
            <div class="error" v-if="!$v.mute_duration.required">Field is required</div>
            <div class="error" v-if="!$v.mute_duration.minValue">Mute duration should be between 0-7</div>
            <div class="error" v-if="!$v.mute_duration.maxValue">Mute duration should be between 0-7</div>
          </el-col>
        </el-row>

        <el-row class="vertical-center margin-10">
          <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8">
          </el-col>
          <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16">
            <input type="checkbox" value="true" v-model="is_active">
            Is Active
          </el-col>
        </el-row>

        <el-row class="vertical-center" style="text-align:center; margin-top: 25px;">
          <el-col :xs="24" :sm="6" :md="6" :lg="6" :xl="6">
            <el-button type="primary" @click.native="save()">Save</el-button>
          </el-col>
        </el-row>

      </div>
    </el-card>

  </div>
</template>

<script>
import { serverAddr } from '../networkConfig.js';
import CripNotice from 'crip-vue-notice';
import TopBar from "./TopBar";
import { required, minValue, maxValue } from 'vuelidate/lib/validators'

const greaterThanCurrentTime = (value) => new Date(value) > new Date()
const isLocation = (value) => value.split(",")[0]!='' && value.split(",")[0]!=undefined && value.split(",")[1]!='' && value.split(",")[1]!=undefined && !isNaN(parseInt(value.split(",")[0].trim())) && !isNaN(parseInt(value.split(",")[1].trim()))

export default {
  name: "EngagementZoneCreate",
  components: { TopBar },
  props: ["app_id", "g_id", "g_name", "id"],
  data() {
    return {
      mute_duration: 0,
      isLoaderLoading : false,
      isFormLoading : false,
      name: this.g_name,
      location: '',
      img_url: '',
      is_active: true,
      radius: 200,
      loitering_delay: 10000,
      expiry_timestamp: new Date(new Date().setFullYear(new Date().getFullYear()+1)),
    }
  },
  validations: {
    name: {
      required
    },
    location: {
      required,
      isLocation
    },
    radius: {
      required,
      minValue: minValue(0.5)
    },
    loitering_delay: {
      required,
      minValue: minValue(0.5)
    },
    mute_duration: {
      required,
      minValue: minValue(0),
      maxValue: maxValue(7)
    },
    expiry_timestamp: {
      required,
      greaterThanCurrentTime
    },
  },
  created() {
    this.isLoaderLoading = true;
    this.isFormLoading = false;

    serverAddr.post('/v1/geo-zone/get/',{'app_id':this.app_id, 'g_id':this.g_id})
      .then((response)=>{
        console.log('response', response);
        this.location = response.data.data[0].location;
      })
      .catch(function (error) {
        console.log(error);
      })

    if (this.id) {
      serverAddr.post('/v1/engagement-zone/get/', {
        g_id: this.g_id,
        app_id: this.app_id,
        e_id: this.id
      })
      .then(this.getEngagementzoneCallBack)
      .catch(function (error) {
        console.log(error);
      })
    } else {
      this.isLoaderLoading = false;
      this.isFormLoading = true;
    }
  },
  methods: {
    setMuteDuration(value) {
      this.mute_duration = value
      this.$v.mute_duration.$touch()
    },
    setName(value) {
      this.name = value
      this.$v.name.$touch()
    },
    setRadius(value) {
      this.radius = value
      this.$v.radius.$touch()
    },
    setLoiteringDelay(value) {
      this.loitering_delay = value
      this.$v.loitering_delay.$touch()
    },
    setExpiryTimestamp(value) {
      this.expiry_timestamp = value
      this.$v.expiry_timestamp.$touch()
    },
    setLocation(value) {
      this.location = value
      this.$v.location.$touch()
    },

    convertUTCDateToLocalString: function (date) {
      var offset = new Date().getTimezoneOffset()*60*1000;
      var newDate = new Date(date - offset);
      return new Date(newDate).toISOString().replace("Z","");
    },

    getEngagementzoneCallBack: function (response) {

      this.isLoaderLoading = false;
      this.isFormLoading = true;

      this.name = response.data.data[0].name;
      this.location = response.data.data[0].location;
      this.is_active = response.data.data[0].is_active;
      this.img_url = response.data.data[0].img_url;
      this.radius = response.data.data[0].radius;
      this.loitering_delay = response.data.data[0].loitering_delay;
      this.mute_duration = response.data.data[0].mute_duration;
      // this.expiry_timestamp = this.convertUTCDateToLocalString(response.data.data[0].expiry_timestamp);
      this.expiry_timestamp = new Date(response.data.data[0].expiry_timestamp);
    },
    save: function () {
      this.$v.$touch();
      if (this.$v.$invalid) {
        } else {
          if (!this.id) {
          console.log('if', this.id);
            serverAddr.post('/v1/engagement-zone/create/', {
              app_id: this.app_id,
            g_id: this.g_id,
            properties: {
              name: this.name,
              location: this.location,
              img_url: this.img_url,
              is_active: this.is_active,
              radius: parseFloat(this.radius),
              loitering_delay: parseFloat(this.loitering_delay),
              expiry_timestamp: new Date(this.expiry_timestamp).getTime(),
              mute_duration : Number(this.mute_duration)
            }
          })
            .then(this.postSaveCallBack)
            .catch(function (error) {
              console.log(error);
            })
        } else {
          console.log('else', this.id);
          serverAddr.post('/v1/engagement-zone/update/', {
            app_id: this.app_id,
            g_id: this.g_id,
            e_id: this.id,
            properties: {
              name: this.name,
              location: this.location,
              img_url: this.img_url,
              is_active: this.is_active,
              radius: parseFloat(this.radius),
              loitering_delay: parseFloat(this.loitering_delay),
              expiry_timestamp: new Date(this.expiry_timestamp).getTime(),
              mute_duration : Number(this.mute_duration)
            }
          })
          .then(this.postSaveCallBack)
          .catch(function (error) {
            console.log(error);
          })
        }
      }
    },
    postSaveCallBack: function (response) {
      console.log("Response: ", response);
      if (response.data.status) {

        var msg = this.id ? "Engagement Zone updated successfully." : "Engagement Zone created successfully."
        new CripNotice({
          title: "Success",
          description: msg,
          className: "notices",
          type: "success",
          duration: 3,
        });
        this.$router.push({path:`/engagement-zone-list/${this.app_id}/${this.g_id}`});
      }
    }
  },
}
</script>
<style scoped>
.EngagementZoneCreate{
  font-size: 14px;
}
input{
  padding: 10px 15px;
}
.units{
  display: block; font-size: 0.9em; color: #333; font-weight: bold;
}
</style>



