export default {
  namespaced: true,
  state: {
    storeNamesMap: [],
    brandNamesMap: [{br_id: 'default', brand_name: 'default'}],
    profileNamesMap: [],
    brandList: []
  },
  mutations: {
    clearstore: (state, payload) => {
      state.storeNamesMap = [];
      state.brandNamesMap = [{br_id: 'default', brand_name: 'default'}];
      state.profileNamesMap = [];
    },
    setStoreNamesMap: (state, payload) => {
      state.storeNamesMap = payload
    },
    setBrandNamesMap: (state, payload) => {
      state.brandNamesMap = payload
    },
    setProfileNamesMap: (state, payload) => {
      state.profileNamesMap = payload
    },

    clearStoreNamesMap: (state, payload) => {
      state.storeNamesMap = []
    },
    clearBrandNamesMap: (state, payload) => {
      state.brandNamesMap = [{br_id: 'default', brand_name: 'default'}];
    },
    clearProfileNamesMap: (state, payload) => {
      state.profileNamesMap = []
    },

    updateStoreNamesMap: (state, payload) => {
      var isPresent = _.some(state.storeNamesMap, payload);
      if(!isPresent){
        state.storeNamesMap.push(payload);
      }
    },
    updateBrandNamesMap: (state, payload) => {
      var isPresent = _.some(state.brandNamesMap, payload);
      if(!isPresent){
        state.brandNamesMap.push(payload);
      }
    },
    updateProfileNamesMap: (state, payload) => {
      var isPresent = _.some(state.profileNamesMap, payload);
      if(!isPresent){
        state.profileNamesMap.push(payload);
      }
    },
    setBrandList: (state, payload) =>{
      state.brandList = payload;
    }
  }
}



