<template>
    <div class="BrandStaffList">
    <TopBar></TopBar>

    <el-card class="box-card" style="max-width:90vw; margin:10px auto;">
      <div class="text item">
        <h3 style="margin: 0px; display: flex; align-items: center;">
          <span @click="redirectToBack()" style="cursor: pointer">Brand List > &nbsp;</span>
          <span style="flex-grow: 1; color: #66b1ff;">Brand Staff</span>
          <router-link :to="{path: `/brand/manager/create/${app_id}/${id}`}">
            <el-button type="success" size="mini">Add Brand manager</el-button>
          </router-link>
        </h3>
      </div>
    </el-card>

    <el-card class="box-card" style="max-width:90vw; margin:10px auto;">
      <div class="text item">
        <h3 style="margin: 0px; align-items: center; color: #66b1ff;"> Brand Managers </h3>
      </div>
    </el-card>

    <el-row :gutter="15" style="margin: 0 auto; width: 90vw;">

      <el-col :xs="24" :sm="24" :md="12" :lg="8" :xl="8" v-for="(profile, index) in [1,2,3,4,5]" :key="'loader' +index" style="margin-bottom: 15px" v-if="isLoading">
        <el-card style="height: 270px;">
          <content-placeholders>
            <content-placeholders-heading :img="true" />
            <content-placeholders-text :lines="3" />
            <content-placeholders-text :lines="3" />
          </content-placeholders>
        </el-card>
      </el-col>

      <div style="border: 1px solid #ddd; text-align: center; font-size: 14px; width: 50%; margin: 0 auto; margin-top: 50px; padding: 20px;" v-if="!managerProfiles.length"> No store manager is assigned
      </div>

      <el-col :xs="24" :sm="24" :md="12" :lg="8" :xl="8" v-for="(profile, index) in managerProfiles" :key="index" style="margin-bottom: 15px">

        <el-card class="box-card" style="width: 100%;">

            <el-row class="vertical-center text-left" style="background-color: #F1F1F1; padding: 10px 15px;">
              <el-col :span="8">
                <h4 style="margin: 0px;"> Attribute </h4>
              </el-col>

              <el-col :span="16">
                <h4 style="margin: 0px;"> Value </h4>
              </el-col>
            </el-row>

            <el-row class="vertical-center text-left" style="padding: 5px 10px; border-bottom: 1px solid #F1F1F1;" v-for="(profile_attr, index) in profile.profile_attributes" :key="index">
              <el-col :span="8">
                {{profile_attr.display_name}}
              </el-col>
              <el-col :span="16">
                {{profile_attr.value}}
              </el-col>
            </el-row>

            <el-row class="vertical-center text-left" style="padding: 5px 10px; border-bottom: 1px solid #F1F1F1;">
              <el-col :span="8">
                Username
              </el-col>
              <el-col :span="16">
                {{profile.profile.username}}
              </el-col>
            </el-row>

            <div style="font-size: 0.8em; padding: 5px 10px;">
              {{profile.p_id}}
            </div>
            <div class="text-right" style="margin-top: 10px;">
              <el-button type="text"  @click.native="RemoveManager(index)">Remove</el-button>
            </div>

        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { serverAddr } from '../networkConfig.js';
import CripNotice from 'crip-vue-notice';
import TopBar from "./TopBar";

export default {
  name: "BrandStaffList",
  components: {TopBar},
  props: ["app_id", "id"],
  data(){
    return {
      isLoading : true,
      managerProfiles: [],
    }
  },
  created(){
    serverAddr.post('/v1/brand/get/manager/',{app_id: this.app_id, br_id:this.id})
    .then(this.getManagerProfileCallback)
    .catch(function (error) {
      console.log(error);
    })
  },
  methods:{
    getManagerProfileCallback: function(response){
      this.isLoading = false;
      this.managerProfiles = response.data.data;
    },
    RemoveManager: function(index) {
      serverAddr.post('/v1/profile/brand-manager/remove-brand/',{
        br_id: this.id,
        app_id: this.app_id,
        p_id: this.managerProfiles[index].profile.uuid
      })
      .then(this.postRemoveCallback)
      .catch(function (error) {
        console.log(error);
      })
    },
    postRemoveCallback: function(response){
      if(response.data.status){
        new CripNotice({
          title: "Success",
          description: "Association Removed successfully.",
          className: "notices",
          type: "success",
          duration: 3,
        });
        serverAddr.post('/v1/brand/get/manager/',{app_id: this.app_id, br_id:this.id})
        .then(this.getManagerProfileCallback)
        .catch(function (error) {
          console.log(error);
        })
      }
    },
    redirectToBack(){
      this.$router.go(-1);
    }
  }
}
</script>



