<template>
    <div class="ProfileList">
    <TopBar></TopBar>

    <el-row :gutter="15" style="margin: 0 auto; width: 90vw;">
      <el-col :xs="24" :sm="24" :md="12" :lg="8" :xl="8" v-for="(profile, index) in [1,2,3,4,5]" :key="'loader' +index" style="margin-bottom: 15px" v-if="isLoading">
        <el-card style="height: 270px;">
          <content-placeholders>
            <content-placeholders-heading :img="true" />
            <content-placeholders-text :lines="3" />
            <content-placeholders-text :lines="3" />
          </content-placeholders>
        </el-card>
      </el-col>

      <el-col :xs="24" :sm="24" :md="12" :lg="8" :xl="8" v-for="(profile, index) in profiles" :key="index" style="margin-bottom: 15px">
        <el-card class="box-card">
          <el-dialog title="Add Location" :visible.sync="dialogVisible">
            <input type="text" v-model="location" placeholder="latitude, longitude">
            <span slot="footer" class="dialog-footer">
              <el-button @click="dialogVisible = false">Cancel</el-button>
              <el-button type="primary" @click="updateLocation()">Confirm</el-button>
            </span>
          </el-dialog>

          <div style="width: 100%; height: 180px; overflow-y: auto;">
            <el-row class="vertical-center text-left" style="background-color: #F1F1F1; padding: 10px 15px;">
              <el-col :span="8">
                <h4 style="margin: 0px;"> Attribute </h4>
              </el-col>

              <el-col :span="16">
                <h4 style="margin: 0px;"> Value </h4>
              </el-col>
            </el-row>

            <el-row class="vertical-center text-left" style="padding: 5px 10px; border-bottom: 1px solid #F1F1F1;" v-for="(profile_attr, index) in profile.profile_attributes" :key="index">
              <el-col :span="8">
                <span style="font-size: 0.7em; font-weight: bold;">{{profile_attr.display_name}}</span>
              </el-col>
              <el-col :span="16">
                {{profile_attr.value}}
              </el-col>
            </el-row>

            <el-row class="vertical-center text-left" style="padding: 5px 10px; border-bottom: 1px solid #F1F1F1;">
              <el-col :span="8">
                <span style="font-size: 0.7em; font-weight: bold;">Role</span>
              </el-col>
              <el-col :span="16">
                <p style="margin: 0px;" v-for="(role, index) in profile.roles" :key="index">{{index + 1}}. {{role.name}} </p>
              </el-col>
            </el-row>
            <el-row class="vertical-center text-left" style="padding: 5px 10px; border-bottom: 1px solid #F1F1F1;">
              <el-col :span="8">
                <span style="font-size: 0.7em; font-weight: bold;">Username</span>
              </el-col>
              <el-col :span="16">
                {{profile.profile.username}}
              </el-col>
            </el-row>
            <el-row class="vertical-center text-left" style="padding: 5px 10px; border-bottom: 1px solid #F1F1F1;" v-if="profile.profile.customer_id">
              <el-col :span="8">
                <span style="font-size: 0.7em; font-weight: bold;">Customer Id</span>
              </el-col>
              <el-col :span="24">
                {{profile.profile.customer_id}}
              </el-col>
            </el-row>

            <div style="font-size: 0.8em; padding: 5px 10px;">
              {{profile.p_id}}
            </div>
          </div>

            <div class="text-right" style="margin-top: 10px;">
              <el-button type="text" v-if="checkRole(profile, 'Consumer')" @click.native="openProfilePreferredBrands(profile)">Preferred Brands</el-button>
              <el-button type="text"  @click.native="edit(index)">Edit</el-button>
              <el-button type="text"  @click.native="goToUpdateCustomerId(index)">Update Customer Id</el-button>
              <!-- <el-button type="primary"  @click.native="remove(index)">Delete</el-button> -->

              <!-- Need to check if geo-point is present for Profile in profile_attribute -->
              <!-- <el-button type="text"  @click.native="openDialog(index)">Update Location</el-button> -->
              <!-- <el-button type="text"  @click.native="deleteProfile(profile, index)">Delete</el-button> -->
              <el-button type="text"  @click.native="deleteProfileDialogVisible = true; selectedIndexForDelete = index; selectedProfileForDelete = profile">Delete</el-button>
            </div>

        </el-card>
      </el-col>
    </el-row>
    <el-dialog title="Delete Profile" :visible.sync="deleteProfileDialogVisible">
      <h3 style="color: tomato;">Are you sure? This action can not be reversed.</h3>
      <span slot="footer" class="dialog-footer">
        <el-button @click="deleteProfileDialogVisible=false; selectedIndexForDelete=null; selectedProfileForDelete=null">Cancel</el-button>
        <el-button type="primary" @click="deleteProfile()">Confirm</el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
import { serverAddr } from '../networkConfig.js';
import CripNotice from 'crip-vue-notice';
import TopBar from "./TopBar";

export default {
  name: "ProfileList",
  components: {TopBar},
  props: ["app_id"],
  data(){
    return {
      selectedProfileForDelete : null,
      selectedIndexForDelete : null,
      deleteProfileDialogVisible: false,
      isLoading : false,
      profiles: [],
      selected_id: '',
      location: '',
      dialogVisible: false
    }
  },
  created(){
    this.isLoading = true;
    this.initProfileData();
  },
  computed: {
    authUser() {
      return this.$store.state.general.authUser;
    }
  },
  methods:{
    goToUpdateCustomerId: function(index){
      if(this.profiles[index].profile.customer_id == null){
        this.profiles[index].profile.customer_id = '';
      }
      this.$router.push({path: `/update-customer-id/${this.app_id}/${this.profiles[index].profile.uuid}/${this.profiles[index].profile.customer_id}`})
    },
    deleteProfile: function(){
      this.deleteProfileDialogVisible = false;
      console.log('delete profile', this.selectedProfileForDelete);
      console.log('delete profile index', this.selectedIndexForDelete);
      serverAddr.post('/v1/profile/delete/',{app_id:this.app_id, p_id: this.selectedProfileForDelete.p_id})
      .then(response => {
        console.log('deleteProfile: ', response);
        if(response.data.status){
          this.profiles.splice(this.selectedIndexForDelete, 1);
        }
      })
      .catch(function (error) {
        console.log(error);
      })
    },
    checkRole: function(profile, roleName) {
      if(!profile.roles){
        return false;
      }
      return profile.roles.some(function(el) {
        return el.name === roleName;
      });
    },
    initProfileData: function(response){
    serverAddr.post('/v1/profile/',{'app_id':this.app_id})
      .then(this.getProfileCallback)
      .catch(function (error) {
        console.log(error);
      })
    },
    openProfilePreferredBrands: function(profile){
      console.log('profile', profile);
      this.$router.push({path: `/profile-preferred-brands/app_id/${this.app_id}/p_id/${profile.p_id}/${profile.profile.username}`});
    },
    getProfileCallback: function(response){
      this.isLoading = false;
      this.profiles = response.data.data;
    },
    edit: function(index){
      this.$router.push({path: `/profile-create/${this.app_id}/${this.profiles[index].profile.uuid}`})
    },
    openDialog: function(index){
      this.selected_id = this.profiles[index].profile.uuid;
      this.dialogVisible = true;
    },
    updateLocation: function(){
      this.dialogVisible = false;
      serverAddr.post('/v1/add-location/',
      {
        id:this.selected_id,
        properties:{
          location:this.location
        }
      })
      .then(this.postUpdateCallback)
      .catch(function (error) {
        console.log(error);
      })
    },
    remove: function(index) {
    serverAddr.post('/v1/profile/delete/',{p_id:this.profiles[index].p_id})
      .then(this.postRemoveCallback)
      .catch(function (error) {
        console.log(error);
      })
    },
    postUpdateCallback: function(response){
      if(response.data.status){
        this.location = '';
        new CripNotice({
          title: "Success",
          description: "Location Updated successfully.",
          className: "notices",
          type: "success",
          duration: 3,
        });
      }
    },
    postRemoveCallback: function(response){
      if(response.data.status){
        new CripNotice({
          title: "Success",
          description: "Profile deleted successfully.",
          className: "notices",
          type: "success",
          duration: 3,
        });
        serverAddr.post('/v1/profile/')
        .then(this.getProfileCallback)
        .catch(function (error) {
          console.log(error);
        })
      }
    }
  }
}
</script>



