<template>
  <div class="GeozoneList">
    <TopBar></TopBar>

      <el-card class="box-card" style="max-width:80vw; margin:10px auto;">
        <div slot="header" class="clearfix">
          <h3 style="margin: 0px; display: flex; align-items: center;">
            <span @click="redirectToBack()" style="cursor: pointer">App Dashboard > &nbsp;</span>
            <span style="flex-grow: 1; color: #66b1ff;">Geozones</span>
            <router-link :to="{path: `/geozone-create/${app_id}/`}">
            <el-button type="success" size="mini">New Geozone</el-button>
            </router-link>
            </h3>
        </div>
        <div class="text item">
          <el-row>
            <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24" v-for="(loader,index) in [1,2,3,4,5,6]" :key="'loader' + index" style="margin-bottom: 25px;" v-if="isLoading">
              <content-placeholders>
                <!-- <content-placeholders-heading :img="true" /> -->
                <content-placeholders-text :lines="2" />
              </content-placeholders>
            </el-col>
          </el-row>

          <el-row style="font-size: 0.8em; border-bottom: 1px solid #eee; padding-bottom: 10px;" class="vertical-center margin-bottom-10 bold" v-if="!isLoading">
            <el-col :xs="24" :sm="5" :md="5" :lg="5" :xl="5">
              Name
            </el-col>
            <el-col :xs="24" :sm="4" :md="4" :lg="4" :xl="4">
              Location
            </el-col>
            <el-col :xs="24" :sm="3" :md="3" :lg="3" :xl="3">
              City
            </el-col>
            <el-col :xs="24" :sm="3" :md="3" :lg="3" :xl="3">
              Loitering Delay
            </el-col>
            <el-col :xs="24" :sm="3" :md="3" :lg="3" :xl="3">
              Status
            </el-col>
             <el-col :xs="24" :sm="2" :md="2" :lg="2" :xl="2">
              Size
            </el-col>
            <el-col :xs="24" :sm="4" :md="4" :lg="4" :xl="4">
                Actions
            </el-col>
          </el-row>

          <el-row class="vertical-center margin-bottom-10">
            <el-col :span="24" v-if="resultLength0 && !isLoading">
              <h5 style="color: #666;"> No Result Found...</h5>
            </el-col>
          </el-row>

          <el-row style="font-size: 0.8em;" class="vertical-center margin-bottom-10" v-for="(geozone, index) in geozones" :key="index">
            <el-col :xs="24" :sm="5" :md="5" :lg="5" :xl="5">
              {{geozone.name}}
            </el-col>
            <el-col :xs="24" :sm="4" :md="4" :lg="4" :xl="4">
              {{geozone.location}}
            </el-col>
            <el-col :xs="24" :sm="3" :md="3" :lg="3" :xl="3">
              {{geozone.city}}
            </el-col>
            <el-col :xs="24" :sm="3" :md="3" :lg="3" :xl="3">
              {{geozone.loitering_delay}}&nbsp;<span v-if="geozone.loitering_delay">msecs</span>
            </el-col>
            <el-col :xs="24" :sm="3" :md="3" :lg="3" :xl="3">
              {{geozone.is_active? 'Active': 'Disabled'}}
            </el-col>
             <el-col :xs="24" :sm="2" :md="2" :lg="2" :xl="2">
              {{geozone.radius}}m
            </el-col>
            <el-col :xs="24" :sm="4" :md="4" :lg="4" :xl="4">
              <span>
                <el-button type="text" @click.native="remove(index)">Delete</el-button> |
              </span>
              <span>
              </span>
                <el-button type="text" @click.native="edit(index)">Edit</el-button> |
              <span>
                <el-button type="text" @click.native="openEngagementZoneList(geozone)">Details</el-button>
              </span>
            </el-col>
          </el-row>
        </div>
      </el-card>

  </div>
</template>

<script>
import { serverAddr } from '../networkConfig.js';
import CripNotice from 'crip-vue-notice';
import TopBar from "./TopBar";

export default {
  name: "GeozoneList",
  props: ["app_id"],
  components: { TopBar },
  data() {
    return {
      resultLength0: false,
      isLoading: false,
      geozones: []
    }
  },
  created() {
    this.isLoading = true;
    this.resultLength0 = false;
    serverAddr.post('/v1/geo-zone/',{app_id: this.app_id})
    .then(this.getGeozoneCallback)
    .catch(function (error) {
      console.log(error);
    })
  },
  methods: {
    openEngagementZoneList: function (geozone) {
      this.$router.push({path: `/engagement-zone-list/${this.app_id}/${geozone.g_id}`})
    },
    getGeozoneCallback: function (response) {
      this.isLoading = false;
      if(response.data.status){
        this.geozones = response.data.data;
        if(!this.geozones.length){
          this.resultLength0 = true;
        }
      }
    },
    edit: function (index) {
      var id = this.geozones[index].g_id;
      this.$router.push({ path: `/geozone-create/${this.app_id}/${id}` })
    },
    remove: function(index) {
    serverAddr.post('/v1/geo-zone/delete/',{app_id:this.app_id, g_id:this.geozones[index].g_id})
      .then(this.postRemoveCallback)
      .catch(function (error) {
        console.log(error);
      })
    },
    postRemoveCallback: function(response){
      if(response.data.status){
        new CripNotice({
          title: "Success",
          description: "Zone deleted successfully.",
          className: "notices",
          type: "success",
          duration: 3,
        });
        serverAddr.post('/v1/geo-zone/',{app_id: this.app_id})
        .then(this.getGeozoneCallback)
        .catch(function (error) {
          console.log(error);
        })
      }
    },
    redirectToBack(){
      this.$router.go(-1);
    }
  }
}
</script>



