<template>
  <div class="GlobalNotificationPanel">
    <TopBar></TopBar>
    <el-card class="box-card" style="margin: 0px 20px; min-height: 90vh;">
  <el-row :gutter="10" style="padding: 0px 20px;">
    <el-col :span="24">
      &nbsp;
        <!-- <h4>Search Zone/ Stores to create global notifications...</h4> -->
    </el-col>
    <!-- <el-row :gutter="10" style="padding: 20px 20px 0 20px; margin-bottom: 20px;"> -->
    <el-col :span="24" style="padding: 0px 0px 20px 0px;">
        <el-radio v-model="search_for" size="small" label="GEOZONE" border>Zone Notification</el-radio>
        <el-radio v-model="search_for" size="small" label="STORE" border>Store Notification</el-radio>
    </el-col>
  <!-- </el-row> -->
    <el-col :span="24" style="padding-bottom: 20px;">
        <el-radio v-model="search_with" label="COUNTRY">Search by Country</el-radio>
        <el-radio v-model="search_with" label="STATE">Search by State</el-radio>
        <el-radio v-model="search_with" label="CITY">Search by City</el-radio>
        <el-radio v-model="search_with" label="GEOZONE">Search by Zone</el-radio>
        <!-- <el-radio v-show="search_for=='STORE'" v-model="search_with" label="EZONE">Search by eZone</el-radio> -->
        <el-radio v-show="search_for=='STORE'" v-model="search_with" label="STORE">Search by Store</el-radio>
    </el-col>
    <el-col :span="4">
        <label for="">Country</label>
        <!-- <input class="form-input" type="text" placeholder="City" v-model="city"> -->
        <multiselect v-model="country"
                  deselect-label=""
                  select-label=""
                  :show-labels="false"
                  placeholder="Select Country"
                  :options="all_country_dropdown"
                  :searchable="true"
                  :allow-empty="true">
      </multiselect>
    </el-col>
    <el-col :span="4">
        <label for="" v-show="showStateDropdown">State</label>
        <!-- <input class="form-input" type="text" placeholder="State" v-model="state"> -->
        <multiselect v-model="state"
                  deselect-label=""
                  select-label=""
                  :show-labels="false"
                  placeholder="Select State"
                  :options="all_states_dropdown"
                  :searchable="true"
                  :allow-empty="true"
                  v-show="showStateDropdown">
      </multiselect>
    </el-col>
    <el-col :span="4">
        <label for="" v-show="showCityDropdown">City</label>
        <!-- <input class="form-input" type="text" placeholder="City" v-model="city"> -->
        <multiselect v-model="city"
                  deselect-label=""
                  select-label=""
                  :show-labels="false"
                  placeholder="Select City"
                  :options="all_city_dropdown"
                  :searchable="true"
                  :allow-empty="true"
                  v-show="showCityDropdown">
      </multiselect>
    </el-col>
    <el-col :span="4">
      <label for="" v-show="showGeozoneDropdown">Zone</label>
      <multiselect v-model="selected_geozone"
                  deselect-label=""
                  select-label=""
                  track-by="g_id"
                  label="name"
                  placeholder="Select zone"
                  :options="geozone_dropdown"
                  :searchable="true"
                  :allow-empty="true"
                  v-show="showGeozoneDropdown">
        <template slot="singleLabel" slot-scope="{ option }">{{ option.name }}</template>
      </multiselect>
    </el-col>
    <el-col :span="4">
      <label for="" v-show="showEzoneDropdown">Engagement Zone</label>
      <multiselect v-model="selected_ezone"
                  deselect-label=""
                  select-label=""
                  track-by="e_id"
                  label="name"
                  placeholder="Select EZone"
                  :options="ezone_dropdown"
                  :searchable="true"
                  :allow-empty="true"
                  v-show="showEzoneDropdown">
        <template slot="singleLabel" slot-scope="{ option }">{{ option.name }}  <span style="font-size: 0.6em; font-weight: bold; color: #999;">#{{option.geozone_name}}</span></template>
      </multiselect>
    </el-col>
    <el-col :span="4">
        <label for="" v-show="showStoreDropdown">Stores</label>
      <multiselect v-model="selected_store"
                  deselect-label=""
                  select-label=""
                  track-by="st_id"
                  label="name"
                  placeholder="Select store"
                  :options="store_dropdown"
                  :searchable="true"
                  :allow-empty="true"
                  v-show="showStoreDropdown">
        <template slot="singleLabel" slot-scope="{ option }">{{ option.name }}</template>
      </multiselect>
    </el-col>
    <el-col :span="4">
        <!-- <input class="form-input" type="text" placeholder="Brand" v-model="brand" v-show="search_for!='GEOZONE'"> -->
        <label for="" v-show="showBrandDropdown">Brand</label>
      <multiselect v-model="selected_brand"
                  deselect-label=""
                  select-label=""
                  track-by="br_id"
                  label="name"
                  placeholder="Select brand"
                  :options="brand_dropdown"
                  :searchable="true"
                  :allow-empty="true"
                  v-show="showBrandDropdown">
        <template slot="singleLabel" slot-scope="{ option }">{{ option.name }}</template>
      </multiselect>
    </el-col>
    <el-col :span="4">
        <label for="">&nbsp;</label><br>
        <el-button type="primary"  @click.native="search()">Search</el-button>
    </el-col>
  </el-row>

  <el-row style="padding: 20px 20px;">
    <el-col :span="24">
      <div v-show="search_with== 'COUNTRY'">
        <h4> Selected Country </h4>
        <p v-for="item, index in search_query.country" :key="index">
          <i class="el-icon-circle-close-outline" style="color:red;cursor: pointer;" @click="removeQueryParam(item, index, search_query.country)"></i> &nbsp;&nbsp;&nbsp;
          {{item}}
        </p>
      </div>
      <div v-show="search_with== 'CITY'">
        <h4> Selected Cities </h4>
        <p v-for="item, index in search_query.city" :key="index">
          <i class="el-icon-circle-close-outline" style="color:red;cursor: pointer;" @click="removeQueryParam(item, index, search_query.city)"></i> &nbsp;&nbsp;&nbsp;
          {{item}}
        </p>
      </div>
      <div v-show="search_with== 'STATE'">
        <h4> Selected States </h4>
        <p v-for="item, index in search_query.state" :key="index">
          <i class="el-icon-circle-close-outline" style="color:red;cursor: pointer;" @click="removeQueryParam(item, index, search_query.state)"></i> &nbsp;&nbsp;&nbsp;
          {{item}}
        </p>
      </div>
      <div v-show="search_with== 'GEOZONE'">
        <h4> Selected Zones </h4>
        <p v-for="item, index in search_query.geozone_name" :key="index">
          <i class="el-icon-circle-close-outline" style="color:red;cursor: pointer;" @click="removeQueryParam(item, index, search_query.geozone_name)"></i> &nbsp;&nbsp;&nbsp;
          {{item}}
        </p>
      </div>
      <div v-show="search_with== 'EZONE'">
        <h4> Selected EZones </h4>
        <p v-for="item, index in search_query.ezone_name" :key="index">
          <i class="el-icon-circle-close-outline" style="color:red;cursor: pointer;" @click="removeQueryParam(item, index, search_query.ezone_name)"></i> &nbsp;&nbsp;&nbsp;
          {{item}}
        </p>
      </div>
      <div v-show="search_with== 'STORE'">
        <h4> Selected Stores </h4>
        <p v-for="item, index in search_query.store_name" :key="index">
          <i class="el-icon-circle-close-outline" style="color:red;cursor: pointer;" @click="removeQueryParam(item, index, search_query.store_name)"></i> &nbsp;&nbsp;&nbsp;
          {{item}}
        </p>
      </div>
    </el-col>
  </el-row>

  <el-row class="vertical-center margin-bottom-10">
    <el-col :span="24" v-if="resultLength0">
      <h5 style="color: #666; padding: 20px 20px; border-top:1px solid #EEE;"> No Result Found...</h5>
    </el-col>
  </el-row>

  <el-row :gutter="10" style="margin-bottom: 20px; padding: 20px 20px; border-top:1px solid #EEE; max-height: 300px; overflow-y: scroll;" v-if="search_result.length > 0">
    <el-col :span="24">
        <span style="font-size: 1.1em; font-weight: bold;">{{search_result.length}} {{response_title}} Matched... </span>
        <el-button  v-if="search_result.length > 0" type="default" size="mini" @click.native="show_new_notification_form = true" style="margin-left: 20px;">Create Notification for {{search_result.length}} {{response_title}}<span v-if="search_result.length > 1">s</span> </el-button>
        <br/>
        <br/>
        <div v-for="(result, index) in search_result" :key="index" :class="{'gray': index % 2 == 0}" style="padding: 10px 10px; border-bottom: 1px solid #EEE;">
            <b>{{result.name}}</b> | <span style="font-size: 0.7em;">{{result.uuid}} </span>
        </div>
    </el-col>
  </el-row>


  <el-row :gutter="10" style="padding: 20px 20px; border-top:1px solid #EEE;" v-if="show_new_notification_form && search_result.length > 0">
      <h4>New Notification</h4>
        <el-col :span="24">
      <label for="">Text<span style="color: red">*</span></label>
      <input class="form-input" v-model="notification.text" auto-complete="off" :placeholder="textPlaceholder"></input>
      <p style="font-size: 0.8em; color: #999; margin-top: 0px; font-weight: bold;" class="margin-bottom-15">available wildcards <span v-for="item in available_wildcards">{{item}}, </span></p>
    </el-col>
    <el-col :span="24">
      <label for="">Sub-Text</label>
      <input class="form-input margin-bottom-15" v-model="notification.sub_text" auto-complete="off" :placeholder="subTextPlaceholder"></input>
      <p style="font-size: 0.8em; color: #999; margin-top: 0px; font-weight: bold;" class="margin-bottom-15">available wildcards <span v-for="item in available_wildcards_sub_text">{{item}}, </span></p>
    </el-col>

    <el-col :span="12">
      <label for="">Image URL</label>
      <input class="form-input margin-bottom-15" v-model="notification.image_url" auto-complete="off" placeholder="image_url"></input>
    </el-col>
    <el-col :span="12">
      <label for="">Action URL</label>
      <input class="form-input margin-bottom-15" v-model="notification.action_url" auto-complete="off" placeholder="action_url"></input>
    </el-col>

    <el-col :span="12">
      <label for="">Start Date<span style="color: red">*</span></label>
      <br>
      <el-date-picker
        v-model="notification.validity_start"
        type="datetime"
        :format="'dd-MM-yyyy HH:mm'"
        placeholder="Select date and time"
        @input="set_validity_end(notification.validity_start)">
      </el-date-picker>
      <!-- <input type="datetime-local" class="form-input margin-bottom-15" v-model="notification.validity_start" auto-complete="off" placeholder="validity start"></input> -->
    </el-col>
    <el-col :span="12">
      <label for="">Valid Untill<span style="color: red">*</span></label>
      <br>
      <el-date-picker
        v-model="notification.validity_end"
        type="datetime"
        :format="'dd-MM-yyyy HH:mm'"
        placeholder="Select date and time"
        >
      </el-date-picker>
      <!-- <input type="datetime-local" class="form-input margin-bottom-15" v-model="notification.validity_end" auto-complete="off" placeholder="validity end"></input> -->
    </el-col>

    <el-col :span="8" class="margin-bottom-15">
      <label for=""> Matching Type<span style="color: red">*</span></label>
      <select v-model="notification.notification_match_type" placeholder="Please select notification match type">
        <option label="General" value="GENERAL"></option>
        <option label="Multi Match Brand" value="MULTI_MATCH_BRAND" v-show="search_for!='GEOZONE'"></option>
        <option label="Multi Match Store" value="MULTI_MATCH_STORE" v-show="search_for!='GEOZONE'"></option>
        <option label="Brand" value="BRAND" v-show="search_for!='GEOZONE' && brand_selected_for_current_search.br_id"></option>
      </select>
    </el-col>
    <el-col :span="8" class="margin-bottom-15">
      <label for=""> Notification Type<span style="color: red">*</span></label>
      <select v-model="selected_notification_type" placeholder="Please select notification type" @change="showInventoryRequestInput">
        <option :label="item.name" :value="item" v-for="(item, index) in notification_types" :key="index"></option>
      </select>
    </el-col>
    <el-col :span="8" class="margin-bottom-15">
      <label for=""> Action Type<span style="color: red">*</span></label>
      <select v-model="selected_notification_action_type" placeholder="Please select notification type">
        <option v-show="search_for=='GEOZONE' && item.name!='In Store' || search_for=='STORE'" :label="item.name" :value="item" v-for="(item, index) in notification_action_types" :key="index"></option>
      </select>
    </el-col>
    <el-col :span="24" class="margin-bottom-15">
      <label>Inventory Request Params</label>
      <textarea class="form-textarea" v-if="isInventoryRequestInputVisible" v-model="inventory_request_params"></textarea>
    </el-col>
    <el-col :span="3" class="margin-bottom-15">
      <el-checkbox label="Is Active" name="isActive" v-model="notification.isActive"></el-checkbox>
    </el-col>
    <el-col :span="3" class="margin-bottom-15" v-for="(value, day) in active_days" :key="day">
      <label for="">{{day}}<span style="color: red">*</span></label>
      <input v-mask="'####-####'" class="form-input" v-model="active_days[day]"></input>
    </el-col>



    <el-col :span="24" class="margin-bottom-15">
      <el-button type="primary" @click.native="saveNotification(search_for)" :loading="create_notification_inprocess" v-if="search_for=='GEOZONE'">Save Zone Notification</el-button>
      <el-button type="primary" @click.native="saveNotification(search_for)" :loading="create_notification_inprocess" v-if="search_for=='STORE'">Save Store Notification</el-button>
      <el-button type="primary" @click.native="create_notification_inprocess = false">Cancel</el-button>
    </el-col>
  </el-row>
  </el-card>

  </div>
</template>
<script>
import { serverAddr } from "../networkConfig.js";
import CripNotice from "crip-vue-notice";
import TopBar from "./TopBar";
import Multiselect from 'vue-multiselect';

export default {
  name: "GlobalNotificationPanel",
  components: { TopBar, multiselect:Multiselect },
  props: ["app_id"],
  data() {
    return {
      inventory_request_params : '',
      isInventoryRequestInputVisible: false,
      textPlaceholder : 'Welcome {{user_name}} to {{geozone_name}}',
      subTextPlaceholder : '{{consumer_preferred_brands}} here',
      search_query: {
          country: [], //this.city ? this.city.toLowerCase().trim(): "",
          city: [], //this.city ? this.city.toLowerCase().trim(): "",
          state: [], //this.state ? this.state.toLowerCase().trim(): "",
          geozone_name: [], //this.selected_geozone ? this.selected_geozone.name: "",
          store_name: [], //this.selected_store ? this.selected_store.name: "",
          ezone_name: [], //this.selected_ezone ? this.selected_ezone.name: "",
          brand: "", //this.selected_brand ? this.selected_brand.name: ""
        },
      search_with: "GEOZONE",
      city: "",
      country: "",
      geozone_dropdown: [],
      ezone_dropdown: [],
      store_dropdown: [],
      brand_dropdown: [],
      all_country_dropdown: [],
      all_city_dropdown: [],
      selected_match_type_brands: [],
      available_wildcards: ["{{user_name}}", "{{store_name}}", "{{geozone_name}}", "{{ezone_name}}"],
      available_wildcards_sub_text: ["{{consumer_preferred_brands}}"],
      state: "",
      search_for: "GEOZONE",
      selected_geozone: null,
      selected_ezone: null,
      selected_store: null,
      selected_brand: null,
      search_result: [],
      response_title: "",
      selected_notification_type: {},
      selected_notification_action_type: {},
      notification: {
        isActive: true,
        text: '',
        sub_text: '',
        image_url: '',
        action_url: '',
        validity_start: new Date(),
        validity_end: new Date(new Date().setFullYear(new Date().getFullYear()+1)),
        notification_match_type : ''
        },
      show_new_notification_form: false,
      notification_types: [],
      notification_action_types: [],
      create_notification_inprocess: false,
      brand_selected_for_current_search: {},
      active_days: {
        SUNDAY: "0800-1700",
        MONDAY: "0800-1700",
        TUESDAY: "0800-1700",
        WEDNESDAY: "0800-1700",
        THURSDAY: "0800-1700",
        FRIDAY: "0800-1700",
        SATURDAY: "0800-1700"
      },
      resultLength0: false,
      all_states_dropdown: []
      // ["Andhra Pradesh",
      //   "Arunachal Pradesh",
      //   "Assam",
      //   "Bihar",
      //   "Chhattisgarh",
      //   "Goa",
      //   "Gujarat",
      //   "Haryana",
      //   "Himachal Pradesh",
      //   "Jammu and Kashmir",
      //   "Jharkhand",
      //   "Karnataka",
      //   "Kerala",
      //   "Madhya Pradesh",
      //   "Maharashtra",
      //   "Manipur",
      //   "Meghalaya",
      //   "Mizoram",
      //   "Nagaland",
      //   "Odisha",
      //   "Punjab",
      //   "Rajasthan",
      //   "Sikkim",
      //   "Tamil Nadu",
      //   "Telangana",
      //   "Tripura",
      //   "Uttar Pradesh",
      //   "Uttarakhand",
      //   "West Bengal"]
    };
  },
  watch: {
    // whenever question changes, this function will run
    search_for: function (newVal, oldVal) {
      if(newVal == 'GEOZONE'){
        this.search_with = 'GEOZONE';
      }
    },
    country: function (newCountry, oldCountry) {
      this.all_states_dropdown = [];
      this.all_city_dropdown = [];
      this.state = "";
      this.city = "";
      if(!newCountry){
      }else{
        this.addUniqueToList(this.search_query.country, newCountry);
        this.getStates();
      }
    },
    state: function (newState, oldState) {
      this.all_city_dropdown = [];
      this.city = "";
      if(!newState){
      }else{
        this.addUniqueToList(this.search_query.state, newState);
        this.getCities();
      }
    },
    city: function (newCity, oldCity) {
      this.geozone_dropdown = [];
      this.selected_geozone = null;
      if(!newCity){
      }else{
        this.addUniqueToList(this.search_query.city, newCity);
        this.getGeoZones();
      }
    },
    selected_geozone: function (newVal, oldVal) {
      this.ezone_dropdown = [];
      this.selected_ezone = null;
      if(!newVal){
      }else{
        this.addUniqueToList(this.search_query.geozone_name, newVal.name);
        this.geteZones();
      }
    },
    selected_ezone: function (newVal, oldVal) {
      this.store_dropdown = [];
      this.selected_store = null;
      if(!newVal){
        }else{
        this.addUniqueToList(this.search_query.ezone_name, newVal.name);
        this.getStores();
      }
    },
    selected_store: function (newVal, oldVal) {
      if(!newVal){
        }else{
        this.addUniqueToList(this.search_query.store_name, newVal.name);
        this.getStores();
      }
    }
  },
  computed: {
    showBrandDropdown: function () {
      return this.search_for == 'STORE';
    },
    showStoreDropdown: function () {
      return this.search_for == 'STORE' && ['STORE'].some(item => item == this.search_with);
    },
    showEzoneDropdown: function () {
      return this.search_for == 'STORE' && ['EZONE', 'STORE'].some(item => item == this.search_with);
    },
    showGeozoneDropdown: function () {
      return ['GEOZONE', 'EZONE', 'STORE'].some(item => item == this.search_with);
    },
    showCityDropdown: function () {
      return ['CITY', 'GEOZONE', 'EZONE', 'STORE'].some(item => item == this.search_with);
    },
    showStateDropdown: function () {
      return ['STATE', 'CITY', 'GEOZONE', 'EZONE', 'STORE'].some(item => item == this.search_with);
    },
  },
  created() {
    // if(this.search_for == 'GEOZONE'){
    //   this.notification.notification_match_type = 'GENERAL'
    // }
    this.getInitialDropdownData();
  },
  methods: {
    showInventoryRequestInput(){
      if(this.selected_notification_type.name == 'Inventory'){
        this.isInventoryRequestInputVisible = true;
      } else{
        this.inventory_request_params = '';
        this.isInventoryRequestInputVisible = false;
      }
    },
    set_validity_end(date){
      console.log(new Date(new Date(date).setFullYear(new Date(date).getFullYear()+1)));
      this.notification.validity_end = new Date(new Date(date).setFullYear(new Date(date).getFullYear()+1));
    },
    removeQueryParam: function(item, index, arr){
      arr.splice(index, 1);
    },
    addUniqueToList: function(arr, item){
      if( !(arr.indexOf(item) > -1) ){
        arr.push(item);
      }
    },
    getStates: function(){
      serverAddr
      .post("/v1/dropdowns/states/", {country: this.country})
      .then(response => {
        console.log('geo-zone-get-states-dropdown', response);
        this.all_states_dropdown = response.data.data ? response.data.data.map(item => item.state) : [];
      })
      .catch(function(error) {
        console.log(error);
      });
    },
    getCities: function(){
      serverAddr
      // .post("/v1/geo-zone/get-cities/", {app_id: this.app_id})
      .post("/v1/dropdowns/cities-for-state/", {
          country: this.country,
          state: this.state
        })
      .then(response => {
        console.log('geo-zone-get-cities-dropdown', response);
        this.all_city_dropdown = response.data.data ? response.data.data.map(item => item.city) : [];
      })
      .catch(function(error) {
        console.log(error);
      });
    },
    getGeoZones: function(){
      serverAddr
      .post("/v1/get-geo-zone-for-city/", {app_id: this.app_id, city: this.city})
      .then(response => {
        console.log('get-geo-zone-for-city', response);
        this.geozone_dropdown = response.data.data;
      })
      .catch(function(error) {
        console.log(error);
      });
    },
    geteZones: function(){
      serverAddr
      // .post("/v1/engagement-zone-for-dropdown/", {app_id: this.app_id})
      .post("/v1/get-engagement-zone-for-geozone/", {app_id: this.app_id, geozone_name: this.selected_geozone ? this.selected_geozone.name: ""})
      .then(response => {
        console.log('get-engagement-zone-for-geozone', response);
        this.ezone_dropdown = response.data.data;
      })
      .catch(function(error) {
        console.log(error);
      });
    },
    getStores(){
      serverAddr
      // .post("/v1/all-store-for-dropdown/", {app_id: this.app_id})
      .post("/v1/get-stores-for-ezone/", {app_id: this.app_id, ezone_name: this.selected_ezone ? this.selected_ezone.name: ""})
      .then(response => {
        console.log('get-stores-for-ezone', response);
        this.store_dropdown = response.data.data;
      })
      .catch(function(error) {
        console.log(error);
      });
    },
    getInitialDropdownData: function(){
      serverAddr
      .post("/v1/dropdowns/countries/", {})
      .then(response => {
        console.log('geo-zone-get-countries-dropdown', response);
        this.all_country_dropdown = response.data.data ? response.data.data.map(item => item.country) : [];
      })
      .catch(function(error) {
        console.log(error);
      });

      serverAddr
      .post("/v1/brand-for-dropdown/", {app_id: this.app_id})
      .then(response => {
        console.log('brand-for-dropdown', response);
        this.brand_dropdown = response.data.data ? response.data.data : [];
      })
      .catch(function(error) {
        console.log(error);
      });

      serverAddr
      .post("/v1/notification-types/get/", {})
      .then(response => {
        // console.log('notification types', response);
        this.notification_types = response.data.data[0].notification_types;

        this.notification_types.forEach(element => {
          if(element.name == 'Deals & Offers'){
            this.notification_types_ready = true;
            this.selected_notification_type = element
          }
        });
      })
      .catch(function(error) {
        console.log(error);
      });

    serverAddr
      .post("/v1/notification-action-types/get/", {})
      .then(response => {
        // console.log('notification types', response);
        this.notification_action_types =
          response.data.data[0].notification_action_types;

          this.notification_action_types.forEach(element =>{
            if(element.name == 'Entry'){
              this.selected_notification_action_type = element;
            }
          })
      })
      .catch(function(error) {
        console.log(error);
      });
    },
    search: function() {
      this.resultLength0 = false;
      this.show_new_notification_form = false;
      this.create_notification_inprocess = false;
      this.selected_match_type_brands = [];
      this.brand_selected_for_current_search = this.selected_brand && this.selected_brand.name ? JSON.parse(JSON.stringify(this.selected_brand)) : {};
      var query = {
        app_id: this.app_id,
        search_query: {
          country: this.search_with == 'COUNTRY' ? this.search_query.country : [],
          city: this.search_with == 'CITY' ? this.search_query.city : [],
          state: this.search_with == 'STATE' ? this.search_query.state : [],
          geozone_name: this.search_with == 'GEOZONE' ? this.search_query.geozone_name : [],
          store_name: this.search_with == 'STORE' ? this.search_query.store_name : [],
          ezone_name: this.search_with == 'EZONE' ? this.search_query.ezone_name : [],
          brand: this.selected_brand ? this.selected_brand.name: ""
        },
        search_for: this.search_for
      };

      console.log("search: ", query);
      var validation_error = [];
      if(this.search_with == 'COUNTRY' && query.search_query.country.length == 0){
        validation_error.push('Select at least 1 country');
      }
      if(this.search_with == 'STATE' && query.search_query.state.length == 0){
        validation_error.push('Select at least 1 state');
      }
      if(this.search_with == 'CITY' && query.search_query.city.length == 0){
        validation_error.push('Select at least 1 city');
      }
      if(this.search_with == 'GEOZONE' && query.search_query.geozone_name.length == 0){
        validation_error.push('Select at least 1 zone');
      }
      if(this.search_with == 'STORE' && query.search_query.store_name.length == 0){
        validation_error.push('Select at least 1 store');
      }

      if(validation_error.length){
        new CripNotice({
          title: "Error",
          description: validation_error.join(' ,'),
          className: "notices",
          type: "error",
          duration: 3
        });
        return;
      }
      serverAddr
        .post("/v1/filter-geozone-store-for-notification/", query)
        .then(response => {
          this.searchCallBack(response, this.search_for);
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    searchCallBack: function(response, search_result_for) {
      console.log("searchCallBack response", response);
      if (search_result_for == "GEOZONE") {
        this.response_title = "Geozone";
      } else if (search_result_for == "STORE") {
        this.response_title = "Store";
      }
      this.search_result = response.data.data;
      if(this.search_result.length == 0){
        this.resultLength0 = true;
      }else{
        this.resultLength0 = false;
      }
    },
    saveNotification: function(notification_for) {
      if(this.notification.text && this.notification.validity_start && this.notification.validity_end && this.notification.notification_match_type && this.selected_notification_type && this.selected_notification_action_type){
        this.create_notification_inprocess = true;
        var url = '';
        var store_or_gzone_list = [];
        this.search_result.forEach((item)=>{
          store_or_gzone_list.push(item.uuid);
        });
        var payload = {};
        payload.app_id = this.app_id;
        payload.notification = JSON.parse(JSON.stringify(this.notification));
        payload.notification.validity_start = new Date(payload.notification.validity_start).getTime();
        payload.notification.validity_end = new Date(payload.notification.validity_end).getTime();
        payload.action_type = this.selected_notification_action_type;
        payload.notification_type = this.selected_notification_type;
        payload.active_days = this.active_days;
        payload.inventory_request_params = this.inventory_request_params;

        if(notification_for == 'GEOZONE'){
          payload.selected_match_type_brands = [];
          url = '/v1/geozone/notification/create/';
          payload.g_id = store_or_gzone_list;
        }else if(notification_for == 'STORE'){
          payload.selected_match_type_brands = [this.brand_selected_for_current_search].map(brand => brand.br_id);
          url = '/v1/store/notification/create/';
          payload.st_id = store_or_gzone_list;
        }
        console.log('payload', payload);
        serverAddr
        .post(url, payload)
        .then(response => {
          if(response.data.status){
            this.$router.go(-1);
            new CripNotice({
            title: "Success",
            description: "Notification created successfully.",
            className: "notices",
            type: "success",
            duration: 3,
          });
          }
          this.create_notification_inprocess = false;
          console.log('create notification reaponse: ', response);
          // this.notification_types = response.data.data[0].notification_types;
        })
        .catch(function(error) {
          console.log(error);
        });
      }
    }
  }
};
</script>


<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>
<style lang="css" scoped>
.GlobalNotificationPanel{
  font-size: 14px;
}
.form-input {
  padding: 10px 15px;
}

.margin-bottom-15 {
  margin-bottom: 15px;
}
.gray{
  background-color: #f1f1f1;
}
label{
  font-weight: bold;
  font-size: 0.8em;
}
.form-textarea{
  width: 100%;
  height: 80px;
  resize:none;
  border: 1px solid silver;
  border-radius: 4px;
  outline: none;
  font-size: 14px;
}
</style>



