<template>
  <div class="AdminRoomList">
    <TopBar></TopBar>

    <div style="border: 1px solid #ddd; text-align: center; font-size: 14px; width: 50%; margin: 0 auto; margin-top: 50px; padding: 20px;" v-if="!rooms.length">No store is assigned</div>
    <el-card class="box-card" style="max-width:80vw; margin:10px auto; overflow: initial;" v-if="rooms.length">
      <div class="text item">
        <el-row :gutter="20">
          <el-col :xs="24" :sm="12" :md="12" :lg="6" :xl="6">
            <!-- <label for="">Brands</label> -->
              <multiselect v-model="selected_brand"
                          deselect-label=""
                          select-label=""
                          track-by="id"
                          label="name"
                          :custom-label="storeIdWithName"
                          placeholder="Select brand"
                          :options="brand_dropdown"
                          :searchable="true"
                          :allow-empty="true">
                <div slot="singleLabel" slot-scope="{ option }">{{ option.brand_name }}</div>
              </multiselect>
          </el-col>
          <el-col :xs="24" :sm="12" :md="12" :lg="6" :xl="6" class="top-margin">
            <!-- <label for="">Search</label> -->
            <input type="text" placeholder="Search here" v-model="searchString" style="min-height: 40px; font-size: 14px;">
          </el-col>
        </el-row>
      </div>
    </el-card>
    <div class="desktop-chat-layout">
      <div class="chat-rooms-list">
        <el-card
          v-for="(room, index) in filteredBrandList.length ? filteredBrandList : rooms"
          :key="index"
          @click.native="openChatRoom(room)"
          :class="{'current-room':currentRoom.storeId == room.storeId && currentRoom.consumerUUID == room.consumerUUID && currentRoom.brandId == room.brandId}"
          class="box-card"
          style="width: 100%; margin-bottom: 10px; cursor: pointer;"
        >
          <div class="text item">
            <div style="font-size: 0.8em; padding: 10px;">
              <div>
                <b>Consumer:</b>
                {{getProfileNameFromCache(room.consumerUUID) ? getProfileNameFromCache(room.consumerUUID): room.consumerUUID}}
              </div>
              <div>
                <b>Store:</b>
                {{getStoreNameFromCache(room.storeId) ? getStoreNameFromCache(room.storeId): room.storeId}}
              </div>
              <div>
                <b>Brand:</b>
                {{getBrandNameFromCache(room.brandId) ? getBrandNameFromCache(room.brandId): room.brandId}}
              </div>
            </div>
          </div>
        </el-card>
      </div>
      <Chat style="width: 65%;" v-if="rooms.length"></Chat>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import { serverAddr, chatServerAddr } from "../networkConfig.js";
import CripNotice from "crip-vue-notice";
import Chat from "./Chat";
import TopBar from "./TopBar";
import {EventBus} from '../eventBus.js';
import { access } from '@/constants';
import { findIndex as _findIndex } from 'lodash';
import Multiselect from 'vue-multiselect';


export default {
  name: "AdminRoomList",
  components: {TopBar, Chat,  multiselect:Multiselect},
  props: ["st_id", "app_id"],
  data(){
    return {
      searchString: '',
      brand_dropdown: [],
      filteredBrandList: [],
      selected_brand: '',
      userRoles: JSON.parse(window.localStorage.getItem("roles")),
      stores: [],
      rooms: [],
      storeIdList: [],
      brandIdList: [],
      profileIdList: [],
      currentRoom: { storeId: null, consumerUUID: null, brandId: null }
    };
  },
  watch: {
    selected_brand: function (newBrand, oldBrand) {
      if(newBrand){
        console.log('selected', newBrand);
        this.filteredBrandList = this.rooms.filter(item => (item.storeId == newBrand.st_id) && (item.brandId == newBrand.br_id));
        console.log('filteredBrandList', this.filteredBrandList);
      }else{
        console.log('not selected', oldBrand);
        this.filteredBrandList = [];
      }
    },
    searchString: function(newString, oldString) {
      if(newString.trim()){
        console.log('newString', newString);
        EventBus.$emit("searchMsgString", this.consumerUUID, newString.trim());
      }else{
        console.log('oldString', oldString);
      }
    }
  },
  mounted() {

    EventBus.$on('ROOM_ACCESS_PERMISSIONS_CHANGED', (data) => {
        console.log('<Chat> ROOM_ACCESS_PERMISSIONS_CHANGED: ', data);

        var index = _findIndex(this.rooms, (room) => { return room.storeId == data.storeId && room.brandId == data.brandId && room.consumerUUID == data.consumerUUID})

        this.rooms[index].access = data.access;
    });
    var getAssignedStoreUrl = "";
      console.log('this.authUser', 'CCA or SM');
    if(this.checkRole(access.CUSTOMER_CARE_ADMIN)){
      console.log('this.authUser', 'CCA');
      getAssignedStoreUrl = '/customer-care-admin/get-conversation-rooms/';
      chatServerAddr.post(getAssignedStoreUrl,{"app_id": this.authUser.app_id})
      .then(this.getConversationRoomCallback)
      .catch(function (error) {
        console.log(error);
      })
    }else if(this.checkRole(access.STORE_MANAGER)){
      console.log('this.authUser', 'SM');
      getAssignedStoreUrl = '/store-manager/get-conversation-rooms/';
      chatServerAddr.post(getAssignedStoreUrl,{"app_id": this.authUser.app_id})
      .then(this.getConversationRoomCallback)
      .catch(function (error) {
        console.log(error);
      })
    } else if(this.checkRole(access.BE_ADMIN)){
      console.log('this.authUser', 'BE_ADMIN');
      console.log('st_id', this.st_id)
      var storeIdListForRooms = [];
      storeIdListForRooms.push(this.st_id);
      chatServerAddr.post('/get-conversation-rooms/',{"stores": storeIdListForRooms})
      .then(this.getConversationRoomCallback)
      .catch(function (error) {
        console.log(error);
      })
    }
  },
  computed: {
    authUser() {
      return this.$store.state.general.authUser;
    },
    storeNamesMap() {
      return this.$store.state.cache.storeNamesMap;
    },
    brandNamesMap() {
      return this.$store.state.cache.brandNamesMap;
    },
    profileNamesMap() {
      return this.$store.state.cache.profileNamesMap;
    }
  },
  methods: {
    searchMessage(){
      console.log('searchMessage', this.searchString);
      if(this.searchString){
        EventBus.$emit("searchMsgString", this.consumerUUID, this.searchString.trim());
      }
    },
    checkRole: function(roleName) {
      if (!this.userRoles) {
        return false;
      }
      return this.userRoles.some(function(el) {
        return el.name === roleName;
      });
    },
    getStoreCallback: function(response) {
      this.stores = response.data.data;
      var storesIdList = [];
      for (var i = 0; i < this.stores.length; i++) {
        storesIdList.push(this.stores[i].store.uuid);
      }
      // console.log('storesIdList',storesIdList);
      chatServerAddr
        .post("/get-conversation-rooms/", { stores: storesIdList })
        .then(this.getConversationRoomCallback)
        .catch(function(error) {
          console.log(error);
        });
    },
    storeIdWithName ({ brand_name, st_id }) {
      var storeName = this.getStoreNameFromCache(st_id) ? this.getStoreNameFromCache(st_id) : st_id;
      return `${brand_name} - [${storeName}]`
    },
    getConversationRoomCallback: function(response) {

      if(response.status){
        this.rooms = response.data.data;
        if(this.rooms.length){

          for(var i=0; i<this.rooms.length; i++){
            // console.log('temp', this.getBrandArray(this.rooms[i].brandId));
            var obj = {};
            obj.id = this.rooms[i];
            obj.br_id = this.rooms[i].brandId;
            obj.st_id = this.rooms[i].storeId;
            obj.store_name = '';
            if(this.rooms[i].brandId == 'default'){
              obj.brand_name = 'default'
            } else {
              obj.brand_name = this.getBrandNameFromCache(this.rooms[i].brandId);
            }
            this.brand_dropdown.push(obj);
          }
        }

        try{
          this.currentRoom.consumerUUID = this.rooms[0].consumerUUID;
          this.currentRoom.storeId = this.rooms[0].storeId;
          this.currentRoom.brandId = this.rooms[0].brandId;
        }catch(e){

        }

        EventBus.$emit("currentRoomChanged", this.currentRoom);
        // console.log("this.rooms: ", this.rooms);
        if(this.rooms.length){
          this.openChatRoom(this.rooms[0])
        }

        this.storeIdList = this.getStoreIdListForMap(this.rooms);
        this.brandIdList = this.getBrandIdListForMap(this.rooms);
        this.profileIdList = this.getProfileIdListForMap(this.rooms);

        this.getNamesMapForStoreBrandProfile(this.storeIdList, this.brandIdList, this.profileIdList);
      }
    },

    getStoreNameFromCache(storeId){
      var store = _.find(this.storeNamesMap, {st_id: storeId})
      if(store) {
          return store.store_name;
      }else{
        return null
      }
    },

    getBrandNameFromCache(brandId){
      var brand = _.find(this.brandNamesMap, {br_id: brandId})
      if(brand) {
          return brand.brand_name;
      }else{
        return null
      }
    },

    getProfileNameFromCache(profileId){
      var profile = _.find(this.profileNamesMap, {p_id: profileId})
      if(profile) {
          return profile.profile_name;
      }else{
        return null
      }
    },

    getStoreIdListForMap(rooms){
      var storeIdList = [];
      for(var i=0; i<rooms.length;i++){
        if(!_.find(this.storeNamesMap, {st_id: rooms[i].storeId})) {
          storeIdList.push(rooms[i].storeId);
        }
      }

      return storeIdList
    },
    getBrandIdListForMap(rooms){
      var brandIdList = [];
      for(var i=0; i<rooms.length;i++){
        if(!_.find(this.brandNamesMap, {br_id: rooms[i].brandId})) {
          brandIdList.push(rooms[i].brandId);
        }
      }
      return brandIdList;
    },
    getProfileIdListForMap(rooms){
      var profileIdList = [];
      for(var i=0; i<rooms.length;i++){
        if(!_.find(this.profileNamesMap, {p_id: rooms[i].consumerUUID})) {
          profileIdList.push(rooms[i].consumerUUID);
        }
      }
      return profileIdList;
    },
    openChatRoom: function(room) {
      console.log("openChatRoommmmmmmm: ", room);
      this.currentRoom.storeId = room.storeId;
      this.currentRoom.brandId = room.brandId;
      this.currentRoom.consumerUUID = room.consumerUUID;
      setTimeout(()=>{
        EventBus.$emit("currentRoomChanged", room);
      },0)
    },
    redirectToBack() {
      this.$router.go(-1);
    },
    getStoreNamesMap(storeIdList) {
      // console.log('getStoreNamesMap() ', storeIdList);
      if(storeIdList.length){
        return serverAddr.post('/v1/profile/get-store-names-map/', {
          app_id: this.authUser.app_id ? this.authUser.app_id : this.app_id,
          storeIdList: storeIdList
        });
      }else{
        return {data: {data: []}}
      }
    },
    getBrandNamesMap(brandIdList) {
      console.log('getBrandNamesMap() ', brandIdList);
      if(brandIdList.length){
        return serverAddr.post('/v1/profile/get-brand-names-map/', {
          app_id: this.authUser.app_id ? this.authUser.app_id : this.app_id,
          brandIdList: brandIdList
        });
      }else{
        return {data: {data: []}}
      }
    },
    getProfileNamesMap(profileIdList) {
      // console.log('getProfileNamesMap() ', profileIdList);
      if(profileIdList.length){
        return serverAddr.post('/v1/profile/get-profile-names-map/', {
          app_id: this.authUser.app_id ? this.authUser.app_id : this.app_id,
          profileIdList: profileIdList
        });
      }else{
        return {data: {data: []}}
      }
    },
    getNamesMapForStoreBrandProfile(storeIdList, brandIdList, profileIdList){
      // console.log('getNamesMapForStoreBrandProfile',storeIdList, brandIdList, profileIdList);

      axios.all([this.getStoreNamesMap(storeIdList), this.getBrandNamesMap(brandIdList), this.getProfileNamesMap(profileIdList)])
      .then(axios.spread( (storeMap, brandMap, profileMap) => {
        console.log('getNamesMapForStoreBrandProfile Response', storeMap, brandMap, profileMap);

        for(var i=0; i<storeMap.data.data.length;i++){
          this.$store.commit('cache/updateStoreNamesMap', storeMap.data.data[i]);
        }

        for(var i=0; i<brandMap.data.data.length;i++){
          this.$store.commit('cache/updateBrandNamesMap', brandMap.data.data[i]);
        }

        for(var i=0; i<profileMap.data.data.length;i++){
          this.$store.commit('cache/updateProfileNamesMap', profileMap.data.data[i]);
        }
      }));
    }
  }
};
</script>

<style scoped>
.desktop-chat-layout {
  display: flex;
  padding: 0px 15px;
  box-sizing: border-box;
}
.chat-rooms-list {
  padding: 0px 15px 15px 0px;
  height: 86vh;
  flex-grow: 1;
}
.current-room {
  background-color: #deeaff;
}
@media screen and (max-width: 768px) {
  .top-margin{
    margin-top: 15px;
  }
}
</style>
<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>



