<template>
    <div class="StoreManagerCreate">
    <TopBar></TopBar>

    <el-row :gutter="15" style="margin: 0 auto; width: 90vw;">
      <el-col :xs="24" :sm="24" :md="12" :lg="8" :xl="8" v-for="(profile, index) in profiles" :key="index" style="margin-bottom: 15px">

      <el-card class="box-card" style="width: 100%;">
        <!-- <div slot="header" class="clearfix" style="display:flex;"> -->

        <!-- </div> -->

          <el-row class="vertical-center text-left" style="background-color: #F1F1F1; padding: 10px 15px;">
            <el-col :span="8">
              <h4 style="margin: 0px;"> Attribute </h4>
            </el-col>

            <el-col :span="16">
              <h4 style="margin: 0px;"> Value </h4>
            </el-col>
          </el-row>

          <el-row class="vertical-center text-left" style="padding: 5px 10px; border-bottom: 1px solid #F1F1F1;" v-for="(profile_attr, index) in profile.profile_attributes" :key="index">
            <el-col :span="8">
              {{profile_attr.display_name}}
            </el-col>
            <el-col :span="16">
              {{profile_attr.value}}
            </el-col>
          </el-row>

          <el-row class="vertical-center text-left" style="padding: 5px 10px; border-bottom: 1px solid #F1F1F1;">
            <el-col :span="8">
              Username
            </el-col>
            <el-col :span="16">
              {{profile.profile.username}}
            </el-col>
          </el-row>

          <div style="font-size: 0.8em; padding: 5px 10px;">
            {{profile.p_id}}
          </div>
          <div class="text-right" style="margin-top: 10px;">
            <el-button type="text"  @click.native="AddManager(index)">Assign Manager</el-button>
          </div>

      </el-card>
      </el-col>
    </el-row>

  </div>
</template>

<script>
import { serverAddr } from '../networkConfig.js';
import CripNotice from 'crip-vue-notice';
import TopBar from "./TopBar";

export default {
  name: "StoreManagerCreate",
  components: {TopBar},
  props: ["app_id","st_id"],
  data(){
    return {
      profiles: [],
    }
  },
  created(){
    this.initProfileData();
  },
  methods:{
    initProfileData: function(response){
    serverAddr.post('/v1/profile/store-manager/',{'app_id':this.app_id})
      .then(this.getProfileCallback)
      .catch(function (error) {
        console.log(error);
      })
    },
    getProfileCallback: function(response){
      this.profiles = response.data.data;
    },
    AddManager: function(index){
      serverAddr.post('/v1/profile/store-manager/assign-store/',
      {
        app_id:this.app_id,
        st_id:this.st_id,
        p_id:this.profiles[index].profile.uuid
      })
      .then(this.postAddManagerCallback)
      .catch(function (error) {
        console.log(error);
      })
    },
    postAddManagerCallback: function(response){
      if(response.data.status){
        this.$router.go(-1);
        new CripNotice({
          title: "Success",
          description: "Manager assigned successfully.",
          className: "notices",
          type: "success",
          duration: 3,
        });
      }
    }
  }
}
</script>



