<template>
  <div class="ManageStoreManager">
    <TopBar></TopBar>

    <el-row :gutter="15" style="margin: 0 auto; width: 90vw;">
      <el-col :xs="24" :sm="24" :md="12" :lg="8" :xl="8" v-for="(profile, index) in [1,2,3,4,5]" :key="'loader' +index" style="margin-bottom: 15px" v-if="isLoading">
        <el-card style="height: 270px;">
          <content-placeholders>
            <content-placeholders-heading :img="true" />
            <content-placeholders-text :lines="3" />
            <content-placeholders-text :lines="3" />
          </content-placeholders>
        </el-card>
      </el-col>

      <el-card class="box-card">
        <div class="clearfix">
          <h3 style="margin: 0px; display: flex; align-items: center;">
            <span @click="redirectToBack()" style="cursor: pointer">App Dashboard > &nbsp;</span>
            <span style="flex-grow: 1; color: #66b1ff;">Manage store manager</span>
            <router-link :to="{path: `/manage-customer-care/${app_id}/`}">
              <el-button type="success" size="mini">Manage customer care</el-button>
            </router-link>
          </h3>
        </div>
      </el-card>

      <el-col :xs="24" :sm="24" :md="12" :lg="8" :xl="8" v-for="(profile, index) in profiles" :key="index" style="margin: 50px 0 15px 0;">
        <el-card class="box-card">

          <div style="width: 100%; height: 180px; overflow-y: auto;">
            <el-row class="vertical-center text-left" style="background-color: #F1F1F1; padding: 10px 15px;">
              <el-col :span="8">
                <h4 style="margin: 0px;"> Attribute </h4>
              </el-col>

              <el-col :span="16">
                <h4 style="margin: 0px;"> Value </h4>
              </el-col>
            </el-row>

            <el-row class="vertical-center text-left" style="padding: 5px 10px; border-bottom: 1px solid #F1F1F1;" v-for="(profile_attr, index) in profile.profile_attributes" :key="index">
              <el-col :span="8">
                <span style="font-size: 0.7em; font-weight: bold;">{{profile_attr.display_name}}</span>
              </el-col>
              <el-col :span="16">
                {{profile_attr.value}}
              </el-col>
            </el-row>

            <el-row class="vertical-center text-left" style="padding: 5px 10px; border-bottom: 1px solid #F1F1F1;">
              <el-col :span="8">
                <span style="font-size: 0.7em; font-weight: bold;">Role</span>
              </el-col>
              <el-col :span="16">
                <p style="margin: 0px;" v-for="(role, index) in profile.roles" :key="index">{{index + 1}}. {{role.name}} </p>
              </el-col>
            </el-row>
            <el-row class="vertical-center text-left" style="padding: 5px 10px; border-bottom: 1px solid #F1F1F1;">
              <el-col :span="8">
                <span style="font-size: 0.7em; font-weight: bold;">Username</span>
              </el-col>
              <el-col :span="16">
                {{profile.profile.username}}
              </el-col>
            </el-row>
            <el-row class="vertical-center text-left" style="padding: 5px 10px; border-bottom: 1px solid #F1F1F1;" v-if="profile.profile.customer_id">
              <el-col :span="8">
                <span style="font-size: 0.7em; font-weight: bold;">Customer Id</span>
              </el-col>
              <el-col :span="24">
                {{profile.profile.customer_id}}
              </el-col>
            </el-row>

            <div style="font-size: 0.8em; padding: 5px 10px;">
              {{profile.p_id}}
            </div>
          </div>

            <div class="text-right" style="margin-top: 10px;">
              <el-button type="text" @click.native="openProfileBrands(profile)">Assign Brands</el-button>
              <el-button type="text"  @click.native="openProfileStores(profile)">Assign Store</el-button>
              <el-button type="text"  @click.native="openProfileCity(profile)">Assign Region</el-button>
              <el-button type="text"  @click.native="openProfileAssignedStore(profile)">Assigned Stores</el-button>
            </div>

        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>

import { serverAddr } from '../networkConfig.js';
import CripNotice from 'crip-vue-notice';
import TopBar from "./TopBar";

export default {
  name: 'ManageStoreManager',
  components: {TopBar},
  props: ["app_id"],
  data(){
    return{
      isLoading: false,
      profiles: []
    }
  },
  created(){
    this.initProfileData();
  },
  methods:{
    initProfileData: function(response){
    serverAddr.post('/v1/profile/store-manager/',{'app_id':this.app_id})
      .then(this.getProfileCallback)
      .catch(function (error) {
        console.log(error);
      })
    },
    getProfileCallback: function(response){
      this.isLoading = false;
      this.profiles = response.data.data;
    },
    openProfileBrands: function(profile){
      this.$router.push({path: `/store-manager-assign-brand/${this.app_id}/${profile.p_id}/${profile.profile.username}`});
    },
    openProfileStores: function(profile){
      this.$router.push({path: `/store-manager-assign-store/${this.app_id}/${profile.p_id}/${profile.profile.username}`});
    },
    openProfileCity: function(profile){
      this.$router.push({path: `/store-manager-assign-city/${this.app_id}/${profile.p_id}/${profile.profile.username}`});
    },
    openProfileAssignedStore: function(profile){
      this.$router.push({path: `/store-manager-get-assigned-store/${this.app_id}/${profile.p_id}/${profile.profile.username}`});
    },
    redirectToBack() {
      this.$router.go(-1);
    }
  }
}
</script>

