<template>
  <div class="CustomerCareAssignStore">
    <TopBar></TopBar>

    <el-card class="box-card" style="margin:10px auto; max-width: 80vw;">
      <h3 style="margin: 0px; display: flex; align-items: center;">
        <span @click="redirectToBack()" style="cursor: pointer">{{username}}
          <i class="el-icon-arrow-right"></i> &nbsp;</span>
        <span style="flex-grow: 1; color: #66b1ff;">Manage stores</span>
      </h3>
    </el-card>

    <el-card class="box-card card-row" style="max-width:80vw; margin:10px auto;" v-for="(loader,index) in [1,2,3,4]" :key="'loader' + index" v-if="isLoading">

      <el-row>
        <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24" style="margin-bottom: 25px;">
          <content-placeholders>
            <content-placeholders-text :lines="2" />
          </content-placeholders>
        </el-col>
      </el-row>

    </el-card>


    <el-row class="card-row" v-if="!isLoading && !stores.length">
      <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24" style="text-align: center;">
        <div style="border: 1px solid #000; padding: 10px; text-align: center; margin: 20px auto; display: inline-block;">
          No stores to assign
        </div>
      </el-col>
    </el-row>



    <div v-for="(store, index) in stores" :key="index" style="padding: 5px 10px;" v-if="!isLoading">
      <el-card class="box-card" style="max-width:80vw; margin:10px auto;">

        <div class="text item">

          <el-row class="card-row">
            <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24" style="text-align: right;">
              <span>
                <span style="color: #409EFF; cursor: pointer;" @click="assignStore(store.store.uuid)">Assign Store</span>
              </span>
            </el-col>
          </el-row>


          <el-row class="card-row" style="border-bottom: 1px solid #F1F1F1;">
            <el-col :xs="8" :sm="8" :md="8" :lg="8" :xl="8">
                Store id:
            </el-col>
            <el-col :xs="16" :sm="16" :md="16" :lg="16" :xl="16">
                {{store.st_id}}
            </el-col>
          </el-row>

          <el-row class="vertical-center card-row" v-for="(store_attr, index) in store.store_attributes" :key="index">
            <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8" v-if="store_attr.display_name == 'City' || store_attr.display_name == 'Country' || store_attr.display_name == 'State' || store_attr.display_name == 'Name'" style="border-bottom: 1px solid #F1F1F1;">

              {{store_attr.display_name}}

            </el-col>

            <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16" v-if="store_attr.display_name == 'City' || store_attr.display_name == 'Country' || store_attr.display_name == 'State' || store_attr.display_name == 'Name'" style="border-bottom: 1px solid #F1F1F1;">

              {{store_attr.value}}

            </el-col>
          </el-row>

        </div>

      </el-card>
    </div>

  </div>
</template>

<script>

import { serverAddr } from '../networkConfig.js';
import CripNotice from 'crip-vue-notice';
import TopBar from "./TopBar";

export default {
  name: 'CustomerCareAssignStore',
  props: ["app_id", "p_id", "username"],
  components: { TopBar },
  data(){
    return{
      isLoading: false,
      stores: [],
      selected_ids: []
    }
  },
  created(){
    this.getEzoneStores();
  },
  methods: {
    assignStore(st_id){
    serverAddr.post('/v1/profile/customer-care-admin/assign-store/', { 'app_id': this.app_id, 'p_id': this.p_id, 'st_id':  st_id})
      .then(this.assignStoreCallback)
      .catch(function (error) {
        console.log(error);
      });
    },
    assignStoreCallback(response){
      if(response.status){
        new CripNotice({
          title: "Success",
          description: "Store assigned successfully.",
          className: "notices",
          type: "success",
          duration: 3,
        });
      }
    },
    redirectToBack() {
      this.$router.go(-1);
    },
    getEzoneStores() {
      this.isLoading = true;
      serverAddr.post('/v1/all-store/', { 'app_id': this.app_id })
        .then(this.getStoreCallback)
        .catch(function (error) {
          console.log(error);
        });
    },
    getStoreCallback: function (response) {
      this.isLoading = false;
      console.log('getStoreCallback this.stores:', response.data);
      this.stores = response.data.data;
    },
  }
}
</script>



