export const pushyAppId = '5bdc624f6f4b49664308fc4f'
export const productionAddr = 'https://inventa.live/api/'
export const chatServerUrl = 'https://inventa.live/chat/'
export const chatProductionAddr = 'https://inventa.live/chat/'

export const serverAddr = axios.create({
    baseURL: productionAddr,
    timeout: 30000,
    headers: {},
    withCredentials: true,
    transformRequest: [function (data, headers) {
      // Do whatever you want to transform the data
      const access_token = window.localStorage.getItem("x-access-token");
      const refresh_token = window.localStorage.getItem("x-refresh-token");
      if(access_token && refresh_token){
        headers['x-access-token'] = access_token;
        headers['x-refresh-token'] = refresh_token;
      }
      headers['Content-Type'] = "application/json;charset=UTF-8";
      return JSON.stringify(data);
    }],
    transformResponse: [function(data) {
        data = JSON.parse(data)
        if ("tokenParams" in data && data.tokenParams.isTokenRefreshed){
          delete serverAddr.defaults.headers['x-access-token'];
          window.localStorage.setItem("x-access-token", data.tokenParams["x-access-token"]);
        }
        if (!data.status) {
          new CripNotice({
            title: "Oops! Something went wrong!",
            description: JSON.stringify(data.validation),
            type: "error",
            duration: 4,
          })
        }
        return data;
    }]
})

export const chatServerAddr = axios.create({
  baseURL: chatProductionAddr,
  timeout: 30000,
  headers: {},
  withCredentials: true,
  transformRequest: [function (data, headers) {
    // Do whatever you want to transform the data
    const access_token = window.localStorage.getItem("x-access-token");
    const refresh_token = window.localStorage.getItem("x-refresh-token");
    if(access_token && refresh_token){
      headers['x-access-token'] = access_token;
      headers['x-refresh-token'] = refresh_token;
    }
    headers['Content-Type'] = "application/json;charset=UTF-8";
    return JSON.stringify(data);
  }],
})

export const chatServerAddrForChatImage = axios.create({
  baseURL: chatProductionAddr,
  timeout: 30000,
  headers: {},
  withCredentials: true,
  transformRequest: [function (data, headers) {
    // Do whatever you want to transform the data
    const access_token = window.localStorage.getItem("x-access-token");
    const refresh_token = window.localStorage.getItem("x-refresh-token");
    if(access_token && refresh_token){
      headers['x-access-token'] = access_token;
      headers['x-refresh-token'] = refresh_token;
    }
    headers['Content-Type'] = "multipart/form-data;";
    return data
    // return JSON.stringify(data);
  }],
})



