    font-size: 1em;
    border-radius: 4px;
    columns: #58716F;
}
.consumer-online{
    border: 1px solid rgb(78, 140, 255);
}
.access-blocked{
    border: 1px solid tomato;
    background-color: rgb(255, 99, 71, 0.2);
}
.consumer-online-status-text{
    font-size: 0.8em;
    font-weight: bold;
}
</style>

<style>
.msg-balloon a.inline-link {
    color: inherit!important;
    text-decoration: none!important;
    padding: 3px 10px;
    border-radius: 4px;
    font-size: 0.8em;
    background-color: rgba(0,0,0,0.2)!important;
}
</style>



