<template>
  <div class="BusinessEntityCreate text-center">
    <TopBar></TopBar>
  <el-row style="max-width:80vw; margin: 0 auto;">
    <el-col class="box-card" style="margin-bottom:20px;">
      <!-- <el-card> -->
        <el-card class="box-card" style="max-width:60vw; margin:0 auto;">
          <div class="text item">
            <el-row class="vertical-center" style="text-align:center;">
              <el-col :xs="24" :sm="6" :md="6" :lg="6" :xl="6">
                Business Entity name:
              </el-col>
              <el-col :xs="24" :sm="18" :md="18" :lg="18" :xl="18">
                <input style="width:80%;" type="text" v-model="name">
              </el-col>
            </el-row>
          </div>
        </el-card>
        <el-row :gutter="20" class="vertical-center" style="text-align:center; align-items:start;">
          <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
            <el-card class="box-card" style="max-width:60vw; margin:20px auto;">
              <div slot="header" class="clearfix" style="display:flex">
                <div style="margin:auto">Login Credentails For Business Entity Admin: </div>
                <div>
                  <el-button type="primary" size="mini" @click.native="addAdminCreds()">Add Admin</el-button>
                </div>
              </div>
              <div class="text item">
                <el-row class="vertical-center margin-bottom-10" style="text-align:center;" v-for="(user, index) in userCreds" :key="index">
                  <el-col :xs="24" :sm="5" :md="5" :lg="5" :xl="5">
                    Username:
                  </el-col>
                  <el-col :xs="24" :sm="6" :md="6" :lg="6" :xl="6">
                    <input style="width:100%;" type="text" v-model="user.username">
                  </el-col>
                  <el-col :xs="24" :sm="5" :md="5" :lg="5" :xl="5">
                    Password:
                  </el-col>
                  <el-col :xs="24" :sm="6" :md="6" :lg="6" :xl="6">
                    <input style="width:100%;" type="password" v-model="user.password">
                  </el-col>
                  <el-col v-if="index!=0" :xs="24" :sm="2" :md="2" :lg="2" :xl="2">
                    <div style="cursor: pointer;" v-on:click="removeAdmin(index)">&#x2715;</div>
                  </el-col>
                </el-row>
              </div>
            </el-card>
          </el-col>
        </el-row>
        <el-row :gutter="20" class="vertical-center" style="text-align:center; align-items:start;">
          <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
            <el-card class="box-card" style="max-width:60vw; margin:20px auto;">
              <div slot="header" class="clearfix">
                <span>Select Roles for Business Entity: </span>
              </div>
              <div class="text item">
                <el-row class="vertical-center" :gutter="10" v-for="(role,index) in roles" :key="index" v-if="role.name != `Platform Admin`">
                  <el-col :xs="24" :sm="2" :md="2" :lg="2" :xl="2" class="text-left">
                    <span>
                      <input name="roles" :value="role.name" type="checkbox" v-model="selectedRoles">
                    </span>
                  </el-col>
                  <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8" class="text-left">
                    {{role.name}}
                  </el-col>
                  <el-col :xs="24" :sm="14" :md="14" :lg="14" :xl="14" class="text-left">
                    {{role.r_id}}
                  </el-col>
                </el-row>
              </div>
            </el-card>
          </el-col>
        </el-row>

        <el-card class="box-card" style="max-width:60vw; margin:20px auto;">
          <div slot="header" class="clearfix">
            <span>Select Relations for Business Entity: </span>
          </div>
          <div class="text item">
            <el-row class="vertical-center" :gutter="10" v-for="(relation,index) in relations" :key="index" v-if="relation.relation.node1_type != `platform`">
              <el-col :xs="24" :sm="2" :md="2" :lg="2" :xl="2" class="text-left">
                <span>
                  <input name="relations" :value="relation" type="checkbox" v-model="selectedRelations">
                </span>
              </el-col>
              <el-col :xs="24" :sm="6" :md="6" :lg="6" :xl="6" class="text-left">
                {{relation.relation.node1_arg_value}} : {{relation.relation.node1_type}}
              </el-col>
              <el-col :xs="24" :sm="10" :md="10" :lg="10" :xl="10" class="text-left">
                -- [: {{relation.relation.relation}} { <span v-for="param in relation.parameters" :key="param.key">{{param.display_name}}</span> } ] -->
              </el-col>
              <el-col :xs="24" :sm="6" :md="6" :lg="6" :xl="6" class="text-left">
                {{relation.relation.node2_arg_value}} : {{relation.relation.node2_type}}
              </el-col>
            </el-row>
          </div>
        </el-card>

        <el-button type="primary" @click.native="save()">Create Business Entity</el-button>

      <!-- </el-card> -->

    </el-col>

  </el-row>


  </div>
</template>

<script>
import { serverAddr } from '../networkConfig.js';
import CripNotice from 'crip-vue-notice';
import TopBar from "./TopBar";

export default {
  name: "BusinessEntityCreate",
  components: { TopBar },
  data() {
    return {
      name: '',
      userCreds: [{
        username: '',
        password: ''
      }],
      roles: [],
      relations: [],
      selectedRoles: [],
      selectedRelations: [],
    }
  },
  created() {
    serverAddr.post('/v1/get-platform-roles/')
      .then(this.getRolesCallBack)
      .catch(function (error) {
        console.log(error);
      })
    serverAddr.post('/v1/platform-relation-config/')
      .then(this.getRelationsCallBack)
      .catch(function (error) {
        console.log(error);
      })
  },
  mounted() {

  },
  methods: {
    getRolesCallBack: function (response) {
      if (response.data.status) {
        this.roles = response.data.data
      }
    },
    getRelationsCallBack: function (response) {
      if (response.data.status) {
        this.relations = response.data.data
      }
    },
    addAdminCreds: function () {
      var user = {
        username: '',
        password: ''
      }
      this.userCreds.push(user);
    },
    removeAdmin: function (index) {
      this.userCreds.splice(index, 1);
    },
    save: function () {
      if (!this.name) {
        alert("Please fill Business Entity name");
      } else if (!this.userCreds[0].username || !this.userCreds[0].password) {
        alert("Login Credentails are compulsory. Please add in first row.");
      } else if (this.selectedRoles.length <= 0) {
        alert("Please select atleast one role");
      } else {
        var params = {
          be_properties: {
            name: this.name
          },
          admin: this.userCreds,
          roles: this.selectedRoles,
          relations: this.selectedRelations
        }
        serverAddr.post('/v1/business-entity/create/', params)
          .then(this.postSaveCallBack)
          .catch(function (error) {
            console.log(error);
          })
      }
    },
    postSaveCallBack: function (response) {
      console.log("Response: ", response);
      if (response.data.status) {
        new CripNotice({
          title: "Success",
          description: "BE created successfully.",
          className: "notices",
          type: "success",
          duration: 3,
        });
        this.$router.push({path: '/business-entity-list'});
      }
    }
  }
}
</script>



