<template>
    <div class="StoreAssignBrand">
    <TopBar></TopBar>

      <el-row :gutter="15" style="margin: 0 auto; width: 90vw;">
        <el-col :xs="24" :sm="24" :md="12" :lg="8" :xl="8" v-for="(loader,index) in [1,2,3,4,5,6]" :key="'loader' + index" style="margin-bottom: 25px;" v-if="isLoading">
          <el-card class="box-card" style="width: 100%;">
            <content-placeholders>
              <!-- <content-placeholders-heading :img="true" /> -->
              <content-placeholders-text :lines="2" />
            </content-placeholders>
          </el-card>
        </el-col>
      </el-row>

    <el-row :gutter="15" style="margin: 0 auto; width: 90vw;">
      <el-col :xs="24" :sm="24" :md="12" :lg="8" :xl="8" v-for="(brand, index) in brands" :key="index" style="margin-bottom: 15px">

      <el-card class="box-card" style="width: 100%;">
        <!-- <div slot="header" class="clearfix" style="display:flex;"> -->

        <!-- </div> -->


          <el-row class="vertical-center text-left" style="padding: 5px 10px; border-bottom: 1px solid #F1F1F1;">
            <el-col :span="8">
              Name
            </el-col>
            <el-col :span="16">
              {{brand.name}}
            </el-col>
          </el-row>

          <div style="font-size: 0.8em; padding: 5px 10px;">
            {{brand.br_id}}
          </div>
          <div class="text-right" style="margin-top: 10px;">
            <el-button type="text"  @click.native="AddBrandToStore(index)">Assign Brand to Store</el-button>
          </div>

      </el-card>
      </el-col>
    </el-row>

  </div>
</template>

<script>
import { serverAddr } from '../networkConfig.js';
import CripNotice from 'crip-vue-notice';
import TopBar from "./TopBar";

export default {
  name: "StoreAssignBrand",
  components: {TopBar},
  props: ["app_id","st_id"],
  data(){
    return {
      isLoading : true,
      brands: [],
    }
  },
  created(){
    this.isLoading = true;
    this.initBrandData();
  },
  methods:{
    initBrandData: function(response){
      serverAddr.post('/v1/brand/', {app_id: this.app_id})
      .then(this.getBrandCallback)
      .catch(function (error) {
        console.log(error);
      })
    },
    getBrandCallback: function(response){
      this.isLoading = false;
      this.brands = response.data.data;
    },
    AddBrandToStore: function(index){
      serverAddr.post('/v1/store/assign-brand/',
      {
        app_id:this.app_id,
        st_id:this.st_id,
        br_id:this.brands[index].br_id
      })
      .then(this.postAddBrandToStoreCallback)
      .catch(function (error) {
        console.log(error);
      })
    },
    postAddBrandToStoreCallback: function(response){
      if(response.data.status){
        this.$router.go(-1);
        new CripNotice({
          title: "Success",
          description: "Brand assigned successfully.",
          className: "notices",
          type: "success",
          duration: 3,
        });
      }
    }
  }
}
</script>



