<template>
    <div class="PlatformList">
    <TopBar></TopBar>

    <el-card class="box-card text-center" style="max-width:80vw; margin:0 auto;">
      <!-- <div slot="header" class="clearfix" v-if="platforms.length > 0">
        <span class="heading">Platform ID: </span><span>{{platforms[0].b_id}}</span>
      </div> -->
      <div class="text item">
        <el-row class="vertical-center text-left">
          <el-col :xs="24" :sm="6" :md="6" :lg="6" :xl="6">
            <h3>Username</h3>
          </el-col>
          <el-col :xs="24" :sm="10" :md="10" :lg="10" :xl="10">
            <h3>Profile ID</h3>
          </el-col>
          <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8">
            <h3>Platform ID:</h3>
          </el-col>
        </el-row>
        <el-row class="vertical-center text-left" v-for="(platform, index) in platforms" :key="index">
          <el-col :xs="24" :sm="6" :md="6" :lg="6" :xl="6">
            {{platform.username}}
          </el-col>
          <el-col :xs="24" :sm="10" :md="10" :lg="10" :xl="10">
            {{platform.p_id}}
          </el-col>
          <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8">
            {{platform.b_id}}
          </el-col>
        </el-row>
      </div>
    </el-card>

  </div>
</template>

<script>
import { serverAddr } from '../networkConfig.js';
import CripNotice from 'crip-vue-notice';
import TopBar from "./TopBar";

export default {
  name: "PlatformList",
  components: {TopBar},
  data(){
    return {
      platforms: []
    }
  },
  created(){
    serverAddr.post('/v1/platform/')
    .then(this.getPlatformCallback)
    .catch(function (error) {
      console.log(error);
    })
  },
  methods:{
    getPlatformCallback: function(response){
      this.platforms = response.data.data;
    }
  }
}
</script>
<style scoped>
  .heading{
    font-size: 19px;
    font-weight: bold;
  }
</style>




