<template>
  <div class="ConsumerChat">
    <TopBar></TopBar>

    <div class="desktop-chat-layout">
      <div class="chat-rooms-list">
          <el-card
            @click.native="openChatRoom"
            class="box-card"
            style="width: 100%; margin-bottom: 10px; cursor: pointer;">
            <div class="text item">
              <div style="font-size: 0.8em; padding: 10px;">
                <b>storeID:</b> {{currentRoom.storeId}}  <br/> <b>consumerUUID:</b> {{currentRoom.consumerUUID}} <br/> <b>BrandId:</b> {{currentRoom.brandId}}
              </div>
            </div>
          </el-card>
      </div>
      <Chat style="width: 65%;"></Chat>
    </div>
  </div>
</template>

<script>
import { serverAddr, chatServerAddr } from '../networkConfig.js';
import CripNotice from 'crip-vue-notice';
import Chat from "./Chat";
import TopBar from "./TopBar";
import {EventBus} from '../eventBus.js';

export default {
  name: "ConsumerChat",
  components: {TopBar, Chat},
  data(){
    return {
    }
  },
  created(){
    console.log('this.$route.params',  this.$route.params);
  },
  mounted(){
    this.openChatRoom();
  },
  computed: {
    currentRoom(){
      return {
        storeId: this.$route.params.storeId,
        consumerUUID: this.$route.params.consumerUUID,
        brandId: this.$route.params.brand_id
      }
    }
  },
  methods:{
    openChatRoom: function(){
      console.log('openChatRoom: ', this.currentRoom);
      EventBus.$emit('currentRoomChanged', this.currentRoom);
    },
    redirectToBack(){
      this.$router.go(-1);
    }
  }
}
</script>

<style scoped>
.desktop-chat-layout{
    display: flex;
    padding: 0px 15px;
    box-sizing: border-box;
}
.chat-rooms-list{
    padding: 0px 15px 15px 0px;
    height: 86vh;
    flex-grow: 1;
}
.current-room{
  background-color: #DEEAFF;
}
</style>



