<template>
  <div class="BrandList">
    <TopBar></TopBar>


    <el-card class="box-card" style="max-width:80vw; margin:10px auto;">
      <div class="text item">
        <h3 style="margin: 0px; display: flex; align-items: center;">
          <span style="flex-grow: 1; color: #66b1ff;">Brands</span>
        </h3>
      </div>
    </el-card>

    <div>
      <el-card class="box-card" style="max-width:80vw; margin:10px auto;" v-for="(loader,index) in [1,2,3,4]" :key="'loader' + index" v-if="isLoader">
        <el-row>
            <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24" style="margin-bottom: 25px;">
              <content-placeholders>
                <!-- <content-placeholders-heading :img="true" /> -->
                <content-placeholders-text :lines="2" />
              </content-placeholders>
            </el-col>
          </el-row>

      </el-card>

      <el-card class="box-card" style="max-width:80vw; margin:10px auto;" v-if="resultLength0 && !isLoading">
        <el-row>
            <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24" style="margin-bottom: 25px;">
              <h5 style="color: #666;"> No Result Found...</h5>
            </el-col>
          </el-row>
      </el-card>

      <el-card class="box-card" style="max-width:80vw; margin:10px auto;" v-for="(brand, index) in brands" :key="index">
        <div class="text item">
          <div style="font-size: 0.8em; padding: 10px; border-bottom: 1px solid #F1F1F1; display: flex;">
            <span style="flex-grow: 1;">{{brand.br_id}}</span>
            <span>
              <span style="color: #409EFF; cursor: pointer;"  @click="edit(index)">Edit</span> |
              <span style="color: #409EFF; cursor: pointer;"  @click="remove(index)">Delete</span> |
              <span style="color: #409EFF; cursor: pointer;"  @click="manageStaff(index)">Manage Staff</span>
            </span>
          </div>
          <el-row class="vertical-center" style="border-bottom: 1px solid #F1F1F1; padding: 10px;">
            <el-col :xs="24" :sm="6" :md="6" :lg="6" :xl="6">
              Name:
            </el-col>
            <el-col :xs="24" :sm="18" :md="18" :lg="18" :xl="18">
              {{brand.name}}
            </el-col>
          </el-row>
        </div>
      </el-card>
    </div>

  </div>
</template>

<script>
import { serverAddr } from '../networkConfig.js';
import CripNotice from 'crip-vue-notice';
import TopBar from "./TopBar";

export default {
  name: "BrandList",
  components: { TopBar },
  props: ["app_id"],
  data() {
    return {
      resultLength0: false,
      isLoader : true,
      brands: []
    }
  },
  created() {
    this.isLoader = true;
    this.resultLength0 = false;
    serverAddr.post('/v1/brand/',{app_id: this.app_id})
    .then(this.getBrandCallback)
    .catch(function (error) {
      console.log(error);
    })
  },
  methods: {
    getBrandCallback: function (response) {
      this.isLoader = false;
      if(response.data.status){
        this.brands = response.data.data;
        if(!this.brands.length){
          this.resultLength0 = true;
        }
      }
    },
    edit: function (index) {
      var id = this.brands[index].br_id;
      this.$router.push({ path: `/brand-create/${this.app_id}/${id}` })
    },
    manageStaff: function(index){
      var id = this.brands[index].br_id;
      this.$router.push({path: `/brand-staff-list/${this.app_id}/${id}`})
    },
    remove: function(index) {
    serverAddr.post('/v1/brand/delete/',{app_id: this.app_id, br_id:this.brands[index].br_id})
      .then(this.postRemoveCallback)
      .catch(function (error) {
        console.log(error);
      })
    },
    postRemoveCallback: function(response){
      if(response.data.status){
        new CripNotice({
          title: "Success",
          description: "Brand deleted successfully.",
          className: "notices",
          type: "success",
          duration: 3,
        });
        serverAddr.post('/v1/brand/',{app_id: this.app_id})
        .then(this.getBrandCallback)
        .catch(function (error) {
          console.log(error);
        })
      }
    }
  }
}
</script>



