<template>
  <div class="ProfileCreate">
    <TopBar></TopBar>

    <el-card class="box-card" style="max-width:80vw; margin:10px auto;" v-if="id">
        <h3 class="form-section-header" v-if="id">Profile</h3>

        <div v-if="!isAuthCredLoading">
          <el-row class="vertical-center margin-top-20" :gutter="10">
            <el-col :span="12" class="text-left">
              Username<span style="color: red">*</span>
              <input type="text" v-model="username" placeholder="sampleuser@sampledomain.com" required>
            </el-col>
            <el-col :span="12" class="text-left">
              Password<span style="color: red">*</span>
              <input type="password" v-model="password" required>
            </el-col>
          </el-row>
          <el-row class="vertical-center margin-top-20" :gutter="10">
            <el-col :span="24" class="text-left">
              <el-button type="primary" @click.native="register()" v-if="id">Update Credentials</el-button>
            </el-col>
          </el-row>
        </div>

        <el-row :gutter="15" style="margin: 10px auto;" v-if="isAuthCredLoading">
          <el-col :xs="24" :sm="24" :md="12" :lg="24" :xl="24" v-for="(loader,index) in [1,2,3,4]" :key="'loader' + index" style="margin-bottom: 25px;">
              <content-placeholders>
              <!-- <content-placeholders-heading :img="true" /> -->
              <content-placeholders-text :lines="2" />
            </content-placeholders>
          </el-col>
        </el-row>

    </el-card>


    <el-card class="box-card" style="max-width:80vw; margin:10px auto;">
        <h3 class="form-section-header" v-if="!id">Profile</h3>
        <el-row class="vertical-center margin-top-20" v-if="!id" :gutter="10">
          <el-col :span="12" class="text-left">
            <span class="form-label">Username<span style="color: red">*</span></span>
            <input type="text" v-model="username" placeholder="sampleuser@sampledomain.com" required>
          </el-col>
          <el-col :span="12" class="text-left">
            <span class="form-label">Password<span style="color: red">*</span></span>
            <input type="password" v-model="password" required>
          </el-col>
        </el-row>

        <!-- <h3 class="form-section-header" style="margin-top: 30px;">Profile</h3> -->

        <el-row :gutter="15" style="margin: 10px auto;" v-if="isProfileLoading">
          <el-col :xs="24" :sm="24" :md="12" :lg="24" :xl="24" v-for="(loader,index) in [1,2,3,4]" :key="'loader' + index" style="margin-bottom: 25px;">
              <content-placeholders>
              <!-- <content-placeholders-heading :img="true" /> -->
              <content-placeholders-text :lines="2" />
            </content-placeholders>
          </el-col>
        </el-row>

        <div v-for="(pa,index) in profile_attributes" :key="index">
          <el-row class="vertical-center margin-top-20" :gutter="10">
            <el-col :span="24" class="form-label">
              {{pa.display_name}}<span v-if="pa.required" style="color: red">*</span>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="24" class="text-left" v-if="pa.value_type=='text' && pa.key!='notification_token'">
              <input type="text" v-model="dynamic_form_model[pa.key].value" :required="pa.required" :placeholder="textInputPlaceholder(pa.key)">
              <p class="form-error-msg" v-if="!dynamic_form_model[pa.key].isFormFieldValid"> {{dynamic_form_model[pa.key].display_name + ' is required.'}}</p>
            </el-col>
            <el-col :span="24" class="text-left" v-if="pa.value_type=='text' && pa.key=='notification_token'">
              <input type="text" v-model="dynamic_form_model[pa.key].value" :required="pa.required" disabled>
              <p class="form-error-msg" v-if="!dynamic_form_model[pa.key].isFormFieldValid"> {{dynamic_form_model[pa.key].display_name + ' is required.'}}</p>
            </el-col>
            <el-col :span="24" class="text-left" v-if="pa.value_type=='text_area'">
              <textarea v-model="dynamic_form_model[pa.key].value" rows="4" cols="50" :required="pa.required"></textarea>
              <p class="form-error-msg" v-if="!dynamic_form_model[pa.key].isFormFieldValid"> {{dynamic_form_model[pa.key].display_name + ' is required.'}}</p>
            </el-col>
            <el-col :span="24" class="text-left" v-if="pa.value_type=='checkbox'">
              <input type="checkbox" v-model="dynamic_form_model[pa.key].value" :required="pa.required">
              <p class="form-error-msg" v-if="!dynamic_form_model[pa.key].isFormFieldValid"> {{dynamic_form_model[pa.key].display_name + ' is required.'}}</p>
            </el-col>
            <el-col :span="24" class="text-left" v-if="pa.value_type=='number'">
              <input type="number" v-model="dynamic_form_model[pa.key].value" :required="pa.required">
              <p class="form-error-msg" v-if="!dynamic_form_model[pa.key].isFormFieldValid"> {{dynamic_form_model[pa.key].display_name + ' is required.'}}</p>
            </el-col>
            <el-col :span="24" class="text-left" v-if="pa.value_type=='tel'">
              <input type="tel" v-model="dynamic_form_model[pa.key].value" :required="pa.required">
              <p class="form-error-msg" v-if="!dynamic_form_model[pa.key].isFormFieldValid"> {{dynamic_form_model[pa.key].display_name + ' is required.'}}</p>
            </el-col>
            <el-col :span="24" class="text-left" v-if="pa.value_type=='float'">
              <input type="number" v-model="dynamic_form_model[pa.key].value" :required="pa.required">
              <p class="form-error-msg" v-if="!dynamic_form_model[pa.key].isFormFieldValid"> {{dynamic_form_model[pa.key].display_name + ' is required.'}}</p>
            </el-col>
            <el-col :span="24" class="text-left" v-if="pa.value_type=='date'">
              <input type="date" v-model="dynamic_form_model[pa.key].value" :required="pa.required">
              <p class="form-error-msg" v-if="!dynamic_form_model[pa.key].isFormFieldValid"> {{dynamic_form_model[pa.key].display_name + ' is required.'}}</p>
            </el-col>
            <el-col :span="24" class="text-left" v-if="pa.value_type=='time'">
              <input type="time" v-model="dynamic_form_model[pa.key].value" :required="pa.required">
              <p class="form-error-msg" v-if="!dynamic_form_model[pa.key].isFormFieldValid"> {{dynamic_form_model[pa.key].display_name + ' is required.'}}</p>
            </el-col>
            <el-col :span="24" class="text-left" v-if="pa.value_type=='password'">
              <input type="password" v-model="dynamic_form_model[pa.key].value" :required="pa.required">
              <p class="form-error-msg" v-if="!dynamic_form_model[pa.key].isFormFieldValid"> {{dynamic_form_model[pa.key].display_name + ' is required.'}}</p>
            </el-col>
            <el-col :span="24" class="text-left" v-if="pa.value_type=='geo_point' && pa.key!='location'">
              <input type="text" v-model="dynamic_form_model[pa.key].value" :required="pa.required" placeholder="latitude, longitude">
              <p class="form-error-msg" v-if="!dynamic_form_model[pa.key].isFormFieldValid"> {{dynamic_form_model[pa.key].display_name + ' is required.'}}</p>
            </el-col>
            <el-col :span="24" class="text-left" v-if="pa.value_type=='geo_point' && pa.key=='location'">
              <input type="text" v-model="dynamic_form_model[pa.key].value" :required="pa.required" placeholder="latitude, longitude" disabled>
              <p class="form-error-msg" v-if="!dynamic_form_model[pa.key].isFormFieldValid"> {{dynamic_form_model[pa.key].display_name + ' is required.'}}</p>
            </el-col>
          </el-row>
        </div>

         <el-row class="vertical-center margin-top-20" :gutter="10">
            <el-col :span="24" class="form-label">
              Role<span style="color: red">*</span>
            </el-col>
          </el-row>

          <div v-if="!isRoleLoading">
            <el-row>
              <el-col :span="24" class="text-left">
                <select v-model="selected_role">
                    <option :value="{}">Select profile role</option>
                    <option v-for="(option,index) in roles" :key="index" :value="option" v-if="option.name!='BE Admin'"> {{ option.name }} </option>
                </select>
              </el-col>
            </el-row>

            <el-row class="vertical-center margin-top-20" :gutter="10">
              <el-col :span="24">
                <el-button type="primary" @click.native="save()">Save Profile</el-button>
              </el-col>
            </el-row>
          </div>

          <el-row :gutter="15" style="margin: 10px auto;" v-if="isRoleLoading">
            <el-col :xs="24" :sm="24" :md="12" :lg="24" :xl="24" style="margin-bottom: 25px;">
                <content-placeholders>
                <!-- <content-placeholders-heading :img="true" /> -->
                <content-placeholders-text :lines="2" />
              </content-placeholders>
            </el-col>
          </el-row>

    </el-card>


    <el-card class="box-card" style="max-width:80vw; margin:10px auto;" v-if="id">
        <h3 class="form-section-header" v-if="id">Assigned Roles</h3>

        <el-row :gutter="15" style="margin: 10px auto;" v-if="isAssignedRolesLoading">
          <el-col :xs="24" :sm="24" :md="12" :lg="24" :xl="24" v-for="(loader,index) in [1,2,3,4]" :key="'loader' + index" style="margin-bottom: 25px;">
              <content-placeholders>
              <!-- <content-placeholders-heading :img="true" /> -->
              <content-placeholders-text :lines="2" />
            </content-placeholders>
          </el-col>
        </el-row>
        <el-row class="vertical-center margin-top-20" :gutter="10">
          <el-col :span="24" class="text-left">
              <p style="margin: 0px; padding: 10px;" v-for="(role, index) in existing_roles" :key="index">
                <i class="el-icon-circle-close-outline" style="color:red;cursor: pointer;" @click="removeRole(role)"></i> &nbsp;&nbsp;&nbsp;
                {{index + 1}}. {{role.name}}
              </p>
          </el-col>

        </el-row>
    </el-card>





  </div>
</template>

<script>
import { serverAddr } from '../networkConfig.js';
import CripNotice from 'crip-vue-notice';
import TopBar from "./TopBar";

export default {
  name: "ProfileCreate",
  components: { TopBar },
  props: ["app_id", "id"],
  data() {
    return {
      isAuthCredLoading : true,
      isAssignedRolesLoading : true,
      isProfileLoading : true,
      isRoleLoading : true,
      profile_attributes: [],
      dynamic_form_model: {},
      username: '',
      password:'',
      selected_role: {},
      roles: [],
      existing_roles: []
    }
  },
  created() {
    serverAddr.post('/v1/profile-config/', { 'app_id': this.app_id })
    .then(this.getProfileConfigCallback)
    .catch(function (error) {
      console.log(error);
    });
    serverAddr.post('/v1/get-business-entity-roles/')
    .then(this.getRoleCallback)
    .catch(function (error) {
      console.log(error);
    })
    if (this.id) {
      serverAddr.post('/v1/profile/get/', {
        app_id: this.app_id,
        p_id: this.id
      })
      .then(this.getProfileCallBack)
      .catch(function (error) {
        console.log(error);
      })
    }
  },
  methods: {
    textInputPlaceholder(key){
      if(key == 'name'){
        return 'e.g. Anand'
      } else{
        return ''
      }
    },
    initProfileData: function(){
      serverAddr.post('/v1/profile/get/', {
        app_id: this.app_id,
        p_id: this.id
      })
        .then(this.getProfileCallBack)
        .catch(function (error) {
          console.log(error);
        })
    },
    removeRole: function(role){
      serverAddr.post('/v1/profile/remove-role/', {
        r_id: role.uuid,
        p_id: this.id
      })
        .then(()=>{
          this.initProfileData();
        })
        .catch(function (error) {
          console.log(error);
        })

    },
    getRoleCallback: function (response) {
      this.isRoleLoading = false;
      if(response.data.status){
        this.roles = response.data.data;

        this.roles.forEach((role)=>{
          if(role.name == 'Consumer'){
            this.selected_role = role;
          }
        })
      }
    },
    getProfileConfigCallback: function (response) {
      this.isProfileLoading = false;
      if (response.data.status) {
        this.profile_attributes = response.data.data[0].profile_attributes;
        this.dynamic_form_model = {};
        // setting up dynamic profile form model
        this.profile_attributes.forEach((item, index) => {
          this.$set(this.dynamic_form_model, item.key, { ...item, value: '', isFormFieldValid: true });
        })
      }
    },
    getProfileCallBack: function (response) {
      this.isAuthCredLoading = false;
      this.isAssignedRolesLoading = false;
      console.log('getProfileCallBack', response);
      if (response.data.status) {
        for(var i=0;i<this.profile_attributes.length; i++){
          for(var j=0;j<response.data.data[0].profile_attributes.length; j++){
            if(this.profile_attributes[i].key == response.data.data[0].profile_attributes[j].key)
            this.profile_attributes[i] = response.data.data[0].profile_attributes[j];
          };
        };
        if("username" in response.data.data[0].profile){
          this.username = response.data.data[0].profile.username;
        };
        this.dynamic_form_model = {};
        // setting up dynamic profile form model
        this.profile_attributes.forEach((item, index) => {
          this.$set(this.dynamic_form_model, item.key, { ...item, isFormFieldValid: true });
        })

        // autofill lat,lng field from proflie
        var isGeoPointFieldPresent = false;
        var gopPointFieldKey = "";
        for (var key in this.dynamic_form_model){
          if(this.dynamic_form_model[key].value_type == 'geo_point'){
            isGeoPointFieldPresent = true;
            gopPointFieldKey = key;
            break;
          }
        }
        console.log('isGeoPointFieldPresent: ', isGeoPointFieldPresent, 'gopPointFieldKey: ', gopPointFieldKey);
        if(isGeoPointFieldPresent ){
          if(response.data.data[0].location && response.data.data[0].location.latitude && response.data.data[0].location.longitude){
            this.dynamic_form_model[gopPointFieldKey].value = response.data.data[0].location.latitude + ',' + response.data.data[0].location.longitude;
          }
        }

        this.existing_roles = response.data.data[0].roles;
        console.log('this.existing_roles',this.existing_roles)
      }
    },
    validateProfile: function () {
      let isProfileValid = true;
      for (var key in this.dynamic_form_model) {
        console.log(this.dynamic_form_model[key]);
        if (this.dynamic_form_model[key].required && !('' + this.dynamic_form_model[key].value).trim()) {
          isProfileValid = false;
          this.dynamic_form_model[key].isFormFieldValid = false;
        } else {
          this.dynamic_form_model[key].isFormFieldValid = true;
        }
      }
      return isProfileValid;
    },
    save: function () {
      // if(this.username && this.password){

        console.log(this.dynamic_form_model);
        let isProfileValid = this.validateProfile();
        if (!isProfileValid) {
          // show incomplete form error
          console.log('invalid profile');
          console.log(this.dynamic_form_model);
        } else {
          console.log('valid profile');
          console.log(this.dynamic_form_model);

          var properties = {};
          Object.keys(this.dynamic_form_model).forEach((item, index)=>{
            properties[item] = this.dynamic_form_model[item].value;
          });

          // console.log('request properties', properties);


          // network call for save profile
          if (!this.id) {
            serverAddr.post('/v1/profile/create/', {
              app_id: this.app_id,
              properties: properties,
              role: this.selected_role,
              username: this.username,
              password: this.password,
            })
              .then(this.postSaveCallBack)
              .catch(function (error) {
                console.log(error);
              })
          } else {
            serverAddr.post('/v1/profile/update/', {
              app_id: this.app_id,
              p_id: this.id,
              properties: properties,
              role: this.selected_role,
            })
              .then(this.postSaveCallBack)
              .catch(function (error) {
                console.log(error);
              })
          }
        }
      // }
    },
    register: function () {
      if(this.id && this.username && this.password){
        serverAddr.post('/v1/user-registration/', {
          p_id: this.id,
          username: this.username,
          password: this.password
        })
        .then(this.postSaveCallBack)
        .catch(function (error) {
          console.log(error);
        })
      }
    },
    postSaveCallBack: function (response) {
      console.log("Response: ", response, this.app_id);
      if (response.data.status) {
        this.$router.go(-1);
        new CripNotice({
          title: "Success",
          description: this.id ? 'Profile updated successfully.' : 'Profile created successfully.',
          className: "notices",
          type: "success",
          duration: 3,
        });

        if (this.id) {
          this.initProfileData();
        }
        // this.$router.push({ path: `/profile-list/${this.app_id}` });
      }
    }
  }
}
</script>


<style>
.form-error-msg {
  font-size: 0.7em;
  color: red;
  margin-top: 0px;
}

.form-label{
  font-size: 0.8em; font-weight: bold;
}
.form-section-header{
  color: lightskyblue;
}
</style>



