<template>
    <div class="RelationCreate">
        <TopBar></TopBar>
        <el-card class="box-card" style="max-width:80vw; margin:10px auto;">
        <div class="text item">
            <div>
                <select v-model="selectedRelations" v-on:change="getDataForRelation()">
                    <option v-for="(relation, index) in relations" :key="index" :value="relation">
                       ( {{relation.relation.node1_arg_value}} : {{relation.relation.node1_type}} ) -- [: {{relation.relation.relation}} ] -> ( {{relation.relation.node2_arg_value}} : {{relation.relation.node2_type}} )
                    </option>
                </select>
            </div>
            <div v-for="(value, key, index) in nodeDataForRelation" :key="key">
              <h3>{{key}}</h3>
              <p v-for="neo4j_node in value" :key="neo4j_node.uuid" v-on:click="setNodeForRelation(index, key, neo4j_node.uuid)" v-bind:class="{'selected':neo4j_node.uuid == node[key]}">
                {{neo4j_node.uuid}}
              </p>
            </div>
            <div style="margin-top: 30px;">
              <div v-for="(pa,index) in selected_parameters" :key="index">
                <el-row class="vertical-center margin-top-20" :gutter="10" v-if="pa.required">
                  <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8" class="text-right">
                    {{pa.display_name}} *
                  </el-col>
                  <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16" class="text-left" v-if="pa.value_type=='text'">
                    <input type="text" v-model="dynamic_form_model[pa.key].value" required>
                    <p class="form-error-msg" v-if="!dynamic_form_model[pa.key].isFormFieldValid"> {{dynamic_form_model[pa.key].display_name + ' is required.'}}</p>
                  </el-col>
                  <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16" class="text-left" v-if="pa.value_type=='text_area'">
                    <textarea v-model="dynamic_form_model[pa.key].value" rows="4" cols="50" required></textarea>
                    <p class="form-error-msg" v-if="!dynamic_form_model[pa.key].isFormFieldValid"> {{dynamic_form_model[pa.key].display_name + ' is required.'}}</p>
                  </el-col>
                  <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16" class="text-left" v-if="pa.value_type=='checkbox'">
                    <input type="checkbox" v-model="dynamic_form_model[pa.key].value" required>
                    <p class="form-error-msg" v-if="!dynamic_form_model[pa.key].isFormFieldValid"> {{dynamic_form_model[pa.key].display_name + ' is required.'}}</p>
                  </el-col>
                  <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16" class="text-left" v-if="pa.value_type=='number'">
                    <input type="number" v-model="dynamic_form_model[pa.key].value" required>
                    <p class="form-error-msg" v-if="!dynamic_form_model[pa.key].isFormFieldValid"> {{dynamic_form_model[pa.key].display_name + ' is required.'}}</p>
                  </el-col>
                  <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16" class="text-left" v-if="pa.value_type=='tel'">
                    <input type="tel" v-model="dynamic_form_model[pa.key].value" required>
                    <p class="form-error-msg" v-if="!dynamic_form_model[pa.key].isFormFieldValid"> {{dynamic_form_model[pa.key].display_name + ' is required.'}}</p>
                  </el-col>
                  <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16" class="text-left" v-if="pa.value_type=='float'">
                    <input type="number" v-model="dynamic_form_model[pa.key].value" required>
                    <p class="form-error-msg" v-if="!dynamic_form_model[pa.key].isFormFieldValid"> {{dynamic_form_model[pa.key].display_name + ' is required.'}}</p>
                  </el-col>
                  <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16" class="text-left" v-if="pa.value_type=='date'">
                    <input type="date" v-model="dynamic_form_model[pa.key].value" required>
                    <p class="form-error-msg" v-if="!dynamic_form_model[pa.key].isFormFieldValid"> {{dynamic_form_model[pa.key].display_name + ' is required.'}}</p>
                  </el-col>
                  <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16" class="text-left" v-if="pa.value_type=='time'">
                    <input type="time" v-model="dynamic_form_model[pa.key].value" required>
                    <p class="form-error-msg" v-if="!dynamic_form_model[pa.key].isFormFieldValid"> {{dynamic_form_model[pa.key].display_name + ' is required.'}}</p>
                  </el-col>
                  <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16" class="text-left" v-if="pa.value_type=='password'">
                    <input type="password" v-model="dynamic_form_model[pa.key].value" required>
                    <p class="form-error-msg" v-if="!dynamic_form_model[pa.key].isFormFieldValid"> {{dynamic_form_model[pa.key].display_name + ' is required.'}}</p>
                  </el-col>
                  <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16" class="text-left" v-if="pa.value_type=='geo_point'">
                    <input type="text" v-model="dynamic_form_model[pa.key].value" required placeholder="latitude, longitude">
                    <p class="form-error-msg" v-if="!dynamic_form_model[pa.key].isFormFieldValid"> {{dynamic_form_model[pa.key].display_name + ' is required.'}}</p>
                  </el-col>
                </el-row>
                <el-row class="vertical-center margin-top-20" :gutter="10" v-else>
                  <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8" class="text-right">
                    {{pa.display_name}}
                  </el-col>
                  <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16" class="text-left" v-if="pa.value_type=='text'">
                    <input type="text" v-model="dynamic_form_model[pa.key].value">
                    <!-- <p class="form-error-msg" v-if="!dynamic_form_model[pa.key].isFormFieldValid"> {{dynamic_form_model[pa.key].display_name + ' is required.'}}</p> -->
                  </el-col>
                  <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16" class="text-left" v-if="pa.value_type=='text_area'">
                    <textarea v-model="dynamic_form_model[pa.key].value" rows="4" cols="50"></textarea>
                    <!-- <p class="form-error-msg" v-if="!dynamic_form_model[pa.key].isFormFieldValid"> {{dynamic_form_model[pa.key].display_name + ' is required.'}}</p> -->
                  </el-col>
                  <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16" class="text-left" v-if="pa.value_type=='checkbox'">
                    <input type="checkbox" v-model="dynamic_form_model[pa.key].value">
                    <!-- <p class="form-error-msg" v-if="!dynamic_form_model[pa.key].isFormFieldValid"> {{dynamic_form_model[pa.key].display_name + ' is required.'}}</p> -->
                  </el-col>
                  <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16" class="text-left" v-if="pa.value_type=='number'">
                    <input type="number" v-model="dynamic_form_model[pa.key].value">
                    <!-- <p class="form-error-msg" v-if="!dynamic_form_model[pa.key].isFormFieldValid"> {{dynamic_form_model[pa.key].display_name + ' is required.'}}</p> -->
                  </el-col>
                  <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16" class="text-left" v-if="pa.value_type=='tel'">
                    <input type="tel" v-model="dynamic_form_model[pa.key].value">
                    <!-- <p class="form-error-msg" v-if="!dynamic_form_model[pa.key].isFormFieldValid"> {{dynamic_form_model[pa.key].display_name + ' is required.'}}</p> -->
                  </el-col>
                  <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16" class="text-left" v-if="pa.value_type=='float'">
                    <input type="number" v-model="dynamic_form_model[pa.key].value">
                    <!-- <p class="form-error-msg" v-if="!dynamic_form_model[pa.key].isFormFieldValid"> {{dynamic_form_model[pa.key].display_name + ' is required.'}}</p> -->
                  </el-col>
                  <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16" class="text-left" v-if="pa.value_type=='date'">
                    <input type="date" v-model="dynamic_form_model[pa.key].value">
                    <!-- <p class="form-error-msg" v-if="!dynamic_form_model[pa.key].isFormFieldValid"> {{dynamic_form_model[pa.key].display_name + ' is required.'}}</p> -->
                  </el-col>
                  <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16" class="text-left" v-if="pa.value_type=='time'">
                    <input type="time" v-model="dynamic_form_model[pa.key].value">
                    <!-- <p class="form-error-msg" v-if="!dynamic_form_model[pa.key].isFormFieldValid"> {{dynamic_form_model[pa.key].display_name + ' is required.'}}</p> -->
                  </el-col>
                  <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16" class="text-left" v-if="pa.value_type=='password'">
                    <input type="password" v-model="dynamic_form_model[pa.key].value">
                    <!-- <p class="form-error-msg" v-if="!dynamic_form_model[pa.key].isFormFieldValid"> {{dynamic_form_model[pa.key].display_name + ' is required.'}}</p> -->
                  </el-col>
                  <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16" class="text-left" v-if="pa.value_type=='geo_point'">
                    <input type="text" v-model="dynamic_form_model[pa.key].value" required placeholder="latitude, longitude">
                    <!-- <p class="form-error-msg" v-if="!dynamic_form_model[pa.key].isFormFieldValid"> {{dynamic_form_model[pa.key].display_name + ' is required.'}}</p> -->
                  </el-col>
                </el-row>
              </div>
            </div>
            <el-button type="primary" @click.native="createRelation()" v-if="Object.keys(selectedRelations).length > 0">Create relation</el-button>
        </div>
        </el-card>
    </div>
</template>
<script>
import { serverAddr } from '../networkConfig.js';
import CripNotice from 'crip-vue-notice';
import TopBar from "./TopBar";

export default {
    name: 'RelationCreate',
    components: { TopBar },
    props: ["app_id"],
    data() {
        return {
          relations : [],
          selectedRelations : {
            relation:{
              node1_arg_key : "",
              node1_arg_value : "",
              node1_type : "",
              node2_arg_key : "",
              node2_arg_value : "",
              node2_type : "",
              relation : ""
            },
            parameters:[]
          },
          selected_parameters : [],
          nodeDataForRelation : [],
          dynamic_form_model: {},
          node: {}
        }
    },
    created() {
      // ,{n_id: this.id} need to pass BE ID for next API call....Remaining...
      serverAddr.post('/v1/business-entity/get/relation-config/')
      .then(this.getRelationConfigCallBack)
      .catch(function (error) {
        console.log(error);
      })
    },
    methods: {
      getRelationConfigCallBack: function (response) {
        console.log(response);
        if (response.data.status) {
          this.relations = response.data.data;
          for(var i=0;i<response.data.data.length;i++){
            if(this.relations[i].parameters==null)
              this.relations[i].parameters = [];
          }
        }
      },
      getDataForRelation: function(){
        this.selected_parameters = this.selectedRelations.parameters;
        var params = this.selectedRelations.relation.node_types;
        this.dynamic_form_model = {};
        // setting up dynamic profile form model
        this.selected_parameters.forEach((item, index) => {
          this.$set(this.dynamic_form_model, item.key, { ...item, isFormFieldValid: true });
        })
        serverAddr.post('/v1/get-relational-node-data/', {"nodes": params})
        .then(this.postDataForRelationCallback)
        .catch(function(error){
          console.log(error);
        })
      },
      postDataForRelationCallback: function (response) {
        if(response.data.status){
          this.nodeDataForRelation = response.data.data;
          console.log("this.nodeDataForRelation: ",this.nodeDataForRelation);
        }
      },
      setNodeForRelation: function(index, type, uuid){
        // this.node[type] = uuid;
        if(type==this.selectedRelations.relation.node1_type){
          // this.$set(this.selectedRelations.relation, node1_arg_value, uuid);
          this.selectedRelations.relation.node1_arg_value = uuid;
        } else if(type==this.selectedRelations.relation.node2_type){
          // this.$set(this.selectedRelations.relation, node2_arg_value, suuid);
          this.selectedRelations.relation.node2_arg_value = uuid;
        }
        console.log("NODE: ", index, type, this.node);
      },
      createRelation: function(){
        var params = {
          "relation": this.selectedRelations.relation,
          "properties": this.dynamic_form_model
        }
        console.log("SELECTED: ", params);
        serverAddr.post('/v1/create-relation/',params)
        .then(this.postCreateRelationCallback)
        .catch(function(error){
          console.log(error);
        })
      },
      postCreateRelationCallback: function(response){
        console.log(response.data);
      }
    }
}
</script>

<style scoped>
.selected {
  background-color: aqua;
}
</style>



