<template>
  <div class="GeozoneCreate">
    <TopBar></TopBar>

    <el-card class="box-card" style="max-width:50vw; margin:0 auto;">
      <div class="text item">
        <h3 style="border-bottom: 1px solid #ddd; padding: 0 10px 10px 10px;"> Geozone - Create/Edit</h3>

      <el-row>
        <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24" v-for="(list, index) in [1,2,3,4,5]" :key="'loader' +index" style="margin-bottom: 15px" v-if="isLoading">
          <content-placeholders>
            <content-placeholders-text :lines="2" />
          </content-placeholders>
        </el-col>
      </el-row>


      <div v-if="!isLoading">
        <el-row class="vertical-center margin-10">
          <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8" class="form__label">
            Name <span style="color: red;">*</span>
          </el-col>
          <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16">
            <div class="form-group" :class="{ 'form-group--error': $v.name.$error }">
              <input style="width:80%;" type="text" placeholder="e.g. Rcity mall Ghatkopar Mumba" v-model="name" class="form__input" @input="setName($event.target.value)">
            </div>
            <div class="error" v-if="!$v.name.required">Field is required</div>
          </el-col>
        </el-row>

        <el-row class="vertical-center margin-10">
          <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8">
            Location <span style="color: red;">*</span>
            <br>
            <span class="units">Lat/Long</span>
          </el-col>
          <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16">
            <div class="form-group" :class="{ 'form-group--error': $v.location.$error }">
              <input style="width:80%;" type="text" placeholder="e.g. 19.1208854, 72.9096773" v-model="location" class="form__input" @input="setLocation($event.target.value)">
            </div>
            <div class="error" v-if="!$v.location.required">Field is required</div>
            <div class="error" v-if="!$v.location.isLocation">Please enter valid input (lat, lng)</div>
          </el-col>
        </el-row>

        <el-row class="vertical-center margin-10">
          <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8">
            Image url
          </el-col>
          <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16">
            <div class="form-group">
              <input style="width:80%;" type="text" v-model="img_url" class="form__input">
            </div>
          </el-col>
        </el-row>

        <el-row class="vertical-center margin-10">
          <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8">
            Mute Duration <span style="color: red;">*</span>
          </el-col>
          <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16">
            <div class="form-group" :class="{ 'form-group--error': $v.mute_duration.$error }">
              <input style="width:80%;" type="number" placeholder="Mute notification for days" v-model="mute_duration" min="0" max="7" class="form__input" @input="setMuteDuration($event.target.value)">
            </div>
            <div class="error" v-if="!$v.mute_duration.required">Field is required</div>
            <div class="error" v-if="!$v.mute_duration.minValue">Mute duration should be between 0-7</div>
            <div class="error" v-if="!$v.mute_duration.maxValue">Mute duration should be between 0-7</div>
          </el-col>
        </el-row>

        <el-row class="vertical-center margin-10">
          <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8">
            Call To Action Text
          </el-col>
          <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16">
            <div class="form-group">
              <input style="width:80%;" type="text" v-model="call_to_action_text" class="form__input">
            </div>
          </el-col>
        </el-row>

        <!-- <el-row class="vertical-center margin-10" v-if="takeManualCityState">
          <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8" class="form__label">
            Country <span style="color: red;">*</span>
          </el-col>
          <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16">
            <div class="form-group" :class="{ 'form-group--error': $v.country.$error }">
              <multiselect v-model="country"
                  deselect-label=""
                  select-label=""
                  :show-labels="false"
                  placeholder="Select Country"
                  :options="all_country_dropdown"
                  :searchable="true"
                  :allow-empty="true"
                  @input="setCountry(country)"
                  style="width: 80%;">
              </multiselect>
            </div>
            <div class="error" v-if="!$v.country.required">Field is required</div>
          </el-col>
        </el-row> -->

        <!-- <el-row class="vertical-center margin-10" v-if="takeManualCityState">
          <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8" class="form__label">
            State <span style="color: red;">*</span>
          </el-col>
          <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16">
            <div class="form-group" :class="{ 'form-group--error': $v.state.$error }">
              <multiselect v-model="state"
                  deselect-label=""
                  select-label=""
                  :show-labels="false"
                  placeholder="Select State"
                  :options="all_states_dropdown"
                  :searchable="true"
                  :allow-empty="true"
                  @input="setState(state)"
                  style="width:80%;">
              </multiselect>
            </div>
            <div class="error" v-if="!$v.state.required">Field is required</div>
          </el-col>
        </el-row> -->

        <!-- <el-row class="vertical-center margin-10" v-if="takeManualCityState">
          <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8" class="form__label">
            City <span style="color: red;">*</span>
          </el-col>
          <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16">
            <div class="form-group" :class="{ 'form-group--error': $v.city.$error }">
              <multiselect v-model="city"
                  deselect-label=""
                  select-label=""
                  :show-labels="false"
                  placeholder="Select City"
                  :options="all_city_dropdown"
                  :searchable="true"
                  :allow-empty="true"
                  @input="setCity(city)"
                  style="width:80%;">
              </multiselect>
            </div>
            <div class="error" v-if="!$v.city.required">Field is required</div>
          </el-col>
        </el-row> -->

        <el-row class="vertical-center margin-10" v-if="takeManualCityState">
          <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8" class="form__label">
            City <span style="color: red;">*</span>
          </el-col>
          <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16">
            <div class="form-group" :class="{ 'form-group--error': $v.cityStateCountry.$error }">
              <multiselect v-model="cityStateCountry"
                  deselect-label=""
                  select-label=""
                  :show-labels="false"
                  placeholder="Select City"
                  :options="city_state_country_dropdown"
                  :searchable="true"
                  :allow-empty="true"
                  :custom-label="countryStateCityTagName"
                  @input="setCityStateCountry(cityStateCountry)"
                  style="width:80%;">
              </multiselect>
            </div>
            <div class="error" v-if="!$v.cityStateCountry.required">Field is required</div>
          </el-col>
        </el-row>



        <el-row class="vertical-center margin-10">
          <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8">
            Radius <span style="color: red;">*</span> <span class="units"> in meters</span>
          </el-col>
          <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16">
            <div class="form-group" :class="{ 'form-group--error': $v.radius.$error }">
              <input style="width:80%;" type="text" v-model="radius" class="form__input" @input="setRadius($event.target.value)">
            </div>
            <div class="error" v-if="!$v.radius.required">Field is required</div>
            <div class="error" v-if="!$v.radius.minValue">Raidus must be atleast {{$v.radius.$params.minValue.min}} km.</div>
          </el-col>
        </el-row>
        <el-row class="vertical-center margin-10">
          <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8">
            Loitering Delay <span style="color: red;">*</span>
            <span class="units"> in milliseconds</span>
          </el-col>
          <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16">
            <div class="form-group" :class="{ 'form-group--error': $v.loitering_delay.$error }">
              <input style="width:80%;" type="text" v-model="loitering_delay" class="form__input" @input="setLoiteringDelay($event.target.value)">
            </div>
            <div class="error" v-if="!$v.loitering_delay.required">Field is required</div>
            <div class="error" v-if="!$v.loitering_delay.minValue">Loitering Delay must be atleast {{$v.loitering_delay.$params.minValue.min}} s.</div>
          </el-col>
        </el-row>
        <el-row class="vertical-center margin-10">
          <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8">
            Valid Until <span style="color: red;">*</span>
          </el-col>
          <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16">
            <div class="form-group" :class="{ 'form-group--error': $v.expiry_timestamp.$error }">
              <el-date-picker
                v-model="expiry_timestamp"
                type="datetime"
                :format="'dd-MM-yyyy HH:mm'"
                placeholder="Select date and time"
                @input="setExpiryTimestamp(expiry_timestamp)">
              </el-date-picker>

              <!-- <input style="width:80%;" type="datetime-local" v-model="expiry_timestamp" class="form__input" @input="setExpiryTimestamp($event.target.value)"> -->
            </div>
            <div class="error" v-if="!$v.expiry_timestamp.required">Field is required</div>
            <div class="error" v-if="!$v.expiry_timestamp.greaterThanCurrentTime">Valid Until must be greater than current time.</div>
          </el-col>
        </el-row>

        <el-row class="vertical-center margin-10">
          <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8">
          </el-col>
          <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16">
            <input type="checkbox" value="true" v-model="is_active">
            Is Active
          </el-col>
        </el-row>
        <el-row class="vertical-center" style="text-align:center; margin-top: 15px;">
          <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
            <p  style="margin-top: 0px; color: red; font-size: 0.8em;" v-if="takeManualCityState">Error getting city state from location. Please fill manually.</p>
            <el-button type="primary" @click.native="save()">Save</el-button>
          </el-col>
        </el-row>
        </div>
      </div>
    </el-card>

  </div>
</template>

<script>
import { serverAddr } from '../networkConfig.js';
import CripNotice from 'crip-vue-notice';
import TopBar from "./TopBar";
import { required, minValue, maxValue } from 'vuelidate/lib/validators'
import axios from 'axios';
import Multiselect from 'vue-multiselect';


const greaterThanCurrentTime = (value) => new Date(value) > new Date()
const isLocation = (value) => value.split(",")[0]!='' && value.split(",")[0]!=undefined && value.split(",")[1]!='' && value.split(",")[1]!=undefined && !isNaN(parseInt(value.split(",")[0].trim())) && !isNaN(parseInt(value.split(",")[1].trim()))

export default {
  name: "GeozoneCreate",
  components: { TopBar, multiselect:Multiselect },
  props: ["app_id", "id"],
  data() {
    return {
      cityStateCountry: {
        city: '',
        state: '',
        country: '',
      },
      city_state_country_dropdown: [],
      all_states_dropdown: [],
      all_city_dropdown: [],
      all_country_dropdown: [],
      isLoading : true,
      name: '',
      city: '',
      state: '',
      country: '',
      img_url: '',
      mute_duration: 0,
      call_to_action_text: '',
      takeManualCityState: false,
      // reverse_location: {city: '', state: ''},
      location: '',
      is_active: true,
      radius: 500,
      loitering_delay: 30000,
      expiry_timestamp: new Date(new Date().setFullYear(new Date().getFullYear()+1)),
    }
  },
  validations: {
    name: {
      required
    },
    city: {
      // required
    },
    state: {
      // required
    },
    country: {
      // required
    },
    cityStateCountry: {
      // required
    },
    location: {
      required,
      isLocation
    },
    radius: {
      required,
      minValue: minValue(0)
    },
    loitering_delay: {
      required,
      minValue: minValue(0)
    },
    mute_duration: {
      required,
      minValue: minValue(0),
      maxValue: maxValue(7)
    },
    expiry_timestamp: {
      required,
      greaterThanCurrentTime
    },
  },
  watch: {
    country: function (newCountry, oldCountry) {
      this.all_states_dropdown = [];
      this.all_city_dropdown = [];
      // this.state = "";
      // this.city = "";
      if(!newCountry){
      }else{
        this.getStates();
      }
    },
    state: function (newState, oldState) {
      this.all_city_dropdown = [];
      this.city = "";
      if(!newState){
      }else{
        this.getCities();
      }
    }
  },
  created() {
    serverAddr
      .post("/v1/dropdowns/countries/", {})
      .then(response => {
        console.log('geo-zone-get-countries-dropdown', response);
        this.all_country_dropdown = response.data.data ? response.data.data.map(item => item.country) : [];
      })
      .catch(function(error) {
        console.log(error);
      });

    serverAddr
      .post("/v1/dropdowns/get-all-city-state-country/", {})
      .then(response => {
        console.log('get cities states countries dropdown', response);
        this.city_state_country_dropdown = response.data.data;
      })
      .catch(function(error) {
        console.log(error);
      });

    if (this.id) {
      serverAddr.post('/v1/geo-zone/get/', {
        g_id: this.id,
        app_id: this.app_id,
      })
      .then(this.getGeozoneCallBack)
      .catch(function (error) {
        console.log(error);
      })
    } else {
      this.isLoading = false;
    }
  },
  methods: {
    countryStateCityTagName ({ country, state, city }) {
      if(country, state, city){
        return `${country} -> ${state} -> ${city}`
      }
    },
    setCityStateCountry(value){
      console.log('setCityStateCountry', value);
      this.cityStateCountry = value
      this.$v.cityStateCountry.$touch()
    },
    setMuteDuration(value) {
      this.mute_duration = value
      this.$v.mute_duration.$touch()
    },
    setName(value) {
      this.name = value
      this.$v.name.$touch()
    },
    // setCity(value) {
    //   this.city = value
    //   this.$v.city.$touch()
    // },
    // setState(value) {
    //   this.state = value
    //   this.$v.state.$touch()
    // },
    // setCountry(value) {
    //   this.country = value
    //   this.$v.country.$touch()
    // },
    setRadius(value) {
      this.radius = value
      this.$v.radius.$touch()
    },
    setLoiteringDelay(value) {
      this.loitering_delay = value
      this.$v.loitering_delay.$touch()
    },
    setExpiryTimestamp(value) {
      console.log('setExpiryTimestamp',value);
      this.expiry_timestamp = value
      this.$v.expiry_timestamp.$touch()
    },
    setLocation(value) {
      this.location = value
      this.$v.location.$touch()
    },
    getStates: function(){
      serverAddr
      .post("/v1/dropdowns/states/", {country: this.country})
      .then(response => {
        console.log('geo-zone-get-states-dropdown', response);
        this.all_states_dropdown = response.data.data ? response.data.data.map(item => item.state) : [];
      })
      .catch(function(error) {
        console.log(error);
      });
    },
    getCities: function(){
      serverAddr
      // .post("/v1/geo-zone/get-cities/", {app_id: this.app_id})
      .post("/v1/dropdowns/cities-for-state/", {
          country: this.country,
          state: this.state
        })
      .then(response => {
        console.log('geo-zone-get-cities-dropdown', response);
        this.all_city_dropdown = response.data.data ? response.data.data.map(item => item.city) : [];
      })
      .catch(function(error) {
        console.log(error);
      });
    },
    convertUTCDateToLocalString: function (date) {
      var offset = new Date().getTimezoneOffset()*60*1000;
      var newDate = new Date(date - offset);
      return new Date(newDate).toISOString().replace("Z","");
    },
    getGeozoneCallBack: function (response) {
      this.isLoading = false;

      this.name = response.data.data[0].name;
      // this.city = response.data.data[0].city;
      // this.state = response.data.data[0].state;
      // this.country = response.data.data[0].country;
      this.cityStateCountry.city = response.data.data[0].city;
      this.cityStateCountry.state = response.data.data[0].state;
      this.cityStateCountry.country = response.data.data[0].country;
      this.img_url = response.data.data[0].img_url;
      this.call_to_action_text = response.data.data[0].call_to_action_text;
      this.location = response.data.data[0].location;
      this.is_active = response.data.data[0].is_active;
      this.radius = response.data.data[0].radius;
      this.loitering_delay = response.data.data[0].loitering_delay;
      this.mute_duration = response.data.data[0].mute_duration;
      // this.expiry_timestamp = this.convertUTCDateToLocalString(response.data.data[0].expiry_timestamp);
      this.expiry_timestamp = new Date(response.data.data[0].expiry_timestamp);
      console.log('cityStateCountry',this.cityStateCountry);
    },
    save: function () {
      this.$v.$touch();
      if (this.$v.$invalid) {
        } else {
        // const google_map_api_key = 'AIzaSyBFaOsUx42A4_EJopMIZZZmq4N1UyRE69Y';
        const google_map_api_key = 'AIzaSyBWx06oZ1qYEP3OLaiY-yEaTEHQ_a9nUPA';
        console.log('Getting city state from location coordinates...');
        // &key=${google_map_api_key}
        axios.get(`https://maps.googleapis.com/maps/api/geocode/json?latlng=${this.location.replace(' ', '')}&key=${google_map_api_key}`)
        .then(response => {
          console.log("location response: ", response);
          var results = response.data.results;
          if(results.length){
            if(!this.takeManualCityState){
              this.cityStateCountry.city = "";
              this.cityStateCountry.state = "";
              this.cityStateCountry.country = "";
            }
            for (var ac = 0; ac < results[0].address_components.length; ac++) {
              var component = results[0].address_components[ac];

              switch(component.types[0]) {
                case 'locality':
                      if(component.long_name.trim().length){
                        this.cityStateCountry.city = component.long_name;
                      }
                      break;
                case 'administrative_area_level_1':
                    if(component.long_name.trim().length){
                      this.cityStateCountry.state = component.long_name;
                    }
                    break;
                case 'country':
                    if(component.long_name.trim().length){
                      this.cityStateCountry.country = component.long_name;
                    }
                    // storableLocation.registered_country_iso_code = component.short_name;
                  break;
              }
            }
          }

          console.log('this.reverse_location', this.cityStateCountry.city, this.cityStateCountry.state, this.cityStateCountry.country);
          if((this.cityStateCountry.city == "" || this.cityStateCountry.state == "" || this.cityStateCountry.country == "") && !this.takeManualCityState){
            console.log('could not fild city or state from location...');
            this.takeManualCityState = true;
          }else{
            if(this.cityStateCountry.city && this.cityStateCountry.state && this.cityStateCountry.country){
              this.takeManualCityState = false;
              this.storeGeozone();
            }else{
              console.log('please fill manual city and state...');
            }
          }
        })
        .catch(e => {
          console.log(e);
        })

      }
    },
    storeGeozone(){
        if (!this.id) {
          serverAddr.post('/v1/geo-zone/create/', {
            app_id: this.app_id,
            properties: {
              name: this.name,
              city: this.cityStateCountry.city,
              state: this.cityStateCountry.state,
              country: this.cityStateCountry.country,
              img_url : this.img_url,
              location: this.location,
              is_active: this.is_active,
              call_to_action_text: this.call_to_action_text,
              radius: parseFloat(this.radius),
              loitering_delay: parseFloat(this.loitering_delay),
              expiry_timestamp: new Date(this.expiry_timestamp).getTime(),
              mute_duration : Number(this.mute_duration)
            }
          })
            .then(this.postSaveCallBack)
            .catch(function (error) {
              console.log(error);
            })
        } else {
          serverAddr.post('/v1/geo-zone/update/', {
            app_id: this.app_id,
            g_id: this.id,
            properties: {
              name: this.name,
              city: this.cityStateCountry.city,
              state: this.cityStateCountry.state,
              country: this.cityStateCountry.country,
              img_url : this.img_url,
              call_to_action_text: this.call_to_action_text,
              location: this.location,
              is_active: this.is_active,
              radius: parseFloat(this.radius),
              loitering_delay: parseFloat(this.loitering_delay),
              expiry_timestamp: new Date(this.expiry_timestamp).getTime(),
              mute_duration : Number(this.mute_duration)
            }
          })
          .then(this.postSaveCallBack)
          .catch(function (error) {
            console.log(error);
          })
        }
    },
    postSaveCallBack: function (response) {
      console.log("Response: ", response);
      if (response.data.status) {

        var msg = this.id ? "Zone updated successfully." : "Zone created successfully."

        new CripNotice({
          title: "Success",
          description: msg,
          className: "notices",
          type: "success",
          duration: 3,
        });
        this.$router.push({path : `/geozone-list/${this.app_id}`});
      }
    }
  },
}
</script>

<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>
<style scoped>
.GeozoneCreate{
  font-size: 14px;
}
input{
  padding: 10px 15px;
}
.units{
  display: block; font-size: 0.9em; color: #333; font-weight: bold;
}
</style>



