<template>
  <div class="RoleCreate">
    <TopBar></TopBar>

    <el-card class="box-card" style="max-width:50vw; margin:0 auto;">
      <div class="text item">
        <el-row class="vertical-center" style="text-align:center;">
          <el-col :xs="24" :sm="4" :md="4" :lg="4" :xl="4">
            Role name:
          </el-col>
          <el-col :xs="24" :sm="14" :md="14" :lg="14" :xl="14">
            <div class="form-group" :class="{ 'form-group--error': $v.name.$error }">
              <input style="width:80%;" type="text" v-model="name" class="form__input" @input="setName($event.target.value)">
            </div>
            <div class="error" v-if="!$v.name.required">Field is required</div>
          </el-col>
          <el-col :xs="24" :sm="6" :md="6" :lg="6" :xl="6">
            <el-button type="primary" @click.native="save()">Create Role</el-button>
          </el-col>
        </el-row>
      </div>
    </el-card>

  </div>
</template>

<script>
import { serverAddr } from '../networkConfig.js';
import CripNotice from 'crip-vue-notice';
import TopBar from "./TopBar";
import { required } from 'vuelidate/lib/validators'

export default {
  name: "RoleCreate",
  components: { TopBar },
  props: ["id"],
  data() {
    return {
      name: '',
    }
  },
  validations: {
    name: {
      required
    }
  },
  created() {
    if (this.id) {
      serverAddr.post('/v1/role/get/', {
        r_id: this.id
      })
      .then(this.getRoleCallBack)
      .catch(function (error) {
        console.log(error);
      })
    }
  },
  methods: {
    setName(value) {
      this.name = value
      this.$v.name.$touch()
    },
    getRoleCallBack: function (response) {
      this.name = response.data.data[0].name;
    },
    save: function () {
      this.$v.$touch();
      if (this.$v.$invalid) {
      } else {
        if (!this.id) {
          serverAddr.post('/v1/role/create/', {
            properties: {
              name: this.name
            }
          })
            .then(this.postSaveCallBack)
            .catch(function (error) {
              console.log(error);
            })
        } else {
          serverAddr.post('/v1/role/update/', {
            r_id: this.id,
            properties: {
              name: this.name,
            }
          })
          .then(this.postSaveCallBack)
          .catch(function (error) {
            console.log(error);
          })
        }
      }
    },
    postSaveCallBack: function (response) {
      console.log("Response: ", response);
      if (response.data.status) {
        new CripNotice({
          title: "Success",
          description: "Roles created successfully.",
          className: "notices",
          type: "success",
          duration: 3,
        });
      }
      this.$router.push({path:"/role-list"});
    }
  },
}
</script>



