router.beforeEach((to, from, next) => {
  // console.log('beforeEach to.meta', to.meta);
  // console.log('beforeEach to', to);
  // console.log('beforeEach from', from);
  if(to.meta && to.meta.access){
    try{
      var authorized_to = to.meta.access;
      var roles = JSON.parse(localStorage.getItem('roles'));
      var user_access_permissions = roles.map((item)=>item.name);
      var access_granted = intersection(user_access_permissions, authorized_to);

      if(access_granted.length){
        console.log('Authorized access');
        next();
      }else{
        console.log('Un-Authorized access');
        next({ name: 'dashboard', query: {error: `You don't have permission to access ${startCase(to.name)} page.`} });
      }
    }catch(e){
      console.log('EXCEPTION: error parsing roles from storage...');
      next({ name: 'login' });
    }
  }else{
    console.log('open route...');
    if(to.name === 'login'){
      console.log('login route found in to');
      try{
        var token = localStorage.getItem('x-access-token');
        if(token){
          //auto redirect to dahsboard page
          console.log('auto login redirecting to dahaboard...');
          next({ name: 'dashboard' });
        }else{
          //go to login page
          console.log('old login session not present...');
          next();
        }
      }catch(e){
        //continue to login page
        console.log('exception getting login session...', e);
        next();
      }
    }else{
      next();
    }
  }

})

export default router



