<template>
    <div class="ProfileConfig">
        <TopBar></TopBar>
        <el-row :gutter="10" v-if="app_id" style="padding: 0px 10px;">
            <el-col :xs="24" :sm="24" :md="6" :lg="6" :xl="6">
                <el-card class="box-card">
                    <div slot="header">
                        <h3 style="margin: 0px;">New Profile Attribute</h3>
                    </div>
                    <div class="text item">
                        <p class="form-label">Key&nbsp;<span style="color: red;">*</span></p>
                        <div>
                            <div class="form-group" :class="{ 'form-group--error': $v.key.$error }">
                                <input type="text" v-model="key" class="form__input" @input="setKey($event.target.value)">
                            </div>
                            <div class="error" v-if="!$v.key.required">Field is required</div>
                        </div>

                        <p class="form-label">Display Name&nbsp;<span style="color: red;">*</span></p>
                        <div>
                            <div class="form-group" :class="{ 'form-group--error': $v.display_name.$error }">
                                <input type="text" v-model="display_name" class="form__input" @input="setDisplay_name($event.target.value)">
                            </div>
                            <div class="error" v-if="!$v.display_name.required">Field is required</div>
                        </div>

                        <p class="form-label">Value Type&nbsp;<span style="color: red;">*</span></p>
                        <div class="form-group" :class="{ 'form-group--error': $v.value_type.$error }">
                            <select v-model="value_type" class="form__input" @change="setValue_type($event.target.value)">
                                <option value="">Select</option>
                                <option v-for="(option,index) in select_dropdown" :key="index" :value="option.value"> {{ option.text }} </option>
                            </select>
                        </div>
                        <div class="error" v-if="!$v.value_type.required">Field is required</div>

                        <p class="form-label">Privacy Level&nbsp;<span style="color: red;">*</span></p>
                        <div class="form-group" :class="{ 'form-group--error': $v.privacy_level.$error }">
                            <input type="number" v-model="privacy_level" class="form__input" @input="setPrivacy_level($event.target.value)">
                        </div>
                        <div class="error" v-if="!$v.privacy_level.required">Field is required</div>

                        <div>
                          <p class="form-label">Anonymous User Value&nbsp;<span style="color: red;" v-if="required">*</span></p>
                          <div class="form-group" :class="{ 'form-group--error': anonymous_user_value_error }" @input="setDefault_value($event.target.value)">
                              <input id="inputType" v-model="anonymous_user_value" class="form__input">
                          </div>
                          <div class="error" v-if="anonymous_user_value_error">Field is required</div>
                        </div>


                        <p class="form-label">Required?</p>
                        <input name="isRequired" type="radio" :value="true" v-model="required"> Yes
                        <input name="isRequired" type="radio" :value="false" v-model="required"> No
                        <div style="margin-top: 15px;">
                            <el-button size="mini" type="primary" @click.native="save()">Create Attributes</el-button>
                            <el-button size="mini" type="danger" @click.native="clearEdit()">Clear</el-button>
                        </div>
                    </div>
                </el-card>
            </el-col>

            <el-col :xs="24" :sm="24" :md="18" :lg="18" :xl="18">
                <el-card class="box-card">
                    <div slot="header" class="clearfix">
                        <h3 style="margin: 0px; display: flex;">
                            <span style="flex-grow: 1;">Profile Attributes</span>
                            <el-button type="success" size="mini" @click="createSystemConfigs">Create System Configs</el-button>
                        </h3>
                    </div>
                    <div class="text item">
                        <el-row class="vertical-center margin-bottom-10" style="border-bottom: 1px solid #EEE; padding: 5px 10px; ">
                            <el-col :xs="24" :sm="4" :md="4" :lg="4" :xl="4">
                             Name
                            </el-col>
                            <el-col :xs="24" :sm="4" :md="4" :lg="4" :xl="4">
                             Key
                            </el-col>
                            <el-col :xs="24" :sm="4" :md="4" :lg="4" :xl="4">
                             Privacy Level
                            </el-col>
                            <el-col :xs="24" :sm="4" :md="4" :lg="4" :xl="4">
                             Type
                            </el-col>
                            <el-col :xs="24" :sm="4" :md="4" :lg="4" :xl="4">
                             Is Required
                            </el-col>
                            <el-col :xs="24" :sm="4" :md="4" :lg="4" :xl="4">
                             Action
                            </el-col>
                            <el-col :xs="24" :sm="4" :md="4" :lg="4" :xl="4">
                             Order
                            </el-col>
                        </el-row>
                        <el-row :style="{'background-color': index % 2 == 0 ? 'white' : '#F1F1F1'}" style="padding: 10px 10px;" class="vertical-center" v-for="(profile_attribute, index) in profile_attributes" :key="index">
                            <el-col :xs="24" :sm="4" :md="4" :lg="4" :xl="4">
                                {{profile_attribute.display_name}}
                            </el-col>
                            <el-col :xs="24" :sm="4" :md="4" :lg="4" :xl="4">
                                {{profile_attribute.key}}
                            </el-col>
                            <el-col :xs="24" :sm="4" :md="4" :lg="4" :xl="4">
                                {{profile_attribute.privacy_level}}
                            </el-col>
                            <el-col :xs="24" :sm="4" :md="4" :lg="4" :xl="4">
                                {{profile_attribute.value_type}}
                            </el-col>
                            <el-col :xs="24" :sm="4" :md="4" :lg="4" :xl="4">
                                {{profile_attribute.required}}
                            </el-col>
                            <el-col :xs="24" :sm="4" :md="4" :lg="4" :xl="4">
                                <span v-if="!profile_attribute.is_system_config" style="font-weight: bold; cursor: pointer; font-size: 0.8em;" @click="edit(index)">Edit</span>
                                <span v-if="!profile_attribute.is_system_config"> | </span>
                                <span v-if="!profile_attribute.is_system_config" style="font-weight: bold; cursor: pointer; font-size: 0.8em;" @click="remove(index)">Delete</span>
                            </el-col>
                            <el-col :xs="24" :sm="4" :md="4" :lg="4" :xl="4">
                                <button @click="setUp(profile_attribute,index)">Up</button>
                                <button @click="setDown(profile_attribute,index)">Down</button>
                            </el-col>
                        </el-row>
                    </div>
                     <div style="margin: 10px 0; text-align: right;">
                       <button class="el-button el-button--primary el-button--mini" @click="saveChangedOrderOfProfileAttributes">Save order</button>
                     </div>
                </el-card>
            </el-col>
        </el-row>


    </div>
</template>

<script>
import { serverAddr } from '../networkConfig.js';
import CripNotice from 'crip-vue-notice';
import TopBar from "./TopBar";
import { required, minValue } from 'vuelidate/lib/validators'

export default {
    name: "ProfileConfig",
    components: { TopBar },
    props: ["app_id"],
    data() {
        return {
            anonymous_user_value_error: false,
            original_attribute_key_to_edit: null,
            key: '',
            display_name: '',
            value_type: '',
            privacy_level: 0,
            required: false,
            anonymous_user_value: '',
            profile_attributes: [],
            isEdit:false,
            // select_dropdown: [
            //     {value:"str", render_type:"text", text:"String"},
            //     {value:"boolean", render_type:"radio", text:"Boolean"},
            //     {value:"int", render_type:"number", text:"Number"},
            //     {value:"float", render_type:"number", text:"Float"},
            //     {value:"date", render_type:"date", text:"Date Field"},
            //     {value:"time", render_type:"time", text:"Time Field"},
            //     {value:"password", render_type:"password", text:"Password"}
            // ]
            select_dropdown: [
                {value:"text", text:"String"},
                {value:"text_area", text:"Text-area"},
                {value:"checkbox", text:"Boolean"},
                {value:"number", text:"Number"},
                {value:"tel", text:"Mobile Number"},
                {value:"float", text:"Float"},
                {value:"date", text:"Date Field"},
                {value:"time", text:"Time Field"},
                {value:"password", text:"Password"},
                {value:"geo_point", text:"Location (lat, lng)"}
            ]
        }
    },
    watch: {
      anonymous_user_value: function(newValue, oldValue){

        if((!newValue || !oldValue) && this.required){
          this.anonymous_user_value_error = true;
        } else {
          this.anonymous_user_value_error = false;
        }
      }
    },
    validations: {
        key: {
        required
        },
        display_name: {
        required
        },
        value_type: {
        required
        },
        privacy_level: {
        required
        },
        anonymous_user_value: {
        // required
        }
    },
    created() {
        serverAddr.post('/v1/profile-config/',{'app_id':this.app_id})
        .then(this.getAppCallback)
        .catch(function (error) {
            console.log(error);
        })
    },
    methods: {
        saveChangedOrderOfProfileAttributes(){
          serverAddr.post('/v1/save-order-of-profile-config/', {
            app_id: this.app_id,
            profile_attributes : this.profile_attributes
          })
            .then((response)=>{
              if(response.data){
                console.log('response: ', response);
              }else{
                console.log('error');
              }
            })
            .catch(function (error) {
                console.log(error);
            })
        },
        setUp(profile,index){
          console.log(index > 0, index);
          if(index > 0){
            this.profile_attributes.splice(index, 1);
            this.profile_attributes.splice(index-1, 0, profile);
            console.log('setUp', this.profile_attributes);
          }
        },
        setDown(profile,index){
          console.log(index < this.profile_attributes.length-1, index);
          if(index < this.profile_attributes.length-1){
            this.profile_attributes.splice(index, 1);
            this.profile_attributes.splice(index+1, 0, profile);
            console.log('setDown', this.profile_attributes);
          }
        },
        setDefault_value(value) {
            this.anonymous_user_value = value;
            this.$v.key.$touch();
        },
        setKey(value) {
            this.key = value
            this.$v.key.$touch()
        },
        setDisplay_name(value) {
            this.display_name = value
            this.$v.display_name.$touch()
        },
        setValue_type(value) {
            this.value_type = value;
            this.$v.value_type.$touch();
            console.log('this.value_type', this.value_type);
            if(this.value_type == 'text' || this.value_type == 'text_area' || this.value_type == 'checkbox' || this.value_type == 'geo_point'){
               document.getElementById('inputType').type = 'text';
            }
            else if(this.value_type == 'number' || this.value_type == 'float'){
               document.getElementById('inputType').type = 'number';
            }
            else if(this.value_type == 'tel'){
               document.getElementById('inputType').type = 'tel';
            }
            else if(this.value_type == 'date'){
               document.getElementById('inputType').type = 'date';
            }
            else if(this.value_type == 'time'){
               document.getElementById('inputType').type = 'time';
            }
            else if(this.value_type == 'password'){
               document.getElementById('inputType').type = 'password';
            } else {
              document.getElementById('inputType').type = 'text';
            }
        },
        setPrivacy_level(value) {
            this.privacy_level = value
            this.$v.privacy_level.$touch()
        },
        getAppCallback: function (response) {
            if (response.data.status) {
                this.profile_attributes = response.data.data[0].profile_attributes;
            }
        },
        updateAttributeList: function(response){

        },
        createSystemConfigs(){
            console.log('createSystemConfigs...');
            serverAddr.post('/v1/profile-config/create-system-configs/', { app_id: this.app_id })
            .then((response)=>{
                console.log('response: ', response);
                if(response.data && response.data.status){
                    console.log('success');
                    new CripNotice({
                    title: "Success",
                    description: "Created successfully.",
                    className: "notices",
                    type: "success",
                    duration: 3,
                    });
                    serverAddr.post('/v1/profile-config/',{'app_id':this.app_id})
                    .then(this.getAppCallback)
                    .catch(function (error) {
                        console.log(error);
                    })
                }
            })
            .catch(function (error) {
                console.log(error);
            })
        },
        save: function () {
            this.$v.$touch();
            console.log('this.$v.$invalid)',this.$v.$invalid);
            if(!this.$v.$invalid){
              if(this.required && this.anonymous_user_value == ""){
                this.$v.$touch();
                this.anonymous_user_value_error = true;
              } else {
                if(!this.isEdit){
                    serverAddr.post('/v1/profile-config/create/', {
                        app_id: this.app_id,
                        properties: {
                            original_attribute_key_to_edit: this.key,
                            key: this.key,
                            display_name: this.display_name,
                            value_type: this.value_type,
                            privacy_level: this.privacy_level,
                            required: this.required,
                            anonymous_user_value: this.anonymous_user_value
                        }
                    })
                    .then(this.postSaveCallBack)
                    .catch(function (error) {
                        console.log(error);
                    })
                }else{
                    serverAddr.post('/v1/profile-config/update/', {
                        app_id: this.app_id,
                        properties: {
                            original_attribute_key_to_edit: this.original_attribute_key_to_edit,
                            key: this.key,
                            display_name: this.display_name,
                            value_type: this.value_type,
                            privacy_level: String(this.privacy_level),
                            required: this.required,
                            anonymous_user_value: this.anonymous_user_value
                        }
                    })
                    .then(this.postSaveCallBack)
                    .catch(function (error) {
                        console.log(error);
                    })
                }

              }
            }
        },
        postSaveCallBack: function(response){
            if(response.data.status){
                this.profile_attributes = response.data.data;
                this.isEdit=false;
                this.key = '';
                this.display_name = '';
                this.anonymous_user_value = '';
                this.value_type = '';
                this.privacy_level = '';
                this.required = false;
                this.original_attribute_key_to_edit = null;
                serverAddr.post('/v1/profile-config/',{'app_id':this.app_id})
                .then(this.getAppCallback)
                .catch(function (error) {
                    console.log(error);
                })
            }
        },
        edit: function (index) {
            this.original_attribute_key_to_edit = this.profile_attributes[index].key;
            this.isEdit=true;
            this.key = this.profile_attributes[index].key;
            this.display_name = this.profile_attributes[index].display_name;
            this.value_type = this.profile_attributes[index].value_type;
            this.privacy_level = this.profile_attributes[index].privacy_level;
            this.required = this.profile_attributes[index].required;
            this.anonymous_user_value = this.profile_attributes[index].anonymous_user_value;
        },
        clearEdit: function(){
            this.original_attribute_key_to_edit = null;
            this.isEdit = false;
            this.key  = "";
            this.display_name  = "";
            this.value_type  = "";
            this.privacy_level  = "";
            this.required  = false;
            this.anonymous_user_value = "";
        },
        remove: function (index) {
            serverAddr.post('/v1/profile-config/delete/', {
                app_id: this.app_id,
                key: this.profile_attributes[index].key,
            })
            .then(this.postSaveCallBack)
            .catch(function (error) {
                console.log(error);
            })
        },
    },
}
</script>


<style scoped>
.ProfileConfig{
    font-size: 14px;
}
.form-label{
    /* font-weight: bold; */
    color: #777;
    font-size: 0.8em;
    margin-bottom: 0px;
    margin-top: 20px;
}
</style>



