<template>
  <div class="TopBar" style="background-color: #409EFF;">
    <div class="Top-Navbar">
      <div class="Left">
        <div class="Menu-button">
          <img src="../assets/icon/back.svg" @click="$router.go(-1)" style="height: 25px; vertical-align: middle; margin-right: 10px;">
        </div>
        <div class="Menu-button">
          <i class="el-icon-menu" @click="openSidebar" style="color: #444;"></i>
        </div>
        <div class="logo">
          <router-link  :to="{path: '/dashboard'}" style="text-decoration: none; color: #444;">
            Inventa
          </router-link>
        </div>
      </div>
      <div class="Center text-center">
        Welcome, {{username}}
        <div style="font-size: 0.7em;">
          {{authUser.roles[0].name}}
        </div>
      </div>
      <div class="Right">
        <el-button v-if="authUser.p_id" type="primary" size="mini" @click.native="doLogout()">Logout</el-button>
      </div>
    </div>



    <div  v-if="sidebar" :class="{ 'side-bar': sidebar }" style="text-align: left;">
      <div class="sidebar-menu">
        <div class="close-button">
          <span class="sidebar-logo" style="float:left;">
            <router-link class="links"  :to="{path: '/dashboard'}">
              Inventa
            </router-link>
          </span>
          <span class="close-sidebar-btn" @click="sidebar = false">&#x2715;</span>
        </div>
        <div class="sidebar-menu-item" v-if="authUser.roles[0].name == 'BE Admin' && !this.app_id">
          Please select an app to proceed...
        </div>
        <div class="sidebar-menu-item" v-if="authUser.roles[0].name == 'BE Admin' && this.app_id">
          <el-collapse accordion class="collapse" >
            <el-collapse-item name="1">
               <template slot="title">
                <span class="title">User Profile Config</span>
              </template>
              <p class="menu"><router-link class="links" :to="{path: `/profile-config/${this.app_id}`}"> List All User Profile Config</router-link></p>
              <p class="menu"><router-link class="links" :to="{path: `/profile-config/${this.app_id}`}"> Create New User Profile Config</router-link></p>
            </el-collapse-item>

            <el-collapse-item name="2">
              <template slot="title">
                <span class="title"> Store Profile Config</span>
              </template>
              <p class="menu"><router-link class="links" :to="{path: `/store-config/${this.app_id}`}">List All Store Profile Config</router-link></p>
              <p class="menu"><router-link class="links" :to="{path: `/store-config/${this.app_id}`}">Create New Store Profile Config</router-link></p>
            </el-collapse-item>

            <el-collapse-item name="3">
              <template slot="title">
                <span class="title"> Zone</span>
              </template>
              <p class="menu"><router-link class="links" :to="{path: `/geozone-list/${this.app_id}`}">List All Zones</router-link></p>
              <p class="menu"><router-link class="links" :to="{path: `/geozone-create/${this.app_id}`}">Create New Zone</router-link></p>
            </el-collapse-item>

            <el-collapse-item name="4">
              <template slot="title">
                <span class="title"> Users</span>
              </template>
              <p class="menu"><router-link class="links" :to="{path: `/profile-list/${this.app_id}`}">List All Users</router-link></p>
              <p class="menu"><router-link class="links" :to="{path: `/profile-create/${this.app_id}`}">Create New User</router-link></p>
            </el-collapse-item>

            <el-collapse-item name="5">
              <template slot="title">
                <span class="title"> Brands</span>
              </template>
              <p class="menu"><router-link class="links" :to="{path: `/brand-list/${this.app_id}`}">List All Brands</router-link></p>
              <p class="menu"><router-link class="links" :to="{path: `/brand-create/${this.app_id}`}">Create New Brand</router-link></p>
            </el-collapse-item>
            <el-collapse-item name="6">
              <template slot="title">
                <span class="title"> Relations</span>
              </template>
              <p class="menu"><router-link class="links" :to="{path: `/relation-create/${this.app_id}`}">Create New Relation</router-link></p>
            </el-collapse-item>
            <el-collapse-item name="7">
              <template slot="title">
                <span class="title"> Stores</span>
              </template>
              <p class="menu"><router-link class="links" :to="{path: `/app-store-list/${this.app_id}`}">List All Stores</router-link></p>
              <p class="menu"><router-link class="links" :to="{path: `/app-store/${this.app_id}`}">Create New Store</router-link></p>
            </el-collapse-item>
            <el-collapse-item name="8">
              <template slot="title">
                <span class="title"> Broadcast Notification</span>
              </template>
              <p class="menu"><router-link class="links" :to="{path: `/broadcast-notification/${this.app_id}`}">Manage</router-link></p>
            </el-collapse-item>
          </el-collapse>
        </div>

        <div class="sidebar-menu-item" v-if="authUser.roles[0].name == 'Platform Admin'">
          <el-collapse accordion class="collapse">
            <el-collapse-item name="1">
              <template slot="title">
                <span class="title"> Platform</span>
              </template>
              <p class="menu"><router-link class="links" :to="{path: `/platform-list`}"> List</router-link></p>
            </el-collapse-item>

            <el-collapse-item name="2">
              <template slot="title">
                <span class="title"> Services</span>
              </template>
              <p class="menu"><router-link class="links" :to="{path: `/service-list`}">List</router-link></p>
            </el-collapse-item>

            <el-collapse-item name="3">
              <template slot="title">
                <span class="title"> Roles</span>
              </template>
              <p class="menu"><router-link class="links" :to="{path: `/role-list`}">List</router-link></p>
            </el-collapse-item>

            <el-collapse-item name="4">
              <template slot="title">
                <span class="title"> Business Entity</span>
              </template>
              <p class="menu"><router-link class="links" :to="{path: `/business-entity-create`}">Create</router-link></p>
              <p class="menu"><router-link class="links" :to="{path: `/business-entity-list`}">List</router-link></p>
            </el-collapse-item>
          </el-collapse>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { serverAddr } from '../networkConfig.js';
import { EventBus } from '../eventBus.js';

export default {
  name: "TopBar",
  data() {
    return {
      sidebar: false,
      username : window.localStorage.getItem("username"),
      logged_in_details: [],
      role: {}
   }
  },
  created() {
    if(!this.authUser.p_id){
      this.getAuthUserFromLocalStorage();
    }
    // serverAddr.post('/v1/user-role/', {p_id: window.localStorage.getItem("p_id")})
    // .then(this.postLoggedInRoleCallBack)
    // .catch(function (error) {
    //     console.log(error);
    // })
  },
  computed: {
    authUser() {
      return this.$store.state.general.authUser;
    },
    app_id() {
      return this.$store.state.general.app_id;
    }
  },
  methods: {
    getAuthUserFromLocalStorage: function(){
      var authUser = {};
      authUser.p_id = window.localStorage.getItem('p_id');
      authUser.username = window.localStorage.getItem('username');
      try{
        authUser.roles = JSON.parse(window.localStorage.getItem('roles'));
      }catch(e){
        authUser.roles = [];
      }
      this.$store.commit('general/setAuthUser', authUser);
      console.log('this.authUser', this.authUser);
      if(!this.authUser.p_id){
        this.$router.push({path: "/"});
      }
    },
    window : function(event) {
      if (event.target == modal) {
          console.log(event.target );
      }
    },
    openSidebar:function(){
      this.sidebar = !this.sidebar;
    },
    postLoggedInRoleCallBack: function(response){
      this.logged_in_details = response.data.data;
      window.localStorage.setItem("role", response.data.data[0].role);
      this.role = response.data.data[0].role;
      EventBus.$emit('authChange', this.logged_in_details);
    // postLoggedInRoleCallBack: function(response){
    //   this.logged_in_details = response.data.data;
    //   window.localStorage.setItem("role",  JSON.stringify(response.data.data[0].roles));
    //   EventBus.$emit('authChange', this.logged_in_details);
    },
    doLogout: function () {
      var OneSignal = window.OneSignal || [];
      OneSignal.push( () => {
        OneSignal.deleteTags(["userId", "username", 'role']).then((tagsDeleted) => {});
      });

      serverAddr.post('/v1/user-logout/', {username: this.username})
      .then(this.postLogoutCallBack)
      .catch(function (error) {
        window.localStorage.clear();
        delete serverAddr.defaults.headers['x-access-token'];
        delete serverAddr.defaults.headers['x-refresh-token'];
        this.$router.push({ path: "/" });
      }.bind(this));
    },
    postLogoutCallBack: function (response) {
      window.localStorage.clear();
      delete serverAddr.defaults.headers['x-access-token'];
      delete serverAddr.defaults.headers['x-refresh-token'];
      this.$router.push({ path: "/" });
    }
  }
}
</script>
<style scoped>
  .TopBar .Menu-button, .logo{
    display: inline-block;
    cursor: pointer;
  }
  .TopBar .sidebar-logo{
    font-size: 18px;
    text-decoration: none;
  }
  .TopBar .side-bar{
    height: 100vh;
    background: #EEE;
    z-index: 2;
    position: fixed;
    padding-left: 20px;
    top: 0px;
    box-shadow: 15px -1px 20px -18px rgba(0,0,0,0.44);
    transition: left 300s ;
    transition:all 500ms ease;
    width: 350px;

    /* &.close {
        max-height: 0;
    } */
  }
  .TopBar .side-bar .menu{
    margin: 0;
    padding: 5px 0 5px 15px;
    cursor: pointer;
    font-size: 14px;
  }
  .close-button{
    background: #EEE;
    padding: 15px 0;
    text-align: right;
    color: #444;
  }
  .close-sidebar-btn{
    padding:10px 10px;
    text-align: right;
    cursor: pointer;
  }
  .TopBar .side-bar .title{
    font-size: 16px;
    color: #444;
  }
  .links{
    text-decoration: none;
    color: #666;
  }


</style>
<style>
  .el-carousel__mask, .el-cascader-menu, .el-cascader-menu__item.is-disabled:hover, .el-collapse-item__header, .el-collapse-item__wrap{
    background: #EEE;
  }
  .topIcon.el-icon-arrow-right:before{
    color: #fff;
  }

.el-collapse-item__content{
  padding-bottom: 0px;
}
</style>





