export default {
  namespaced: true,
  state: {
    authUser: {},
    app_id: null
  },
  mutations: {
    clearstore: (state, payload) => {
      state.authUser = {}
    },
    setAuthUser: (state, payload) => {
      state.authUser =  payload
    },
    setAppId: (state, payload) => {
      state.app_id =  payload
    }
  }
}



