<template>

  <section class="StoreManagerAssignBrand">
    <TopBar></TopBar>
    <el-card class="box-card" style="margin: 0px 20px; min-height: 90vh;">
    <h2>
        <span @click="redirectToBack">{{username}}
            <i class="el-icon-arrow-right"></i> &nbsp;</span>
        <span style="flex-grow: 1; color: #66b1ff;">Manage Brands</span>
    </h2>
    <!-- <h2>Manage Preferred Brands</h2> -->
     <el-row>
      <el-col :xs="24" :sm="8" :md="6" :lg="6" :xl="6">
        <label for="">Brands</label>
        <multiselect v-model="selected_brand"
                    deselect-label=""
                    select-label=""
                    track-by="br_id"
                    label="name"
                    :custom-label="brandStoreDropdownLabel"
                    placeholder="Select brand"
                    :options="brand_dropdown"
                    :searchable="true"
                    :allow-empty="true">
          <div slot="singleLabel" slot-scope="{ option }">{{ option.name }}</div>
        </multiselect>
      </el-col>
     </el-row>


      <el-button type="primary" @click.native="save()" style=" margin-top: 20px;">Save</el-button>
      <h4>Store manager Preferred Brands</h4>
      <div v-for="(store, index) in assigned_brands_response" :key="index" style="border-bottom: 1px solid #EEE; padding: 5px 0px;">
        <p v-for="(brand,index1) in store.assigned_brands" :key="index1">
          <i class="el-icon-circle-close-outline" style="color:red;cursor: pointer;" @click="remove(brand.br_id, store.st_id)"></i> &nbsp;&nbsp;&nbsp;{{store.name}}
          {{brand.name}} ({{store.store_name}})
        </p>
      </div>
    </el-card>

  </section>

</template>

<script>

import { serverAddr } from '../networkConfig.js';
import CripNotice from 'crip-vue-notice';
import TopBar from "./TopBar";
import Multiselect from 'vue-multiselect';


  export default  {
    name: "StoreManagerAssignBrand",
    components: { TopBar, multiselect:Multiselect },
    props: ['app_id', 'p_id', 'username'],
    created() {
        this.getInitialData();
    },
    mounted() {

    },
    data() {
      return {
          brand_dropdown: [],
          assigned_brands_response: [],
          selected_brand: ""
      }
    },
    methods: {
      brandStoreDropdownLabel ({ name, store_name }) {
        return `${name}  (${store_name})`
      },
      save(){
        if(this.selected_brand){
          serverAddr
            .post("/v1/store/store-manager-assign-brand/",
            {
              app_id: this.app_id,
              p_id: this.p_id,
              br_id: this.selected_brand.br_id,
              st_id: this.selected_brand.st_id
            })
            .then(response => {

              if(response.data.status){
                this.getAssignedBrands();
                this.selected_brand = "";

                new CripNotice({
                  title: "Success",
                  description: 'Brand assigned',
                  className: "notices",
                  type: 'success',
                  duration: 3
                });
                }
            })
            .catch(function(error) {
                console.log(error);
            });
        }
      },
      getAssignedBrands(){
        serverAddr
          .post("/v1/profile/store-manager/get-assigned-brand/",
          {
            app_id: this.app_id,
            p_id: this.p_id,
          })
          .then(this.getAssignedBrandsCallBack)
          .catch(function(error) {
              console.log(error);
          });
      },
      getAssignedBrandsCallBack: function(response) {
        if(response.data.status){
          this.assigned_brands_response = response.data.data;
        }
      },
      remove(br_id, st_id){
        serverAddr
          .post("/v1/profile/store-manager/remove-assigned-brand/",
          {
            app_id: this.app_id,
            p_id: this.p_id,
            br_id: br_id,
            st_id: st_id
          })
          .then(response => {
              console.log('remove-assigned-brand', response);
              this.getAssignedBrands();
          })
          .catch(function(error) {
              console.log(error);
          });
      },
      getInitialData(){
        serverAddr
        .post("/v1/multiple-store-store-manager/get/brand/", {app_id: this.app_id, p_id: this.p_id})
        .then(response => {
          if(response.data.status){
            console.log('brand-for-dropdown', response);
              this.brand_dropdown = response.data.data;
            }
        })
        .catch(function(error) {
            console.log(error);
        });
        this.getAssignedBrands();

      },
      redirectToBack() {
        this.$router.go(-1);
      }

    },// methdods end
}
</script>

<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>
<style scoped>
.StoreManagerAssignBrand {
}
label {
  font-weight: bold;
  font-size: 0.8em;
}
</style>



