<template lang="html">

  <section class="ProfilePreferredBrands">
    <TopBar></TopBar>
    <el-card class="box-card" style="margin: 0px 20px; min-height: 90vh;">
    <h2>
        <span style="color: #66b1ff;">{{username}}
            <i class="el-icon-arrow-right"></i> &nbsp;</span>
        <span style="flex-grow: 1;">Manage Online Preferred Brands</span>
    </h2>
    <!-- <h2>Manage Preferred Brands</h2> -->
    <label for="">Brand</label>
    <multiselect v-model="selected_brand"
                deselect-label=""
                select-label=""
                track-by="br_id"
                label="name"
                placeholder="Select brand"
                :options="brand_dropdown"
                :searchable="true"
                :allow-empty="true">
      <div slot="singleLabel" slot-scope="{ option }">{{ option.name }}</div>
    </multiselect>
    <el-button type="primary" @click.native="save()" style=" margin-top: 20px;">Save</el-button>
    <h4>Consumer Preferred Brands</h4>
    <p v-for="(item, index) in preferred_brands" :key="index" style="border-bottom: 1px solid #EEE; padding: 5px 0px;">
      <i class="el-icon-circle-close-outline" style="color:red;cursor: pointer;" @click="remove(item)"></i> &nbsp;&nbsp;&nbsp;
      {{item.name}}
    </p>
    </el-card>

  </section>

</template>

<script lang="js">
import { serverAddr } from '../networkConfig.js';
import CripNotice from 'crip-vue-notice';
import TopBar from "./TopBar";
import Multiselect from 'vue-multiselect';


  export default  {
    name: "ProfilePreferredBrands",
    components: { TopBar, multiselect:Multiselect },
    props: ['p_id', 'app_id', 'username'],
    created() {
        this.getInitialData();
    },
    mounted() {

    },
    data() {
      return {
          brand_dropdown: [],
          preferred_brands: [],
          selected_brand: null
      }
    },
    methods: {
      save(){
        serverAddr
            .post("/v1/profile/consumer/assign-preferred-brand/", {app_id: this.app_id, p_id: this.p_id, br_id: this.selected_brand.br_id})
            .then(response => {
                console.log('assign-preferred-brand', response);
                this.getPreferredBrands();
            })
            .catch(function(error) {
                console.log(error);
            });
      },
      remove(item){
        serverAddr
            .post("/v1/profile/consumer/remove-preferred-brand/", {app_id: this.app_id, p_id: this.p_id, br_id: item.br_id})
            .then(response => {
                console.log('remove-preferred-brand', response);
                this.getPreferredBrands();
            })
            .catch(function(error) {
                console.log(error);
            });
      },
        getInitialData(){
            serverAddr
            .post("/v1/brand-for-dropdown/", {app_id: this.app_id})
            .then(response => {
                console.log('brand-for-dropdown', response);
                this.brand_dropdown = response.data.data;
            })
            .catch(function(error) {
                console.log(error);
            });
            this.getPreferredBrands();

        },

        getPreferredBrands(){
          serverAddr
            .post("/v1/profile/consumer/get-preferred-brand/", {app_id: this.app_id, p_id: this.p_id})
            .then(response => {
                console.log('get-preferred-brand', response.data.data);
                this.preferred_brands = response.data.data;
            })
            .catch(function(error) {
                console.log(error);
            });
        }
    },// methdods end
    computed: {
      authUser() {
        return this.$store.state.general.authUser;
      }
    }
}
</script>

<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>
<style scoped>
  .ProfilePreferredBrands {

  }
  label{
  font-weight: bold;
  font-size: 0.8em;
}
</style>



