<template>
<div class="Chat">
  <div class="chat-box">
      <div class="new-msg-form">
          <div style="font-size: 0.85rem; margin-bottom: 10px;">
            <el-switch v-if="checkRole(systemAccess.BE_ADMIN)" v-model="toggleRoomAccess" @change="roomAccessChanged()"></el-switch>
            <div v-if="checkRole(this.systemAccess.STORE_MANAGER) || this.checkRole(this.systemAccess.CUSTOMER_CARE_ADMIN)">
              <span style="color: tomato;" v-if="!isRoomOpenForStaff">
                Room access restricted to BE Admin only
              </span>
              <span style="color: #58716F;" v-else>
                Room open for staff
              </span>
            </div>
          </div>

          <div v-if="isAdmin" style="display: flex;">
            <div class="consumer-online-status-text" v-if="lastSeenTimeStamp" style="color: rgb(78, 140, 255);">Consumer Online...</div>
            <div class="consumer-online-status-text" v-else style="color: #999;">Consumer Offline...</div>
            <!-- <div style="font-size:0.7em; text-align: right; color: #58716F; flex-grow: 1;">
                (Use Shift + Enter to send the message)
            </div> -->
          </div>
            <textarea class="new-msg-input"
            placeholder="Write your message here..."
            @keyup="detectSubmit"
            :class="{'consumer-online': lastSeenTimeStamp && isAdmin, 'access-blocked': accessBlocked}"
            v-model.trim="message" :disabled="accessBlocked"/>
            <!-- <el-button v-on:click="sendTextMessage()" type="primary" size="mini" class="sendMessageBtnForMobile">Send Message</el-button> -->

            <div style="margin: 5px 0;" v-if="!accessBlocked">
                <img src="../assets/img/photo.svg" style="width: 50px; height: 50px; cursor: pointer; vertical-align: middle;" v-on:click="handleImageUpload()">
                <el-button v-on:click="sendImage()" type="primary" size="mini">Send Image</el-button>
                <!-- <input type="file" ref="file" style="display: none;" accept="image/png, image/jpeg"> -->
                <input type="file" ref="file" style="display: none;" name="upload" accept="image/jpeg,image/gif,image/png,application/pdf,image/x-eps" id="fileUpload" />
            </div>
      </div>
      <div class="messages-container" v-if="!isNewConversation">
          <div v-for="(msg, index) in messages" :key="index">
              <div v-if="msg.user =='Server'">
                <p v-if="isAdmin" style="text-align:center;" v-html="msg.message"></p>
              </div>
              <div v-else class="msg-balloon-container">
                <div v-if="isAdmin">
                    <div v-if="msg.userType=='Consumer'" class="msg-balloon left">
                        <div v-if="msg.type == 'image'">
                            <a :href="msg.image" target="_blank"><img :src="msg.image" style="height: 100px; width: 100px; object-fit: cover;"></a>
                        </div>
                        <p v-html="msg.message"></p>
                        <p class="balloon-meta" v-if="msg.user.startsWith('anonymous--')">{{ msg.profileName ? msg.profileName : msg.user.split('--')[1] }}@{{msg.userType}}</p>
                        <p class="balloon-meta" v-else>{{ msg.profileName ? msg.profileName : msg.user }}@{{msg.userType}}</p>
                        <!-- <p class="balloon-meta">{{ msg.user }}@{{msg.userType}}</p> -->
                    </div>
                    <div v-else class="msg-balloon right">
                      <div v-if="msg.type == 'image'">
                        <a :href="msg.image" target="_blank"><img :src="msg.image" style="height: 100px; width: 100px; object-fit: cover;"></a>
                      </div>
                        <p v-html="msg.message"></p>
                        <p class="balloon-meta" v-if="msg.user.startsWith('anonymous--')">{{ msg.profileName ? msg.profileName : msg.user.split('--')[1] }}@{{msg.userType}}</p>
                        <p class="balloon-meta" v-else>{{ msg.profileName ? msg.profileName : msg.user }}@{{msg.userType}}</p>
                        <!-- <p class="balloon-meta">{{ msg.user }}@{{msg.userType}}</p> -->
                    </div>
                </div>

                <div v-else>
                    <div v-if="msg.userType=='Consumer'" class="msg-balloon right">
                      <div v-if="msg.type == 'image'">
                        <a :href="msg.image" target="_blank"><img :src="msg.image" style="height: 100px; width: 100px; object-fit: cover;"></a>
                      </div>
                        <p v-html="msg.message"></p>
                        <p class="balloon-meta" v-if="msg.user.startsWith('anonymous--')">{{ msg.profileName ? msg.profileName : msg.user.split('--')[1] }}@{{msg.userType}}</p>
                        <p class="balloon-meta" v-else>{{ msg.profileName ? msg.profileName : msg.user }}@{{msg.userType}}</p>
                    </div>
                    <div v-else class="msg-balloon left">
                        <div v-if="msg.type == 'image'">
                            <a :href="msg.image" target="_blank"><img :src="msg.image" style="height: 100px; width: 100px; object-fit: cover;"></a>
                        </div>
                        <p v-html="msg.message"></p>
                        <p class="balloon-meta">Admin@{{msg.userType}}</p>
                    </div>
                </div>
              </div>
        </div>
        <div class="load-more-chat" @click="getOldMessages">
            Load More...
        </div>
      </div>
      <div class="messages-container" v-if="isNewConversation">
          <el-row>
            <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24" v-for="(loader,index) in [1,2,3,4]" :key="'loader' + index" style="margin-bottom: 25px;" v-if="messages.length">
              <content-placeholders>
                <content-placeholders-text :lines="2" />
              </content-placeholders>
            </el-col>
          </el-row>
      </div>
  </div>
    </div>

</template>

<script>
import io from 'socket.io-client';
import {chatServerUrl, serverAddr, chatServerAddr, chatServerAddrForChatImage} from '../networkConfig.js';
import anchorme from "anchorme";
import {EventBus} from '../eventBus.js';
import {access} from '../constants.js';
import { find as _find } from 'lodash';
// import TopBar from "./TopBar";

export default {
    name:"Chat",
    // props: ["store_id","consumer_UUID", "chat_server_url", "roomListReady"],
    // components: {TopBar},
    data() {
        return {
            lastSeenTimeStampIntervalId: null,
            lastSeenTimeStamp: false,
            profile_name: '',
            image: null,
            file: '',
            toggleRoomAccess: true,
            isRoomOpenForStaff: true,
            systemAccess: JSON.parse(JSON.stringify(access)),
            accessBlocked: false,
            message: '',
            storeId: null,
            consumerUUID: null,
            brandId: null,
            consumerNotificationToken: null,
            chat_server_url: null,
            isNewConversation: true,
            isConsumerOnline: false,
            messages: [],
            userRoles: JSON.parse(window.localStorage.getItem("roles")),
            isAdmin: false,
            socket : io( this.chat_server_url ? this.chat_server_url : chatServerUrl,
            {
              secure: true,
              rejectUnauthorized: false,
              path: '/chat/socket.io',
              transports: ['websocket'],
              query: "user=admin"
            })
        }
    },
    computed: {
      appId() {
        return localStorage.getItem('app_id');
      },
      authUser() {
        return this.$store.state.general.authUser;
      },
    },
    methods: {
       getUserLastSeenTimeStamp(consumerUUID){
        //  setInterval(()=>{
           chatServerAddr.post('/get-last-seen-time-stamp/', {consumerUUID : consumerUUID})
           .then(result => {
                var lastSeen_TimeStamp = result.data.data[0].timestamp;
                this.getDiffOfLastSeenTimeStamp(lastSeen_TimeStamp, consumerUUID);
           })
           .catch(function (error) {
               console.log(error);
           });
        //  },10000)
      },

      getDiffOfLastSeenTimeStamp(lastSeen_TimeStamp, consumerUUID){
        var thresholdTime = 2;
        var diffTime = parseInt((parseInt(new Date().getTime()) - parseInt(lastSeen_TimeStamp))/1000);
        var diffMin = parseInt(diffTime/60);
        console.log('consumerUUID', consumerUUID);
        console.log('lastSeenTimeStamp', lastSeen_TimeStamp);
        console.log("diffTime", diffTime);
        console.log("diffMin", diffMin);
        if(thresholdTime > diffMin && (this.consumerUUID == consumerUUID)){
            this.lastSeenTimeStamp = true;
        } else {
            this.lastSeenTimeStamp = false;
        }
      },

       getProfileNameByID(profileId){
        serverAddr
          .post("/v1/profile-name-by-id/", {
            app_id: this.authUser.app_id ? this.authUser.app_id : this.appId,
            p_id: profileId
            })
          .then(this.getProfileNameByIDCallback)
          .catch(function(error) {
              console.log(error);
          });
      },
      getProfileNameByIDCallback(response){
        console.log('getProfileNameByIDCallback', response.data);
        this.profile_name = response.data.data[0].profile_name;
      },
      sendImage(){
        this.file = this.$refs.file.files[0];
        // this.gerateBase64(this.file);
        this.sendImageMessage();
      },
      handleImageUpload(){
          this.$refs.file.click();
      },
      // gerateBase64(url){
      //     var reader = new FileReader();

      //       reader.onload = ((e) =>{
      //          this.image = e.target.result;
      //           this.sendImageMessage(this.image);
      //       })
      //       reader.readAsDataURL(url);
      // },
      roomAccessChanged(){
        var currentRoom = {
          consumerUUID: this.consumerUUID,
            storeId: this.storeId,
            brandId: this.brandId,
            token: window.localStorage.getItem("x-access-token")
        }

        if(this.toggleRoomAccess){
          console.log('restoring default room access', this.toggleRoomAccess);
          this.socket.emit('ROOM_ACCESS_PERMISSIONS_CHANGE_TO_DEFAULT', currentRoom);
        }else{
          console.log('restricting room access to BE Admin only', this.toggleRoomAccess);
          this.socket.emit('ROOM_ACCESS_PERMISSIONS_CHANGE_TO_BE_ADMIN_ONLY', currentRoom);
        }
      },
      refreshRoomAccessPermissions(roomdata){
        if( this.consumerUUID == roomdata.consumerUUID && this.storeId == roomdata.storeId && this.brandId == roomdata.brandId){
          console.log('roomdata', roomdata);
          if(roomdata.access){
            var accessToSm =  roomdata.access.some((role) => {
              return role === this.systemAccess.STORE_MANAGER;
            });

            var accessToCCA =  roomdata.access.some((role) => {
              return role === this.systemAccess.STORE_MANAGER;
          });
          }

          this.isRoomOpenForStaff = accessToSm && accessToCCA;
          //current user is not Store manager or customer care admin;
          this.accessBlocked = !this.isRoomOpenForStaff && (this.checkRole(this.systemAccess.STORE_MANAGER) || this.checkRole(this.systemAccess.CUSTOMER_CARE_ADMIN));
          this.toggleRoomAccess = this.isRoomOpenForStaff;
        }
          console.log('refreshRoomAccessPermissions', roomdata, this.accessBlocked);
          console.log('accessToSm', this.systemAccess, accessToSm);
          console.log('accessToCCA', accessToCCA);
      },
        getConsumerNotificationToken(){
            // get_notification_token for consumer
            serverAddr.post('/v1/profile/get-notification-token-by-profile-id/', {
                "p_ids": [this.consumerUUID]
            })
            .then(result => {
                if(result.data.data && result.data.data.length){
                    this.consumerNotificationToken = result.data.data[0].notification_token;
                }else{
                    console.log('consumer notification token not found...');
                }
                })
            .catch(function (error) {
                console.log(error);
            });
        },
        sendNotification(message){
            // get_notification_token for consumer
            chatServerAddr.post('/pushy-send-notification/', message)
            .then(result => {
                console.log('send notification response: ', result);
            })
            .catch(function (error) {
                console.log(error);
            });
        },
        resetDataModelsOnRoomChange(){
            // this.isNewConversation = true;
            console.log('resetDataModelsOnRoomChange');
            this.isConsumerOnline = false;
            this.messages = [];
            this.message = '';
            this.isAdmin = !this.checkRole(this.systemAccess.CONSUMER);
            if(!this.isAdmin){
                this.consumerUUID = window.localStorage.getItem("p_id");
            }
            this.getConsumerNotificationToken();
            this.initiateRoom();
        },
        // sendTextMessage(){
        //   var message = this.message;
        //   if(message.trim().length){
        //       this.sendMessage(message);
        //   }else{
        //       console.log('cant send blank msg...');
        //       this.message = "";
        //   }
        // },
        detectSubmit(e){
            if(e.keyCode === 13){
                if(e.shiftKey){
                    var message = this.message;
                    if(message.trim().length){
                        this.sendMessage(message);
                    }else{
                        console.log('cant send blank msg...');
                        this.message = "";
                    }
                }
            }
        },
        preProcessMsgForDisplay(msgObj, key){
            if(!key) key = 'message';
            // to detect and show anchor links in the text
            if(msgObj.type == "text"){
              msgObj[key] = anchorme(msgObj[key], {
                              truncate:[26,15],
                              attributes:[{
                                  name:"target",
                                  value:"_blank"
                              },{
                                  name:"class",
                                  value:"inline-link"
                              }
                              ]
                          });
              // console.log('processed msg: ', msgObj);
              return msgObj;
            }
            return msgObj;
        },
        sendMessage(message) {
            if(this.consumerNotificationToken && this.isAdmin){
                var notification_msg = {
                    device_notification_tokens: [this.consumerNotificationToken],
                    message: {
                        title: message,
                        st_id: this.storeId,
                        p_id: window.localStorage.getItem("p_id"),
                        manager: window.localStorage.getItem("username"),
                        brandId: this.brandId,
                        type: 'text'
                    },
                    notification_type: "MSG"
                };
            }else{
                var notification_msg = null;
            }

            var socket_msg = {
                profileName: this.profile_name,
                user: window.localStorage.getItem("username"),
                userUUID: window.localStorage.getItem("p_id"),
                consumerUUID: this.consumerUUID,
                token: window.localStorage.getItem("x-access-token"),
                storeId: this.storeId,
                brandId: this.brandId,
                message: message,
                userType: JSON.parse(window.localStorage.getItem("roles"))[0].name,
                type: 'text'
            };

            console.log('sendMessage socket emit');
            this.socket.emit('SEND_MESSAGE', socket_msg);
            if(notification_msg){
                this.sendNotification(notification_msg);
            }
            this.message = '';
        },
        sendImageMessage() {

          if(this.file){

            var chatImagedata = {
              user: window.localStorage.getItem("username"),
              userUUID: window.localStorage.getItem("p_id"),
              consumerUUID: this.consumerUUID,
              token: window.localStorage.getItem("x-access-token"),
              storeId: this.storeId,
              brandId: this.brandId,
              userType: JSON.parse(window.localStorage.getItem("roles"))[0].name,
              type: "image",
              image : this.file,
            }

            var formData = new FormData();

            for( var key in chatImagedata){
              formData.append(key, chatImagedata[key]);
            }

            chatServerAddrForChatImage.post('/save-chat-images/', formData)
              .then(result => {
                console.log('save chat image: ', result.data);

                if(result.data.imageUrl){

                  if(this.consumerNotificationToken && this.isAdmin){
                    var notification_msg = {
                        device_notification_tokens: [this.consumerNotificationToken],
                        message: {
                            title: result.data.imageUrl,
                            st_id: this.storeId,
                            p_id: window.localStorage.getItem("p_id"),
                            brandId: this.brandId,
                            message: 'New image',
                            type: 'image'
                        },
                        notification_type: "MSG"
                    };
                    console.log('notification_msg', notification_msg);
                  }else{
                    var notification_msg = null;
                  }

                  var socket_msg = {
                      user: window.localStorage.getItem("username"),
                      userUUID: window.localStorage.getItem("p_id"),
                      consumerUUID: this.consumerUUID,
                      token: window.localStorage.getItem("x-access-token"),
                      storeId: this.storeId,
                      brandId: this.brandId,
                      image: result.data.imageUrl,
                      userType: JSON.parse(window.localStorage.getItem("roles"))[0].name,
                      type: "image"
                  };

                  this.socket.emit('SEND_MESSAGE', socket_msg);

                  if(notification_msg){
                      this.sendNotification(notification_msg);
                  }
                }
              })
              .catch(function (error) {
                  console.log(error);
              });

          }

        },
        initiateRoom(){
            console.log('chat component initiateRoom consumerUUID', this.consumerUUID);
            console.log('chat component initiateRoom storeId', this.storeId);
            this.socket.emit('adduser',{
                user: window.localStorage.getItem("username"),
                consumerUUID: this.consumerUUID,
                token: window.localStorage.getItem("x-access-token"),
                storeId: this.storeId,
                brandId: this.brandId

            });
        },
        getOldMessages(){
            var oldestAvailableMsg = this.messages[this.messages.length -1];
            oldestAvailableMsg.token = window.localStorage.getItem("x-access-token");
            console.log('getting old msgs before: ', oldestAvailableMsg);
            this.socket.emit('GET_OLD_MESSAGES', oldestAvailableMsg);
        },
        checkRole(role_name){
          return this.userRoles.some(function(el) {
              return el.name === role_name;
          });
        }
    },
    created(){
      console.log('Chat initialized...');

        this.lastSeenTimeStampIntervalId = setInterval(()=>{
              this.getUserLastSeenTimeStamp(this.consumerUUID);
        },60000)
        console.log('lastSeenTimeStampIntervalId', this.lastSeenTimeStampIntervalId);

        EventBus.$on('currentRoomChanged', (data) => {
          console.log('<Chat> currentRoomChanged: ', data);


          this.consumerUUID = data.consumerUUID;
          this.storeId = data.storeId;
          this.brandId = data.brandId

          this.refreshRoomAccessPermissions(data);
          this.resetDataModelsOnRoomChange();
          this.getProfileNameByID(window.localStorage.getItem("p_id"));


          this.getUserLastSeenTimeStamp(this.consumerUUID);
        });


        this.socket.on('reconnect', () =>{
            console.log('reconnected...');
            this.resetDataModelsOnRoomChange();
        });

        this.socket.on('disconnect', () => {
            console.log('user disconnecting');
        });


        this.socket.on('ON__JOIN_ROOM_ACK', (data) =>{
            console.log('ON__JOIN_ROOM_ACK...', data);
             this.socket.emit('GET_INITIAL_MESSAGES',{
                consumerUUID: this.consumerUUID,
                storeId: this.storeId,
                brandId: this.brandId,
                token: window.localStorage.getItem("x-access-token")
            });
            // this.socket.emit('GET_ONLINE_STATUS',{
            //   consumerUUID: this.consumerUUID,
            //     storeId: this.storeId,
            //     brandId: this.brandId,
            //     requestedBy: window.localStorage.getItem("p_id"),
            //     token: window.localStorage.getItem("x-access-token")
            // });
        });

        this.socket.on('UPDATED_LAST_SEEN_TIME_STAMP', (requestData) =>{
            console.log('UPDATED_LAST_SEEN_TIME_STAMP', requestData);
            var lastSeen_TimeStamp = requestData.timestamp;
            this.getDiffOfLastSeenTimeStamp(lastSeen_TimeStamp, requestData.consumerUUID);
        });

        // this.socket.on('SEND_ONLINE_STATUS', (requestData) =>{
        //     console.log('SEND_ONLINE_STATUS', requestData);
        //     var userType = this.checkRole(this.systemAccess.CONSUMER) ? "CONSUMER" : "NON-CONSUMER"
        //     this.socket.to(requestData.socket_id).emit('SEND_ONLINE_STATUS_RESPONSE', {userType});
        // });

        // this.socket.on('SEND_ONLINE_STATUS_RESPONSE', (data) =>{
        //     console.log('SEND_ONLINE_STATUS_RESPONSE', data);
        //     if(data.userType.toUpperCase() === 'CONSUMER'){
        //         this.isConsumerOnline = true;
        //     }
        // });

          EventBus.$on('searchMsgString', (data, searchMsgString) => {
            this.socket.emit('SEARCH_MSGS', {
              consumerUUID: this.consumerUUID,
              token: window.localStorage.getItem("x-access-token")
            });

            this.socket.on('EMIT__GET_SEARCHED_MSGS', (searchedMsgs) => {
              this.messages = searchedMsgs.filter(msg => (msg.message == searchMsgString) || (msg.message.includes(searchMsgString)))
                console.log("SEARCH_MSGS: ", this.messages);
                // EventBus.$emit("EventBus_updaterooms", roomdata);
                // this.consumerUUID = roomdata.consumerUUID;
            });

          })



        this.socket.on('updaterooms', (roomdata) => {
            console.log("Room in Mongo: ", roomdata);
            EventBus.$emit("EventBus_updaterooms", roomdata);
            // this.consumerUUID = roomdata.consumerUUID;
        });

        this.socket.on('ROOM_ACCESS_PERMISSIONS_CHANGED', (roomdata) => {
            console.log("ON ROOM_ACCESS_PERMISSIONS_CHANGED: ", roomdata);
            this.refreshRoomAccessPermissions(roomdata);
            EventBus.$emit('ROOM_ACCESS_PERMISSIONS_CHANGED', roomdata);
        });

        this.socket.on('updatechat', (data) => {
            console.log('new msg updatechat: ', data);
            this.isNewConversation = false;
            if(data.isSystemMsg){
                if(data.userType.toUpperCase() === 'CONSUMER'){
                    this.isConsumerOnline = data.isUserOnline;
                }
            }else{
              if(data.consumerUUID == this.consumerUUID){
                this.messages.unshift(this.preProcessMsgForDisplay(data));
              }
            }
            // you can also do this.messages.push(data)
        });

        this.socket.on('loadinitialchat', (data) => {
            console.log('loadinitialchat: ', data);
            this.isNewConversation = data.length ? false : true;

            for(var i=0;i<data.length;i++){
                this.messages.push(this.preProcessMsgForDisplay(data[i]));
            }
        });

        this.socket.on('GET_OLD_MESSAGES', (data) => {
            console.log('GET_OLD_MESSAGES: ', data);
            // this.messages.unshift(data);
            if(!data.olderMsgs.length){
                console.log('No More messages...');
            }else{
                // this.messages.push.apply( this.messages, data.olderMsgs);
                data.olderMsgs.forEach(element => {
                    this.messages.push(this.preProcessMsgForDisplay(element));
                });
            }

            // you can also do this.messages.push(data)
        });
    },
    beforeRouteUpdate(to, from, next){

    },
    beforeDestroy(){
        console.log('EMIT_LAST_SEEN_STATUS_ON_PAGE_DESTROYED');
        this.socket.emit('EMIT_LAST_SEEN_STATUS_ON_PAGE_DESTROYED', {
            consumerUUID : this.consumerUUID
        })
        this.socket.disconnect();
        EventBus.$off();
        clearInterval(this.lastSeenTimeStampIntervalId)
    },

    mounted() {
    }
}
</script>
<style  scoped>
.Chat{
    background-color: #FFF;
    padding: 15px;
    box-sizing: border-box;
    border-radius: 4px;
}
.chat-box{
    box-shadow: #555B6E;
    display: flex;
    height: 86vh;
    flex-direction: column;
}

.chat-box .new-msg-form{
    margin-bottom: 15px;
}

.messages-container{
    flex-grow: 1;
    /* white-space: pre; */
    box-sizing: border-box;
    background-color: #FFF;
    border-radius: 4px;
    /* height: 70vh; */
    overflow-y: scroll;
    /* transition: all 5s ease-out; */
}
.msg-balloon-container {
    word-break: break-all;
    padding: 10px 15px;
    border-radius: 4px;
}
.msg-balloon-container p {
    margin: 0px;
}

.balloon-meta{
    font-size: 0.75em;
    text-align: left;
    margin-top: 10px;
    font-weight: bold;
}
.balloon-meta::before{
    content: "#"
}

.msg-balloon{
    border-radius: 4px;
    padding: 10px 15px;
    position: relative;
}
.msg-balloon.left{
    background-color: #eaeaea;
    color: 222222;
    margin-right: 150px;
}
.msg-balloon.right{
    background-color: rgb(78, 140, 255);
    color: #FFF;
    margin-left: 150px;
}
/* .msg-balloon.right:after {
	left: 100%;
	top: 50%;
	border: solid transparent;
	content: " ";
	height: 0;
	width: 0;
	position: absolute;
	pointer-events: none;
	border-color: rgba(136, 183, 213, 0);
	border-left-color: rgb(78, 140, 255);
	border-width: 10px;
	margin-top: -10px;
} */
.load-more-chat{
    color: rgb(78, 140, 255);
    text-align: center;
    cursor: pointer;
    padding: 10px;
    border:1px solid transparent;
    font-size: 0.9em;
    font-weight: bold;
    transition: all 0.4s ease-in;
    margin: 0px 20px 20px 20px;
    background-color: #F4F7F7;
}
.load-more-chat:hover{
    border:1px solid #CCDBDC;
    border-radius: 4px;
    background-color: #F4F7F7;
}
.new-msg-input{
    background-color: #f4f7f9;
    box-sizing: border-box;
    display: block;
    width: 100%;
    height: 80px;
    resize: none;
    border: 1px solid #EEE;
    padding: 15px;
    outline: none;
    font-size: 1em;
    border-radius: 4px;
    columns: #58716F;
}
.consumer-online{
    border: 1px solid rgb(78, 140, 255);
}
.access-blocked{
    border: 1px solid tomato;
    background-color: rgb(255, 99, 71, 0.2);
}
.consumer-online-status-text{
    font-size: 0.8em;
    font-weight: bold;
}
