  margin-top: 10px;
  display: none;
} */
/* @media screen and (max-width: 765px){
  .sendMessageBtnForMobile{
    display: block;
  }
} */
</style>

<style>
.msg-balloon a.inline-link {
    color: inherit!important;
    text-decoration: none!important;
    padding: 3px 10px;
    border-radius: 4px;
    font-size: 0.8em;
    background-color: rgba(0,0,0,0.2)!important;
}
</style>



