<template>
    <div class="EngagementZoneList">
    <TopBar></TopBar>

      <el-card class="box-card" style="max-width:80vw; margin:10px auto;">
        <div slot="header" class="clearfix">
          <h3 style="margin: 0px; display: flex; align-items: center;">
            <span style="flex-grow: 1"><i class="el-icon-back" @click="redirectToBack()" style="cursor: pointer;"></i>&nbsp;&nbsp;Geozone Details</span>
            <!-- <router-link :to="{path: `/geozone/notification/create/${app_id}/${g_id}`}">
              <el-button type="success" size="mini">New Zone Notification</el-button>
            </router-link> -->
          </h3>
        </div>

        <el-row>
          <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24" v-for="(loader,index) in [1,2]" :key="'loader' + index" style="margin-bottom: 25px;" v-if="geoZoneDetailsIsLoading">
            <content-placeholders>
              <!-- <content-placeholders-heading :img="true" /> -->
              <content-placeholders-text :lines="2" />
            </content-placeholders>
          </el-col>
        </el-row>

        <el-row style="font-size: 14px; margin-bottom: 20px;" class="vertical-center">
          <el-col :xs="24" :sm="5" :md="5" :lg="5" :xl="5">
            <label>Geozone Name</label>
            {{geoZoneDetails.name}}&nbsp;
          </el-col>
          <el-col :xs="24" :sm="5" :md="5" :lg="5" :xl="5">
            <label>Location</label>
            {{geoZoneDetails.location}}&nbsp;
          </el-col>
          <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8">
            <label>Geozone Id</label>
            {{geoZoneDetails.g_id}}&nbsp;
          </el-col>
          <el-col :xs="24" :sm="3" :md="3" :lg="3" :xl="3">
            <label>Status</label>
            {{geoZoneDetails.is_active? 'Active': 'Disabled'}}&nbsp;
          </el-col>
          <el-col :xs="24" :sm="3" :md="3" :lg="3" :xl="3">
            <label>Size</label>
            {{geoZoneDetails.radius}}m
          </el-col>
        </el-row>
        <el-row style="font-size: 14px;">
          <el-col :xs="24" :sm="5" :md="5" :lg="5" :xl="5">
            <label>City</label>
              {{geoZoneDetails.city}}&nbsp;
            </el-col>
            <el-col :xs="24" :sm="5" :md="5" :lg="5" :xl="5">
            <label>State</label>
              {{geoZoneDetails.state}}&nbsp;
            </el-col>
            <el-col :xs="24" :sm="5" :md="5" :lg="5" :xl="5">
            <label>Country</label>
              {{geoZoneDetails.country}}&nbsp;
            </el-col>
            <el-col :xs="24" :sm="5" :md="5" :lg="5" :xl="5">
            <label>Valid till</label>
              {{geoZoneDetails.expiry_timestamp | formatDate}}&nbsp;
            </el-col>
            <el-col :xs="24" :sm="4" :md="4" :lg="4" :xl="4">
            <label>Loitering Delay</label>
              {{geoZoneDetails.loitering_delay}}&nbsp;
            </el-col>
        </el-row>
      </el-card>

      <el-card class="box-card" style="max-width:80vw; margin:10px auto;">
        <div slot="header" class="clearfix">
          <h3 style="margin: 0px; display: flex; align-items: center;">
            <span @click="redirectToBack()" style="cursor: pointer">{{geoZoneDetails.name}}
              <i class="el-icon-arrow-right"></i> &nbsp;</span>
            <span style="flex-grow: 1; color: #66b1ff;">Engagement Zones</span>
            <!-- <router-link :to="{path: `/engagement-zone-create/${app_id}/${g_id}/${geoZoneDetails.name}/${''}`}"> -->
            <router-link :to="{ name: 'EngagementZoneCreate', params: {app_id : this.app_id, g_id: this.g_id, g_name:this.geoZoneDetails.name}}">
              <el-button type="success" size="mini">New Engagement Zone</el-button>
            </router-link>
          </h3>
        </div>
        <div class="text item">
          <el-row>
            <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24" v-for="(loader,index) in [1,2,3]" :key="'loader' + index" style="margin-bottom: 25px;" v-if="engagementZoneIsLoading">
              <content-placeholders>
                <!-- <content-placeholders-heading :img="true" /> -->
                <content-placeholders-text :lines="2" />
              </content-placeholders>
            </el-col>
          </el-row>

          <el-row style="font-size: 14px; border-bottom: 1px solid #eee; padding-bottom: 10px;" class="vertical-center margin-bottom-10">
            <el-col :xs="24" :sm="5" :md="5" :lg="5" :xl="5">
               <label>Name</label>
            </el-col>
            <el-col :xs="24" :sm="3" :md="3" :lg="3" :xl="3">
               <label>Location</label>
            </el-col>
            <el-col :xs="24" :sm="7" :md="7" :lg="7" :xl="7">
               <label>Id</label>
            </el-col>
            <el-col :xs="24" :sm="3" :md="3" :lg="3" :xl="3">
               <label>Status</label>
            </el-col>
            <el-col :xs="24" :sm="2" :md="2" :lg="2" :xl="2">
               <label>Size</label>
            </el-col>

            <el-col :xs="24" :sm="4" :md="4" :lg="4" :xl="4">
               <label>Actions</label>
            </el-col>
          </el-row>

          <el-row class="vertical-center margin-bottom-10">
            <el-col :span="24" v-if="engagementZoneResultLength0 && !engagementZoneIsLoading">
              <h5 style="color: #666;"> No Result Found...</h5>
            </el-col>
          </el-row>

          <el-row style="font-size: 14px;" class="vertical-center margin-bottom-10" v-for="(engagementZone, index) in engagementZones" :key="index">
            <el-col :xs="24" :sm="5" :md="5" :lg="5" :xl="5">
              {{engagementZone.name}}
            </el-col>
            <el-col :xs="24" :sm="3" :md="3" :lg="3" :xl="3">
              {{engagementZone.location}}
            </el-col>
            <el-col :xs="24" :sm="7" :md="7" :lg="7" :xl="7">
              {{engagementZone.e_id}}
            </el-col>
            <el-col :xs="24" :sm="3" :md="3" :lg="3" :xl="3">
              {{engagementZone.is_active? 'Active': 'Disabled'}}
            </el-col>
            <el-col :xs="24" :sm="2" :md="2" :lg="2" :xl="2">
              {{engagementZone.radius}}m
            </el-col>

            <el-col :xs="24" :sm="4" :md="4" :lg="4" :xl="4">
              <span>
                <el-button type="text" @click.native="remove(index)">Delete</el-button> |
              </span>
              <span>
              </span>
                <el-button type="text" @click.native="edit(index)">Edit</el-button> |
              <span>
                <el-button type="text" @click.native="openStoreList(engagementZone)">Details</el-button>
                <!-- <router-link :to="{path: `/store-list/${app_id}/${engagementZone.g_id}`}">Manage Stores</router-link> -->
              </span>
            </el-col>
          </el-row>
        </div>
      </el-card>

      <el-card class="box-card" style="max-width:80vw; margin:10px auto;">
        <div slot="header" class="clearfix">
          <h3 style="margin: 0px; display: flex; align-items: center;">
            <span @click="redirectToBack()" style="cursor: pointer">{{geoZoneDetails.name}}
            <i class="el-icon-arrow-right"></i> &nbsp;</span>
            <span style="flex-grow: 1; color: #66b1ff;">Notifications</span>
            <router-link :to="{path: `/geozone/notification/create/${app_id}/${g_id}`}">
              <el-button type="success" size="mini">New Zone Notification</el-button>
            </router-link>
          </h3>
        </div>

        <div class="text item">

          <el-row>
            <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24" v-for="(loader,index) in [1,2,3]" :key="'loader' + index" style="margin-bottom: 25px;" v-if="geoZoneNotificationIsLoading">
              <content-placeholders>
                <!-- <content-placeholders-heading :img="true" /> -->
                <content-placeholders-text :lines="2" />
              </content-placeholders>
            </el-col>
          </el-row>

          <el-row class="vertical-center margin-bottom-10">
            <el-col :span="24" v-if="geoZoneNotificationResultLength0 && !geoZoneNotificationIsLoading">
              <h5 style="color: #666;"> No Result Found...</h5>
            </el-col>
          </el-row>

          <el-row :gutter="10" style="padding: 15px 20px; font-size: 14px;" v-for="(item, index) in geoNotification" :key="index" class="notification-item">
            <el-col :span="10">
              <div>
                <label>Notification Id</label>
                  <span>{{item.nt_id}}</span>
              </div>
              <div>
                <label>Notification Text</label>
                {{item.notification[0].text}} <br>
                <span style="font-size: 0.8em; color: #666;">{{item.notification[0].sub_text}}</span>
              </div>
            </el-col>
            <el-col :span="4">
              <div>
                <label>Notification Type</label>
                {{item.notification_type[0].name}}
              </div>
              <div>
                <label>Action Type</label>
                {{item.notification_action_type[0].name}}
              </div>
            </el-col>
            <el-col :span="6">
              <div>
                <label>Match Type</label>
                {{item.notification[0].notification_match_type}}
              </div>
              <div v-if="item.notification[0].notification_match_type == 'BRAND'">
                <label>Associated Brands</label>
                  <span v-for="(brand, index1) in item.notification_brands" :key="'brand'+index1" style="font-size: 0.9em;">
                    {{brand.name}} <span v-if="index1 < item.notification_brands.length - 1">, </span>
                  </span>
              </div>
            </el-col>
            <el-col :span="4">
              <el-button type="text"  @click.native="editGeoNodification(item.nt_id)">Edit</el-button>
              <el-button type="text"  @click.native="removeGeoNodification(item.nt_id)">Delete</el-button>
            </el-col>
          </el-row>


          <!-- <el-row style="font-size: 0.8em;" class="vertical-center margin-bottom-10" v-for="(notification, index) in geoNotification" :key="index">
            <el-col :xs="24" :sm="5" :md="5" :lg="5" :xl="5">
              {{notification.notification[0].text}}
            </el-col>
            <el-col :xs="24" :sm="5" :md="5" :lg="5" :xl="5">
              {{notification.notification_type[0].name}}
            </el-col>
            <el-col :xs="24" :sm="9" :md="9" :lg="9" :xl="9">
              {{notification.nt_id}}
            </el-col>
            <el-col :xs="24" :sm="4" :md="4" :lg="4" :xl="4" style="text-align: right;">
              <span>
                <el-button type="text" @click.native="removeGeoNodification(notification.nt_id)">Delete</el-button> |
              </span>
              <span>
              </span>
                <el-button type="text" @click.native="editGeoNodification(notification.nt_id)">Edit</el-button>
              <span>
              </span>
            </el-col>
          </el-row> -->
        </div>
      </el-card>
  </div>
</template>

<script>
import { serverAddr } from '../networkConfig.js';
import CripNotice from 'crip-vue-notice';
import TopBar from "./TopBar";

export default {
  name: "EngagementZoneList",
  components: {TopBar},
  props: ["app_id", "g_id"],
  data(){
    return {
      geoZoneNotificationResultLength0: false,
      engagementZoneResultLength0: false,
      geoZoneDetails : {},
      geoZoneDetailsIsLoading : false,
      engagementZoneIsLoading : false,
      geoZoneNotificationIsLoading : false,
      engagementZones: [],
      selected_id: '',
      location: '',
      dialogVisible: false,
      geoNotification: []
    }
  },
  created(){
    this.geoZoneDetailsIsLoading = true;
    serverAddr.post('/v1/geo-zone/get/',{'app_id':this.app_id, 'g_id':this.g_id})
    .then((response)=>{
      this.geoZoneDetails = response.data.data[0];
      this.geoZoneDetailsIsLoading = false;
    })
    .catch(function (error) {
      console.log(error);
    }),

    this.engagementZoneIsLoading = true;
    this.engagementZoneResultLength0 = false;
    serverAddr.post('/v1/engagement-zone/',{'app_id':this.app_id, 'g_id':this.g_id})
    .then(this.getEZCallback)
    .catch(function (error) {
      console.log(error);
    }),

    this.geoZoneNotificationIsLoading = true;
    this.geoZoneNotificationResultLength0 = false;
    serverAddr.post('/v1/geozone/notification/',{'app_id':this.app_id, 'g_id':this.g_id})
    .then(this.getGZNotificationCallback)
    .catch(function (error) {
      console.log(error);
    })
  },
  methods:{
    openStoreList: function (engagementZone) {
      this.$router.push({path: `/store-list/${this.app_id}/${this.g_id}/${engagementZone.e_id}`})
    },
    getEZCallback: function(response){
      this.engagementZoneIsLoading = false;
      this.engagementZones = response.data.data;
      if(!this.engagementZones.length){
        this.engagementZoneResultLength0 = true;
      }
    },
    edit: function(index){
      // this.$router.push({path: `/engagement-zone-create/${this.app_id}/${this.g_id}/${this.engagementZones[index].e_id}`})
      this.$router.push({name: 'EngagementZoneCreate', params: {app_id : this.app_id, g_id: this.g_id, id:this.engagementZones[index].e_id}})
    },
    openDialog: function(index){
      this.selected_id = this.engagementZones[index].store.uuid;
      this.dialogVisible = true;
    },
    updateLocation: function(){
      this.dialogVisible = false;
      serverAddr.post('/v1/add-location/',
      {
        id:this.selected_id,
        properties:{
          location:this.location
        }
      })
      .then(this.postUpdateCallback)
      .catch(function (error) {
        console.log(error);
      })
    },
    remove: function(index) {
      serverAddr.post('/v1/engagement-zone/delete/',{app_id:this.app_id, g_id:this.g_id, e_id:this.engagementZones[index].e_id})
      .then(this.postRemoveCallback)
      .catch(function (error) {
        console.log(error);
      })
    },
    postUpdateCallback: function(response){
      if(response.data.status){
        this.location = '';
        new CripNotice({
          title: "Success",
          description: "Location Updated successfully.",
          className: "notices",
          type: "success",
          duration: 3,
        });
      }
    },
    postRemoveCallback: function(response){
      if(response.data.status){
        new CripNotice({
          title: "Success",
          description: "Store deleted successfully.",
          className: "notices",
          type: "success",
          duration: 3,
        });
        serverAddr.post('/v1/engagement-zone/',{'app_id':this.app_id, 'g_id':this.g_id})
        .then(this.getEZCallback)
        .catch(function (error) {
          console.log(error);
        })
      }
    },
    getGZNotificationCallback: function(response){
      this.geoZoneNotificationIsLoading = false;
      this.geoNotification = response.data.data;
      if(!this.geoNotification.length){
        this.geoZoneNotificationResultLength0 = true;
      }
    },
    editGeoNodification: function(nt_id){
      this.$router.push({path: `/geozone/notification/create/${this.app_id}/${this.g_id}/${nt_id}`})
    },
    removeGeoNodification: function(nt_id) {
      serverAddr.post('/v1/drilled-down-notification/delete/',{
        app_id: this.app_id,
        nt_id: nt_id,
        g_id: this.g_id,
        st_id: null
      })
      .then(this.postRemoveGNTCallback)
      .catch(function (error) {
        console.log(error);
      })
    },
    postRemoveGNTCallback: function(response){
      if(response.data.status){
        this.geoNotification = [];
        new CripNotice({
          title: "Success",
          description: "Store deleted successfully.",
          className: "notices",
          type: "success",
          duration: 3,
        });
        serverAddr.post('/v1/geozone/notification/',{'app_id':this.app_id, 'g_id':this.g_id})
        .then(this.getGZNotificationCallback)
        .catch(function (error) {
          console.log(error);
        })
      }
    },
    redirectToBack(){
      this.$router.push({path: `/geozone-list/${this.app_id}`});
    }
  }
}
</script>
<style scoped>
  label{
    display: block;
    font-weight: bold;
    font-size: 0.9em;
    margin-top: 10px;
  }
  .notification-item{
    margin-bottom: 15px;
    padding: 10px 0px;
    border-bottom: 1px solid #EEE;
  }
  p{
    margin: 0px;
  }
</style>



