<template>
  <div class="CustomerCareAssignCity">
    <TopBar></TopBar>

     <el-card class="box-card" style="margin:10px auto; max-width: 80vw;">
      <h3 style="margin: 0px; display: flex; align-items: center;">
        <span @click="redirectToBack()" style="cursor: pointer">{{username}}
          <i class="el-icon-arrow-right"></i> &nbsp;</span>
        <span style="flex-grow: 1; color: #66b1ff;">Manage regions</span>
      </h3>
    </el-card>

    <el-card class="box-card" style="margin: 0px auto; min-height: 20vh; max-width: 80vw;">
      <el-row :gutter="10" style="padding: 0px 20px;">
        <el-col :xs="24" :sm="12" :md="6" :lg="6" :xl="6">
            <label for="">Country</label>
            <!-- <input class="form-input" type="text" placeholder="City" v-model="city"> -->
            <multiselect v-model="country"
                      deselect-label=""
                      select-label=""
                      :show-labels="false"
                      placeholder="Select Country"
                      :options="all_country_dropdown"
                      :searchable="true"
                      :allow-empty="true">
          </multiselect>
        </el-col>
        <el-col :xs="24" :sm="12" :md="6" :lg="6" :xl="6" class="media-xs-view">
            <label for="">State</label>
            <!-- <input class="form-input" type="text" placeholder="State" v-model="state"> -->
            <multiselect v-model="state"
                      deselect-label=""
                      select-label=""
                      :show-labels="false"
                      placeholder="Select State"
                      :options="all_states_dropdown"
                      :searchable="true"
                      :allow-empty="true"
                      >
          </multiselect>
        </el-col>
        <el-col :xs="24" :sm="12" :md="6" :lg="6" :xl="6" class="media-xs-view">
            <label for="">City</label>
            <!-- <input class="form-input" type="text" placeholder="City" v-model="city"> -->
            <multiselect v-model="city"
                      deselect-label=""
                      select-label=""
                      :show-labels="false"
                      placeholder="Select City"
                      :options="all_city_dropdown"
                      :searchable="true"
                      :allow-empty="true"
                      >
          </multiselect>
        </el-col>
        <el-col :xs="24" :sm="12" :md="6" :lg="6" :xl="6" style="margin-top: 15px;" class="media-xs-view">
         <el-button type="primary" @click.native="assignCity">Assign Region</el-button>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="24" style="margin-top: 50px;">
          <h4>Customer care assigned cities</h4>
          <p v-for="(city, index) in assigned_cities" :key="index" style="border-bottom: 1px solid #EEE; padding: 5px 0px;">
            <i class="el-icon-circle-close-outline" style="color:red;cursor: pointer;" @click="removeAssignedCity(city)"></i> &nbsp;&nbsp;&nbsp;
            {{city.city}}
          </p>
        </el-col>
      </el-row>
    </el-card>

  </div>
</template>

<script>

import { serverAddr } from '../networkConfig.js';
import TopBar from "./TopBar";
import Multiselect from 'vue-multiselect';
import CripNotice from "crip-vue-notice";

export default {
  name: 'CustomerCareAssignCity',
  components: {TopBar,  multiselect:Multiselect},
  props: ["app_id", "p_id", "username"],
  data(){
    return{
      all_country_dropdown: [],
      all_city_dropdown: [],
      all_states_dropdown: [],
      assigned_cities: [],
      city: '',
      state: '',
      country: ''
    }
  },
  watch: {
    country: function (newCountry, oldCountry) {
      this.all_states_dropdown = [];
      this.all_city_dropdown = [];
      this.state = "";
      this.city = "";
      if(!newCountry){
      }else{
        this.getStates();
      }
    },
    state: function (newState, oldState) {
      this.all_city_dropdown = [];
      this.city = "";
      if(!newState){
      }else{
        this.getCities();
      }
    },
    city: function (newCity, oldCity) {
      if(!newCity){
      }else{
      }
    },
  },
  created(){

    this.getCountries();
    this.getAssignedCities();
  },
  methods:{
    getGeozoneCallback: function (response) {
      this.isLoading = false;
      if(response.data.status){
        this.geozones = response.data.data;
      }
    },
    assignGeoZone: function (g_id) {

    },
    addUniqueToList: function(arr, item){
      if( !(arr.indexOf(item) > -1) ){
        arr.push(item);
      }
    },
    getCountries: function (params) {
      serverAddr
      .post("/v1/dropdowns/countries/", {})
      .then(response => {
        console.log('geo-zone-get-countries-dropdown', response);
        this.all_country_dropdown = response.data.data ? response.data.data.map(item => item.country) : [];
      })
      .catch(function(error) {
        console.log(error);
      });
    },
    getAssignedCities: function (params) {
      serverAddr
      .post("/v1/profile/customer-care/get-assigned-city/", {
        app_id: this.app_id,
        p_id: this.p_id,
      })
      .then(this.getAssignedRegionsCallback)
      .catch(function(error) {
        console.log(error);
      });
    },
    getAssignedRegionsCallback: function(response) {
      if(response.data.status){
          var tempRegions = response.data.data[0].region[0];

          var sortedRegions = [];


        if(tempRegions){

          for(var key in tempRegions){
            if(key.startsWith('city')){
              sortedRegions.push(key);                    //get city from 'city_india_maharashtra_shirajgaon_mozari'
            }
          }

          console.log('sortedRegions', sortedRegions);

          for(var i=0; i<sortedRegions.length;i++){
            var temp = sortedRegions[i].split("_");

            var obj = {
              country: temp[1],
              state: temp[2],
              city: temp.slice(3, temp.length).join(' '),
            }

            this.assigned_cities.push(obj);
          }

        }
      }
    },
    getStates: function(){
      serverAddr
      .post("/v1/dropdowns/states/", {country: this.country})
      .then(response => {
        console.log('geo-zone-get-states-dropdown', response);
        this.all_states_dropdown = response.data.data ? response.data.data.map(item => item.state) : [];
      })
      .catch(function(error) {
        console.log(error);
      });
    },
    getCities: function(){
      serverAddr
      // .post("/v1/geo-zone/get-cities/", {app_id: this.app_id})
      .post("/v1/dropdowns/cities-for-state/", {
          country: this.country,
          state: this.state
        })
      .then(response => {
        console.log('geo-zone-get-cities-dropdown', response);
        this.all_city_dropdown = response.data.data ? response.data.data.map(item => item.city) : [];
      })
      .catch(function(error) {
        console.log(error);
      });
    },
    assignCity: function() {
      if(this.country && this.state && this.city){
        serverAddr
        .post("/v1/profile/customer-care/assign-city/", {
            app_id: this.app_id,
            p_id: this.p_id,
            country: this.country,
            state: this.state,
            city: this.city
          })
        .then(response => {
          if(response.data.status){
            this.assigned_cities = [];
            this.getAssignedCities();
            new CripNotice({
              title: "Success",
              description: 'City assigned',
              className: "notices",
              type: 'success',
              duration: 3
            });
          }
        })
        .catch(function(error) {
          console.log(error);
        });
      }else{
        new CripNotice({
          title: "Error",
          description: 'Country, State, City are compulsory',
          className: "notices",
          type: 'error',
          duration: 3
        });
      }
    },
    removeAssignedCity: function(region) {
      console.log('removeAssignedCity', region);
      serverAddr
      .post("/v1/profile/customer-care/remove-assigned-city/", {
          app_id: this.app_id,
          p_id: this.p_id,
          country: region.country,
          state: region.state,
          city: region.city
        })
      .then(response => {
        if(response.data.status){
          this.assigned_cities = [];
          this.getAssignedCities();
          new CripNotice({
              title: "Success",
              description: 'City removed',
              className: "notices",
              type: 'success',
              duration: 3
          });
        }
      })
      .catch(function(error) {
        console.log(error);
      });
    },
    redirectToBack() {
      this.$router.go(-1);
    },
  }
}
</script>
<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>
<style>
@media screen and (max-width: 765px) {
  .media-xs-view{
    margin-top: 20px;
  }
}
</style>



