<template>
  <div class="BroadcastNotification">
    <TopBar></TopBar>
    <el-card class="box-card" style="margin:10px auto; max-width: 80vw; overflow: visible;">
      <div slot="header" class="clearfix">
        <h3 style="margin: 0px; display: flex; align-items: center;">
          <span @click="redirectToBack()" style="cursor: pointer">
            App Dashboard
            <i class="el-icon-arrow-right"></i> &nbsp;
          </span>
          <span style="flex-grow: 1; color: #66b1ff;">Broadcast Notification</span>
        </h3>
      </div>

      <div>
        <el-radio-group v-model="zone_type" size="small">
          <el-radio label="GZONE" border @click.native="openDropDownSection('GZONE')">Geozone</el-radio>
          <el-radio label="EZONE" border @click.native="openDropDownSection('EZONE')">Ezone</el-radio>
        </el-radio-group>
      </div>

      <el-row style="margin-top: 25px;" :gutter="20" v-if="showGeozneDropDownSection">
        <el-col :xs="24" :sm="12" :md="12" :lg="6" :xl="4">
          <label for>Geozones</label>
          <multiselect
            v-model="selected_geozone"
            select-label
            track-by="g_id"
            label="name"
            placeholder="Select GeoZone"
            :options="geoZones"
            :searchable="true"
            :allow-empty="true"
            :show-labels="false"
            @select="selecteZoneToSendNotification"
          ></multiselect>
        </el-col>
      </el-row>

      <el-row style="margin-top: 25px;" :gutter="20" v-if="showEozneDropDownSection">
        <el-col :xs="24" :sm="12" :md="12" :lg="6" :xl="4">
        <label for>Geozones</label>
            <multiselect
            v-model="selected_geozone_for_Ezone"
            deselect-label=""
            select-label=""
            track-by="g_id"
            label="name"
            placeholder="Select GeoZone"
            :options="geoZones"
            :searchable="true"
            :allow-empty="true"
            :show-labels="false"
            @select="getEngagementZoneByGeoZone"
            ></multiselect>
        </el-col>

        <el-col :xs="24" :sm="12" :md="12" :lg="6" :xl="4" class="media-xs-view">
          <label for>Ezones</label>
          <multiselect
            v-model="selected_ezone"
            deselect-label=""
            select-label=""
            track-by="e_id"
            label="name"
            placeholder="Select EZone"
            :options="eZones"
            :searchable="true"
            :allow-empty="true"
            :show-labels="false"
            @select="selecteZoneToSendNotification"
          ></multiselect>
        </el-col>
      </el-row>

      <el-row style="margin-top: 25px;" :gutter="20" v-if="selectedZoneList.length">
        <el-col :span="24">
            <h4> Selected {{zone_type + 'S'}} </h4>
            <p v-for="(item, index) in selectedZoneList" :key="index">
            <i class="el-icon-circle-close-outline" style="color:red;cursor: pointer;" @click="removeQueryParam(item, index, selectedZoneList)"></i> &nbsp;&nbsp;&nbsp;
            {{item.name}}
            </p>
            <el-button type="default" size="mini"  @click.native="showNotificationForm">create notification for: {{selectedZoneList.length}} {{zone_type}}<span v-if="selectedZoneList.length> 1">S</span></el-button>
        </el-col>
      </el-row>

      <el-row :gutter="10" style="padding: 20px 20px;" v-if="selectedZoneList.length && isNotificationForm">
        <h4>New Notification</h4>
        <el-col :xs="24" :sm="12" :md="12" :lg="12" :xl="12">
            <label for="">Text<span style="color: red">*</span></label>
            <input class="form-input" v-model="notification.text" auto-complete="off" placeholder="Message text">
        </el-col>

        <el-col :xs="24" :sm="12" :md="12" :lg="12" :xl="12">
            <label for="">Sub-Text</label>
            <input class="form-input margin-bottom-15" v-model="notification.sub_text" auto-complete="off" placeholder="Message sub-text">
        </el-col>

        <el-col :xs="24" :sm="12" :md="12" :lg="12" :xl="12">
            <label for="">Image URL</label>
            <input class="form-input margin-bottom-15" v-model="notification.image_url" auto-complete="off" placeholder="image_url">
        </el-col>

        <el-col :xs="24" :sm="12" :md="12" :lg="12" :xl="12">
            <label for="">Action URL</label>
            <input class="form-input margin-bottom-15" v-model="notification.action_url" auto-complete="off" placeholder="action_url">
        </el-col>

        <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8" class="margin-bottom-15">
            <label for=""> Matching Type<span style="color: red">*</span></label>
            <select v-model="notification.notification_match_type" placeholder="Please select notification match type">
                <option label="Broadcast" value="BROADCAST"></option>
            </select>
        </el-col>

        <el-col :span="24" class="margin-bottom-15">
            <el-button type="primary" @click.native="sendNotification">Save</el-button>
            <!-- <el-button type="primary" @click.native="create_notification_inprocess = false">Cancel</el-button> -->
        </el-col>
      </el-row>
    </el-card>
  </div>
</template>
<script>
import { serverAddr } from "../networkConfig.js";
import CripNotice from "crip-vue-notice";
import TopBar from "./TopBar";
import Multiselect from "vue-multiselect";

export default {
  name: "BroadcastNotification",
  components: { TopBar, multiselect: Multiselect },
  props: ["app_id"],
  data() {
    return {
        zone_type: '',
        showGeozneDropDownSection: false,
        showEozneDropDownSection: false,
        isNotificationForm: false,
        selected_ezone: '',
        selected_geozone: '',
        selected_geozone_for_Ezone: '',
        geoZones: [],
        eZones: [],
        selectedZoneList: [],
        notification: {
        text: '',
        sub_text: '',
        image_url: '',
        action_url: '',
        notification_match_type : 'BROADCAST'
        },
    };
  },
   watch:{
      selected_geozone: function(newGeozone, oldGeozone){
        //   console.log('selected_geozone', newGeozone, oldGeozone);
          this.eZones = [];
          this.selected_ezone = '';
          this.isNotificationForm = false;
        //   this.selectedZoneList = [];
          if(newGeozone){
              this.addUniqueToList(this.selectedZoneList, newGeozone);
          }
      },
      selected_geozone_for_Ezone: function(newGeozoneForEzone, oldGeozoneEzone){
        //   console.log('selected_geozone', newGeozone, oldGeozone);
          this.eZones = [];
          this.selected_ezone = '';
          this.selectedZoneList = [];
          this.isNotificationForm = false;
      },
      selected_ezone: function(newEzone, oldEzone){
          this.isNotificationForm = false;

          if(newEzone){
              this.addUniqueToList(this.selectedZoneList, newEzone);
          }
      },
      zone_type: function(newZoneType, oldZoneType){
          this.selectedZoneList = [];

          this.notification= {
            text: '',
            sub_text: '',
            image_url: '',
            action_url: '',
            notification_match_type: 'BROADCAST'
          }
      }
  },
  created(){
      serverAddr.post('/v1/geo-zone-for-dropdown/',{app_id: this.app_id})
        .then(this.getGeozoneCallback)
        .catch(function (error) {
        console.log(error);
        })
  },
  methods: {
    sendNotification: function() {
        if(this.selectedZoneList.length && this.notification.text && this.notification.notification_match_type){
            var g_ids_or_e_ids = [];
            if(this.zone_type == 'GZONE'){
              for(var i=0;i<this.selectedZoneList.length;i++){
                g_ids_or_e_ids.push(this.selectedZoneList[i].g_id)
              }
            } else if(this.zone_type == 'EZONE'){
                for(var i=0;i<this.selectedZoneList.length;i++){
                  g_ids_or_e_ids.push(this.selectedZoneList[i].e_id)
                }
            }
            console.log('g_ids_or_e_ids', g_ids_or_e_ids);
            serverAddr.post('/v1/send-broadcast-notification/',{
              app_id: this.app_id,
              broadcast_type : this.zone_type,
              g_ids_or_e_ids : g_ids_or_e_ids,
              notification : {
                sub_text: this.notification.sub_text,
                action_url: this.notification.action_url,
                image_url: this.notification.image_url,
                notification_match_type: this.notification.notification_match_type,
                text: this.notification.text,
                title: this.notification.text,
                isActive: true
              }
            })
              .then(this.sendNotificationCallback)
              .catch(function (error) {
              console.log(error);
            })
        }
    },
    sendNotificationCallback: function(response) {
      if(response.status){
        console.log('sendNotificationCallback', response);
        this.notification= {
            text: '',
            sub_text: '',
            image_url: '',
            action_url: '',
            notification_match_type: 'BROADCAST'
        }
        this.isNotificationForm = false;
        this.selectedZoneList = [];
        this.zone_type = [];
        this.showGeozneDropDownSection = false;
        this.showEozneDropDownSection = false;
      }
    },
    showNotificationForm: function(params) {
        this.isNotificationForm = true;
    },
    addUniqueToList: function(arr, item){
      if( !(arr.indexOf(item) > -1) ){
        arr.push(item);
      }
    },
    removeQueryParam: function(item, index, arr){
      arr.splice(index, 1);
    },
    selecteZoneToSendNotification(){
        setTimeout(()=>{
            console.log('selecteZoneToSendNotification', this.selected_ezone);
            // this.addUniqueToList(this.selectedZoneList, this.selected_ezone);
        },0)
    },
    getEngagementZoneByGeoZone(){
        setTimeout(()=>{
            if(this.selected_geozone_for_Ezone){
                serverAddr.post('/v1/get-engagement-zone-for-geozone/',{app_id: this.app_id, geozone_name: this.selected_geozone_for_Ezone.name})
                    .then(this.getEzoneCallback)
                    .catch(function (error) {
                        console.log(error);
                })
            }
        },0)
    },
    redirectToBack() {
      this.$router.go(-1);
    },
    openDropDownSection(label) {
      this.selected_ezone = '';
      this.selected_geozone = '';

      if (label == "GZONE") {
        this.showGeozneDropDownSection = true;
        this.showEozneDropDownSection = false;
      } else {
        this.showGeozneDropDownSection = false;
        this.showEozneDropDownSection = true;
      }
    },
    getGeozoneCallback: function (response) {
    //   this.isLoading = false;
      if(response.data.status){
        this.geoZones = response.data.data;
      }
    },
    getEzoneCallback: function (response) {
      this.selected_ezone = '';
      if(response.data.status){
        this.eZones = response.data.data;
      }
    },
  }
};
</script>
<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>
<style>
.BroadcastNotification{
    font-size: 14px;
}
.form-input {
  padding: 10px 15px;
}

.margin-bottom-15 {
  margin-bottom: 15px;
}
.gray{
  background-color: #f1f1f1;
}
label{
  font-weight: bold;
  font-size: 0.8em;
}
@media screen and (max-width: 767px) {
  .media-xs-view{
    margin-top: 20px;
  }
}
</style>




