<template>
    <div class="ConsumerChatRoomsList">
    <TopBar></TopBar>

    <el-card class="box-card" style="max-width:80vw; margin:10px auto;">
      <div class="text item">
        <h3 style="margin: 0px; display: flex; align-items: center;">
          <span style="flex-grow: 1; color: #66b1ff;">Chat room list</span>
          <router-link :to="{path: `/consumer-available-stores-for-subscription`}">
            <el-button type="success" size="mini">Subscribe New Store</el-button>
          </router-link>
        </h3>
      </div>
    </el-card>

    <div v-for="(chat, index) in chatRooms" :key="index">
      <el-card class="box-card" style="max-width:80vw; margin:10px auto;">
        <div style="color: #409EFF; cursor: pointer; text-align: right;" @click="redirectToChatroom(chat.st_id, chat.brand.uuid)">Chat</div>

        <el-row class="card-row" style="border-bottom: 1px solid #F1F1F1;">
            <el-col :xs="8" :sm="8" :md="8" :lg="8" :xl="8">
                Brand name:
            </el-col>
            <el-col :xs="16" :sm="16" :md="16" :lg="16" :xl="16">
              {{chat.brand.name}}
            </el-col>

            <el-col :xs="8" :sm="8" :md="8" :lg="8" :xl="8">
                Brand id:
            </el-col>
            <el-col :xs="16" :sm="16" :md="16" :lg="16" :xl="16">
              <span v-if="chat.brand.uuid">
                {{chat.brand.uuid}}
              </span>
              <span v-else>
                ---
              </span>
            </el-col>
        </el-row>

        <el-row class="card-row" style="border-bottom: 1px solid #F1F1F1;">
          <el-col :xs="8" :sm="8" :md="8" :lg="8" :xl="8">
              Store id:
          </el-col>
          <el-col :xs="16" :sm="16" :md="16" :lg="16" :xl="16">
              {{chat.st_id}}
          </el-col>
        </el-row>

        <!-- <div class="text item">
          <div style="font-size: 0.8em; border-bottom: 1px solid #F1F1F1; display: flex;">
            <span style="flex-grow: 1;">chat Id : {{chat.st_id}}</span>
              <span>
                <span style="color: #409EFF; cursor: pointer;" @click="redirectToChatroom(chat.chat.uuid)">Chat</span> |
                <span style="color: #409EFF; cursor: pointer;" @click="UnsubscribeStore(chat.chat.uuid)">Unsubscribe</span>
              </span>
          </div>
        </div> -->

          <el-row class="vertical-center card-row" v-for="(store_attr, index1) in chat.store_attr" :key="index1">
            <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8" v-if="store_attr.display_name == 'City' || store_attr.display_name == 'Country' || store_attr.display_name == 'State' || store_attr.display_name == 'Name'" style="border-bottom: 1px solid #F1F1F1;">
              {{store_attr.display_name}}
            </el-col>
            <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16" v-if="store_attr.display_name == 'City' || store_attr.display_name == 'Country' || store_attr.display_name == 'State' || store_attr.display_name == 'Name'" style="border-bottom: 1px solid #F1F1F1;">
              {{store_attr.value}}
            </el-col>
          </el-row>

      </el-card>
    </div>

  </div>
</template>

<script>
import { serverAddr } from '../networkConfig.js';
import CripNotice from 'crip-vue-notice';
import TopBar from "./TopBar";

export default {
  name: "ConsumerChatRoomsList",
  components: {TopBar},
  data(){
    return {
      stores: [],
      selected_id: '',
      chatRooms: [],
      defaultStoreRoom: []
    }
  },
  created(){
    // serverAddr.post('/v1/profile/consumer/get-subscribed-store/',{"p_id": window.localStorage.getItem("p_id")})
    // .then(this.getStoreCallback)
    // .catch(function (error) {
    //   console.log(error);
    // })
    serverAddr.post('/v1/profile/consumer/get-subscribed-store-chat-room/',{
      "p_id": window.localStorage.getItem("p_id")
      })
    .then(this.getStoreCallback)
    .catch(function (error) {
      console.log(error);
    })
  },
  methods:{
    getStoreCallback: function(response){
      this.stores = response.data.data;

      for(var i=0; i<this.stores.length; i++){

        // var defaultStoreObj = {
        //   store_name: 'Default',
        //   id: ''
        // }

        // this.defaultStoreRoom.push(defaultStoreObj);

        var defaultRoom = {
          brand:  { uuid: null, name: 'defualt'},
          st_id:  this.stores[i].st_id,
          store_attr: this.stores[i].store_attributes
        }

        this.chatRooms.push(defaultRoom);

        for(var j=0; j<this.stores[i].brands.length; j++){
        console.log('this.chatRooms', this.stores[i].brands[j]);

        var room = {
          brand:  this.stores[i].brands[j],
          st_id:  this.stores[i].st_id,
          store_attr: this.stores[i].store_attributes
        }

          // var temp = [];
          // temp.push(...this.stores[j].brands);

          // var room = {
          //   st_id: this.stores[j].st_id,
          //   brand: temp[i],
          //   store_attributes: this.stores[j].store_attributes
          // }
        this.chatRooms.push(room);
        // console.log('chatRooms', room);
        }
      }
    },
    redirectToChatroom: function(store_id, brand_id){
      this.$router.push({path:`/consumer-chat/${store_id}/${window.localStorage.getItem("p_id")}/${brand_id}`})
    },
    UnsubscribeStore: function(store_id){
      serverAddr.post('/v1/store/unsubscribe-for-notification/',{"p_id": window.localStorage.getItem("p_id"), "st_id":store_id})
      .then(this.$router.go(this.$router.current))
      .catch(function (error) {
        console.log(error);
      })
    },
    redirectToBack(){
      this.$router.go(-1);
    }
  }
}
</script>
<style scoped>
  .card-row .el-col{
    padding: 10px 0;
  }
</style>



