<template>
  <div class="BusinessEntityCreate">
    <TopBar></TopBar>

    <div style="padding:0 20px; max-width:70vw; margin: 0 auto;">
      <el-card class="box-card" style="margin:0 auto;">
        <div class="text item">
          <el-row class="vertical-center" style="text-align:center;">
            <el-col :xs="24" :sm="6" :md="6" :lg="6" :xl="6">
              Business Entity name:
            </el-col>
            <el-col :xs="24" :sm="14" :md="14" :lg="14" :xl="14">
              <input style="width:80%; padding: 10px 15px;" type="text" v-model="name">
            </el-col>
            <el-col :xs="24" :sm="4" :md="4" :lg="4" :xl="4">
              <el-button type="primary" @click.native="save()">Save</el-button>
            </el-col>
          </el-row>
        </div>
      </el-card>

      <el-row class="vertical-center" style="align-items:start;" :gutter="20">
        <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
          <el-card class="box-card" style="margin:20px auto;">
            <div slot="header" class="clearfix" style="display:flex">
              <div style="margin:auto">Business Entity Admin: </div>
            </div>
            <div class="text item">
              <el-row class="hr-line" :gutter="15">
                <el-col :xs="24" :sm="6" :md="6" :lg="6" :xl="6">
                  <input style="width:100%; padding: 10px 15px;" type="text" v-model="username" placeholder="Username">
                </el-col>
                <el-col :xs="24" :sm="9" :md="9" :lg="9" :xl="9">
                  <input style="width:100%; padding: 10px 15px;" type="text" v-model="password" placeholder="Password">
                </el-col>
                <el-col :xs="24" :sm="9" :md="9" :lg="9" :xl="9">
                  <el-button type="primary" @click.native="addAdminCreds()">Add Admin</el-button>
                </el-col>
              </el-row>

              <el-row :gutter="15" class="vertical-center margin-bottom-10 margin-top-20"  v-for="(user, index) in userCreds" :key="user.uuid + index">
                <el-col :xs="24" :sm="6" :md="6" :lg="6" :xl="6">
                  {{user.username}}
                </el-col>
                <el-col :xs="24" :sm="9" :md="9" :lg="9" :xl="9">
                  <input style="padding: 10px 15px;" type="text" v-model="new_password[index]" placeholder="New Password">
                </el-col>
                <el-col :xs="24" :sm="9" :md="9" :lg="9" :xl="9">
                  <el-button type="primary" @click.native="changeAdminPassword(index)">Change Password</el-button>
                  <el-button type="danger" style="display:inline-block;" @click.native="removeAdmin(index)" icon="el-icon-delete" circle></el-button>
                </el-col>
              </el-row>
            </div>
          </el-card>
        </el-col>
      </el-row>

      <el-row lass="vertical-center" style="align-items:start;" :gutter="20">
        <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
          <el-card class="box-card" style="margin:20px auto;">
            <div slot="header" class="clearfix">
              <span>Roles</span>
            </div>
            <div class="text item">
              <el-row>
                <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24" class="text-center">
                  <el-row class="vertical-center margin-bottom-10" :gutter="10" v-for="(role,index) in roles" :key="role.r_id + index" v-if="role.name != `Platform Admin`">
                    <el-col :xs="24" :sm="20" :md="20" :lg="20" :xl="20" class="text-left">
                      {{role.name}}
                    </el-col>
                    <el-col :xs="24" :sm="4" :md="4" :lg="4" :xl="4" class="text-left">
                      <el-button type="danger" v-if="selectedRoles.indexOf(role.name) != -1" style="display:inline-block;" @click.native="openConfirmationDialog(index)" icon="el-icon-delete" circle></el-button>
                      <el-button type="success" icon="el-icon-plus" circle v-else @click.native="AddRole(index)"></el-button>
                    </el-col>
                  </el-row>
                </el-col>
              </el-row>
            </div>
          </el-card>
        </el-col>
      </el-row>

    <el-card class="box-card" style="margin:20px auto;">
      <div slot="header" class="clearfix">
        <span>Select Relations for Business Entity: </span>
      </div>
      <div class="text item text-center">
        <el-row class="vertical-center" :gutter="10" v-for="(relation,index) in relations" :key="index" v-if="relation.relation.node1_type != `platform`">
          <el-col :xs="24" :sm="2" :md="2" :lg="2" :xl="2" class="text-left">
            <span>
              <input name="relations" :value="relation" type="checkbox" v-model="selectedRelations">
            </span>
          </el-col>
          <el-col :xs="24" :sm="6" :md="6" :lg="6" :xl="6" class="text-left">
            {{relation.relation.node1_arg_value}} : {{relation.relation.node1_type}}
          </el-col>
          <el-col :xs="24" :sm="10" :md="10" :lg="10" :xl="10" class="text-left">
             -- [: {{relation.relation.relation}} { <span v-for="param in relation.parameters" :key="param.key">{{param.display_name}}</span> } ] -->
          </el-col>
          <el-col :xs="24" :sm="6" :md="6" :lg="6" :xl="6" class="text-left">
            {{relation.relation.node2_arg_value}} : {{relation.relation.node2_type}}
          </el-col>
        </el-row>
        <br>
        <el-button type="primary" @click.native="saveRelations()">Save</el-button>
      </div>
    </el-card>
    </div>

    <el-dialog title="WARNING" :visible.sync="confirmationDialogVisible" width="50%" center>
      <span>All the Relations under this Entity containing this Role will be deleted. Are you sure?</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="confirmationDialogVisible = false">Cancel</el-button>
        <el-button type="primary" @click="removeRole(index)">Confirm</el-button>
      </span>
    </el-dialog>


  </div>
</template>

<script>
import { serverAddr } from '../networkConfig.js';
import CripNotice from 'crip-vue-notice';
import TopBar from "./TopBar";

export default {
  name: "BusinessEntityCreate",
  components: {TopBar},
  props: ["id"],
  data(){
    return {
      name: '',
      userCreds:[],
      roles: [],
      username: '',
      password: '',
      assigned_roles: [],
      selectedRoles: [],
      confirmationDialogVisible: false,
      tempIndexForRoleRemoval: '',
      new_password: [],
      relations: [],
      selectedRelations: [],
    }
  },
  created(){
    serverAddr.post('/v1/business-entity/get/',{b_id: this.id})
    .then(this.getBECallBack)
    .catch(function (error) {
      console.log(error);
    })
    serverAddr.post('/v1/get-platform-roles/')
    .then(this.getRolesCallBack)
    .catch(function (error) {
      console.log(error);
    })
    serverAddr.post('/v1/platform-relation-config/')
    .then(this.getRelationsCallBack)
    .catch(function (error) {
      console.log(error);
    })
  },
  methods:{
    getBECallBack: function (response) {
      if (response.data.status) {
        this.name = response.data.data[0].name;
        this.assigned_roles = response.data.data[0].roles;
        this.userCreds = response.data.data[0].userCreds;
        for(var i=0;i<response.data.data[0].roles.length;i++){
          this.selectedRoles.push(response.data.data[0].roles[i].name);
        };
        serverAddr.post('/v1/business-entity/get/relation-config/',{b_id: this.id})
        .then(this.getRelationConfigCallBack)
        .catch(function (error) {
          console.log(error);
        })
      }
    },
    getRelationConfigCallBack: function (response) {
      if (response.data.status) {
        var temp = response.data.data;
        for(var i=0;i<temp.length;i++){
          temp[i].relation.migration_id = temp[i].relation.migration_id.split("__").length > 1 ? temp[i].relation.migration_id.split("__")[1] : temp[i].relation.migration_id;
          if(temp[i].parameters==null)
            temp[i].parameters = [];
        }
        this.selectedRelations = temp;
      }
    },
    getRolesCallBack: function (response) {
      if (response.data.status) {
        this.roles = response.data.data;
      }
    },
    getRelationsCallBack: function (response) {
      if (response.data.status) {
        this.relations = response.data.data
      }
    },
    addAdminCreds: function(username, password){
      if(!this.username || !this.password){
        alert("Please fill required fields");
      } else {
        var params = {
          b_id: this.id,
          properties: {
            username: this.username,
            password: this.password
          },
        }
        serverAddr.post('/v1/business-entity/create/admin/',params)
        .then(this.postSaveCallBack)
        .catch(function (error) {
          console.log(error);
        })
      }
    },
    removeAdmin: function(index){
      if(this.userCreds.length>1){
        var params = {
          b_id: this.id,
          p_id: this.userCreds[index].uuid
        }
        serverAddr.post('/v1/business-entity/delete/admin/',params)
        .then(this.postSaveCallBack)
        .catch(function (error) {
          console.log(error);
        })
      } else {
        alert("Atleast one Admin is required")
      }
    },
    changeAdminPassword: function (index) {
      serverAddr.post('/v1/business-entity/update/admin-password/',{
        b_id: this.id,
        p_id: this.userCreds[index].uuid,
        password : this.new_password[index]
      })
      .then(this.postSaveCallBack)
      .catch(function(error){
        console.log(error);
      })
    },
    openConfirmationDialog: function(index){
      this.confirmationDialogVisible = true;
      this.tempIndexForRoleRemoval = index;
    },
    AddRole: function(index){
      serverAddr.post('/v1/business-entity/create/role/',{
        n_id: this.id,
        properties: {
          name: this.roles[index].name
        }
      })
      .then(this.postSaveCallBack)
      .catch(function(error){
        console.log(error);
      })
    },
    removeRole: function(){
      var r_id = null;
      for(var i=0;i<this.assigned_roles.length;i++){
        if(this.roles[this.tempIndexForRoleRemoval].name == this.assigned_roles[i].name){
          r_id = this.assigned_roles[i].uuid;
        }
      }
      if(r_id){
        serverAddr.post('/v1/business-entity/delete/role/',{
          n_id: this.id,
          r_id: r_id,
        })
        .then(this.postSaveCallBack)
        .catch(function(error){
          console.log(error);
        })
      } else {
        alert("Error occured while Removing Role!");
      }
    },
    save: function () {
      if(!this.name){
        alert("Please fill Business Entity name");
      } else {
        var params = {
          b_id: this.id,
          properties: {
            name: this.name
          },
        }
        serverAddr.post('/v1/business-entity/update/',params)
        .then(this.postSaveCallBack)
        .catch(function (error) {
          console.log(error);
        })
      }
    },
    saveRelations: function () {
      for(var i=0;i<this.selectedRelations.length;i++){
        if(this.selectedRelations[i].parameters.length<=0 && this.selectedRelations[i].relation==null){
          this.selectedRelations.splice(i,1);
        }
      }
      var params = {
        b_id: this.id,
        relations: this.selectedRelations
      }
      serverAddr.post('/v1/business-entity/update/relation-config/',params)
      .then(this.postSaveCallBack)
      .catch(function (error) {
        console.log(error);
      })
    },
    postSaveCallBack: function (response) {
      if (response.data.status) {
        new CripNotice({
          title: "Success",
          description: "Business Entity updated successfully.",
          className: "notices",
          type: "success",
          duration: 3,
        });
        window.location.reload();
      }
    }
  }
}
</script>



