<template>
  <div class="AppList">
    <TopBar></TopBar>

    <div>
      <el-card class="box-card" style="max-width:80vw; margin:10px auto;">
        <div slot="header" class="clearfix">
          <h3 style="margin:auto"> Apps </h3>
        </div>
        <div class="text item">
          <el-row class="vertical-center text-center margin-bottom-10" v-for="(app, index) in apps" :key="index">
            <el-col :xs="24" :sm="3" :md="3" :lg="3" :xl="3">
              App Name <br>
              {{app.name}}
            </el-col>
            <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8">
              App ID <br>
              {{app.app_id}}
            </el-col>
            <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8">
              Client API ID <br>
              {{app.client_api_id ? app.client_api_id : '--'}}
            </el-col>
            <el-col :xs="24" :sm="5" :md="5" :lg="5" :xl="5">
              <span>
                <el-button type="primary" @click.native="edit(index)">Edit</el-button>
              </span>
              <span>
                <el-button type="primary" @click.native="deleteAppDialogVisible=true; selectedIndexForDelete=index">Delete</el-button>
              </span>
            </el-col>
          </el-row>
        </div>
      </el-card>

      <el-dialog title="Delete App" :visible.sync="deleteAppDialogVisible">
        <h3 style="color: tomato;">Are you sure? This action can not be reversed.</h3>
        <span slot="footer" class="dialog-footer">
          <el-button @click="deleteAppDialogVisible=false; selectedIndexForDelete=null;">Cancel</el-button>
          <el-button type="primary" @click="remove(selectedIndexForDelete)">Confirm</el-button>
        </span>
      </el-dialog>
    </div>

  </div>
</template>

<script>
import { serverAddr } from '../networkConfig.js';
import CripNotice from 'crip-vue-notice';
import TopBar from "./TopBar";

export default {
  name: "AppList",
  components: { TopBar },
  data() {
    return {
      apps: [],
      deleteAppDialogVisible: false,
      selectedIndexForDelete: null
    }
  },
  created() {
    serverAddr.post('/v1/app/')
    .then(this.getAppCallback)
    .catch(function (error) {
      console.log(error);
    })
  },
  methods: {
    getAppCallback: function (response) {
      if(response.data.status){
        this.apps = response.data.data;
      }
    },
    edit: function (index) {
      var id = this.apps[index].app_id;
      this.$router.push({ path: `/app-create/${id}` })
    },
    remove: function(index) {
    serverAddr.post('/v1/app/delete/',{app_id:this.apps[index].app_id})
      .then(this.postRemoveCallback)
      .catch(function (error) {
        console.log(error);
      })
    },
    postRemoveCallback: function(response){
      if(response.data.status){
        this.deleteAppDialogVisible=false;
        this,selectedIndexForDelete=null;
        new CripNotice({
          title: "Success",
          description: "App deleted successfully.",
          className: "notices",
          type: "success",
          duration: 3,
        });
        serverAddr.post('/v1/app/')
        .then(this.getAppCallback)
        .catch(function (error) {
          console.log(error);
        })
      }
    }
  }
}
</script>



