<template>
  <div class="AppStoreList">
    <TopBar></TopBar>
    <el-card class="box-card" style="margin:10px auto; max-width: 80vw; overflow: visible;">
      <div slot="header" class="clearfix">
        <h3 style="margin: 0px; display: flex; align-items: center;">
          <span @click="redirectToBack()" style="cursor: pointer">App Dashboard
            <i class="el-icon-arrow-right"></i> &nbsp;</span>
          <span style="flex-grow: 1; color: #66b1ff;">Stores</span>
          <router-link :to="{path: `/app-store/${app_id}`}">
            <el-button type="success" size="mini">New Unassigned Store</el-button>
          </router-link>
        </h3>
      </div>

      <div>
        <el-radio-group v-model="store_type" size="small">
          <el-radio label="APP_STORE" border @click.native="getAppStores">Unassigned Stores</el-radio>
          <el-radio label="EZONE_STORE" border @click.native="getEzoneStores">EZone Stores</el-radio>
        </el-radio-group>
        <el-button size="mini" type="primary" style="float:right;margin: 10px;" @click.native="openMultipleAssignToEzoneDialog()">Assign Selected Stores</el-button>
        <el-button size="mini" style="float:right;margin: 10px;" @click.native="selected_ids = []">Clear Selection</el-button>
      </div>

      <el-row :gutter="20" style="margin-top: 25px;" v-if="store_type == 'APP_STORE'">
        <el-col :span="6">
          <label for="">Country</label>
          <multiselect v-model="country" deselect-label="" select-label="" :show-labels="false" placeholder="Select Country" :options="all_country_dropdown" :searchable="true" :allow-empty="true">
          </multiselect>
        </el-col>
        <el-col :span="6">
          <label for="">State</label>
          <multiselect v-model="state" deselect-label="" select-label="" :show-labels="false" placeholder="Select State" :options="all_states_dropdown" :searchable="true" :allow-empty="true">
          </multiselect>
        </el-col>
        <el-col :span="6">
          <label for="">City</label>
          <multiselect v-model="city" deselect-label="" select-label="" :show-labels="false" placeholder="Select City" :options="all_city_dropdown" :searchable="true" :allow-empty="true">
          </multiselect>
        </el-col>
        <el-col :span="6">
          <label for="">&nbsp;</label><br>
          <el-button type="primary" @click.native="searchStore()">Search</el-button>
          <el-button type="primary" @click.native="resetSearchAllStore()">Reset</el-button>
        </el-col>
      </el-row>
      <el-row style="margin-top: 25px;" :gutter="20" v-else>
        <el-col :span="6">
          <label for="">Engagement Zone</label>
          <multiselect v-model="selected_ezone" deselect-label="" select-label="" track-by="e_id" label="name" placeholder="Select EZone" :options="ezones" :searchable="true" :allow-empty="true">
          </multiselect>
        </el-col>
        <el-col :span="6">
          <label for="">&nbsp;</label><br>
          <el-button type="primary" @click.native="searchEzone()">Search</el-button>
          <el-button type="primary" @click.native="resetSearchEzone()">Reset</el-button>
        </el-col>
      </el-row>
      <el-row>
        <el-col :xs="24" :sm="24" :md="12" :lg="24" :xl="24" v-for="(profile, index) in [1,2,3]" :key="'loader' +index" style="padding: 10px;" v-if="isLoading">
          <div style="margin-bottom: 30px;">
            <content-placeholders>
              <content-placeholders-heading :img="true" />
              <content-placeholders-text :lines="2" />
            </content-placeholders>
          </div>
        </el-col>

        <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24" v-for="(store, index) in stores" :key="index" style="padding: 30px 10px;">
          <!-- <el-card class="box-card"> -->

          <el-dialog title="Select Engagement Zone" :visible.sync="assignToEzonedialogVisible">
            <select v-model="ezone">
              <option :value="ezone" v-for="(ezone, index) in ezones" :key="index">{{ezone.name}}</option>
            </select>
            <span slot="footer" class="dialog-footer">
              <el-button @click="assignToEzonedialogVisible=false">Cancel</el-button>
              <el-button type="primary" @click="assignEzone()">Confirm</el-button>
            </span>
          </el-dialog>

          <el-dialog title="Select Engagement Zone" :visible.sync="assignMultiToEzonedialogVisible">
            <select v-model="ezone">
              <option :value="ezone" v-for="(ezone, index) in ezones" :key="index">{{ezone.name}}</option>
            </select>
            <span slot="footer" class="dialog-footer">
              <el-button @click="assignMultiToEzonedialogVisible=false">Cancel</el-button>
              <el-button type="primary" @click="assignMultiEzone()">Confirm</el-button>
            </span>
          </el-dialog>

          <el-dialog title="Add Location" :visible.sync="dialogVisible">
            <input type="text" v-model="location" placeholder="latitude, longitude">
            <span slot="footer" class="dialog-footer">
              <el-button @click="dialogVisible = false">Cancel</el-button>
              <el-button type="primary" @click="updateLocation()">Confirm</el-button>
            </span>
          </el-dialog>

          <div class="text item">

            <el-row class="vertical-center text-left" style="background-color: #F1F1F1; padding: 10px 15px;">
              <el-col :span="8">
                <h4 style="margin: 0px;"> Attribute </h4>
              </el-col>

              <el-col :span="14">
                <h4 style="margin: 0px;"> Value </h4>
              </el-col>
              <el-col :span="2">
                <input type="checkbox" v-model="selected_ids" :value="store.store.uuid" number>
              </el-col>
            </el-row>

            <el-row class="vertical-center" style="font-size: 14px; border-bottom: 1px solid #F1F1F1; padding: 10px;" v-for="(store_attr, index) in store.store_attributes" :key="index">
              <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8" class="bold">
                {{store_attr.display_name}}
              </el-col>
              <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16">
                {{store_attr.value}}
              </el-col>
            </el-row>

            <div style="font-size: 0.8em; padding: 10px; border-bottom: 1px solid #F1F1F1;">
              <span style="flex-grow: 1;">{{store.store.uuid}}</span>
            </div>

            <div style="font-size: 0.8em;" v-show="selected_ids.length == 0">
              <el-button v-show="store_type=='APP_STORE'" type="text" style="padding-bottom: 0px;" @click.native="removeAppStore(index)">Delete</el-button>
              <el-button v-show="store_type=='APP_STORE'" type="text" style="padding-bottom: 0px;" @click.native="openAssignToEzoneDialog(index)">Assign Engagement Zone</el-button>

              <el-button v-show="store_type=='EZONE_STORE'" type="text" style="padding-bottom: 0px; margin-left: 10px;" @click.native="redirectToStoreNodification(store.g_id, store.e_id, store.st_id)">Notification</el-button>
              <el-button v-show="store_type=='EZONE_STORE'" type="text" style="padding-bottom: 0px;" @click.native="ezoneStoreEdit(index, store.g_id, store.e_id, store.st_id)">Edit</el-button>
              <el-button v-show="store_type=='EZONE_STORE'" type="text" style="padding-bottom: 0px;" @click.native="removeEzoneStore(index, store.g_id, store.e_id, store.st_id)">Delete</el-button>
              <el-button v-show="store_type=='EZONE_STORE'" type="text" style="padding-bottom: 0px;" @click.native="openDialog(index)">Update Location</el-button>
              <el-button v-show="store_type=='EZONE_STORE'" type="text" style="padding-bottom: 0px;" @click.native="redirectToBrandManagement(store.g_id, store.e_id, store.st_id)">Manage Brands</el-button>
              <el-button v-show="store_type=='EZONE_STORE'" type="text" style="padding-bottom: 0px;" @click.native="redirectToStoreManagement(store.g_id, store.e_id, store.st_id)">Manage Staff</el-button>
              <el-button v-show="store_type=='EZONE_STORE'" type="text" style="padding-bottom: 0px;" @click.native="redirectToChatRoom(store.st_id)" v-if="checkRole('BE Admin')">Chat</el-button>

              <!-- if logged in user is BE ADMIN then chat option will be enable otherwise it will be disable-->
            </div>
          </div>
        </el-col>
      </el-row>
      <br>
    </el-card>
  </div>
</template>

<script>
import { serverAddr } from '../networkConfig.js';
import CripNotice from 'crip-vue-notice';
import TopBar from "./TopBar";
import Multiselect from 'vue-multiselect';

export default {
  name: "AppStoreList",
  components: { TopBar, multiselect: Multiselect },
  props: ["app_id"],
  data() {
    return {
      search_query: {
        country: [], //this.city ? this.city.toLowerCase().trim(): "",
        city: [], //this.city ? this.city.toLowerCase().trim(): "",
        state: [], //this.state ? this.state.toLowerCase().trim(): "",
      },
      state: "",
      city: "",
      country: "",
      selected_ezone: null,
      ezone_dropdown: [],
      all_city_dropdown: [],
      all_states_dropdown: [],
      all_country_dropdown: [],
      store_type: 'APP_STORE',
      isLoading: false,
      stores: [],
      ezones: [],
      ezone: {},
      selected_id: '',
      selected_gid: '',
      selected_eid: '',
      selected_ids: [],
      location: '',
      assignToEzonedialogVisible: false,
      assignMultiToEzonedialogVisible: false,
      dialogVisible: false
    }
  },
  created() {
    this.getInitialDropdownData();

    serverAddr.post('/v1/engagement-zone-for-dropdown/', { 'app_id': this.app_id })
      .then(this.getEzoneCallback)
      .catch(function (error) {
        console.log(error);
      })

    if (this.store_type == 'APP_STORE') {
      this.selected_ids = [];
      this.getAppStores();
    } else if (this.store_type = "EZONE_STORE") {
      this.selected_ids = [];
      this.getEzoneStores();
    }
  },
  watch: {
    country: function (newCountry, oldCountry) {
      this.all_states_dropdown = [];
      this.all_city_dropdown = [];
      this.state = "";
      this.city = "";
      if (!newCountry) {
      } else {
        this.getStates();
      }
    },
    state: function (newState, oldState) {
      this.all_city_dropdown = [];
      this.city = "";
      if (!newState) {
      } else {
        this.addUniqueToList(this.search_query.state, newState);
        this.getCities();
      }
    },
    city: function (newCity, oldCity) {
      this.geozone_dropdown = [];
      this.selected_geozone = null;
      if (!newCity) {
      } else {
        this.addUniqueToList(this.search_query.city, newCity);
      }
    },
    // selected_ezone: function (newVal, oldVal) {
    //   if(!newVal){
    //     }else{
    //     this.addUniqueToList(this.search_query.ezone_name, newVal.name);
    //   }
    // },
    store_type: function (newVal, oldVal) {
      this.selected_ids = [];
    },
  },
  computed: {
    authUser() {
      return this.$store.state.general.authUser;
    }
  },
  methods: {
    checkRole: function(roleName) {
      if(!this.authUser.roles){
        return false;
      }
      return this.authUser.roles.some(function(el) {
        return el.name === roleName;
      });
  },
    redirectToChatRoom: function(store_id){
      console.log('redirectToChatRoom',store_id)
      this.$router.push({path:`/admin-room-list/${store_id}/${this.app_id}`})
    },
    resetSearchEzone: function(){
      this.isLoading = true;
      this.selected_ezone = '';
      serverAddr.post('/v1/all-store/', { 'app_id': this.app_id })
        .then(this.getStoreCallback)
        .catch(function (error) {
          console.log(error);
      });
    },
    resetSearchAllStore: function(){
      this.isLoading = true;
      this.country = '';
      this.state = '';
      this.city = '';
      serverAddr.post('/v1/app-store/', { 'app_id': this.app_id })
        .then(this.getStoreCallback)
        .catch(function (error) {
          console.log(error);
        });
    },
    searchEzone: function () {
      console.log(this.selected_ezone);
      if (this.selected_ezone) {
        this.isLoading = true;
        serverAddr
          .post("/v1/store/", {'app_id': this.app_id, 'g_id': this.selected_ezone.g_id, 'e_id': this.selected_ezone.e_id})
          .then(this.getStoreCallback)
          .catch(function (error) {
            console.log(error);
          });
      }
    },
    searchStore: function () {
      console.log('searchStore', this.country, this.state, this.city);
      if(this.city) {
        this.isLoading = true;
        serverAddr
          .post("/v1/app-store/city/", {'app_id': this.app_id, 'city': this.city})
          .then(this.getStoreCallback)
          .catch(function (error) {
            console.log(error);
          });
      }
    },
    addUniqueToList: function (arr, item) {
      if (!(arr.indexOf(item) > -1)) {
        arr.push(item);
      }
    },
    getInitialDropdownData: function () {
      serverAddr
        .post("/v1/dropdowns/countries/", {})
        .then(response => {
          console.log('geo-zone-get-countries-dropdown', response);
          this.all_country_dropdown = response.data.data ? response.data.data.map(item => item.country) : [];
        })
        .catch(function (error) {
          console.log(error);
        });
    },
    getStates: function () {
      serverAddr
        .post("/v1/dropdowns/states/", { country: this.country })
        .then(response => {
          console.log('geo-zone-get-states-dropdown', response);
          this.all_states_dropdown = response.data.data ? response.data.data.map(item => item.state) : [];
        })
        .catch(function (error) {
          console.log(error);
        });
    },
    getCities: function () {
      serverAddr
        // .post("/v1/geo-zone/get-cities/", {app_id: this.app_id})
        .post("/v1/dropdowns/cities-for-state/", {
          country: this.country,
          state: this.state
        })
        .then(response => {
          console.log('geo-zone-get-cities-dropdown', response);
          this.all_city_dropdown = response.data.data ? response.data.data.map(item => item.city) : [];
        })
        .catch(function (error) {
          console.log(error);
        });
    },
    openDialog: function (index) {
      console.log('this.stores[index]', this.stores[index]);
      this.selected_gid = this.stores[index].g_id;
      this.selected_eid = this.stores[index].e_id;
      this.selected_id = this.stores[index].store.uuid;
      this.dialogVisible = true;
    },
    updateLocation: function () {
      this.dialogVisible = false;
      serverAddr.post('/v1/store/update-location/',
        {
          app_id: this.app_id,
          st_id: this.selected_id,
          g_id: this.selected_gid,
          e_id: this.selected_eid,
          properties: {
            location: this.location
          }
        })
        .then(this.postUpdateCallback)
        .catch(function (error) {
          console.log(error);
        })
    },
    ezoneStoreEdit: function (index, g_id, e_id, st_id) {
      console.log('edit store', index);
      this.$router.push({ path: `/store-create/${this.app_id}/${g_id}/${e_id}/${st_id}` })
    },
    removeEzoneStore: function (index, g_id, e_id, st_id) {
      serverAddr.post('/v1/store/delete/', { app_id: this.app_id, g_id: g_id, e_id: e_id, st_id: st_id })
        .then(this.postEzoneStoreRemoveCallback)
        .catch(function (error) {
          console.log(error);
        })
    },
    postEzoneStoreRemoveCallback: function (response) {
      if (response.data.status) {
        new CripNotice({
          title: "Success",
          description: "Store deleted successfully.",
          className: "notices",
          type: "success",
          duration: 3,
        });
        this.getEzoneStores();
      }
    },
    postRemoveCallback: function (response) {
      if (response.data.status) {
        new CripNotice({
          title: "Success",
          description: "Store deleted successfully.",
          className: "notices",
          type: "success",
          duration: 3,
        });
        serverAddr.post('/v1/store/', { 'app_id': this.app_id, 'g_id': this.g_id, 'e_id': this.e_id })
          .then(this.getStoreCallback)
          .catch(function (error) {
            console.log(error);
          })
      }
    },
    getAppStores() {
      this.isLoading = true;
      serverAddr.post('/v1/app-store/', { 'app_id': this.app_id })
        .then(this.getStoreCallback)
        .catch(function (error) {
          console.log(error);
        });
    },
    getEzoneStores() {
      this.isLoading = true;
      serverAddr.post('/v1/all-store/', { 'app_id': this.app_id })
        .then(this.getStoreCallback)
        .catch(function (error) {
          console.log(error);
        });
    },
    getStoreCallback: function (response) {
      this.isLoading = false;
      console.log('getStoreCallback this.stores:', response.data);
      this.stores = response.data.data;
    },
    getEzoneCallback: function (response) {
      this.ezones = response.data.data;
    },
    openAssignToEzoneDialog: function (index) {
      this.selected_id = this.stores[index].store.uuid;
      this.assignToEzonedialogVisible = true;
    },
    openMultipleAssignToEzoneDialog: function () {
      this.assignMultiToEzonedialogVisible = true;
    },
    assignEzone: function () {
      this.assignToEzonedialogVisible = false;
      serverAddr.post('/v1/app-store/assign-ezone/',
        {
          st_id: this.selected_id,
          app_id: this.app_id,
          g_id: this.ezone.g_id,
          e_id: this.ezone.e_id
        })
        .then(this.postUpdateCallback)
        .catch(function (error) {
          console.log(error);
        })
    },
    assignMultiEzone: function () {
      this.assignMultiToEzonedialogVisible = false;
      if (this.store_type == 'APP_STORE') {
        serverAddr.post('/v1/app-store/assign-ezone/',
          {
            st_id: this.selected_ids,
            app_id: this.app_id,
            g_id: this.ezone.g_id,
            e_id: this.ezone.e_id
          })
          .then(this.postUpdateCallback)
          .catch(function (error) {
            console.log(error);
          })
      } else {
        serverAddr.post('/v1/store/assign-ezone/',
          {
            st_id: this.selected_ids,
            app_id: this.app_id,
            g_id: this.ezone.g_id,
            e_id: this.ezone.e_id
          })
          .then(this.postUpdateCallback)
          .catch(function (error) {
            console.log(error);
          })
      }
    },
    removeAppStore: function (index) {
      serverAddr.post('/v1/app-store/delete/', { app_id: this.app_id, st_id: this.stores[index].st_id })
        .then(this.postRemoveCallback)
        .catch(function (error) {
          console.log(error);
        })
    },
    postUpdateCallback: function (response) {
      if (response.data.status) {
        new CripNotice({
          title: "Success",
          description: "Store Updated successfully.",
          className: "notices",
          type: "success",
          duration: 3,
        });

        if (this.store_type == 'APP_STORE') {
          this.getAppStores();
        } else if (this.store_type = "EZONE_STORE") {
          this.getEzoneStores();
        }
        // serverAddr.post('/v1/app-store/', {'app_id':this.app_id})
        // .then(this.getStoreCallback)
        // .catch(function (error) {
        //   console.log(error);
        // })
      }
    },
    redirectToStoreNodification: function (g_id, e_id, st_id) {
      this.$router.push({ path: `/store/notification-list/${this.app_id}/${e_id}/${st_id}` })
    },
    redirectToStoreManagement: function (g_id, e_id, st_id) {
      this.$router.push({ path: `/store/staff-list/${this.app_id}/${st_id}` })
    },
    redirectToBrandManagement: function (g_id, e_id, st_id) {
      this.$router.push({ path: `/store/brand-list/${this.app_id}/${e_id}/${st_id}` })
    },
    redirectToBack() {
      this.$router.go(-1);
    }
  }
}
</script>
<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>
<style scoped>
.AppStoreList {
  font-size: 14px;
}
label {
  font-weight: bold;
  font-size: 0.9em;
  margin-top: 10px;
}
</style>



