<template>
  <div class="Login">

 <el-card shadow="always" class="Card">
    <div class="login-form">
      <div>
        <label style="font-size:0.8em; color: #888;">Username</label><br>
        <input type="text" name="Username" v-model="username" class="input">
      </div>

      <div v-if="show_password == false" style="margin-top: 20px;">
        <label style="font-size:0.8em; color: #888;">Password</label><br>
        <input type="password" name="password" class="input"  @keyup.enter="doLogin()" v-model="password">
      </div>


        <div v-else style="margin-top: 20px;">
          <label style="font-size:0.8em;">Password</label><br>
          <input type="text" name="password" class="input"  @keyup.enter="doLogin()" v-model="password">
        </div>

        <div v-if="change_password">
          <div v-if="show_password == false">
            <label style="font-size:0.8em;">New Password</label><br>
            <input type="password" name="password" class="input"  @keyup.enter="changePassword()" v-model="new_password">
          </div>

          <div v-else>
            <label style="font-size:0.8em;">New Password</label><br>
            <input type="text" name="password" class="input"  @keyup.enter="changePassword()" v-model="new_password">
          </div>
        </div>

        <div>
          <input type="checkbox" class="row-bg" v-model="show_password"><span style="font-size:0.8em;"> Show password</span>
        </div>

        <div v-if="change_password" style="text-align: center; margin: 10px 0;">
          <el-button type="primary"  @click.native="changePassword()">Change Password</el-button>
        </div>

        <div v-else style="text-align: center; margin: 10px 0;">
          <el-button type="primary"  @click.native="doLogin()">Login</el-button>
        </div>

        <div style="margin:0 auto;text-align:center; font-size:0.8em; cursor:pointer;" v-if="!change_password" v-on:click="toggleView(true)">
          Change Password
        </div>

        <div style="margin:0 auto; font-size:0.8em; cursor:pointer;text-align:center;" v-if="change_password" v-on:click="toggleView(false)">
          Login
        </div>



    </div>
  </el-card>

  </div>
</template>
<script>
import {serverAddr} from '../networkConfig.js';
import CripNotice from 'crip-vue-notice';

export default {
  name: 'Login',
  data(){
    return {
      username:'',
      password:'',
      new_password:'',
      show_password:false,
      change_password:false
    }
  },
  created(){
  },
  methods: {
    toggleView : function(bool){
      this.change_password = bool;
    },
    doLogin : function(){
      serverAddr.post('/v1/user-login/',{
        username : this.username,
        password : this.password
      })
      .then(this.postLoginCallBack)
      .catch(function(error){
        console.log(error);
      })
    },
    changePassword : function(){
      serverAddr.post('/v1/user-change-password/',{
        username : this.username,
        password : this.password,
        new_password : this.new_password
      })
      .then(this.postLoginCallBack)
      .catch(function(error){
        console.log(error);
      })
    },
    postLoginCallBack : function(response){
      if(response.data.status){
        window.localStorage.setItem("x-access-token", response.data.data[0]["x-access-token"]);
        window.localStorage.setItem("p_id", response.data.data[0]["p_id"]);
        window.localStorage.setItem("app_id", response.data.data[0]["app_id"]);
        window.localStorage.setItem("username", response.data.data[0]["username"]);
        window.localStorage.setItem("roles", JSON.stringify(response.data.data[0]["roles"]));
        window.localStorage.setItem("x-refresh-token", response.data.data[0]["x-refresh-token"]);
        var authUser = {};
        authUser.username = response.data.data[0]["username"];
        authUser.roles = response.data.data[0]["roles"];
        authUser.p_id = response.data.data[0]["p_id"];
        authUser.app_id = response.data.data[0]["app_id"];

        this.$store.commit('general/setAuthUser', authUser);
        // window.localStorage.setItem("platform_id", response.data.data[0]["platform_id"]);
        // window.localStorage.setItem("app_id", response.data.data[0]["app_id"]);
        // window.localStorage.setItem("b_id", response.data.data[0]["b_id"]);

        // EventBus.$emit('authChange', this.logged_in_details);
        this.$router.push({path: "/dashboard"});
      }
    }
  }
}
</script>


<style lang="css" scoped>
.input{
  padding: 10px 15px;
}
</style>



