<template>
    <div class="ServiceList">
    <TopBar></TopBar>

    <div v-for="(service, index) in services" :key="index">
      <el-card class="box-card" style="max-width:80vw; margin:10px auto;">
        <div slot="header" class="clearfix" style="display:flex;">
          <h2 style="font-weight:bold; margin:auto" >{{service.name}}</h2>
          <div>
            <el-button type="primary"  @click.native="edit(index)">Edit</el-button>
            <el-button type="primary"  @click.native="remove(index)">Delete</el-button>
          </div>
        </div>
        <div class="text item">
          <el-row class="vertical-center text-center">
            <el-col :xs="24" :sm="6" :md="6" :lg="6" :xl="6">
              <h3> Name </h3>
            </el-col>
            <el-col :xs="24" :sm="6" :md="6" :lg="6" :xl="6">
              <h3> Key </h3>
            </el-col>
            <el-col :xs="24" :sm="6" :md="6" :lg="6" :xl="6">
              <h3> Value </h3>
            </el-col>
            <el-col :xs="24" :sm="6" :md="6" :lg="6" :xl="6">
              <h3> Type </h3>
            </el-col>
          </el-row>
          <el-row class="vertical-center text-center" v-for="(service_attr, index) in service.service_attributes" :key="index">
            <el-col :xs="24" :sm="6" :md="6" :lg="6" :xl="6">
              {{service_attr.display_name}}
            </el-col>
            <el-col :xs="24" :sm="6" :md="6" :lg="6" :xl="6">
              {{service_attr.key}}
            </el-col>
            <el-col :xs="24" :sm="6" :md="6" :lg="6" :xl="6">
              {{service_attr.value}}
            </el-col>
            <el-col :xs="24" :sm="6" :md="6" :lg="6" :xl="6">
              {{service_attr.value_type}}
            </el-col>
          </el-row>
        </div>
      </el-card>
    </div>

  </div>
</template>

<script>
import { serverAddr } from '../networkConfig.js';
import CripNotice from 'crip-vue-notice';
import TopBar from "./TopBar";

export default {
  name: "ServiceList",
  components: {TopBar},
  data(){
    return {
      services: []
    }
  },
  created(){
    serverAddr.post('/v1/platfrom-service/')
    .then(this.getServiceCallback)
    .catch(function (error) {
      console.log(error);
    })
  },
  methods:{
    getServiceCallback: function(response){
      this.services = response.data.data;
    },
    edit: function(index){
      var id = this.services[index].s_id;
      this.$router.push({path: `/service-create/${id}`})
    },
    remove: function(index) {
    serverAddr.post('/v1/service/delete/',{s_id:this.services[index].s_id})
      .then(this.postRemoveCallback)
      .catch(function (error) {
        console.log(error);
      })
    },
    postRemoveCallback: function(response){
      if(response.data.status){
        new CripNotice({
          title: "Success",
          description: "Service deleted successfully.",
          className: "notices",
          type: "success",
          duration: 3,
        });
        serverAddr.post('/v1/service/')
        .then(this.getServiceCallback)
        .catch(function (error) {
          console.log(error);
        })
      }
    }
  }
}
</script>



