<template>
    <div class="SubscribedStoreList">
    <TopBar></TopBar>

    <el-card class="box-card" style="max-width:80vw; margin:10px auto;">
      <div class="text item">
        <h3 style="margin: 0px; display: flex; align-items: center;">
          <span style="flex-grow: 1; color: #66b1ff;">Subscribed Stores</span>
          <router-link :to="{path: `/consumer-available-stores-for-subscription`}">
            <el-button type="success" size="mini">Subscribe Store</el-button>
          </router-link>
        </h3>
      </div>
    </el-card>

    <div v-for="(store, index) in stores" :key="index">
      <el-card class="box-card" style="max-width:80vw; margin:10px auto;">

        <div class="text item">
          <div style="font-size: 0.8em; padding: 10px; border-bottom: 1px solid #F1F1F1; display: flex;">
            <span style="flex-grow: 1;">{{store.st_id}}</span>
            <span>
              <span style="color: #409EFF; cursor: pointer;" @click="UnsubscribeStore(store.st_id)">Unsubscribe</span>
            </span>
          </div>

          <el-row class="vertical-center" style="border-bottom: 1px solid #F1F1F1; padding: 10px;" v-for="(store_attr, index) in store.store_attributes" :key="index">
            <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8">
              {{store_attr.display_name}}
            </el-col>
            <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16">
              {{store_attr.value}}
            </el-col>
          </el-row>
        </div>
      </el-card>
    </div>

  </div>
</template>

<script>
import { serverAddr } from '../networkConfig.js';
import CripNotice from 'crip-vue-notice';
import TopBar from "./TopBar";

export default {
  name: "SubscribedStoreList",
  components: {TopBar},
  data(){
    return {
      stores: [],
      selected_id: '',
      app_id: ''
    }
  },
  computed: {
    authUser() {
      return this.$store.state.general.authUser;
    }
  },
  created(){
    serverAddr.post('/v1/profile/consumer/get-subscribed-store/',
    {
      "p_id": window.localStorage.getItem("p_id")
    })
    .then(this.getStoreCallback)
    .catch(function (error) {
      console.log(error);
    })
  },
  methods:{
    getStoreCallback: function(response){
      this.stores = response.data.data;
    },
    UnsubscribeStore: function(store_id){
      serverAddr.post('/v1/store/unsubscribe-for-notification/',{"p_id": window.localStorage.getItem("p_id"), "st_id":store_id})
      .then(this.$router.go(this.$router.current))
      .catch(function (error) {
        console.log(error);
      })
    },
    redirectToBack(){
      this.$router.go(-1);
    }
  }
}
</script>



