<template>
  <div class="StoreNotificationCreate">
    <TopBar></TopBar>
    <el-card class="box-card" style="margin: 0px 20px; min-height: 90vh;">
      <h4>Store Notification</h4>

      <el-row>
        <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24" v-for="(loader,index) in [1,2,3,4,5,6,7]" :key="'loader' + index" style="margin-bottom: 25px;" v-if="showLoader">
          <content-placeholders>
            <!-- <content-placeholders-heading :img="true" /> -->
            <content-placeholders-text :lines="2" />
          </content-placeholders>
        </el-col>
      </el-row>

      <el-row :gutter="10" style="padding: 20px 20px; border-top:1px solid #EEE;" v-if="showForm">
        <el-col :span="24">
        <label for="">Text<span style="color: red">*</span></label>
        <input class="form-input" v-model="notification.text" auto-complete="off" :placeholder="textPlaceholder"></input>
        <p style="font-size: 0.8em; color: #999; margin-top: 0px; font-weight: bold;" class="margin-bottom-15">available wildcards <span v-for="item in available_wildcards">{{item}}, </span></p>
      </el-col>
      <el-col :span="24">
        <label for="">Sub-Text</label>
        <input class="form-input margin-bottom-15" v-model="notification.sub_text" auto-complete="off" :placeholder="subTextPlaceholder"></input>
        <p style="font-size: 0.8em; color: #999; margin-top: 0px; font-weight: bold;" class="margin-bottom-15">available wildcards <span v-for="item in available_wildcards_sub_text">{{item}}, </span></p>
      </el-col>

      <el-col :span="12">
        <label for="">Image URL</label>
        <input class="form-input margin-bottom-15" v-model="notification.image_url" auto-complete="off" placeholder="image_url"></input>
      </el-col>
      <el-col :span="12">
        <label for="">Action URL</label>
        <input class="form-input margin-bottom-15" v-model="notification.action_url" auto-complete="off" placeholder="action_url"></input>
      </el-col>

      <el-col :span="12">
        <label for="">Start Date<span style="color: red">*</span></label>
        <br>
            <el-date-picker
              v-model="notification.validity_start"
              type="datetime"
              :format="'dd-MM-yyyy HH:mm'"
              placeholder="Select date and time"
              @input="set_validity_end(notification.validity_start)">
            </el-date-picker>
        <!-- <input type="datetime-local" class="form-input margin-bottom-15" v-model="notification.validity_start" auto-complete="off" placeholder="validity start"></input> -->
      </el-col>
      <el-col :span="12">
        <label for="">Valid Till<span style="color: red">*</span></label>
        <br>
            <el-date-picker
              v-model="notification.validity_end"
              type="datetime"
              :format="'dd-MM-yyyy HH:mm'"
              placeholder="Select date and time"
              >
            </el-date-picker>
        <!-- <input type="datetime-local" class="form-input margin-bottom-15" v-model="notification.validity_end" auto-complete="off" placeholder="validity end"></input> -->
      </el-col>

      <el-col :span="8" class="margin-bottom-15">
        <label for=""> Matching Type<span style="color: red">*</span></label>
        <select v-model="notification.notification_match_type" placeholder="Please select notification match type">
          <option label="General" value="GENERAL"></option>
          <option label="Multi Match Brand" value="MULTI_MATCH_BRAND" v-show="type!='GEOZONE'"></option>
          <option label="Multi Match Store" value="MULTI_MATCH_STORE" v-show="type!='GEOZONE'"></option>
          <option label="Brand" value="BRAND" v-show="type!='GEOZONE'"></option>
        </select>
      </el-col>
      <el-col :span="8" class="margin-bottom-15">
        <label for=""> Notification Type<span style="color: red">*</span></label>
        <select v-model="selected_notification_type" placeholder="Please select notification type" @change="showInventoryRequestInput">
          <option :label="item.name" :value="item" v-for="(item, index) in notification_types" :key="index"></option>
        </select>
      </el-col>
      <el-col :span="8" class="margin-bottom-15">
        <label for=""> Action Type<span style="color: red">*</span></label>
        <select v-model="selected_notification_action_type" placeholder="Please select notification type">
          <option :label="item.name" :value="item" v-for="(item, index) in notification_action_types" :key="index"></option>
        </select>
      </el-col>
      <el-col :span="24" class="margin-bottom-15" v-show="notification.notification_match_type=='BRAND' && !nt_id">
        <label for=""> Associated Brands with Notification</label>
        <multiselect v-model="selected_match_type_brands"
                    deselect-label=""
                    select-label=""
                    track-by="uuid"
                    label="name"
                    placeholder="Select brands to associate with notification..."
                    :options="store_brand_list"
                    :preserve-search="true"
                    :searchable="true"
                    :allow-empty="true"
                    v-show="notification.notification_match_type=='BRAND' && !nt_id">
          <template slot="tag" slot-scope="props"><span style="cursor: pointer; display: inline-block; padding: 2px 10px; background-color: #EEE; border-radius: 5px; margin: 0px 5px;" @click="props.remove(props.option)">{{ props.option.name }}</span></template>
        </multiselect>
      </el-col>
      <el-col :span="24" class="margin-bottom-15">
        <label>Inventory Request Params</label>
        <textarea class="form-textarea" v-if="isInventoryRequestInputVisible" v-model="inventory_request_params"></textarea>
      </el-col>
      <el-col :span="3" class="margin-bottom-15">
        <el-checkbox label="Is Active" name="isActive" v-model="notification.isActive"></el-checkbox>
      </el-col>
      <el-col :span="3" class="margin-bottom-15" v-for="(value, day) in active_days" :key="day">
        <label for="">{{day}}<span style="color: red">*</span></label>
        <input v-mask="'####-####'" class="form-input" v-model="active_days[day]"></input>
      </el-col>

      <el-col :span="24" class="margin-bottom-15">
        <!-- <el-button type="primary" @click.native="save(type)" :loading="create_notification_inprocess" v-if="type=='GEOZONE'">Save Zone Notification</el-button> -->
        <el-button type="primary" @click.native="save(type)" :loading="create_notification_inprocess" v-if="type=='STORE'">Save Store Notification</el-button>
        <el-button type="primary" @click.native="create_notification_inprocess = false">Cancel</el-button>
      </el-col>
    </el-row>
  </el-card>

  </div>
</template>

<script>
import { serverAddr } from "../networkConfig.js";
import CripNotice from "crip-vue-notice";
import TopBar from "./TopBar";
import { required, url } from "vuelidate/lib/validators";
import Multiselect from 'vue-multiselect';

const greaterThanCurrentTime = value => new Date(value) > new Date();

export default {
  name: "StoreNotificationCreate",
  components: { TopBar, multiselect:Multiselect},
  props: ['app_id', "st_id", 'nt_id'],
  data() {
    return {
      inventory_request_params: '',
      isInventoryRequestInputVisible: false,
      textPlaceholder : 'Welcome {{user_name}} to {{geozone_name}}',
      subTextPlaceholder : '{{consumer_preferred_brands}} here',
      showLoader : true,
      showForm : false,
      type : 'STORE',
       available_wildcards: ["{{user_name}}", "{{store_name}}", "{{geozone_name}}", "{{ezone_name}}"],
        available_wildcards_sub_text: ["{{consumer_preferred_brands}}"],
        selected_notification_type: null,
        selected_match_type_brands: null,
        store_brand_list: [],
        selected_notification_action_type: null,
        create_notification_inprocess: false,
        notification: {isActive: true, text: '', sub_text: '', image_url: '', action_url: '', validity_start: new Date(), validity_end: new Date(new Date().setFullYear(new Date().getFullYear()+1))},
        notification_types: [],
        notification_types_ready: false,
        notification_action_types: [],
        notification_action_types_ready: false,
        active_days: {
            SUNDAY: "0800-1700",
            MONDAY: "0800-1700",
            TUESDAY: "0800-1700",
            WEDNESDAY: "0800-1700",
            THURSDAY: "0800-1700",
            FRIDAY: "0800-1700",
            SATURDAY: "0800-1700"
        },
    };
  },
  validations: {
    name: {
      required
    },
    image_url: {
      required,
      url
    },
    action_url: {
      required,
      url
    },
    validity_start: {
      required,
      greaterThanCurrentTime
    },
    validity_end: {
      required,
      greaterThanCurrentTime,
      greaterThanStartTime(value) {
        return new Date(value) > new Date(this.validity_start);
      }
    },
    selected_type: {
      required
    }
  },
  created() {

    this.getNotificationTypes();

    // if (this.nt_id) {

    //   serverAddr
    //     .post("/v1/notification/get/", {
    //       app_id: this.app_id,
    //       nt_id: this.nt_id
    //     })
    //     .then(this.postGNUpadateCallBack)
    //     .catch(function(error) {
    //       console.log(error);
    //     });
    // }
  },
  methods: {
    showInventoryRequestInput(){
      if(this.selected_notification_type.name == 'Inventory'){
          this.isInventoryRequestInputVisible = true;
      } else{
          this.inventory_request_params = '';
          this.isInventoryRequestInputVisible = false;
        }
    },
    set_validity_end(date){
      console.log(new Date(new Date(date).setFullYear(new Date(date).getFullYear()+1)));
      this.notification.validity_end = new Date(new Date(date).setFullYear(new Date(date).getFullYear()+1));
    },
    getNotificationTypes(){
            serverAddr
            .post("/v1/notification-types/get/", {})
            .then(response => {
                // console.log('notification types', response);
                this.notification_types = response.data.data[0].notification_types;
                this.notification_types_ready = true;
                this.notification_types.forEach(element => {
                  if(element.name == 'Deals & Offers'){
                    this.notification_types_ready = true;
                    this.selected_notification_type = element
                  }
                });
                this.getNotificationActionTypes();
            })
            .catch(function(error) {
                console.log(error);
            });
        },
        getNotificationActionTypes(){
        serverAddr
            .post("/v1/notification-action-types/get/", {})
            .then(response => {
                // console.log('notification types', response);
                this.notification_action_types = response.data.data[0].notification_action_types;
                this.notification_action_types_ready = true;
                this.notification_action_types.forEach(element => {
                  if (element.name == "Entry") {
                    this.selected_notification_action_type = element;
                  }
                });
                this.getBrandsForStore()
            })
            .catch(function(error) {
                console.log(error);
            });
        },
        getBrandsForStore(){
          serverAddr
              .post("/v1/stores/get-unique-brands/", {
                "app_id": this.app_id,
                "st_ids": [this.st_id]
              })
              .then(response => {
                  console.log('getBrandsForStore', response);
                  this.store_brand_list = response.data.data[0].associated_brands;
                  if(this.nt_id){
                    this.showLoader = true;
                    this.showForm = false;
                    this.loadNotification();
                  } else {
                    this.showLoader = false;
                    this.showForm = true;
                  }
              })
              .catch(function(error) {
                  console.log(error);
              });

        },
    loadNotification(){
        serverAddr
            .post("/v1/app/notification-for-edit/", {app_id: this.app_id, search_for: this.type, nt_id: this.nt_id})
            .then(response => {
              this.showLoader = false;
              this.showForm = true;
                console.log('notification-for-edit: ', response.data.data[0]);
                this.notification.isActive = response.data.data[0].notification[0].isActive;
                this.notification.text = response.data.data[0].notification[0].text;
                this.notification.sub_text = response.data.data[0].notification[0].sub_text;
                this.notification.image_url = response.data.data[0].notification[0].image_url;
                this.notification.notification_match_type = response.data.data[0].notification[0].notification_match_type;
                this.notification.action_url = response.data.data[0].notification[0].action_url;
                this.inventory_request_params = response.data.data[0].notification[0].inventory_request_params;
                // this.notification.validity_start = this.convertUTCDateToLocalString(response.data.data[0].notification[0].validity_start);
                this.notification.validity_start = new Date(response.data.data[0].notification[0].validity_start);
                // this.notification.validity_end = this.convertUTCDateToLocalString(response.data.data[0].notification[0].validity_end);
                this.notification.validity_end = new Date(response.data.data[0].notification[0].validity_end);
                try{
                    var active_days_temp = JSON.parse(response.data.data[0].notification[0].active_days);
                    var is_active_days_valid = active_days_temp.hasOwnProperty('SUNDAY') &&
                    active_days_temp.hasOwnProperty('MONDAY') &&
                    active_days_temp.hasOwnProperty('TUESDAY') &&
                    active_days_temp.hasOwnProperty('WEDNESDAY') &&
                    active_days_temp.hasOwnProperty('THURSDAY') &&
                    active_days_temp.hasOwnProperty('FRIDAY') &&
                    active_days_temp.hasOwnProperty('SATURDAY');
                    this.active_days = is_active_days_valid ? active_days_temp : this.active_days;
                }catch(e){
                    console.log('active_days JSON parse error, setting default');
                }
                var notification_types_index = this.notification_types.findIndex(x => x.uuid==response.data.data[0].notification_type[0].uuid);
                this.selected_notification_type = this.notification_types[notification_types_index];
                console.log("notification_types_index", notification_types_index);
                this.showInventoryRequestInput();
                var notification_action_types_index = this.notification_action_types.findIndex(x => x.uuid==response.data.data[0].notification_action_type[0].uuid);
                this.selected_notification_action_type = this.notification_action_types[notification_action_types_index];
                console.log("notification_action_types_index", notification_action_types_index);
            })
            .catch(function(error) {
            console.log(error);
            });
        },

    setImageUrl(value) {
      this.image_url = value;
      this.$v.image_url.$touch();
    },
    setActionUrl(value) {
      this.action_url = value;
      this.$v.action_url.$touch();
    },
    setValidityStart(value) {
      this.validity_start = value;
      this.$v.validity_start.$touch();
    },
    setValidityEnd(value) {
      this.validity_end = value;
      this.$v.validity_end.$touch();
    },
    setSelectedType(value) {
      this.selected_type = value;
      this.$v.selected_type.$touch();
    },
    getNotificationTypeCallBack: function(response) {
      this.notificationTypes = response.data.data[0].notification_types;
    },
    updateNotification(type){
      if(this.notification.text && this.notification.validity_start && this.notification.validity_end && this.notification.notification_match_type && this.selected_notification_type && this.selected_notification_action_type){
          this.create_notification_inprocess = true;
          var url = '';
          // var store_or_gzone_list = [];
          // this.search_result.forEach((item)=>{
          //     store_or_gzone_list.push(item.uuid);
          // });
          var payload = {};
          payload.app_id = this.app_id;
          payload.nt_id = this.nt_id;
          payload.notification = JSON.parse(JSON.stringify(this.notification));
          payload.notification.validity_start = new Date(payload.notification.validity_start).getTime();
          payload.notification.validity_end = new Date(payload.notification.validity_end).getTime();
          payload.action_type = this.selected_notification_action_type;
          payload.notification_type = this.selected_notification_type;
          payload.active_days = this.active_days;
          payload.inventory_request_params = this.inventory_request_params;

          if(this.type == 'GEOZONE'){
              payload.selected_match_type_brands = [];
              url = '/v1/geozone/notification/update/';
              payload.g_id = [this.g_id];
          }else if(this.type == 'STORE'){
              // payload.selected_match_type_brands = this.selected_match_type_brands.map(brand => brand.br_id);
              payload.selected_match_type_brands = [];
              url = '/v1/store/notification/update/';
              payload.st_id = [this.st_id];
          }
          console.log('payload', payload);
          serverAddr
          .post(url, payload)
          .then(response => {
              if(response.data.status){
              new CripNotice({
              title: "Success",
              description: "Notification created successfully.",
              className: "notices",
              type: "success",
              duration: 3,
              });
              }
              this.create_notification_inprocess = false;
              console.log('create notification reaponse: ', response);
              // this.notification_types = response.data.data[0].notification_types;
          })
          .catch(function(error) {
              console.log(error);
          });
      }
        },
        saveNotification: function(notification_for) {
          if(this.notification.text && this.notification.validity_start && this.notification.validity_end && this.notification.notification_match_type && this.selected_notification_type && this.selected_notification_action_type){
            this.create_notification_inprocess = true;
            var url = '';
            // var store_or_gzone_list = [];
            // this.search_result.forEach((item)=>{
            //   store_or_gzone_list.push(item.uuid);
            // });
            var payload = {};
            payload.app_id = this.app_id;
            // payload.g_id = this.g_id;
            payload.notification = JSON.parse(JSON.stringify(this.notification));
            payload.notification.validity_start = new Date(payload.notification.validity_start).getTime();
            payload.notification.validity_end = new Date(payload.notification.validity_end).getTime();
            payload.action_type = this.selected_notification_action_type;
            payload.notification_type = this.selected_notification_type;
            payload.active_days = this.active_days;
            payload.inventory_request_params = this.inventory_request_params;

            if(notification_for == 'GEOZONE'){
              payload.selected_match_type_brands = [];
              url = '/v1/geozone/notification/create/';
              payload.g_id = [this.g_id];
            }else if(notification_for == 'STORE'){
              payload.selected_match_type_brands =  this.selected_match_type_brands ? [this.selected_match_type_brands.uuid] : [];//.map(brand => brand.br_id);
              url = '/v1/store/notification/create/';
              payload.st_id = [this.st_id];
            }
            console.log('payload', payload);
            serverAddr
            .post(url, payload)
            .then(response => {
              if(response.data.status){
                new CripNotice({
                title: "Success",
                description: "Notification created successfully.",
                className: "notices",
                type: "success",
                duration: 3,
              });
              }
              this.create_notification_inprocess = false;
              console.log('create notification reaponse: ', response);
              // this.notification_types = response.data.data[0].notification_types;
            })
            .catch(function(error) {
              console.log(error);
          });

        }
    },
    save: function(type) {
      // this.$v.$touch();
      // if (this.$v.$invalid) {
      // } else {
      //   }
      if (this.nt_id) {
        this.updateNotification('STORE');
      } else {
        this.saveNotification('STORE');
      }
    },
    convertUTCDateToLocalString: function(date) {
      var offset = new Date().getTimezoneOffset() * 60 * 1000;
      var newDate = new Date(date - offset);
      return new Date(newDate).toISOString().replace("Z", "");
    },
    postGNUpadateCallBack: function(response) {
      this.name = response.data.data[0].notification[0].text;
      this.image_url = response.data.data[0].notification[0].image_url;
      this.action_url = response.data.data[0].notification[0].action_url;
      this.validity_start = this.convertUTCDateToLocalString(
        response.data.data[0].notification[0].validity_start
      );
      this.validity_end = this.convertUTCDateToLocalString(
        response.data.data[0].notification[0].validity_end
      );
      this.selected_type = response.data.data[0].notification_type[0];
    },
    postSaveCallBack: function(response) {
      console.log("Response: ", response);
      if (response.data.status) {
        new CripNotice({
          title: "Success",
          description: "Notification created successfully.",
          className: "notices",
          type: "success",
          duration: 3
        });
        this.$router.push({
          path: `/engagement-zone-list/${this.app_id}/${this.g_id}`
        });
      }
    }
  }
};
</script>
<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>
<style lang="css" scoped>
.form-input {
  padding: 10px 15px;
}

.margin-bottom-15 {
  margin-bottom: 15px;
}
.gray{
  background-color: #f1f1f1;
}
label{
  font-weight: bold;
  font-size: 0.8em;
}
.form-textarea{
  width: 100%;
  height: 80px;
  resize:none;
  border: 1px solid silver;
  border-radius: 4px;
  outline: none;
  font-size: 14px;
}
</style>




