<template>
    <div class="ProfileList">
    <TopBar></TopBar>

    <div v-for="(profile, index) in profiles" :key="index">
      <el-card class="box-card" style="max-width:80vw; margin:10px auto;">
        <div slot="header" class="clearfix" style="display:flex;">
          <h2 style="font-weight:bold; margin:auto" >{{profile.profile.uuid}}</h2>
          <div>
            <el-button type="primary"  @click.native="edit(index)">Edit</el-button>
            <!-- <el-button type="primary"  @click.native="remove(index)">Delete</el-button> -->

            <!-- Need to check if geo-point is present for Profile in profile_attribute -->
            <el-button type="primary" @click.native="openDialog(index)">Update Location</el-button>
          </div>
        </div>
        <div class="text item">
          <el-row class="vertical-center text-center">
            <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8">
              <h3> Name </h3>
            </el-col>
            <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8">
              <h3> Key </h3>
            </el-col>
            <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8">
              <h3> Value </h3>
            </el-col>
          </el-row>
          <el-row class="vertical-center text-center" v-for="(profile_attr, index) in profile.profile_attributes" :key="index">
            <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8">
              {{profile_attr.display_name}}
            </el-col>
            <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8">
              {{profile_attr.key}}
            </el-col>
            <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8">
              {{profile_attr.value}}
            </el-col>
          </el-row>
        </div>
      </el-card>
    </div>
    <el-dialog title="Add Location" :visible.sync="dialogVisible">
      <input type="text" v-model="location" placeholder="latitude, longitude">
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">Cancel</el-button>
        <el-button type="primary" @click="updateLocation()">Confirm</el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
import { serverAddr } from '../networkConfig.js';
import CripNotice from 'crip-vue-notice';
import TopBar from "./TopBar";

export default {
  name: "ProfileList",
  components: {TopBar},
  props: ["app_id"],
  data(){
    return {
      profiles: [],
      selected_id: '',
      location: '',
      dialogVisible: false
    }
  },
  created(){
    serverAddr.post('/v1/profile/consumer/',{'app_id':this.app_id})
    .then(this.getProfileCallback)
    .catch(function (error) {
      console.log(error);
    })
  },
  methods:{
    getProfileCallback: function(response){
      this.profiles = response.data.data;
    },
    edit: function(index){
      var id = this.profiles[index].profile.uuid;
      this.$router.push({path: `/consumer-create/${this.app_id}/${id}`})
    },
    openDialog: function(index){
      this.selected_id = this.profiles[index].profile.uuid;
      this.dialogVisible = true;
    },
    updateLocation: function(){
      this.dialogVisible = false;
      serverAddr.post('/v1/add-location/',
      {
        id:this.selected_id,
        properties:{
          location:this.location
        }
      })
      .then(this.postUpdateCallback)
      .catch(function (error) {
        console.log(error);
      })
    },
    remove: function(index) {
    serverAddr.post('/v1/profile/delete/',{p_id:this.profiles[index].p_id})
      .then(this.postRemoveCallback)
      .catch(function (error) {
        console.log(error);
      })
    },
    postUpdateCallback: function(response){
      if(response.data.status){
        this.location = '';
        new CripNotice({
          title: "Success",
          description: "Location Updated successfully.",
          className: "notices",
          type: "success",
          duration: 3,
        });
      }
    },
    postRemoveCallback: function(response){
      if(response.data.status){
        new CripNotice({
          title: "Success",
          description: "Profile deleted successfully.",
          className: "notices",
          type: "success",
          duration: 3,
        });
        serverAddr.post('/v1/profile/')
        .then(this.getProfileCallback)
        .catch(function (error) {
          console.log(error);
        })
      }
    }
  }
}
</script>



