                            <span v-if="!store_attribute.is_system_config"> | </span>
                            <span v-if="!store_attribute.is_system_config" style="font-weight: bold; cursor: pointer; font-size: 0.8em;" @click="remove(index)">Delete</span>
                          </el-col>
                      </el-row>
                  </div>
              </el-card>
            </el-col>
        </el-row>
