<script>
import { serverAddr } from '../networkConfig.js';
import CripNotice from 'crip-vue-notice';
import TopBar from "./TopBar";
import { required, minValue } from 'vuelidate/lib/validators'

export default {
    name: "StoreConfig",
    components: { TopBar },
    props: ["app_id"],

    data() {
        return {
            original_attribute_key_to_edit: null,
            key: '',
            display_name: '',
            value_type: '',
            privacy_level: "0",
            required: false,
            store_attributes: [],
            isEdit:false,
            // select_dropdown: [
            //     {value:"str", render_type:"text", text:"String"},
            //     {value:"boolean", render_type:"radio", text:"Boolean"},
            //     {value:"int", render_type:"number", text:"Number"},
            //     {value:"float", render_type:"number", text:"Float"},
            //     {value:"date", render_type:"date", text:"Date Field"},
            //     {value:"time", render_type:"time", text:"Time Field"},
            //     {value:"password", render_type:"password", text:"Password"}
            // ]
            select_dropdown: [
                {value:"text", text:"String"},
                {value:"text_area", text:"Text-area"},
                {value:"checkbox", text:"Boolean"},
                {value:"number", text:"Number"},
                {value:"tel", text:"Mobile Number"},
                {value:"float", text:"Float"},
                {value:"date", text:"Date Field"},
                {value:"time", text:"Time Field"},
                {value:"password", text:"Password"},
                {value:"geo_point", text:"Location (lat, lng)"}
            ]
        }
    },
    validations: {
        key: {
        required
        },
        display_name: {
        required
        },
        value_type: {
        required
        },
        privacy_level: {
        required
        }
    },
    created() {
        serverAddr.post('/v1/store-config/',{'app_id':this.app_id})
        .then(this.getAppCallback)
        .catch(function (error) {
            console.log(error);
        })
    },
    methods: {
        setKey(value) {
            this.key = value
            this.$v.key.$touch()
        },
        setDisplay_name(value) {
            this.display_name = value
            this.$v.display_name.$touch()
        },
        setValue_type(value) {
            this.value_type = value
            this.$v.value_type.$touch()
        },
        setPrivacy_level(value) {
            this.privacy_level = value
            this.$v.privacy_level.$touch()
        },
        getAppCallback: function (response) {
            if (response.data.status) {
                this.store_attributes = response.data.data[0].store_attributes;
            }
        },
        updateAttributeList: function(response){

        },
        createSystemConfigs(){
            console.log('createSystemConfigs...');
            serverAddr.post('/v1/store-config/create-system-configs/', { app_id: this.app_id })
            .then((response)=>{
                console.log('response: ', response);
                if(response.data && response.data.status){
                    console.log('success');
                    new CripNotice({
                    title: "Success",
                    description: "Created successfully.",
                    className: "notices",
                    type: "success",
                    duration: 3,
                    });
                    serverAddr.post('/v1/store-config/',{'app_id':this.app_id})
                    .then(this.getAppCallback)
                    .catch(function (error) {
                        console.log(error);
                    })
                }
            })
            .catch(function (error) {
                console.log(error);
            })
         },
        hasWhiteSpace(s) {
            return /\s/g.test(s);
        },
        save: function () {
            this.$v.$touch();
            if(!this.$v.$invalid){
                if(!this.isEdit){
                    serverAddr.post('/v1/store-config/create/', {
                        app_id: this.app_id,
                        properties: {
                            original_attribute_key_to_edit: this.key,
                            key: this.key,//.replace(/\s/g, '_'),
                            display_name: this.display_name,
                            value_type: this.value_type,
                            privacy_level: this.privacy_level,
                            required: this.required,
                        }
                    })
                    .then(this.postSaveCallBack)
                    .catch(function (error) {
                        console.log(error);
                    })
                }else{
                    serverAddr.post('/v1/store-config/update/', {
                        app_id: this.app_id,
                        properties: {
                            original_attribute_key_to_edit: this.original_attribute_key_to_edit,
                            key: this.key,//.replace(/\s/g, '_'),
                            display_name: this.display_name,
                            value_type: this.value_type,
                            privacy_level: this.privacy_level,
                            required: this.required
                        }
                    })
                    .then(this.postSaveCallBack)
                    .catch(function (error) {
                        console.log(error);
                    })
                }
            }
        },
        postSaveCallBack: function(response){
            if(response.data.status){
                this.store_attributes = response.data.data;
                this.isEdit=false;
                this.key = '';
                this.display_name = '';
                this.value_type = '';
                this.privacy_level = '';
                this.required = false;
                this.original_attribute_key_to_edit = null;
                serverAddr.post('/v1/store-config/',{'app_id':this.app_id})
                .then(this.getAppCallback)
                .catch(function (error) {
                    console.log(error);
                })
            }
        },
        edit: function (index) {
            this.original_attribute_key_to_edit = this.store_attributes[index].key;
            this.isEdit=true;
            this.key = this.store_attributes[index].key;
            this.display_name = this.store_attributes[index].display_name;
            this.value_type = this.store_attributes[index].value_type;
            this.privacy_level = this.store_attributes[index].privacy_level;
            this.required = this.store_attributes[index].required;
        },
        remove: function (index) {
            serverAddr.post('/v1/store-config/delete/', {
                app_id: this.app_id,
                key: this.store_attributes[index].key,
            })
            .then(this.postSaveCallBack)
            .catch(function (error) {
                console.log(error);
            })
        },
    },
}
</script>

<style scoped>
.StoreConfig{
    font-size: 14px;
}
  .form-label{
    /* font-weight: bold; */
    color: #777;
    font-size: 0.8em;
    margin-bottom: 0px;
    margin-top: 20px;
  }
</style>



