<template>
  <div class="AppStoreCreate">
    <TopBar></TopBar>

    <el-card class="box-card" style="max-width:80vw; margin:10px auto;">
      <h3 style="margin:0px; padding-bottom: 15px; border-bottom: 1px solid #EEE;">Create Store</h3>
      <div class="text item">
        <p  style="margin-top: 10px; color: red; font-size: 0.8em; text-align: center;" v-if="takeManualCityState">Error getting city state from location. Please fill manually.</p>
        <div v-for="(sa,index) in store_attributes" :key="index" v-if="!(id && sa.key =='location')">
          <el-row class="vertical-center" :gutter="10" style="margin-top: 12px;">

            <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8" class="text-right">
              <span v-if="!takeManualCityState">
                  <span v-if="sa.key!='city' && sa.key!='state' && sa.key!='country'">{{sa.display_name}}<span style="color: red;" v-if="sa.required">*</span></span>
              </span>
              <span v-else>
                {{sa.display_name}}
                <span style="color: red;" v-if="sa.required">*</span>
              </span>
            </el-col>

            <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16" class="text-left" v-if="sa.value_type=='text'">
              <div v-if="sa.key!='city' && sa.key!='state' && sa.key!='country'">
                <input class="form_input" type="text" v-model="dynamic_form_model[sa.key].value" :required="sa.required" :placeholder="getPlaceholder(dynamic_form_model[sa.key].key)">
                <p class="form-error-msg" :class="{hidden : dynamic_form_model[sa.key].isFormFieldValid}"> {{'Invalid ' + dynamic_form_model[sa.key].display_name}}</p>
              </div>
              <div v-else-if="sa.key=='country' && takeManualCityState">
                <!-- <input class="form_input" type="text" v-model="dynamic_form_model[sa.key].value" :required="sa.required"> -->
                <multiselect v-model="country"
                  deselect-label=""
                  select-label=""
                  :show-labels="false"
                  placeholder="Select Country"
                  :options="all_country_dropdown"
                  :searchable="true"
                  :allow-empty="true"
                  style="width: 80%;">
                </multiselect>
                <p class="form-error-msg" :class="{hidden : dynamic_form_model[sa.key].isFormFieldValid}"> {{'Invalid ' + dynamic_form_model[sa.key].display_name}}</p>
              </div>
              <div v-else-if="sa.key=='state' && takeManualCityState">
                <!-- <input class="form_input" type="text" v-model="dynamic_form_model[sa.key].value" :required="sa.required"> -->
                <multiselect v-model="state"
                  deselect-label=""
                  select-label=""
                  :show-labels="false"
                  placeholder="Select State"
                  :options="all_states_dropdown"
                  :searchable="true"
                  :allow-empty="true"
                  style="width:80%;">
                </multiselect>
                <p class="form-error-msg" :class="{hidden : dynamic_form_model[sa.key].isFormFieldValid}"> {{'Invalid ' + dynamic_form_model[sa.key].display_name}}</p>
              </div>
              <div v-else-if="sa.key=='city' && takeManualCityState">
                <!-- <input class="form_input" type="text" v-model="dynamic_form_model[sa.key].value" :required="sa.required"> -->
                <multiselect v-model="city"
                    deselect-label=""
                    select-label=""
                    :show-labels="false"
                    placeholder="Select City"
                    :options="all_city_dropdown"
                    :searchable="true"
                    :allow-empty="true"
                    style="width:80%;">
                  </multiselect>
                <p class="form-error-msg" :class="{hidden : dynamic_form_model[sa.key].isFormFieldValid}">  {{ 'Invalid ' + dynamic_form_model[sa.key].display_name}}</p>
              </div>
            </el-col>

            <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16" class="text-left" v-if="sa.value_type=='text_area'">
              <textarea v-model="dynamic_form_model[sa.key].value" rows="4" cols="50" :required="sa.required"></textarea>
              <p class="form-error-msg" :class="{hidden : dynamic_form_model[sa.key].isFormFieldValid}">  {{ 'Invalid ' + dynamic_form_model[sa.key].display_name}}</p>
            </el-col>
            <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16" class="text-left" v-if="sa.value_type=='checkbox'">
              <input class="form_input" type="checkbox" v-model="dynamic_form_model[sa.key].value" :required="sa.required">
              <p class="form-error-msg" :class="{hidden : dynamic_form_model[sa.key].isFormFieldValid}">  {{ 'Invalid ' + dynamic_form_model[sa.key].display_name}}</p>
            </el-col>
            <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16" class="text-left" v-if="sa.value_type=='number'">
              <input class="form_input" type="number" v-model="dynamic_form_model[sa.key].value" :required="sa.required" :placeholder="getPlaceholder(dynamic_form_model[sa.key].key)">
              <p class="form-error-msg" :class="{hidden : dynamic_form_model[sa.key].isFormFieldValid}">  {{ 'Invalid ' + dynamic_form_model[sa.key].display_name}}</p>
            </el-col>
            <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16" class="text-left" v-if="sa.value_type=='tel'">
              <input class="form_input" type="tel" v-model="dynamic_form_model[sa.key].value" :required="sa.required">
              <p class="form-error-msg" :class="{hidden : dynamic_form_model[sa.key].isFormFieldValid}">  {{ 'Invalid ' + dynamic_form_model[sa.key].display_name}}</p>
            </el-col>
            <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16" class="text-left" v-if="sa.value_type=='float'">
              <input class="form_input" type="number" v-model="dynamic_form_model[sa.key].value" :required="sa.required">
              <p class="form-error-msg" :class="{hidden : dynamic_form_model[sa.key].isFormFieldValid}">  {{ 'Invalid ' + dynamic_form_model[sa.key].display_name}}</p>
            </el-col>
            <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16" class="text-left" v-if="sa.value_type=='date'">
              <input class="form_input" type="date" v-model="dynamic_form_model[sa.key].value" :required="sa.required">
              <p class="form-error-msg" :class="{hidden : dynamic_form_model[sa.key].isFormFieldValid}">  {{ 'Invalid ' + dynamic_form_model[sa.key].display_name}}</p>
            </el-col>
            <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16" class="text-left" v-if="sa.value_type=='time'">
              <input class="form_input" type="time" v-model="dynamic_form_model[sa.key].value" :required="sa.required">
              <p class="form-error-msg" :class="{hidden : dynamic_form_model[sa.key].isFormFieldValid}">  {{ 'Invalid ' + dynamic_form_model[sa.key].display_name}}</p>
            </el-col>
            <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16" class="text-left" v-if="sa.value_type=='password'">
              <input class="form_input" type="password" v-model="dynamic_form_model[sa.key].value" :required="sa.required">
              <p class="form-error-msg" :class="{hidden : dynamic_form_model[sa.key].isFormFieldValid}">  {{ 'Invalid ' + dynamic_form_model[sa.key].display_name}}</p>
            </el-col>
            <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16" class="text-left" v-if="sa.value_type=='geo_point'">
              <input class="form_input" type="text" v-model="dynamic_form_model[sa.key].value" required :placeholder="getPlaceholder(dynamic_form_model[sa.key].key)">
              <p class="form-error-msg" :class="{hidden : dynamic_form_model[sa.key].isFormFieldValid}">  {{ 'Invalid ' + dynamic_form_model[sa.key].display_name}}</p>
            </el-col>
          </el-row>
        </div>
        <el-row class="vertical-center" style="text-align:center; margin-top: 15px;">
          <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
            <el-button type="primary" @click.native="save()">Save</el-button>
          </el-col>
        </el-row>
      </div>
    </el-card>

  </div>
</template>

<script>
import { serverAddr } from '../networkConfig.js';
import CripNotice from 'crip-vue-notice';
import TopBar from "./TopBar";
import axios from 'axios';
import Multiselect from 'vue-multiselect';
import { find as _find, reject as _reject, concat as _concat } from 'lodash';

export default {
  name: "AppStoreCreate",
  components: { TopBar, multiselect:Multiselect },
  props: ["app_id", "g_id", "e_id", "id"],
  data() {
    return {
      country: '',
      state: '',
      city: '',
      isLoaderLoading : false,
      isFormLoading : false,
      store_attributes: [],
      all_country_dropdown: [],
      all_states_dropdown: [],
      all_city_dropdown: [],
      dynamic_form_model: {},
      takeManualCityState: false
    }
  },
  watch: {
    country: function (newCountry, oldCountry) {
      this.all_states_dropdown = [];
      this.all_city_dropdown = [];
      // this.state = "";
      // this.city = "";
      if(!newCountry){
      }else{
        this.getStates();
      }
    },
    state: function (newState, oldState) {
      this.all_city_dropdown = [];
      this.city = "";
      if(!newState){
      }else{
        this.getCities();
      }
    }
  },
  created() {
    serverAddr
      .post("/v1/dropdowns/countries/", {})
      .then(response => {
        console.log('get-countries-dropdown', response);
        this.all_country_dropdown = response.data.data ? response.data.data.map(item => item.country) : [];
      })
      .catch(function(error) {
        console.log(error);
      });

    serverAddr.post('/v1/store-config/', { 'app_id': this.app_id })
      .then(this.getStoreConfigCallback)
      .catch(function (error) {
        console.log(error);
      })
  },
  methods: {
      getPlaceholder: function(key){
        if(key == 'wifi_primary_bssid'){
          return 'e.g. ab:cd:ef:12:34:56, 56:12:34:cd:ab:ef'
        }else if(key == 'name'){
          return 'e.g. Westside R City Mall Mumbai'
        }else if(key == 'location'){
          return 'e.g. 21.01,78.134'
        }else if(key == 'wifi_tx_power'){
          return 'e.g. -30, -20, -10, -5, 10'
        }else if(key == 'wifi_amb_bssid_list'){
          return 'e.g. ab:cd:ef:12:34:56'
        }else if(key == 'ble_primary_address'){
          return 'e.g. ab:cd:ef:12:34:56'
        }else if(key == 'ble_tx_power'){
          return 'e.g. -30, -20, -10, -5, 10'
        }else if(key == 'ble_beacon_master_uuid'){
          return 'e.g. A1B2C3D4-E5F6-A7B8-C9D0-E1F2A3B4C5D6'
        }else if(key == 'ble_beacon_slave_uuid'){
          return 'e.g. E1F2A3B4-C5D6-E7F8-A9B0-C1D2E3F4A5B6'
        }else {
          return ''
        }
      },
      getStates: function(){
      serverAddr
      .post("/v1/dropdowns/states/", {country: this.country})
      .then(response => {
        console.log('geo-zone-get-states-dropdown', response);
        this.all_states_dropdown = response.data.data ? response.data.data.map(item => item.state) : [];
      })
      .catch(function(error) {
        console.log(error);
      });
    },
    getCities: function(){
      serverAddr
      // .post("/v1/geo-zone/get-cities/", {app_id: this.app_id})
      .post("/v1/dropdowns/cities-for-state/", {
          country: this.country,
          state: this.state
        })
      .then(response => {
        console.log('geo-zone-get-cities-dropdown', response);
        this.all_city_dropdown = response.data.data ? response.data.data.map(item => item.city) : [];
      })
      .catch(function(error) {
        console.log(error);
      });
    },
    getSortedAttributes(store_attributes){
      var tempAttributes = store_attributes;
      var country_config = _find(tempAttributes, o => o.key == 'country');
      var state_config = _find(tempAttributes, o => o.key == 'state');
      var city_config = _find(tempAttributes, o => o.key == 'city');
      var otherAttributes = _reject(tempAttributes, o => ['country', 'state', 'city'].indexOf(o.key) > -1);

      return _concat(country_config, state_config, city_config, otherAttributes);
    },
    getStoreConfigCallback: function (response) {
      console.log('getStoreConfigCallback', response);
      if (response.data.status) {
        this.store_attributes = this.getSortedAttributes(response.data.data[0].store_attributes);
        this.dynamic_form_model = {};
        // setting up dynamic store form model
        this.store_attributes.forEach((item, index) => {
          this.$set(this.dynamic_form_model, item.key, { ...item, value: '', isFormFieldValid: true });
        })
      }
    },
    validateStore: function () {
      let isStoreValid = [];
      for (var key in this.dynamic_form_model) {
        var isError = false;
        var value = ('' + this.dynamic_form_model[key].value).trim();

        if (this.dynamic_form_model[key].regex && value) {
          var regexTmp = new RegExp(this.dynamic_form_model[key].regex)
          isError = !regexTmp.test(value)
          console.log('isError after regex check', isError);
        }

        if (this.dynamic_form_model[key].required && !value) {
          console.log('required constraint failed')
          isError = true
        }

        if (isError) {
          isStoreValid.push(this.dynamic_form_model[key].key)
          this.dynamic_form_model[key].isFormFieldValid = false;
        } else {
          this.dynamic_form_model[key].isFormFieldValid = true;
        }

      }
      console.log('isStoreValid',isStoreValid);
      return isStoreValid.length == 0;
    },
    saveStore() {
      // network call for save store
        serverAddr.post('/v1/app-store/create/', {
          app_id: this.app_id,
          g_id: this.g_id,
          e_id: this.e_id,
          properties: this.dynamic_form_model
        })
          .then(this.postSaveCallBack)
          .catch(function (error) {
            console.log(error);
          })
    },
    save: function () {
      console.log(this.dynamic_form_model);
      if(!this.dynamic_form_model.city.value || !this.dynamic_form_model.state.value  || !this.dynamic_form_model.country.value){
        console.log('city state country missing in dynamic model');
        this.getCityStateCountryFromGoogle();
      }
    },
    getCityStateCountryFromGoogle(){
      // const google_map_api_key = 'AIzaSyBFaOsUx42A4_EJopMIZZZmq4N1UyRE69Y';
        const google_map_api_key = 'AIzaSyBWx06oZ1qYEP3OLaiY-yEaTEHQ_a9nUPA';
        console.log('Getting city state from location coordinates...');
        var location_latlng = this.dynamic_form_model['location'].value ? this.dynamic_form_model['location'].value.trim() : '';
        var lat_lng_regex = /^[-+]?([1-8]?\d(\.\d+)?|90(\.0+)?)\s*,\s*[-+]?(180(\.0+)?|((1[0-7]\d)|([1-9]?\d))(\.\d+)?)$/;
        if(!lat_lng_regex.test(location_latlng)){
          console.log('location not filled');
          this.validateStore();
          return
        }
        axios.get(`https://maps.googleapis.com/maps/api/geocode/json?latlng=${location_latlng.replace(' ', '')}&key=${google_map_api_key}`)
        // axios.get(`https://maps.googleapis.com/maps/api/geocode/json?latlng=${location_latlng.replace(' ', '')}&sensor=true&key=${google_map_api_key}`)
          .then(response => {
            console.log("location response: ", response);
            var results = response.data.results;
            if (results.length) {
                this.city = "";
                this.state = "";
                this.country = "";
                console.log('takeManualCityState false, taking values from google');

                for (var ac = 0; ac < results[0].address_components.length; ac++) {
                  var component = results[0].address_components[ac];

                  switch (component.types[0]) {
                    case 'locality':
                      if (component.long_name.trim().length) {
                        this.city = component.long_name;
                      }
                      break;
                    case 'administrative_area_level_1':
                      if (component.long_name.trim().length) {
                        this.state = component.long_name;
                      }
                      break;
                    case 'country':
                      if(component.long_name.trim().length){
                        this.country = component.long_name;
                      }
                      // storableLocation.registered_country_iso_code = component.short_name;
                    break;
                  }
                }
               console.log('this.reverse_location', this.city, this.state, this.country);
            }

            this.populateLocationDropdown();
          })
          .catch(e => {
            console.log(e);
          })
    },
    populateLocationDropdown(){
            if ((this.city.trim() == "" || this.state.trim() == "" || this.country.trim() == "") && !this.takeManualCityState) {
              console.log('could not fild city or state from location...');
              this.takeManualCityState = true;
            } else {
              this.dynamic_form_model.city.value = this.city;
              this.dynamic_form_model.state.value = this.state;
              this.dynamic_form_model.country.value = this.country;

              if (this.dynamic_form_model.city.value.trim().length && this.dynamic_form_model.state.value.trim().length && this.dynamic_form_model.country.value.trim().length) {
                // this.takeManualCityState = false;
                let isStoreValid = this.validateStore();
                if (!isStoreValid) {
                  // show incomplete form error
                  console.log('invalid store');
                  console.log(this.dynamic_form_model);
                } else {
                  console.log('valid store');
                  console.log(this.dynamic_form_model);
                  this.saveStore();
                }
              } else {
                console.log('please fill manual city and state...');
              }
            }
    },
    postSaveCallBack: function (response) {
      console.log("Response: ", response, this.app_id);
      if (response.data.status) {
        this.takeManualCityState = false;
        this.$router.go(-1);
        new CripNotice({
          title: "Success",
          description: "Store created successfully.",
          className: "notices",
          type: "success",
          duration: 3,
        });
        this.$router.push({ path: `/app-store-list/${this.app_id}` });
      }
    }
  }
}
</script>

<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>
<style>
.form-error-msg {
  font-size: 0.7em;
  color: red;
  margin: 0px;
}
.form_input{
  padding: 10px 20px;
  width: 80%;
}
.hidden{
  opacity: 0;
}
</style>



