<template>
    <div class="BusinessEntityList">
    <TopBar></TopBar>
    <div v-for="(business_entity, index) in business_entities" :key="index">
      <el-card class="box-card" style="max-width:80vw; margin:10px auto;">
        <div slot="header" class="clearfix" style="display:flex;">
          <h2 style="font-weight:bold; margin:auto" >{{business_entity.name}}</h2>
          <div>
            <!-- <el-button type="primary"  @click.native="edit(index)">Edit</el-button>
            <el-button type="primary"  @click.native="remove(index)">Delete</el-button> -->
          </div>
        </div>
        <el-row>
          <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8">
            <el-row>
              <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
                  <h3> Admin </h3>
              </el-col>
            </el-row>
            <el-row v-for="(user, index) in business_entity.userCreds" :key="index">
                <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
                  <div> {{user.username}} </div>
                </el-col>
            </el-row>
          </el-col>
          <el-col :xs="24" :sm="10" :md="10" :lg="10" :xl="10">
            <el-row>
              <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
                  <h3> Roles </h3>
              </el-col>
            </el-row>
            <el-row >
              <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
                <span v-for="(role, index) in business_entity.roles" :key="index"> {{role.name}}<span v-if="index != (business_entity.roles.length-1)">,</span> </span>
              </el-col>
              </el-row>
          </el-col>
          <el-col :xs="24" :sm="6" :md="6" :lg="6" :xl="6" style="text-align:right;">
            <el-row>
              <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
                  <!-- <el-button type="primary"  @click.native="edit(index)">Edit</el-button>
                  <el-button type="primary"  @click.native="remove(index)">Delete</el-button> -->
                  <h3>&nbsp;</h3>
              </el-col>
            </el-row>
            <el-row>
              <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
                  <el-button type="primary" size="mini"  @click.native="edit(index)">Edit</el-button>
                  <el-button type="primary" size="mini"  @click.native="remove(index)">Delete</el-button>
              </el-col>
            </el-row>
          </el-col>
        </el-row>

        <!-- <div class="text item">
          <el-row class="vertical-center text-center">
            <el-col :xs="24" :sm="6" :md="6" :lg="6" :xl="6">
              <el-row>
                <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
                  <h3> Admin </h3>
                </el-col>
              </el-row>
              <el-row v-for="(user, index) in business_entity.userCreds" :key="index">
                <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
                  <div> {{user.username}} </div>
                </el-col>
              </el-row>
            </el-col>
            <el-col :xs="24" :sm="18" :md="18" :lg="18" :xl="18">
              <el-row>
                <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8">
                  <h3> Roles </h3>
                </el-col>
                <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16">
                  <h3> Role ID </h3>
                </el-col>
              </el-row>
              <el-row v-for="(role, index) in business_entity.roles" :key="index">
                <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8">
                  <div> {{role.name}} </div>
                </el-col>
                <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16">
                  <div> {{role.uuid}} </div>
                </el-col>
              </el-row>
            </el-col>
          </el-row>
        </div> -->
      </el-card>
    </div>

  </div>
</template>

<script>
import { serverAddr } from '../networkConfig.js';
import CripNotice from 'crip-vue-notice';
import TopBar from "./TopBar";

export default {
  name: "BusinessEntityList",
  components: {TopBar},
  data(){
    return {
      business_entities: []
    }
  },
  created(){
    serverAddr.post('/v1/business-entity/')
    .then(this.getBusinessEntityCallback)
    .catch(function (error) {
      console.log(error);
    })
  },
  methods:{
    getBusinessEntityCallback: function(response){
      this.business_entities = response.data.data;
    },
    edit: function(index){
      var id = this.business_entities[index].b_id;
      this.$router.push({path: `/business-entity-edit/${id}`})
    },
    remove: function(index) {
    serverAddr.post('/v1/business-entity/delete/',{b_id:this.business_entities[index].b_id})
      .then(this.postRemoveCallback)
      .catch(function (error) {
        console.log(error);
      })
    },
    postRemoveCallback: function(response){
      if(response.data.status){
        new CripNotice({
          title: "Success",
          description: "Business Entity deleted successfully.",
          className: "notices",
          type: "success",
          duration: 3,
        });
        serverAddr.post('/v1/business-entity/')
        .then(this.getBusinessEntityCallback)
        .catch(function (error) {
          console.log(error);
        })
      }
    }
  }
}
</script>



