<template>
  <section class="GlobalNotificationList">
    <TopBar></TopBar>
    <el-card class="box-card" style="margin: 0px 20px;">
      <el-row :gutter="10" style="padding: 0px 20px;">
        <el-col :span="24">
            <h3 style="margin-bottom: 0px;">Notifications</h3>
        </el-col>
      </el-row>
      <el-row :gutter="10" style="padding: 20px 20px 0 20px; margin-bottom: 20px;">
        <el-col :span="4">
            <el-radio v-model="search_for" size="small" label="GEOZONE" border>Search Zone</el-radio>
        </el-col>
        <el-col :span="4">
            <el-radio v-model="search_for" size="small" label="STORE" border>Search Store</el-radio>
        </el-col>
        <el-col :span="4">
            <el-button type="primary" size="small" @click.native="loadNotifications()" style="margin-top: 10px;">Search</el-button>
        </el-col>
      </el-row>

      <el-row :gutter="10" style="padding: 0px 20px 15px 20px; border-bottom: 1px solid #EEE;">
        <el-col :span="24">
            <h4 style="margin-bottom: 0px;" v-if="notifications_for=='GEOZONE'">Matching Zone Notifications</h4>
            <h4 style="margin-bottom: 0px;" v-if="notifications_for=='STORE'">Mathing Store Notifications</h4>
        </el-col>
      </el-row>

      <el-row>
        <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24" v-for="(loader,index) in [1,2,3]" :key="'loader' + index" style="margin-bottom: 25px;" v-if="isLoading">
          <content-placeholders>
            <!-- <content-placeholders-heading :img="true" /> -->
            <content-placeholders-text :lines="2" />
          </content-placeholders>
        </el-col>
      </el-row>

      <el-row style="padding: 0px 20px 15px 20px;">
        <el-col :span="24" v-if="resultLength0 && !isLoading">
          <h5 style="color: #666;"> No Result Found...</h5>
        </el-col>
      </el-row>

      <el-row :gutter="10" style="padding: 15px 20px;" v-for="(item, index) in notification_list" :key="index" class="notification-item" v-if="!isLoading">


        <el-col :span="10">
          <div>
            <label for="">Notification id</label>
              <span style="font-size: 0.8em;">{{item.nt_id}}</span>
          </div>
          <div>
            <label for="">Notification Text</label>
            {{item.notification[0].text}} <br>
            <span style="font-size: 0.8em; color: #666;">{{item.notification[0].sub_text}}</span>
          </div>
        </el-col>
        <el-col :span="4">
          <div>
            <label for="">Notification Type</label>
            {{item.notification_type[0].name}}
          </div>
          <div>
            <label for="">Action Type</label>
            {{item.notification_action_type[0].name}}
          </div>
        </el-col>
        <el-col :span="6">
          <div>
            <label for="">Match Type</label>
            {{item.notification[0].notification_match_type}}
          </div>
          <div v-if="item.notification[0].notification_match_type == 'BRAND'">
            <label for="">Associated Brands</label>
              <span v-for="(brand, index1) in item.notification_brands" :key="'brand'+index1" style="font-size: 0.9em;">
                {{brand.name}} <span v-if="index1 < item.notification_brands.length - 1">, </span>
              </span>
          </div>
        </el-col>
        <el-col :span="4">
          <el-button type="text"  @click.native="edit(item.nt_id)">Edit</el-button>
          <el-button type="text"  @click.native="deleteNotification(item.nt_id, index)">Delete</el-button>
        </el-col>
        <el-col :span="24" v-if="notifications_for=='GEOZONE'">
            <label for="">Associated Zones</label>
            <span style="font-size: 0.8em; color: #666;">{{item.geozone_names.join(', ')}}</span>
        </el-col>
        <el-col :span="24" v-if="notifications_for=='STORE'">
            <label for="">Associated Stores</label>
            <span style="font-size: 0.8em; color: #666;">{{item.store_names.join(', ')}}</span>
        </el-col>
      </el-row>
    </el-card>
  </section>
</template>

<script>
import { serverAddr } from "../networkConfig.js";
import CripNotice from "crip-vue-notice";
import TopBar from "./TopBar";

  export default  {
    name: "GlobalNotificationList",
    components: { TopBar},
    props: ['app_id'],
    created() {
      this.loadNotifications();
    },
    data() {
      return {
        resultLength0: false,
        isLoading: false,
        notification_list: [],
        search_for: "GEOZONE",
        notifications_for: "GEOZONE"
      }
    },
    methods: {
      loadNotifications(){
        this.isLoading = true;
        this.resultLength0 = false;
        var notifications_for = this.search_for;
        serverAddr
          .post("/v1/app/notification/", {app_id: this.app_id, search_for: this.search_for})
          .then(response => {
            this.isLoading = false;
            this.notifications_for = notifications_for;
            console.log('app-notifications: ', response);
            this.notification_list = response.data.data;
            if(!this.notification_list.length){
              this.resultLength0 = true;
            }
          })
          .catch(function(error) {
            console.log(error);
          });
      },
      edit(nt_id){
        this.$router.push({path: `/global-notification-edit/app_id/${this.app_id}/type/${this.notifications_for}/nt_id/${nt_id}/`})
      },
      deleteNotification(nt_id, index){
        serverAddr.post('/v1/notification/delete/',{app_id:this.app_id, nt_id: nt_id})
      .then(response =>{
        if(response.data.status && response.data.data[0].nt_id){
          this.notification_list.splice(index, 1);
        }
      })
      .catch(function (error) {
        console.log(error);
      })
      }
    },
    computed: {

    }
}
</script>

<style scoped>
  .GlobalNotificationList {

  }
  .notification-item{
    margin-bottom: 15px;
    padding: 10px 0px;
    border-bottom: 1px solid #EEE;
  }
  label{
    font-weight: bold;
    font-size: 0.8em;
    display: block;
    margin-top: 10px;
  }
  p{
    margin: 0px;
  }
</style>



