<template>
    <div class="StoreNotificationList">
    <TopBar></TopBar>


    <el-card class="box-card" style="max-width:80vw; margin:10px auto;">
        <div slot="header" class="clearfix">
          <h3 style="margin: 0px; display: flex; align-items: center;">
            <span @click="redirectToBack()" style="cursor: pointer">{{storeNameAttribute.length? storeNameAttribute[0].value : 'Store'}}
            <i class="el-icon-arrow-right"></i> &nbsp;</span>
            <span style="flex-grow: 1; color: #66b1ff;">Store Notifications</span>
            <router-link :to="{path: `/store/notification/create/${app_id}/${st_id}`}">
              <el-button type="success" size="mini">New Store Notification</el-button>
            </router-link>
          </h3>
        </div>

        <div class="text item">

          <el-row>
            <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24" v-for="(loader,index) in [1,2,3]" :key="'loader' + index" style="margin-bottom: 25px;" v-if="storeNotificationIsLoading">
              <content-placeholders>
                <!-- <content-placeholders-heading :img="true" /> -->
                <content-placeholders-text :lines="2" />
              </content-placeholders>
            </el-col>
          </el-row>

          <el-row :gutter="10" style="padding: 15px 20px; font-size: 14px;" v-for="(item, index) in storeNotifications" :key="index" class="notification-item">
            <el-col :span="10">
              <div>
                <label>Notification Id</label>
                  <span>{{item.nt_id}}</span>
              </div>
              <div>
                <label>Notification Text</label>
                {{item.notification[0].text}} <br>
                <span style="font-size: 0.8em; color: #666;">{{item.notification[0].sub_text}}</span>
              </div>
            </el-col>
            <el-col :span="5">
              <div>
                <label>Notification Type</label>
                {{item.notification_type[0].name}}
              </div>
              <div>
                <label>Action Type</label>
                {{item.notification_action_type[0].name}}
              </div>
            </el-col>
            <el-col :span="6">
              <div>
                <label>Match Type</label>
                {{item.notification[0].notification_match_type}}
              </div>
              <div v-if="item.notification[0].notification_match_type == 'BRAND'">
                <label>Associated Brands</label>
                  <span v-for="(brand, index1) in item.notification_brands" :key="'brand'+index1" style="font-size: 0.9em;">
                    {{brand.name}} <span v-if="index1 < item.notification_brands.length - 1">, </span>
                  </span>
              </div>
            </el-col>
            <el-col :span="3">
              <el-button type="text"  @click.native="redirectToStoreNodification(item.nt_id)">Edit</el-button>
              <el-button type="text"  @click.native="removeStoreNodification(item.nt_id)">Delete</el-button>
            </el-col>
          </el-row>
        </div>
      </el-card>
    <!-- <el-card class="box-card" style="max-width:80vw; margin:10px auto;">
      <div class="text item">
        <el-row style="font-size: 0.8em;" class="vertical-center margin-bottom-10" v-for="(notification, index) in storeNotifications" :key="index">
          <el-col :xs="24" :sm="5" :md="5" :lg="5" :xl="5">
            {{notification.notification[0].text}}
          </el-col>
          <el-col :xs="24" :sm="5" :md="5" :lg="5" :xl="5">
            {{notification.notification_type[0].name}}
          </el-col>
          <el-col :xs="24" :sm="9" :md="9" :lg="9" :xl="9">
            {{notification.nt_id}}
          </el-col>
          <el-col :xs="24" :sm="4" :md="4" :lg="4" :xl="4" style="text-align: right;">
            <span>
              <el-button type="text" @click.native="removeStoreNodification(notification.nt_id)">Delete</el-button> |
            </span>
            <span>
            </span>
              <el-button type="text" @click.native="redirectToStoreNodification(notification.nt_id)">Edit</el-button>
            <span>
              <el-button type="text" @click.native="openStoreList(notification)">Details</el-button>
              <router-link :to="{path: `/store-list/${app_id}/${engagementZone.g_id}`}">Manage Stores</router-link>
            </span>
          </el-col>
        </el-row>
      </div>
    </el-card> -->

  </div>
</template>

<script>
import { serverAddr } from '../networkConfig.js';
import CripNotice from 'crip-vue-notice';
import TopBar from "./TopBar";

export default {
  name: "StoreNotificationList",
  components: {TopBar},
  props: ["app_id", "st_id","e_id"],
  data(){
    return {
      storeNameAttribute: [],
      storeDetails: {
        store_attributes: []
      },
      storeDetailsIsLoading: false,
      storeNotificationIsLoading: false,
      storeNotifications: [],
      selected_id: '',
      location: '',
      dialogVisible: false
    }
  },
  computed: {
  },
  created(){
    this.storeNotificationIsLoading = true;
    serverAddr.post('/v1/store/notification/', {
        'app_id':this.app_id,
        'st_id':this.st_id
    })
    .then(this.getStoreNtCallback)
    .catch(function (error) {
      console.log(error);
    })

    this.storeDetailsIsLoading = true;
    serverAddr.post('/v1/store/get/', {
        'app_id':this.app_id,
        'st_id':this.st_id,
        'e_id':this.e_id
    })
    .then((response)=>{
      this.storeDetailsIsLoading = false;
      this.storeDetails = response.data.data[0];
       this.storeNameAttribute = this.storeDetails.store_attributes.filter((item)=>{
        return item.key == 'name'
      })
    })
    .catch(function (error) {
      console.log(error);
    })
  },
  methods:{
    getStoreNtCallback: function(response){
      this.storeNotificationIsLoading = false;
      this.storeNotifications = response.data.data;
    },
    redirectToStoreNodification: function(nt_id) {
      this.$router.push({path: `/store/notification/create/${this.app_id}/${this.st_id}/${nt_id}`})
    },
    removeStoreNodification: function(nt_id) {
      serverAddr.post('/v1/drilled-down-notification/delete/',{
        app_id: this.app_id,
        nt_id: nt_id,
        st_id: this.st_id,
        g_id: null
      })
      .then(this.postRemoveCallback)
      .catch(function (error) {
        console.log(error);
      })
    },
    postRemoveCallback: function(response){
      if(response.data.status){
        new CripNotice({
          title: "Success",
          description: "Store deleted successfully.",
          className: "notices",
          type: "success",
          duration: 3,
        });
        serverAddr.post('/v1/store/notification/', {
          'app_id':this.app_id,
          'st_id':this.st_id
        })
        .then(this.getStoreNtCallback)
        .catch(function (error) {
          console.log(error);
        })
      }
    },
    redirectToBack(){
      this.$router.go(-1);
    }
  }
}
</script>
<style scoped>
.StoreNotificationList{
  font-size: 14px;
}
  label{
    display: block;
    font-weight: bold;
    font-size: 0.9em;
    margin-top: 10px;
  }
  .notification-item{
    margin-bottom: 15px;
    padding: 10px 0px;
    border-bottom: 1px solid #EEE;
  }
  p{
    margin: 0px;
  }
</style>



