<template>
  <div class="Dashboard">
    <TopBar></TopBar>

    <el-card class="box-card" style="max-width:90vw; margin:10px auto; background-color: tomato;" v-if="showAccessDeniedError">
      <h4 style="margin: 0px; display: flex; align-items: center; color: #FFF;">
        <span style="flex-grow: 1"> {{$route.query.error}} </span> <el-button type="default" @click.native="closeAccessDeniedMsg" size="mini">Close</el-button>
      </h4>
    </el-card>

    <div class="" style="margin: 30px;" v-if="checkRole('Platform Admin')">
      <el-card class="box-card" style="max-width:100vw; margin: 22px 0;"  >
        <div class="clearfix">
          <span>Platform Admin Dashboard</span>
        </div>
      </el-card>
      <el-row :gutter="20">
        <el-col :xs="24" :sm="6" :md="6" :lg="6" :xl="6">
          <el-card class="text-center">
            <img src="../assets/icon/paltform.png" alt="" width="30px" srcset="">
            <h3>Platform</h3>
            <span> <router-link class="links" :to="{path: '/platform-list'}"> List</router-link></span>
          </el-card>
        </el-col>
        <el-col :xs="24" :sm="6" :md="6" :lg="6" :xl="6">
          <el-card class="text-center">
            <img src="../assets/icon/cogwheel.png" alt="" width="30px" srcset="">
            <h3>Services</h3>
            <span> <router-link class="links" :to="{path: '/service-list'}"> List</router-link></span>
          </el-card>
        </el-col>
        <el-col :xs="24" :sm="6" :md="6" :lg="6" :xl="6">
          <el-card class="text-center">
            <img src="../assets/icon/roles.png" alt="" width="30px" srcset="">
            <h3>Roles</h3>
            <span> <router-link class="links" :to="{path: '/role-list'}"> List</router-link></span>
          </el-card>
        </el-col>
        <el-col :xs="24" :sm="6" :md="6" :lg="6" :xl="6">
            <el-card class="text-center">
              <img src="../assets/icon/line-chart.png" alt="" width="30px" srcset="">
            <h3>Business Entity</h3>
            <router-link class="links" :to="{path: '/business-entity-create'}"> Create</router-link>
            &nbsp; &nbsp; &nbsp;
            <router-link class="links" :to="{path: '/business-entity-list'}"> List</router-link>
          </el-card>
        </el-col>
      </el-row>
    </div>
    <!-- </el-card> -->

    <div style="max-width:90vw; margin:10px auto; padding: 8px; box-sizing: border-box;" v-if="checkRole('BE Admin')">
      <el-card class="box-card">
        <h3 style="margin: 0px; display: flex; align-items: center;"> <span style="flex-grow: 1"> Business Admin Dashboard </span> <router-link style="display:inline; margin-left:20px;" :to="{path: '/app-create'}"><el-button type="success" size="mini">New App</el-button></router-link>
        </h3>
      </el-card>
    </div>

    <el-row class="margin-10" :gutter="16" style="max-width:90vw; margin:10px auto;">

      <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8" v-for="(app, index) in [1,2,3,4,5]" :key="'loading'+index" style="margin-bottom: 15px;" v-if="isLoading && checkRole('BE Admin')">
        <el-card class="box-card">
        <content-placeholders :rounded="true">
          <content-placeholders-img />
          <content-placeholders-heading />
        </content-placeholders>
        </el-card>
      </el-col>

      <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8"  v-for="(app, index) in apps" :key="index" style="margin-bottom: 15px;">
        <el-card class="box-card" v-if="checkRole('BE Admin')">
          <div class="text item">
            <p class="card-label">App Name</p>
            {{app.name}}
            <p class="card-label"> App ID </p>
            <span style="font-size: 0.8em;">{{app.app_id}}</span>
            <p class="card-label"> Client API ID </p>
            <span style="font-size: 0.8em;">{{app.client_api_id ? app.client_api_id : '--'}}</span>
            <div style="margin-top: 10px; border-top: 1px solid #EEE; padding-top: 10px;">
              <el-button type="primary" icon="el-icon-edit" @click.native="edit(index)" circle></el-button>
              <el-button type="primary" icon="el-icon-delete" @click.native="deleteAppDialogVisible=true; selectedIndexForDelete=index" circle></el-button>
              <el-button type="primary" icon="el-icon-d-arrow-right" circle @click.native="openApp(app)"></el-button>
              <!-- <router-link style="display:inline;" :to="{path: `/app-dashboard/${app.app_id}`}"></router-link> -->
            </div>
          </div>
        </el-card>
        <el-dialog title="Delete App" :visible.sync="deleteAppDialogVisible">
          <h3 style="color: tomato;">Are you sure? This action can not be reversed.</h3>
          <span slot="footer" class="dialog-footer">
            <el-button @click="deleteAppDialogVisible=false; selectedIndexForDelete=null;">Cancel</el-button>
            <el-button type="primary" @click="removeApp(selectedIndexForDelete)">Confirm</el-button>
          </span>
        </el-dialog>
      </el-col>
    </el-row>

    <el-card class="box-card" style="max-width:80vw; margin:10px auto;" v-if="checkRole('Consumer')">
      <div slot="header" class="clearfix">
        <span>Consumer Dashboard</span>
      </div>
      <div class="text item">
        <el-row class="text-left">
          <el-col :xs="24" :sm="12" :md="12" :lg="12" :xl="12">
            <h3>Chat</h3>
            <router-link :to="{path: '/consumer-chat-rooms-list'}">Chat rooms</router-link>
          </el-col>
          <el-col :xs="24" :sm="12" :md="12" :lg="12" :xl="12" class="media-xs-view">
            <h3>Subscribed stores</h3>
            <router-link :to="{path: '/consumer-subscribed-store-list'}">Store List</router-link>
          </el-col>
        </el-row>
      </div>
    </el-card>

    <el-card class="box-card" style="max-width:80vw; margin:10px auto;" v-if="checkRole('Store Manager')">
      <div slot="header" class="clearfix">
        <span>Manager Dashboard</span>
      </div>
      <div class="text item">
        <el-row class="text-left">
          <el-col :xs="24" :sm="12" :md="12" :lg="12" :xl="12" class="desktopChatLink">
            <h3>Chat</h3>
            <router-link :to="{path: '/admin-room-list'}">Chat rooms</router-link>
          </el-col>

          <el-col :xs="24" :sm="12" :md="12" :lg="12" :xl="12" class="mobileChatLink">
            <h3>Chat</h3>
            <router-link :to="{path: '/mobile-admin-room-list'}">Mobile Chat rooms</router-link>
          </el-col>

          <el-col :xs="24" :sm="12" :md="12" :lg="12" :xl="12" class="media-xs-view">
            <h3>Notification</h3>
            <router-link class="links" :to="{path: `/broadcast-notification/${app_id}`}">Broadcast Notification</router-link>
          </el-col>
        </el-row>
      </div>
    </el-card>

    <el-card class="box-card" style="max-width:80vw; margin:10px auto;" v-if="checkRole('Customer Care Admin')">
      <div slot="header" class="clearfix">
        <span>Customer Care Admin Dashboard</span>
      </div>
      <div class="text item">
        <el-row class="text-left">
          <el-col :xs="24" :sm="12" :md="12" :lg="12" :xl="12" class="desktopChatLink">
            <h3>Chat</h3>
            <router-link :to="{path: '/admin-room-list'}">Chat rooms</router-link>
          </el-col>

          <el-col :xs="24" :sm="12" :md="12" :lg="12" :xl="12" class="mobileChatLink">
            <h3>Chat</h3>
            <router-link :to="{path: '/mobile-admin-room-list'}">Mobile Chat rooms</router-link>
          </el-col>
        </el-row>
      </div>
    </el-card>

  </div>
</template>

<script>
import {serverAddr, pushyAppId} from '../networkConfig.js';
import TopBar from "./TopBar";
import CripNotice from 'crip-vue-notice';
import { EventBus } from '../eventBus.js';

export default {
  name:"Dashboard",
  components: {TopBar},
  data() {
    return {
      showAccessDeniedError: false,
      isLoading : false,
      role:'',
      apps: [],
      deleteAppDialogVisible: false,
      selectedIndexForDelete: null,
      stores: [],
      chatRooms: []
    }
  },
  beforeRouteUpdate (to, from, next) {
    console.log('dahsboard beforeRouteUpdate', to.query);
    this.showAccessDeniedError = false;
    this.$store.commit('general/setAppId', null);
    this.isLoading = true;
    this.authChangeHandler();
    if(to.query.error){
      this.showAccessDeniedError = true;
    }
    next();
  },
  mounted(){
    // EventBus.$on('authChange', this.authChangeHandler);
    console.log('dahsboard mounted', this.$route.query);
    this.showAccessDeniedError = false;
    this.$store.commit('general/setAppId', null);
    this.isLoading = true;
    this.authChangeHandler();
    if(this.$route.query.error){
      this.showAccessDeniedError = true;
    }
    // this.initiateOneSignalTags();
    this.initiatePushy();
    console.log('$route.query.error: ', this.$route.query.error);

  },
  computed: {
    authUser() {
      return this.$store.state.general.authUser;
    },
    app_id(){
      return localStorage.getItem("app_id");
    }
  },
  methods: {
  redirectToChatroom(store_id, brand_id){
      this.$router.push({path:`/admin-room-list/${store_id}/${brand_id}`})
  },
  initiatePushy(){
    console.log('registering with pushy...', window.Pushy);

    // Register device for push notifications
    window.Pushy.register({ appId: pushyAppId }).then(function (deviceToken) {
        // Print device token to console
        console.log('Pushy device token: ' + deviceToken);

        // Send the token to your backend server via an HTTP GET request
        serverAddr.post('/v1/profile/update-notification-token/', {
          "notification_token": deviceToken
        })
        .then(result => console.log('notification token update callback: ', result))
        .catch(function (error) {
          console.log(error);
        })
    }).catch(function (err) {
        // Handle registration errors
        console.error(err);
    });
  },
  initiateOneSignalTags(){
    console.log('setting one signal tags...');
    var OneSignal = window.OneSignal || [];
    OneSignal.push( () => {
      OneSignal.deleteTags(["userId", "username", 'role']).then((tagsDeleted) => {
        OneSignal.sendTags({
          userId: this.authUser.p_id,
          username: this.authUser.username,
          // role: this.authUser.roles
        }).then((tagsSent) => {
          // Callback called when tags have finished sending
          console.log('tagsSent', tagsSent);
        });
      });


      OneSignal.getUserId().then( (userId) => {
        console.log("OneSignal User ID:", userId);
        // UPDATE USER ID IN PROFILE
      });
    });
  },
  closeAccessDeniedMsg(){
    console.log('closeAccessDeniedMsg');
    this.showAccessDeniedError = false;
    this.$router.push({path: `/dashboard/`, query: {} });
  },
  openApp: function(app){
    this.$store.commit('general/setAppId', app.app_id);
    this.$router.push({path: `/app-dashboard/${app.app_id}/${app.name}`});
    localStorage.setItem('app_id', app.app_id);
  },
  checkRole: function(roleName) {
    if(!this.authUser.roles){
      return false;
    }
    return this.authUser.roles.some(function(el) {
      return el.name === roleName;
    });
  },
    authChangeHandler: function(authCreds){
      if(this.checkRole('BE Admin')){
        serverAddr.post('/v1/app/')
        .then(this.getAppCallback)
        .catch(function (error) {
          console.log(error);
        })
      }
    },
    getAppCallback: function(response){
      this.isLoading = false;
      if(response.data.status){
        this.apps = response.data.data;
      }
    },
    edit: function (index) {
      var id = this.apps[index].app_id;
      this.$router.push({ path: `/app-create/${id}` })
    },
    removeApp: function(index) {
      serverAddr.post('/v1/app/delete/',{app_id:this.apps[index].app_id})
      .then(this.postRemoveCallback)
      .catch(function (error) {
        console.log(error);
      })
    },
    postRemoveCallback: function(response){
      if(response.data.status){
        this.deleteAppDialogVisible=false;
        this.selectedIndexForDelete=null;
        new CripNotice({
          title: "Success",
          description: "App deleted successfully.",
          className: "notices",
          type: "success",
          duration: 3,
        });
        serverAddr.post('/v1/app/')
        .then(this.getAppCallback)
        .catch(function (error) {
          console.log(error);
        })
      }
    }
  },
  beforeDestroy(){
    EventBus.$off('authChange', this.authChangeHandler);
  }
}
</script>
<style scoped>
.card-label{
  margin:  10px 0px 0px 0px;
  font-size: 0.75em;
  font-weight: bold;
}
.links{
  text-decoration: none;
  display: inline;
  /* margin-right: 25px; */
}
.img-icon{
  vertical-align: baseline;
}
  h3{
    margin-top: 0px;
    color: #666;
  }
.card-row .el-col{
  padding: 10px 0;
}
.mobileChatLink{
  display: none;
}
.desktopChatLink{
  display: block;
}
@media screen and (max-width: 767px) {
  .media-xs-view{
    margin-top: 20px;
  }
  .mobileChatLink{
    display: block;
  }
  .desktopChatLink{
    display: none;
  }
}
</style>



