<template>
    <div class="BrandManagerCreate">
    <TopBar></TopBar>

    <el-row :gutter="15" style="margin: 0 auto; width: 90vw;">

      <el-col :xs="24" :sm="24" :md="12" :lg="8" :xl="8" style="margin-bottom: 15px" v-for="(profile, index) in [1,2,3,4,5]" :key="'loader' +index" v-if="isLoading">
        <el-card style="height: 270px;">
          <content-placeholders>
            <content-placeholders-heading :img="true" />
            <content-placeholders-text :lines="3" />
            <content-placeholders-text :lines="3" />
          </content-placeholders>
        </el-card>
      </el-col>

      <div style="border: 1px solid #ddd; text-align: center; font-size: 14px; width: 50%; margin: 0 auto; margin-top: 50px; padding: 20px;" v-if="!profiles.length"> No store managers available to assigned
        <router-link :to="{ path: `/profile-create/${app_id}`}" style="text-decoration: none;">
          <div style="margin-top: 15px; font-size: 13px; color: #409eff; cursor: pointer;">Click here to create store manager</div>
        </router-link>
      </div>

      <el-col :xs="24" :sm="24" :md="12" :lg="8" :xl="8" v-for="(profile, index) in profiles" :key="index" style="margin-bottom: 15px">

        <el-card class="box-card" style="width: 100%;">

          <div style="overflow-y: auto; height: 220px;">
            <el-row class="vertical-center text-left" style="background-color: #F1F1F1; padding: 10px 15px;">
              <el-col :span="8">
                <h4 style="margin: 0px;"> Attribute </h4>
              </el-col>

              <el-col :span="16">
                <h4 style="margin: 0px;"> Value </h4>
              </el-col>
            </el-row>

            <el-row class="vertical-center text-left" style="padding: 5px 10px; border-bottom: 1px solid #F1F1F1;" v-for="(profile_attr, index) in profile.profile_attributes" :key="index">
              <el-col :span="8">
                {{profile_attr.display_name}}
              </el-col>
              <el-col :span="16">
                {{profile_attr.value}}
              </el-col>
            </el-row>

            <el-row class="vertical-center text-left" style="padding: 5px 10px; border-bottom: 1px solid #F1F1F1;">
              <el-col :span="8">
                Username
              </el-col>
              <el-col :span="16">
                {{profile.profile.username}}
              </el-col>
            </el-row>

            <div style="font-size: 0.8em; padding: 5px 10px;">
              {{profile.p_id}}
            </div>
            <div class="text-right" style="margin-top: 10px;">
              <el-button type="text" @click.native="AddManager(index)">Assign Brand Manager</el-button>
            </div>

          </div>

        </el-card>
      </el-col>
    </el-row>

  </div>
</template>

<script>
import { serverAddr } from '../networkConfig.js';
import CripNotice from 'crip-vue-notice';
import TopBar from "./TopBar";

export default {
  name: "BrandManagerCreate",
  components: {TopBar},
  props: ["app_id", "id"],
  data(){
    return {
      isLoading : true,
      profiles: [],
    }
  },
  created(){
    this.initProfileData();
  },
  methods:{
    initProfileData: function(response){
    serverAddr.post('/v1/profile/brand-manager/', {app_id: this.app_id})
      .then(this.getProfileCallback)
      .catch(function (error) {
        console.log(error);
      })
    },
    getProfileCallback: function(response){
      this.isLoading = false;
      this.profiles = response.data.data;
    },
    AddManager: function(index){
      serverAddr.post('/v1/profile/brand-manager/assign-brand/',
      {
        br_id:this.id,
        app_id: this.app_id,
        p_id:this.profiles[index].profile.uuid
      })
      .then(this.postAddManagerCallback)
      .catch(function (error) {
        console.log(error);
      })
    },
    postAddManagerCallback: function(response){
      if(response.data.status){
        new CripNotice({
          title: "Success",
          description: "Manager assigned successfully.",
          className: "notices",
          type: "success",
          duration: 3,
        });
      }
    }
  }
}
</script>



