<template>
    <div class="StoreList">
    <TopBar></TopBar>

     <el-card class="box-card" style="margin:10px auto; max-width: 80vw;">
        <div slot="header" class="clearfix">
          <h3 style="margin: 0px; display: flex; align-items: center;">
            <span style="flex-grow: 1"><i class="el-icon-back" @click="redirectToBack()" style="cursor: pointer;"></i>&nbsp;&nbsp;EZone Details</span>
            <!-- <router-link :to="{path: `/geozone/notification/create/${app_id}/${g_id}`}">
              <el-button type="success" size="mini">New Zone Notification</el-button>
            </router-link> -->
          </h3>
        </div>

        <el-row>
          <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24" v-for="(loader,index) in [1,2]" :key="'loader' + index" style="margin-bottom: 25px;" v-if="ezoneDetailsIsLoading">
            <content-placeholders>
              <!-- <content-placeholders-heading :img="true" /> -->
              <content-placeholders-text :lines="2" />
            </content-placeholders>
          </el-col>
        </el-row>

        <el-row style="font-size: 14px; margin-bottom: 20px;" class="vertical-center">
          <el-col :xs="24" :sm="5" :md="5" :lg="5" :xl="5">
            <label>EZone Name</label>
            {{ezoneDetails.name}}&nbsp;
          </el-col>
          <el-col :xs="24" :sm="5" :md="5" :lg="5" :xl="5">
            <label>Location</label>
            {{ezoneDetails.location}}&nbsp;
          </el-col>
          <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8">
            <label>EZone Id</label>
            {{ezoneDetails.e_id}}&nbsp;
          </el-col>
          <el-col :xs="24" :sm="3" :md="3" :lg="3" :xl="3">
            <label>Status</label>
            {{ezoneDetails.is_active? 'Active': 'Disabled'}}&nbsp;
          </el-col>
          <el-col :xs="24" :sm="3" :md="3" :lg="3" :xl="3">
            <label>Size</label>
            {{ezoneDetails.radius}}m
          </el-col>
        </el-row>
        <el-row style="font-size: 14px;">
          <!-- <el-col :xs="24" :sm="5" :md="5" :lg="5" :xl="5">
            <label>City</label>
              {{ezoneDetails.city}}&nbsp;
            </el-col> -->
            <!-- <el-col :xs="24" :sm="5" :md="5" :lg="5" :xl="5">
            <label>State</label>
              {{ezoneDetails.state}}&nbsp;
            </el-col> -->
            <el-col :xs="24" :sm="5" :md="5" :lg="5" :xl="5">
            <label>Valid till</label>
              {{ezoneDetails.expiry_timestamp | formatDate }}&nbsp;
            </el-col>
            <el-col :xs="24" :sm="6" :md="6" :lg="6" :xl="6">
              <label>Loitering Delay</label>
              {{ezoneDetails.loitering_delay}}&nbsp;
            </el-col>
        </el-row>
      </el-card>



    <el-card class="box-card" style="margin:10px auto; max-width: 80vw;">
      <div slot="header" class="clearfix">
        <h3 style="margin: 0px; display: flex; align-items: center;">
          <span @click="redirectToBack()" style="cursor: pointer">{{ezoneDetails.name}}
          <i class="el-icon-arrow-right"></i> &nbsp;</span>
          <span style="flex-grow: 1; color: #66b1ff;">Stores</span>
          <router-link :to="{path: `/store-create/${app_id}/${g_id}/${e_id}`}">
            <el-button type="success" size="mini">New Store</el-button>
          </router-link>
        </h3>
      </div>
      <el-row>
        <el-col :xs="24" :sm="24" :md="12" :lg="24" :xl="24" v-for="(profile, index) in [1,2,3]" :key="'loader' +index" style="padding: 10px;" v-if="isLoading">
          <div style="margin-bottom: 30px;">
            <content-placeholders>
              <content-placeholders-heading :img="true" />
              <content-placeholders-text :lines="2" />
            </content-placeholders>
          </div>
        </el-col>

        <el-row class="vertical-center margin-bottom-10">
          <el-col :span="24" v-if="resultLength0 && !isLoading">
            <h5 style="color: #666;"> No Result Found...</h5>
          </el-col>
        </el-row>

        <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24" v-for="(store, index) in stores" :key="index" style="padding: 30px 10px;">
          <!-- <el-card class="box-card"> -->

            <el-dialog title="Add Location" :visible.sync="dialogVisible">
              <input type="text" v-model="location" placeholder="latitude, longitude">
              <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">Cancel</el-button>
                <el-button type="primary" @click="updateLocation()">Confirm</el-button>
              </span>
            </el-dialog>

            <div class="text item">

              <el-row class="vertical-center text-left" style="background-color: #F1F1F1; padding: 10px 15px;">
                <el-col :span="8">
                  <h4 style="margin: 0px;"> Attribute </h4>
                </el-col>

                <el-col :span="16">
                  <h4 style="margin: 0px;"> Value </h4>
                </el-col>
              </el-row>

              <el-row class="vertical-center" style="font-size: 14px; border-bottom: 1px solid #F1F1F1; padding: 10px;" v-for="(store_attr, index) in store.store_attributes" :key="index">
                <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8" class="bold">
                  {{store_attr.display_name}}
                </el-col>
                <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16">
                  {{store_attr.value}}
                </el-col>
              </el-row>

              <div style="font-size: 0.8em; padding: 10px; border-bottom: 1px solid #F1F1F1;">
                <span style="flex-grow: 1;">{{store.store.uuid}}</span>
              </div>

              <div style="font-size: 0.8em;">
                <el-button type="text" style="padding-bottom: 0px; margin-left: 10px;"  @click.native="redirectToStoreNodification(store.store.uuid)">Notification</el-button>
                <el-button type="text" style="padding-bottom: 0px;"  @click.native="edit(index)">Edit</el-button>
                <el-button type="text" style="padding-bottom: 0px;"  @click.native="remove(index)">Delete</el-button>
                <el-button type="text" style="padding-bottom: 0px;"  @click.native="openDialog(index)">Update Location</el-button>
                <el-button type="text" style="padding-bottom: 0px;"  @click.native="redirectToBrandManagement(store.store.uuid)">Manage Brands</el-button>
                <el-button type="text" style="padding-bottom: 0px;"  @click.native="redirectToStoreManagement(store.store.uuid)">Manage Staff</el-button>
              </div>
            </div>
        </el-col>
      </el-row>
    </el-card>
  </div>
</template>

<script>
import { serverAddr } from '../networkConfig.js';
import CripNotice from 'crip-vue-notice';
import TopBar from "./TopBar";

export default {
  name: "StoreList",
  components: {TopBar},
  props: ["app_id", "g_id", "e_id"],
  data(){
    return {
      ezoneDetails: {},
      ezoneDetailsIsLoading: false,
      resultLength0: false,
      isLoading: false,
      stores: [],
      selected_id: '',
      selected_gid: '',
      selected_eid: '',
      location: '',
      dialogVisible: false
    }
  },
  created(){
    this.isLoading = true;
    this.resultLength0 = false;
    serverAddr.post('/v1/store/', {'app_id':this.app_id, 'g_id':this.g_id, 'e_id':this.e_id})
    .then(this.getStoreCallback)
    .catch(function (error) {
      console.log(error);
    })

    this.ezoneDetailsIsLoading = true;
    serverAddr.post('/v1/engagement-zone/get/', {'app_id':this.app_id, 'g_id':this.g_id, 'e_id':this.e_id})
    .then((response)=>{
      this.ezoneDetailsIsLoading = false;
      this.ezoneDetails = response.data.data[0];
    })
    .catch(function (error) {
      console.log(error);
    })
  },
  methods:{
    getStoreCallback: function(response){
      this.isLoading = false;
      this.stores = response.data.data;
      if(!this.stores.length){
        this.resultLength0 = true;
      }
    },
    edit: function(index){
      console.log('edit store', index);
      this.$router.push({path: `/store-create/${this.app_id}/${this.g_id}/${this.e_id}/${this.stores[index].store.uuid}`})
    },
    openDialog: function(index){
      console.log('this.stores[index]', this.stores[index]);
      this.selected_gid = this.stores[index].g_id;
      this.selected_eid = this.stores[index].e_id;
      this.selected_id = this.stores[index].store.uuid;
      this.dialogVisible = true;
    },
    updateLocation: function(){
      this.dialogVisible = false;
      serverAddr.post('/v1/store/update-location/',
      {
        app_id: this.app_id,
        st_id:this.selected_id,
        g_id: this.selected_gid,
        e_id: this.selected_eid,
        properties:{
          location: this.location
        }
      })
      .then(this.postUpdateCallback)
      .catch(function (error) {
        console.log(error);
      })
    },
    remove: function(index) {
    serverAddr.post('/v1/store/delete/',{app_id:this.app_id, g_id:this.g_id,  e_id:this.e_id, st_id:this.stores[index].st_id})
      .then(this.postRemoveCallback)
      .catch(function (error) {
        console.log(error);
      })
    },
    postUpdateCallback: function(response){
      if(response.data.status){
        this.location = '';
        new CripNotice({
          title: "Success",
          description: "Location Updated successfully.",
          className: "notices",
          type: "success",
          duration: 3,
        });
      }
    },
    postRemoveCallback: function(response){
      if(response.data.status){
        new CripNotice({
          title: "Success",
          description: "Store deleted successfully.",
          className: "notices",
          type: "success",
          duration: 3,
        });
        serverAddr.post('/v1/store/', {'app_id':this.app_id, 'g_id':this.g_id, 'e_id':this.e_id})
        .then(this.getStoreCallback)
        .catch(function (error) {
          console.log(error);
        })
      }
    },
    redirectToStoreNodification: function(st_id){
      this.$router.push({path: `/store/notification-list/${this.app_id}/${this.e_id}/${st_id}`})
    },
    redirectToStoreManagement: function(st_id){
      this.$router.push({path: `/store/staff-list/${this.app_id}/${st_id}`})
    },
    redirectToBrandManagement: function(st_id){
      this.$router.push({path: `/store/brand-list/${this.app_id}/${this.e_id}/${st_id}`})
    },
    redirectToBack(){
      this.$router.push({path: `/engagement-zone-list/${this.app_id}/${this.g_id}`})
    }
  }
}
</script>
<style scoped>
.StoreList{
    font-size: 14px;
}
  label{
      display: block;
      font-weight: bold;
      font-size: 0.9em;
      margin-top: 10px;
    }
</style>



