<template>
  <div class="RoleList">
    <TopBar></TopBar>

    <div>
      <el-card class="box-card" style="max-width:80vw; margin:10px auto;">
        <div slot="header" class="clearfix">
          <h3 style="margin:auto"> Roles </h3>
        </div>
        <div class="text item">
          <el-row class="vertical-center margin-bottom-10" v-for="(role, index) in roles" :key="index">
            <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8">
              {{role.name}}
            </el-col>
            <el-col :xs="24" :sm="10" :md="10" :lg="10" :xl="10">
              {{role.r_id}}
            </el-col>
            <!-- <el-col :xs="24" :sm="6" :md="6" :lg="6" :xl="6">
              <span>
                <el-button type="primary" size="mini" @click.native="edit(index)">Edit</el-button>
              </span>
              <span>
                <el-button type="primary" size="mini" @click.native="remove(index)">Delete</el-button>
              </span>
            </el-col> -->
          </el-row>
        </div>
      </el-card>
    </div>

  </div>
</template>

<script>
import { serverAddr } from '../networkConfig.js';
import CripNotice from 'crip-vue-notice';
import TopBar from "./TopBar";

export default {
  name: "RoleList",
  components: { TopBar },
  data() {
    return {
      roles: []
    }
  },
  created() {
    serverAddr.post('/v1/get-platform-roles/')
    .then(this.getRoleCallback)
    .catch(function (error) {
      console.log(error);
    })
  },
  methods: {
    getRoleCallback: function (response) {
      if(response.data.status){
        this.roles = response.data.data;
      }
    },
    edit: function (index) {
      var id = this.roles[index].r_id;
      this.$router.push({ path: `/role-create/${id}` })
    },
    remove: function(index) {
    serverAddr.post('/v1/role/delete/',{r_id:this.roles[index].r_id})
      .then(this.postRemoveCallback)
      .catch(function (error) {
        console.log(error);
      })
    },
    postRemoveCallback: function(response){
      if(response.data.status){
        new CripNotice({
          title: "Success",
          description: "Role deleted successfully.",
          className: "notices",
          type: "success",
          duration: 3,
        });
        serverAddr.post('/v1/role/')
        .then(this.getRoleCallback)
        .catch(function (error) {
          console.log(error);
        })
      }
    }
  }
}
</script>



