<template>
  <div class="BrandCreate">
    <TopBar></TopBar>

    <el-card class="box-card" style="max-width:50vw; margin:0 auto;">

      <el-row>
        <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24" style="margin-bottom: 25px;" v-for="(loader,index) in [1,2,3,4]" :key="'loader' + index" v-if="isLoader">
          <content-placeholders>
            <!-- <content-placeholders-heading :img="true" /> -->
            <content-placeholders-text :lines="2" />
          </content-placeholders>
        </el-col>
      </el-row>

      <div class="text item" v-if="!isLoader">
        <el-row class="vertical-center" style="text-align:left;">
          <el-col :xs="24" :sm="6" :md="6" :lg="6" :xl="6" style="padding: 8px 0px;">
            Brand name
          </el-col>
          <el-col :xs="24" :sm="18" :md="18" :lg="18" :xl="18">
            <div class="form-group" :class="{ 'form-group--error': $v.name.$error }" style="padding: 10px 20px;">
              <input style="width:90%; padding: 10px 15px;" type="text" v-model="name" class="form__input" @input="setName($event.target.value)">
            </div>
            <div class="error" v-if="!$v.name.required">Field is required</div>
          </el-col>
        </el-row>

        <el-row class="vertical-center" style="text-align:left;">
          <el-col :xs="24" :sm="6" :md="6" :lg="6" :xl="6" style="padding: 8px 0px;">
            Custom Brand Id
          </el-col>
          <el-col :xs="24" :sm="18" :md="18" :lg="18" :xl="18">
            <div class="form-group" :class="{ 'form-group--error': $v.custom_brand_id.$error }" style="padding: 10px 20px;">
              <input style="width:90%; padding: 10px 15px;" type="text" v-model="custom_brand_id" class="form__input" @input="setCustomBrandId($event.target.value)">
            </div>
            <div class="error" v-if="!$v.custom_brand_id.required">Field is required</div>
          </el-col>
        </el-row>


        <el-row class="vertical-center" style="text-align:left;">
          <el-col :xs="24" :sm="6" :md="6" :lg="6" :xl="6" style="padding: 8px 0px;">
            Brand Weight
          </el-col>
          <el-col :xs="24" :sm="18" :md="18" :lg="18" :xl="18">
            <div class="form-group" :class="{ 'form-group--error': $v.weight.$error }" style="padding: 10px 20px;">
              <input style="width:90%; padding: 10px 15px;" type="text" v-model="weight" class="form__input" @input="setWeight($event.target.value)">
            </div>
            <!-- changed input type to number to send weight in number -->
            <div class="error" v-if="!$v.weight.required">Field is required</div>
          </el-col>
        </el-row>
        <el-row class="vertical-center" style="text-align:left;">
          <el-col :xs="24" :sm="6" :md="6" :lg="6" :xl="6">
            <el-button type="primary" @click.native="save()" style="margin: 20px 0px;">Create Brand</el-button>
          </el-col>
        </el-row>
      </div>
    </el-card>

  </div>
</template>

<script>
import { serverAddr } from '../networkConfig.js';
import CripNotice from 'crip-vue-notice';
import TopBar from "./TopBar";
import { required } from 'vuelidate/lib/validators'

export default {
  name: "BrandCreate",
  components: { TopBar },
  props: ["app_id", "id"],
  data() {
    return {
      isLoader : true,
      name: '',
      custom_brand_id: '',
      weight: '1' // remove '' to send weight in number
    }
  },
  validations: {
    name: {
      required
    },
    custom_brand_id: {
      required
    },
    weight: {
      required
    }
  },
  created() {
    if (this.id) {
      serverAddr.post('/v1/brand/get/', {
        app_id: this.app_id,
        br_id: this.id
      })
      .then(this.getBrandCallBack)
      .catch(function (error) {
        console.log(error);
      })
    } else {
      this.isLoader = false;
    }
  },
  methods: {
    setName(value) {
      this.name = value
      this.$v.name.$touch()
    },
    setCustomBrandId(value) {
      this.custom_brand_id = value
      this.$v.custom_brand_id.$touch()
    },
    setWeight(value) {
      this.weight = value
      this.$v.weight.$touch()
    },
    getBrandCallBack: function (response) {
      this.isLoader = false;
      this.name = response.data.data[0].name;
      this.custom_brand_id = response.data.data[0].custom_brand_id;
      this.weight = response.data.data[0].weight;
    },
    save: function () {
      this.$v.$touch();
      if (this.$v.$invalid) {
      } else {
        if (!this.id) {
          serverAddr.post('/v1/brand/create/', {
            app_id: this.app_id,
            properties: {
              name: this.name,
              custom_brand_id: this.custom_brand_id,
              weight: this.weight
            }
          })
            .then(this.postSaveCallBack)
            .catch(function (error) {
              console.log(error);
            })
        } else {
          serverAddr.post('/v1/brand/update/', {
            br_id: this.id,
            app_id: this.app_id,
            properties: {
              name: this.name,
              custom_brand_id: this.custom_brand_id,
              weight: this.weight
            }
          })
          .then(this.postSaveCallBack)
          .catch(function (error) {
            console.log(error);
          })
        }
      }
    },
    postSaveCallBack: function (response) {
      console.log("Response: ", response);
      if (response.data.status) {
        this.$router.go(-1);
        new CripNotice({
          title: "Success",
          description: "Brand created successfully.",
          className: "notices",
          type: "success",
          duration: 3,
        });
      }
      this.$router.push({path:`/brand-list/${this.app_id}`});
    }
  },
}
</script>

<style scoped>
.center_vertically{
  display: flex;
  align-items: center;
}
</style>



