<template>
  <div class="CustomerCareGetAssignedStoreList">
    <TopBar></TopBar>

    <el-card class="box-card" style="max-width:80vw; margin:10px auto;">
        <div class="clearfix">
          <h3 style="margin: 0px; display: flex; align-items: center;">
            <span @click="redirectToBack">{{username}} > &nbsp;</span>
            <span style="flex-grow: 1; color: #66b1ff;">Assigned stores</span>
            <router-link :to="{path: `/customer-care-assign-store/${app_id}/${p_id}/${username}`}">
              <el-button type="success" size="mini">Assign new store</el-button>
            </router-link>
          </h3>
        </div>
      </el-card>

    <el-card class="box-card card-row" style="max-width:80vw; margin:10px auto;" v-for="(loader,index) in [1,2,3,4]" :key="'loader' + index" v-if="isLoading">

      <el-row>
        <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24" style="margin-bottom: 25px;">
          <content-placeholders>
            <!-- <content-placeholders-heading :img="true" /> -->
            <content-placeholders-text :lines="2" />
          </content-placeholders>
        </el-col>
      </el-row>

    </el-card>

    <el-row class="card-row" v-if="!isLoading && !assignedStoreList.length">
      <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24" style="text-align: center;">
        <div style="border: 1px solid #000; padding: 10px; text-align: center; margin: 20px auto; display: inline-block;">
          No stores assigned
        </div>
      </el-col>
    </el-row>

    <div v-for="(store, index) in assignedStoreList" :key="index" v-if="!isLoading">
      <el-card class="box-card" style="max-width:80vw; margin:10px auto;">


        <el-row class="card-row">
          <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24" style="text-align: right;">
            <span>
              <span style="color: #409EFF; cursor: pointer;" @click="unSubscribeToStore(store.st_id)">Unsubscribe</span>
            </span>
          </el-col>
        </el-row>

        <el-row class="card-row" style="border-bottom: 1px solid #F1F1F1;">
          <el-col :xs="8" :sm="8" :md="8" :lg="8" :xl="8">
              Store id:
          </el-col>
          <el-col :xs="16" :sm="16" :md="16" :lg="16" :xl="16">
              {{store.st_id}}
          </el-col>
        </el-row>


        <el-row class="vertical-center card-row" v-for="(store_attr, index1) in store.store_attributes" :key="index1">
          <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8" v-if="store_attr.display_name == 'City' || store_attr.display_name == 'Country' || store_attr.display_name == 'State' || store_attr.display_name == 'Name'" style="border-bottom: 1px solid #F1F1F1;">
            {{store_attr.display_name}}
          </el-col>
          <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16" v-if="store_attr.display_name == 'City' || store_attr.display_name == 'Country' || store_attr.display_name == 'State' || store_attr.display_name == 'Name'" style="border-bottom: 1px solid #F1F1F1;">
            {{store_attr.value}}
          </el-col>
        </el-row>

      </el-card>
    </div>

  </div>
</template>

<script>

import { serverAddr } from '../networkConfig.js';
import CripNotice from 'crip-vue-notice';
import TopBar from "./TopBar";

export default {
  name: 'CustomerCareGetAssignedStoreList',
  components: {TopBar},
  props: ["app_id", "p_id", "username"],
   data(){
    return{
      isLoading: false,
      assignedStoreList: []
    }
  },
  created(){
    this.getAssignedStoreList();
    this.isLoading = true;
  },
  methods: {
    getAssignedStoreList(){
        serverAddr
        .post("/v1/profile/customer-care-admin/get-assigned-store/", {app_id: this.app_id, p_id: this.p_id})
        .then(response => {
          if(response.data.status){
            console.log('getAssignedStoreList', response);
              this.assignedStoreList = response.data.data;
              this.isLoading = false;
            }
        })
        .catch(function(error) {
            console.log(error);
            this.isLoading = false;
        });

    },
    unSubscribeToStore(st_id){
      console.log('unSubscribeToStore: ', st_id);
      this.isLoading = true;
      serverAddr
        .post("/v1/profile/customer-care-admin/remove-store/",
        {
          app_id: this.app_id,
          p_id: this.p_id,
          st_id: st_id
        })
        .then(response => {
          if(response.data.status){
            this.getAssignedStoreList()
            this.isLoading = false;
          }
        })
        .catch(function(error) {
            console.log(error);
            this.isLoading = false;
        });
    },
    redirectToBack() {
      this.$router.go(-1);
    },
  }
}
</script>
<style>
.card-row .el-col{
    padding: 10px 0;
  }
</style>



