<template>
  <div class="ServiceCreate text-center">
    <TopBar></TopBar>

    <el-card class="box-card" style="max-width:50vw; margin:0 auto;">
      <div class="text item">
        <el-row class="vertical-center" style="text-align:center;">
          <el-col :xs="24" :sm="4" :md="4" :lg="4" :xl="4" style="vertical-align: middle;">
            Service name:
          </el-col>
          <el-col :xs="24" :sm="20" :md="12" :lg="12" :xl="12">
            <input style="width:90%;" type="text" v-model="name">
          </el-col>
          <el-col :xs="24" :sm="24" :md="8" :lg="8" :xl="8">
            <el-button type="danger" size="mini" @click.native="addSA()">Add Service Attribute</el-button>
          </el-col>
        </el-row>
        <div class="text item" style="margin: 20px  0 0;">
        <el-button type="primary" size="mini" @click.native="save()">Create Service</el-button>
        </div>

      </div>
    </el-card>

    <div style="margin:10px;">
      <el-row :gutter="10">
        <el-col style="margin-top:10px;" :xs="24" :sm="6" :md="6" :lg="6" :xl="6" v-for="(sa,index) in service_attributes" :key="index">
          <el-card class="box-card">
            <div slot="header" class="clearfix">
              <span>Service Attribute {{index+1}}: </span>
              <span style="float: right; cursor: pointer;" v-on:click="RemoveAttr(index)">&#x2715;</span>
            </div>
            <div class="text item">
              <el-row class="vertical-center" :gutter="10">
                <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
                   <p class="form-label">Key</p>
                    <input type="text" v-model="sa.key">
                </el-col>
                <!-- <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16">
                </el-col> -->
              </el-row>
              <el-row class="vertical-center" :gutter="10">
                <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
                  <p class="form-label">display name</p>
                  <input type="text" v-model="sa.display_name">
                </el-col>
                <!-- <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16" class="text-left">

                </el-col> -->
              </el-row>
              <el-row class="vertical-center" :gutter="10">
                <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
                  <p class="form-label">value type</p>
                  <select v-model="sa.value_type">
                    <option value="str">String</option>
                    <option value="boolean">Boolean</option>
                    <option value="int">Number</option>
                    <option value="float">Float</option>
                    <option value="date">Date Field</option>
                    <option value="datetime">DateTime Field</option>
                    <option value="password">Password</option>
                  </select>
                </el-col>
                <!-- <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16" class="text-left">
                </el-col> -->
              </el-row>
              <el-row class="vertical-center" :gutter="10">
                <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
                  <p class="form-label">value</p>
                  <input type="text" v-model="sa.value">
                </el-col>
                <!-- <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16" class="text-left">
                </el-col> -->
              </el-row>
              <el-row class="vertical-center margin-top-20" :gutter="10">
                <el-col :xs="24" :sm="12" :md="12" :lg="12" :xl="12" class="text-right">
                  <p class="form-label">Required?</p>
                  <input name="isRequired" type="radio" :value="true" v-model="required"> Yes
                  <input name="isRequired" type="radio" :value="false" v-model="required"> No
                </el-col>
                <!-- <el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16">
                  <span style="margin-left:50px;">
                    <input name="isRequired" type="radio" :value="true" v-model="sa.required"> Yes
                  </span>
                  <span style="margin-left:50px;">
                    <input name="isRequired" type="radio" :value="false" v-model="sa.required"> No
                  </span>
                </el-col> -->
              </el-row>
            </div>
          </el-card>
        </el-col>
                  <!-- <el-button type="primary" @click.native="save()">Create Service</el-button> -->
      </el-row>
    </div>


  </div>
</template>

<script>
import { serverAddr } from '../networkConfig.js';
import CripNotice from 'crip-vue-notice';
import TopBar from "./TopBar";

export default {
  name: "ServiceCreate",
  components: { TopBar },
  props: ["id"],
  data() {
    return {
      name: '',
      service_attributes: []
    }
  },
  created() {
    if (this.id) {
      serverAddr.post('/v1/service/get/', {
        s_id: this.id
      })
      .then(this.getServiceCallBack)
      .catch(function (error) {
        console.log(error);
      })
    }
  },
  methods: {
    getServiceCallBack: function (response) {
      if (response.data.status) {
        this.name = response.data.data[0].name;
        this.service_attributes = response.data.data[0].service_attributes;
      }
    },
    addSA: function () {
      var temp = {
        key: '',
        display_name: '',
        value: '',
        value_type: '',
        required: false
      }
      this.service_attributes.push(temp);
    },
    RemoveAttr: function (index) {
      this.service_attributes.splice(index, 1);
    },
    save: function () {
      var service = {
        name: this.name
      };
      if (this.id) {
        service.s_id = this.id;
      };
      var bool = true;
      if (!this.name) {
        bool = false;
        alert("Please fill service name");
      }
      for (var i = 0; i < this.service_attributes.length; i++) {
        if (!this.service_attributes[i].key || !this.service_attributes[i].value || !this.service_attributes[i].display_name || !this.service_attributes[i].value_type) {
          bool = false;
          alert("Please fill all the fields for service_attributes");
          break;
        }
      }
      if (this.name && bool) {
        if (!this.id) {
          serverAddr.post('/v1/service/create/', {
            service: service,
            service_attributes: this.service_attributes
          })
          .then(this.postSaveCallBack)
          .catch(function (error) {
            console.log(error);
          })
        } else {
          serverAddr.post('/v1/service/update/', {
            service: service,
            service_attributes: this.service_attributes
          })
          .then(this.postSaveCallBack)
          .catch(function (error) {
            console.log(error);
          })
        }
      }
    },
    postSaveCallBack: function (response) {
      console.log("Response: ", response);
      if (response.data.status) {
        new CripNotice({
          title: "Success",
          description: "Service created successfully.",
          className: "notices",
          type: "success",
          duration: 3,
        });
        this.$router.push({path:"/service-list"});
      }
    }
  }
}
</script>
<style scoped>
.form-label{
    /* font-weight: bold; */
    color: #777;
    font-size: 0.8em;
    margin-bottom: 8px;
    /* margin-top: 20px; */
    text-align: left;
}
</style>



